import { Input, instantiate, Label, Node, Size, Sprite, UITransform, Vec3 } from "cc";
import { Player } from "../../../World/2.Td/Player/Player";
import { CfgBuff, CfgBuffItem } from "../../../../Common/Cfg/CfgBuff";
import { UiBubble } from "../../../../Common/Ui/UiBubble";
import { UiMain } from "../UiMain";
import { BuffString } from "../../../World/1.Base/Buff/BuffString";
import { PfMgrIconBuff } from "../../../../Common/PfMgr/PfMgrIconBuff";
import { NodeSizeFit } from "../../../../SL/Cocos/Ui/NodeSizeFit";

// 
export class UiFeature {

    public static ins : UiFeature ; 

    public root : Node ; 

    public content : Node ; 

    public items = new Array< UiFeatureItem >() ; 

    public init( node : Node ) {

        this.root = node ; 
        this.root.active = true ; 

        this.content = this.root.getChildByPath( "sv/view/content" ) ;

        // 
        for( let i = 0 ; i < this.content.children.length ; ++i ) {
            let pfItem = this.content.children[ i ] ;
            this.pfItems.push( pfItem ) ;
            pfItem.active = false ;
        } // end for

    }

    public refresh() {

        this.refresh2() ; 

    }

    // 
    public refresh2() {

        this.clear() ; 

        let list = new Array< [ CfgBuffItem , number ] >() ;

        //  
        let buffList = Player.ins.buff.list ; 
        for( let i = 0 ; i < buffList.length ; ++i ) {

            let buff = buffList[ i ] ;
            let cfgBuffItem = buff.cfg ;

            let item = list.find( ( e )=>{ return e[ 0 ].id == cfgBuffItem.id ; } ) ; 
            if( item == null ) {
                item = [ cfgBuffItem , 1 ] ;
                list.push( item ) ;
            }
            else {
                item[ 1 ]++ ; 
            }

        } // end for

        // 
        for( let i = 0 ; i < list.length ; ++i ) {

            let item = list[ i ] ;

            let cfg = item[ 0 ] ; 
            let count = item[ 1 ] ;

            let root = instantiate( this.pfItems[ cfg.grade - 1 ] ) ;
            root.setParent( this.content , false ) ;
            root.active = true ; 

            let uiItem = new UiFeatureItem() ; 
            uiItem.cfg = cfg ; 
            uiItem.init( root ) ; 
            this.items.push( uiItem ) ;
            uiItem.refresh() ; 
            uiItem.refreshCount( count ) ; 

        } // end for


    }

    // Buff
    public refresh1() {

        this.clear() ; 

        let buffList = Player.ins.buff.list ; 
        for( let i = 0 ; i < buffList.length ; ++i ) {

            let buff = buffList[ i ] ;

            let grade = buff.cfg.grade ; 

            let root = instantiate( this.pfItems[ grade - 1 ] ) ;
            root.setParent( this.content , false ) ;
            root.active = true ; 

            let uiItem = new UiFeatureItem() ; 
            uiItem.cfg = buff.cfg ; 
            uiItem.init( root ) ; 
            this.items.push( uiItem ) ;
            uiItem.refresh() ; 

        } // end for

    }

    public clear() {

        for( let ui of this.items ) {
            ui.root.destroy() ; 
        }
        this.items.splice( 0 , this.items.length ) ; 

    }

    private pfItems = Array< Node >() ;

}

export class UiFeatureItem {

    public cfg : CfgBuffItem ; 

    public root : Node ; 

    public icon : Sprite ; 

    public count : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon = node.getChildByPath( "Icon" ).getComponent( Sprite ) ;
        this.iconSize = this.icon.getComponent( UITransform ).contentSize.clone() ;

        this.count = this.root.getChildByPath( "Count" ).getComponentInChildren( Label ) ;
        this.count.node.parent.active = false ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ;

    }

    private onClick() {

        UiBubble.loadAndCreate( ( ui )=>{

            ui.clickToDestroy() ; 

            let parent = UiMain.ins.root ; 
            let pos = this.root.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ;
            parent.getComponent( UITransform ).convertToNodeSpaceAR( pos , pos ) ;
            pos.y += 80 ;

            ui.root.setParent( parent , false ) ; 

            ui.root.position = pos ; 
            ui.setString( BuffString.getDescription( this.cfg.id ) ) ;
            
        } ) ;

    }

    public refresh() {

        let buffId = this.cfg.id ; 

        // 
        let spr = PfMgrIconBuff.ins.getIconById( buffId ) ;
        this.icon.spriteFrame = spr.spriteFrame ; 

        // 
        NodeSizeFit.trimmed( this.icon , this.iconSize ) ;

    }

    public refreshCount( n : number ) {

        this.count.node.parent.active = n > 1 ; 

        this.count.string = "x " + n ; 

    }

    private iconSize : Size ; 

}