import { math } from "cc";

    
    //   
    export class FrameSecConvert
    {
        // FPS
        public static fps = 60 ;

        // 
        public static toFrame( second : number ) : number {

            return Math.floor( this.fps * second ) ;

         }

        /*
            
            /  /         
        */ 
        public static toSecond( frame : number ) : number {

            return frame / this.fps ;

        }

        ////   
        //public static speedPerSecondToSpeedPerFrame( speedPerSecond )
        //{

        //    return speedPerSecond / this.fps;

        //}
    }