
// 
export class CfgItemDraw {

    public static data = new Map< string , CfgItemDrawItem >() ; 

}

export class CfgItemDrawItem {

    public id : string ; 

    public min = 0 ; 

    public max = 0 ;

    public items = new Array< CfgItemDrawItemItem >() ;

}


export class CfgItemDrawItemItem {

    // ID
    public id : string = null ; 

    // 
    public w = 0 ; 

}

export class CfgItemDrawParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgItemDrawItem() ;

            CfgItemDraw.data.set( id , cfgItem ) ;

            cfgItem.id = id ;
            cfgItem.min = Number(row["Min"]) ; 
            cfgItem.max = Number(row["Max"]) ; 

            for( let i = 1 ; ; ++i ) {

                let key = "Item" + i ;
                let str = row[ key ] as string ; 
                if( str == null || str == "" ) break ; 

                let strs = str.split( "," ) ; 
                if( strs.length < 2 ) continue ; 

                let cfgItemItem = new CfgItemDrawItemItem() ; 
                cfgItem.items.push( cfgItemItem ) ; 
                cfgItemItem.id = strs[ 0 ].trim() ; 
                cfgItemItem.w = Number( strs[ 1 ] ) ; 

            } // end for

        } // end for

    }

}