import { Label, Node } from "cc";
import { LevelCtrl } from "../../World/2.Td/Level/LevelCtrl";


export class UiWaveInfo {

    public static ins : UiWaveInfo = null ; 

    public root : Node ; 

    public txt : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.txt = this.root.getComponent( Label ) ; 

    }

    public refresh() {

        let max = LevelCtrl.ins.levelData.waves.length - 1 ; 

        let current = LevelCtrl.ins.currentWaveIdx ; 

        let str = " {current}/{max} " ; 
        str = str.replace( "{current}" , current.toString() ) ;
        str = str.replace( "{max}" , max.toString() ) ;
        this.txt.string = str ;

    }

}