
export class CfgGeneral {

    public static ins = new CfgGeneral() ;

    //  
    public BagInitDeployedIds = new Array< string >() ; 

    //  
    public BagInitUnlockedIds = new Array< string >() ; 

    //  
    public BagAllArticleIds = new Array< string >() ; 

    //  [  ,  ]
    public bagDeployCellUnlocked = new Map< number , number >( [ [ 6 , 2 ] , [ 7 , 4 ] ]) ;

}

export class CfgGeneralParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] as string ;
            if( id == null || id == "" ) continue ; 
            let val = row[ "Val" ] as string ; 

            switch( id ) {

                case "BagInitDeployedIds" : {
                    let strs = val.split( "," ).filter(Boolean) ; 
                    CfgGeneral.ins.BagInitDeployedIds.push( ... strs ); 
                }
                break ; 

                case "BagInitUnlockedIds" : {
                    let strs = val.split( "," ).filter(Boolean) ; 
                    CfgGeneral.ins.BagInitUnlockedIds.push( ... strs ); 
                }
                break ; 

                case "BagAllArticleIds" : {
                    let strs = val.split( "," ).filter(Boolean);
                    CfgGeneral.ins.BagAllArticleIds.push( ... strs ); 
                }
                break ; 

            }



        } // endf

    }

}