import { sys } from "cc";
import { AdBase } from "./AdBase";
import { AdWx } from "./AdWx";
import { AdTt } from "./AdTt";
import { AdGp } from "./AdGp";

// 
export class Ad extends AdBase  {

    public static ins : Ad = null ;

    public static createIns() {

        switch( sys.platform ) {

            case sys.Platform.WECHAT_GAME : { 

                // this.ins = new AdWx() ; 
                this.ins = new AdBase() ; 
                this.ins.init() ; 

            }
            break ; 

            case sys.Platform.BYTEDANCE_MINI_GAME : {

                this.ins = new AdTt() ; 
                this.ins.init() ; 

            }
            break ; 

            case sys.Platform.ANDROID : {

                this.ins = new AdGp() ; 
                this.ins.init() ; 
                
            }
            break ; 

            default : {

                this.ins = new AdBase() ; 
                this.ins.init() ; 
                
            }
            break ;



        }

    }


}

