import { UITransform, Vec3 } from "cc";
import { CfgBuff, CfgBuffItem } from "../../../../Common/Cfg/CfgBuff";
import { MoneyFly } from "../../../../Common/Misc/MoneyFly";
import { AffixType } from "../../../World/1.Base/Buff/Affix";
import { BuffFactory } from "../../../World/1.Base/Buff/BuffFactory";
import { TowerAllUpdate } from "../../../World/1.Base/Tower/Misc/TowerAllUpdate";
import { Player } from "../../../World/2.Td/Player/Player";
import { PlayerBuffAffect } from "../../../World/2.Td/Player/PlayerBuffAffect";
import { UiMoney001 } from "../../Main/UiMoney001";
import { UiMain } from "../../Main/UiMain";
import { UiFeature } from "../../Main/Feature/UiFeature";
import { FeatureExtract } from "./FeatureExtract";
import { MergeDefinition } from "../../../Bag/Merge/MergeDefinition";
import { PlayerGradeOp } from "../../../World/2.Td/Player/PlayerGradeOp";
import { Buff_RndArticleMergeGradeUpgrade } from "../../../World/1.Base/Buff/Specific/Buff_RndArticleMergeGradeUpgrade";

// 
export class FeatureAcquire {

    public static exe( ids : Array< string > ) {

        for( let i = 0 ; i < ids.length ; ++i ) {

            let id = ids[ i ] ;

            let b = this.now( id ) ;
            if( b == true ) continue ; 

            let buff = BuffFactory.create( id ) ;
            Player.ins.buff.list.push( buff ) ; 

            FeatureExtract.countLimit( id ) ; 

        }


        // 
        TowerAllUpdate.updateAll() ; 

        // 
        PlayerBuffAffect.affectAllRoles() ; 

        UiFeature.ins.refresh() ; 

    }

    /*
         
        Buff
    */ 
    public static now( id : string ) {

        let cfg = CfgBuff.data.get( id ) ;
        if( cfg == null ) return ;

        switch( cfg.affixType ) {

            case AffixType.GetMomeyNow : {

                // 

                // 
                let pos2 = UiMoney001.ins.icon.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ;
                UiMain.ins.root.getComponent( UITransform ).convertToNodeSpaceAR( pos2 , pos2 ) ;
                MoneyFly.money001Fly( UiMain.ins.root , Vec3.ZERO , pos2 ) ; 

                // 
                Player.ins.money001 += cfg.affixArgN ; 
                UiMoney001.ins.refresh() ; 

                return true ; 

            }
            break ; 

            
            case AffixType.MergeEnable : {
                //  

                let str = cfg.affixArgStr ; 
                let strs = str.split( "," ) ; 
                for( let id of strs ) {
                    MergeDefinition.ins.allowData.set( id , true ) ; 
                }
              
                return true ; 

            }
            break ; 

            case AffixType.ExpIncreasePercent : {

                //  
                PlayerGradeOp.expFactor += cfg.affixArgN ; 

            }
            break ;

            case AffixType.RndArticleMergeGradeUpgrade : {

                Buff_RndArticleMergeGradeUpgrade.exe( ) ; 

                return true ; 
            }
            break ; 

        }

        return false ;

    }



}