

export class ListHelper {

    // 
    public static addUniquely< T >( list : Array< T > , e : T ) {

        let idx = list.indexOf( e ) ; 
        if( idx != -1 ) return ; 

        list.push( e ) ; 

    }

    // 
    public static remove< T >( list : Array< T > , e : T ) {

        let idx = list.indexOf( e ) ; 
        if( idx == -1 ) return ; 
        list.splice( idx , 1 ) ; 

    }

    public static clear< T >( list : Array< T > ) {
        list.splice( 0 , list.length ) ; 
    }

}