import { bezier, instantiate, math, randomRangeInt, tween, Vec3 } from "cc";
import { Bullet, BulletMoveType, BulletRotationType, Sender } from "../../Bullet/Bullet";
import { BulletFactory } from "../../Bullet/BulletFactory";
import { BulletMgr } from "../../Bullet/BulletMgr";
import { WorldNode } from "../../Support/Misc/WorldNode";
import { Tower } from "../Entity/Tower";
import { RoleMgr } from "../../Role/Base/RoleMgr";
import { Role } from "../../Role/Base/Role";
import { BulletMoveCalc } from "../../Bullet/BulletMoveCalc";
import { AttackCrit, AttackTargetSelectionType } from "../Entity/AttackCom";
import { AffixType } from "../../Buff/Affix";
import { TowerAttackTargetSelect } from "./TowerAttackTargetSelect";
import { Buff } from "../../Buff/Buff";
import { Vec2dHelper } from "../../Support/Misc/Vec2dHelper";
import { BezierCalc } from "../../../../../SL/Cocos/Misc/BezierCalc";
import { PathMove } from "../../Ai/PathMove";



export class TowerBulletEmit {

    public static exe( tower : Tower ) {

        // 
        let scatterCount = tower.attack.scatterCount ; 

        // 
        let bullet = this.bulletGen( tower ) ; 

        this.buff( tower , bullet ) ; 

        TowerAttackTargetSelect.exe( tower , bullet ) ;

        //  
        this.view( bullet ) ; 
        
        // 
        this.scatter( tower , bullet ) ; 

        // 
        tower.attack.scatterCount = scatterCount ; 
        
        // 
        if( bullet.target.moveType == BulletMoveType.Boomerang ) {
            this.boomerang( bullet ) ;
        }

    }

    // 
    private static boomerang( bullet : Bullet ) {

        let role = bullet.target.targetRole ; 

        let pos1 = bullet.view.root.position.clone() ; 
        let pos2 = role.view.root.position.clone() ; 

        let v = pos2.clone().subtract( pos1 ) ; 
        // 
        let vn = v.clone().normalize() ;
        pos2.add( vn.clone().multiplyScalar( 50 ) ) ;
        

        v = pos2.clone().subtract( pos1 ) ; 

        // 
        let posM = v.clone().multiplyScalar( 0.5 ).add( pos1 ) ; 

        //  bezier 
        let t = v.x ; 
        v.x = v.y ; v.y = t ;

        let n = randomRangeInt( 0 , 2 ) ; 
        if( n == 0 )  {
            // 
            v.y *= -1 ;
        }
        else {
            // 
            v.x *= -1 
        }


        v.normalize() ;
        v.multiplyScalar( 500 ) ;
        let pos11 = posM.clone().add( v ) ;
        let pos12 = posM.clone().add( v.multiplyScalar( -1) ) ;
        // let node = instantiate( bullet.view.root ) ; 
        // node.setParent( bullet.view.root.parent ) ; 
        // node.position = pos11 ; 

        let path = BezierCalc.bezier2Path( pos1 , pos2 , pos11 , 5 ) ; 
        let path2 = BezierCalc.bezier2Path( pos2 , pos1 , pos12 , 5 ) ; 
        path.push( ... path2 ) ;
        path.splice(0, 0, pos1) ; 

        // for( let pos of path ) {

        //     let node = instantiate( bullet.view.root ) ; 
        //     node.setParent( bullet.view.root.parent ) ; 
        //     node.position = pos ; 

        // } // end for

        bullet.pathMove = new PathMove() ; 
        bullet.pathMove.path = path ; 

    }

    //  
    private static view( bullet : Bullet ) {

        switch( bullet.view.rotationType ) {
            case BulletRotationType.Forever : {
                tween( bullet.view.root ).by( 0.5 , { eulerAngles : new Vec3( 0 , 0 , 360 ) } ).repeatForever().start() ; 
            }
            break ; 
        }

    }

    // 
    private static bulletGen( tower : Tower ) : Bullet {
     
        let pos = tower.article.view.root.worldPosition ; 
        let bullet = BulletFactory.create( tower.attack.bulletId ) ;
        bullet.view.root.setParent( WorldNode.ins.layer2 , false ) ;
        bullet.view.root.worldPosition = pos ; 
        BulletMgr.ins.add( bullet ) ;

        // 

        bullet.damge = tower.attack.damage ; 

        // 
        bullet.crit = new AttackCrit() ; 
        bullet.crit.multiple = tower.attack.crit.multiple ; 
        bullet.crit.probability = tower.attack.crit.probability ; 


        bullet.lifeStealing = tower.attack.lifeStealing ; 

        // 
        bullet.sender = new Sender() ; 
        bullet.sender.id = tower.article.cfg.id ; 

        return bullet ; 

    }

    // 
    public static scatter( tower : Tower , bullet : Bullet ) {

        let dir = bullet.move.direction ; 

        let atk = tower.attack ; 
        if( atk.scatterCount == 0 ) return ; 

        // 
        let interval = 15 ; 
        let degree = interval ; 

        for( let i = 0 ; i < atk.scatterCount ; ++i ) {

            // 
            degree = Math.floor( ( i + 2 ) / 2 ) * interval ; 
            if( i % 2 == 1 ) degree *= -1 ; 

            let bullet2 = this.bulletGen( tower ) ; 
            let dir2 = dir.clone() ; 
            Vec2dHelper.rotate( dir2 , degree ) ; 
    
            BulletMoveCalc.exe( bullet2.move , dir2 ) ; 

        } // end for


    }

    //  Buff 
    public static buff( tower : Tower , bullet : Bullet ) {

        let AttackBulletSizeIncrease = 0 ; 

        for( let buff of tower.buff.list ) {

            switch( buff.affix.type ) {

                case AffixType.AttackCritPerAttackNTimes : {
                    let n = buff.affix.argN ;
                    if( tower.attack.count % n == 0 ) {

                        bullet.crit.probability = 1 ;

                    }
                }
                break ; 

                case AffixType.AttackBulletMoveSpeedIncreasePercent : {

                    // 
                    let n = bullet.cfg.moveSpeed * buff.affix.argN ; 
                    bullet.move.speed += n ; 

                }
                break ; 

                case AffixType.AttackBulletBounceRectEnemyIncrease : {

                    bullet.bounce.bounceRectEnemy += buff.affix.argN ; 

                }
                break ;

                case AffixType.AttackBulletBounceBetweenEnemiesIncrease : 
                {
                    bullet.bounce.bounceBetweenEnemies.count += buff.affix.argN ; 
                }
                break ;

                case AffixType.AttackBulletBounceRectIncrease : {
                    bullet.bounce.bounceRect += buff.affix.argN ; 
                }
                break;

                case AffixType.AttackBulletPassIncrease : {
                    bullet.passCount+= buff.affix.argN ; 
                }   
                break ; 

                case AffixType.AttackBulletPassInfiniteAndBounceRectIncrease : {
                    bullet.passCount+= 9999 ; 
                    bullet.bounce.bounceRect += buff.affix.argN ; 
                } 
                break ;

                case AffixType.AttackBulletScatterIncrease : {
                    tower.attack.scatterCount += buff.affix.argN ; 
                }
                break ;

                case AffixType.AttackBulletScatterIncreaseProbability : {

                    let count = buff.affix.argNList[ 0 ] ;
                    let p = buff.affix.argNList[ 1 ] ;
                    p = p * 100 ;
                    if( randomRangeInt( 0, 100 ) < p ) {
                        tower.attack.scatterCount += count ; 
                    }

                }
                break ;

                case AffixType.AttackBulletAddBuff : 
                {
                    bullet.buffIds.push( buff.affix.argStr ) ;
                }
                break ; 

                case AffixType.AttackBulletAddBuffRadius : 
                {
                    bullet.buffIds.push( buff.cfg.id ) ;
                }
                break ; 

                case AffixType.AttackBulletProbabilityAddBuff : 
                case AffixType.AttackDamageIncreasePercentFullHp : 
                case AffixType.AttackBulletExplodeRadiusIncreasePercent : 
                case AffixType.AttackBulletExplodeDamageIncreasePercent : 
                case AffixType.AttackBulletAddBuffRadiusIncreasePercent : 
                case AffixType.AttackBulletAddBuffRadiusBuffIncreasePercent : 
                case AffixType.AttackBulletPoisoningDamageIncreasePercent :
                case AffixType.AttackBulletPoisoningDurationIncreasePercent :
                {
                    bullet.buffs.push( buff ) ;
                }
                break ; 

                case AffixType.AttackBulletSizeIncreasePercent : {
                    AttackBulletSizeIncrease += buff.affix.argN ; 
                }
                break ;
                
                

            }


        } // end for

        if( AttackBulletSizeIncrease > 0 ) {
            // 
            let s = bullet.view.root.scale ;
            s.multiplyScalar( 1 + AttackBulletSizeIncrease ) ; 
            bullet.view.root.scale = s ; 

        } 

    }


}


