

export class CfgArticleUnlock {

    public static data = new Map< string , CfgArticleUnlockItem >() ; 

}

export class CfgArticleUnlockItem {

    public id : string ; 

    public levelIdx = -1 ; 

}

// 
export class CfgArticleUnlockParser {

    public static parseJson( json : any ) {

        let data = CfgArticleUnlock.data ; 

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 

            let cfgItem = new CfgArticleUnlockItem() ; 
            CfgArticleUnlock.data.set( id , cfgItem ) ; 

            cfgItem.id = id ; 

            let str = row[ "LevelIdx" ] as string ;
            
            cfgItem.levelIdx = Number( str ) ; 



        } // end for

    }

}