import { Label } from "cc";
import { Account } from "../../../../../Common/Account/Account";
import { CfgLevelWaveMgr } from "../../../../../Game/World/2.Td/Level/CfgLevelWaveMgr";
import { LevelString } from "../../../../../Game/World/2.Td/Level/LevelString";
import { UiLevel } from "./UiLevel";
import { Node } from "cc";

// 
export class UiLevelInfo {

    public uiLevel : UiLevel = null ; 

    public root : Node ; 

    // 
    public levelName : Label ; 

    // 
    public best : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.levelName = this.root.getChildByPath( "LevelName" ).getComponent( Label ) ; 

        this.best = this.root.getChildByPath( "Best" ).getComponent( Label ) ; 

    }

    public refresh() {

        this.refreshLevelName() ; 

        this.refreshBest() ; 

    }

    public refreshLevelName() {

        let main = CfgLevelWaveMgr.main ; 

        let selLevelIdx = this.uiLevel.selLevelIdx ;

        // 
        let levelData = main[ selLevelIdx ] ; 
        let str = LevelString.getName( levelData.id ) ;
        // 
        str = selLevelIdx.toString() + "." + str ; 
        this.levelName.string = str ; 

    }


    public refreshBest() {

        let selLevelIdx = this.uiLevel.selLevelIdx ;

        // 
        this.best.node.active = true ; 
        if( selLevelIdx < Account.ins.mainLevel.levelIdx ) {
            // 
            this.best.string = "" ; 
        }
        else if( selLevelIdx == Account.ins.mainLevel.levelIdx ) {
            // 
            let main = CfgLevelWaveMgr.main ; 
            let levelData = main[ selLevelIdx ] ; 
            let str = "{1}/{2}" ; 
            str = str.replace( "{1}" , Account.ins.mainLevel.waveIdx.toString() ) ; 
            str = str.replace( "{2}" , ( levelData.waves.length - 1 ).toString() ) ; 
            this.best.string = str ; 
        }
        else {
            // 
            this.best.node.active = false ; 
        }

    }

}