import { Input, instantiate, Label, Node, Prefab, resources, Sprite, Tween, tween, Vec3 } from "cc";
import { UiBar } from "../Bar/UiBar";
import { Stats, StatsItem } from "../../World/1.Base/Support/Misc/Stats";
import { ArticleString } from "../../../Common/String/ArticleString";
import { CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { PfMgrBagArticle } from "../../../Common/PfMgr/PfMgr";
import { UiArticleIcon, UiArticleIconBg } from "../../../Common/Ui/UiArticleIcon";

//  
export class UiStateBtn {

    public root : Node ;

    public init( node : Node ) {

        this.root = node ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ; 

    }

    public onClick( ) {

        if( this.stats == null ) {
         
            UiStats.loadAndCreate( ( ui )=>{
                this.stats = ui ; 
                this.stats.root.setParent( this.root , false ) ;
                this.stats.root.position = new Vec3( 20 , -370 ) ;
    
                this.stats.autoRefreshStart() ;
                
            } ) ;

            return ;

        }

        let b = !this.stats.root.active ; 
        this.stats.root.active = b ; 
        if( b == true ) {
            this.stats.autoRefreshStart() ;
        }
        else {
            this.stats.autoRefreshStop() ;
        }

    }

    public stats : UiStats = null ; 

}


// 
export class UiStats {

    public static loadAndCreate( cb : ( ui : UiStats )=>void ) {

        resources.load( "Game/Ui/Stats" , Prefab , ( err , data )=>{

            let root = instantiate( data ) ; 
            
            let ui = new UiStats() ; 
            ui.init( root ) ;

            cb( ui ) ;

        } ) ; 

    }

    public root : Node ; 

    public pfItem : Node ; 

    public items = new Array< UiStatsItem >() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.pfItem = this.root.getChildByPath( "List/Item" ) ;
        this.pfItem.active = false ; 

    }

    public autoRefreshStart() {

        tween( this.root ).sequence(
            tween( this.root ).call( ()=>{
                this.refresh() ; 
            } ) , 
            tween( this.root ).delay( 0.5 ) , 
        ).repeatForever().start() ;

    }

    public autoRefreshStop() {
        Tween.stopAllByTarget( this.root ) ;
    }

    public refresh() {

        let list = Stats.ins.sort() ; 

        this.setItemCount( list.length ) ; 

        // 
        let total = 0 ;
        for( let item of list ) {
            total += item.damage ; 
        }

        for( let i = 0 ; i < list.length && i < 5 ; ++i ) {

            let item = list[ i ] ;
            let uiItem = this.items[ i ] ;
            uiItem.refresh( item ) ; 

            // 
            let val = item.damage / total ;

            // 
            uiItem.bar.fillRange = val ; 

            // 1
            let val2 = Math.floor( val * 1000 ) / 10 ;
            uiItem.info.string = val2 + " %" ;

        } // end for

    }

    private setItemCount( n : number ) {

        let diff = n - this.items.length ; 
        for( let i = 0 ; i < diff ; ++i ){

            let node = instantiate( this.pfItem ) ;
            node.setParent( this.pfItem.parent ) ;
            node.active = true ; 

            let uiItem = new UiStatsItem() ; 
            uiItem.init( node ) ; 
            this.items.push( uiItem ) ;

        } // end for

        // 
        for( let i = this.items.length + diff ; i < this.items.length ; ++i ) {
            this.items[ i ].root.active = false ; 
        }

    }
    
}

// 
export class UiStatsItem {

    public item : StatsItem = null ; 

    public root : Node ;

    public icon = new UiArticleIcon() ; 
    public iconBg = new UiArticleIconBg() ; 

    public name : Label ; 

    public info : Label ; 

    public bar : Sprite ; 

    public init( node : Node ) {

        this.root = node ;  

        this.icon.init( this.root.getChildByPath( "Icon" ) ) ;
        this.iconBg.init( this.root.getChildByPath( "Icon/bg" ) ) ;

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;

        this.info = this.root.getChildByPath( "Info" ).getComponent( Label ) ;

        this.bar = this.root.getChildByPath( "Bar/bar" ).getComponent( Sprite ) ;
        this.bar.fillRange = 0 ; 


    }

    public refresh( item : StatsItem ) {

        if( this.item == item ) return ; 
        this.item = item ; 

        this.name.string = ArticleString.getName( item.id ) ;

        this.icon.refresh( item.id ) ;
        this.iconBg.refresh( item.id ) ;

    }

}




