import { Player } from "../../../2.Td/Player/Player";
import { PlayerRole } from "../../../2.Td/Player/PlayerRole";
import { Buff } from "../../Buff/Buff";
import { TowerMgr } from "../Entity/TowerMgr";
import { TowerAttributeUpdate } from "./TowerAttributeUpdate";
import { TowerAuraHandler as TowerAuraEmit } from "./TowerAuraEmit";
import { DataIncrease } from "../../Buff/BuffIncreaseCalc";
import { TowerCfgSet } from "../Entity/TowerFactory";
import { BuffFactory } from "../../Buff/BuffFactory";
import { BuffResetMergeGrade } from "../../Buff/BuffResetMergeGrade";

// 
export class TowerAllUpdate {

    public static updateAll() {

        // PlayerRoleAttribute.ins.reset() ; 

        // buff 
        this.resetBuff() ; 

        //  Buff 
        // for( let i = 0 ; i < TowerMgr.ins.data.length ; ++i ) {
        //     let tower = TowerMgr.ins.data[ i ] ;
        //     this.grade( tower ) ; 
        // }

        //  Buff 
        this.playerBuff() ; 
        
        //  Buff 
        TowerAuraEmit.aura() ; 

        // 
        let dataIncr = new DataIncrease() ; 
        for( let i = 0 ; i < TowerMgr.ins.data.length ; ++i ) {
            let tower = TowerMgr.ins.data[ i ] ;
            TowerAttributeUpdate.exe( tower ) ;
            let dataIncr2 = TowerAttributeUpdate.dataIncrease ; 

            // 
            dataIncr.hpMaxIncrease += dataIncr2.hpMaxIncrease + dataIncr2.hpMaxIncrease * dataIncr2.hpMaxIncreaseIncreasePercent ; 

        }

        

        this.role( dataIncr ) ;


    }

    //  Buff 
    private static playerBuff() {

        for( let i = 0 ; i < TowerMgr.ins.data.length ; ++i ) {
            let tower = TowerMgr.ins.data[ i ] ;
            for( let buff of Player.ins.buff.list ) {

                let cfg = buff.cfg ; 

                // 
                if( this.isSpecified( cfg.specified , tower.article.cfg.id ) == false ) continue ; 

                // Buff
                let buffNew = BuffFactory.create( cfg.id ) ;

                //  Buff 
                BuffResetMergeGrade.exe( buff , tower.article.cfg.mergeGrade ) ;

                if( cfg.isAura == false ) {
                    tower.buff.list.push( buffNew ) ;
                }
                else {
                    tower.aura.list.push( buffNew ) ;
                }

            } // end for
        }

    }

    // buff Buff
    private static resetBuff() {

        // Buff
        let remove = ( list : Array< Buff > )=>{
            for( let i = list.length - 1 ; i >= 0 ; --i ) {
                if( list[ i ].isNative == true ) continue ;
                list.splice( i , 1 ) ;
            } // end for
        } ;

        for( let i = 0 ; i < TowerMgr.ins.data.length ; ++i ) {

            let tower = TowerMgr.ins.data[ i ] ;

            remove( tower.buff.list ) ;
            remove( tower.aura.list ) ;

            TowerCfgSet.buff( tower , tower.article.cfg.tower ) ; 

            // Buff
            TowerCfgSet.buffGrade( tower ) ; 

        }

    }

    // 
    private static role( dataIncr : DataIncrease ) {

        let role = PlayerRole.ins.main ; 
        let life = role.life ;
        let hp = life.hp ; 
        // 
        let f = hp.current / hp.max ; 

        if( dataIncr.hpMaxIncrease > 0 ) {
            hp.max = role.cfg.lifeHpMax + Math.round( dataIncr.hpMaxIncrease ) ; 
            hp.current = Math.round( hp.max * f ) ;
            role.view.hpBar.refresh() ; 
        }
        else {
            // 
            hp.max = role.cfg.lifeHpMax ;
            hp.current = Math.round( hp.max * f ) ;
            role.view.hpBar.refresh() ; 
        }

    }

    // 
    private static isSpecified( specified : Set< string > , idEntity : string ) : boolean {

        if( specified == null || specified.size == 0 ) {
            // 
            return true ; 
        }
        
        if( specified.has( idEntity ) == true ){
            // 
            return true ; 
        }

        return false ; 

    }



}


