import { instantiate, Node, randomRangeInt, tween, UITransform, Vec3 } from "cc";
import { PfMgrMoney } from "../PfMgr/PfMgrMoney";


// 
export class MoneyFly {

    // 
    public static money001Fly( parent : Node , pos1 : Vec3 , pos2 : Vec3 , count = 10 , onComplete : ()=>void = null ) {

        this.fly( "Money001" , parent , pos1 , pos2 , count , onComplete ) ; 

    }

    public static money001Fly2( parent : Node , pos1 : Vec3 , nodeTarget : Node , count = 10 , onComplete : ()=>void = null ) {

        let pos2 = nodeTarget.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ;
        parent.getComponent( UITransform ).convertToNodeSpaceAR( pos2 , pos2 ) ;
        this.money001Fly( parent , pos1 , pos2 , count , onComplete ) ;

    }

    public static fly( id : string , parent : Node , pos1 : Vec3 , pos2 : Vec3 , count = 10 , onComplete : ()=>void = null ) {

        let pf = PfMgrMoney.ins.root.getChildByPath( id ) ;

        for( let i = 0 ; i < count ; ++i ) {

            let node = instantiate( pf ) ;
            node.setParent( parent , false ) ;

            // 
            let offset = new Vec3() ; 
            offset.x = randomRangeInt( -150 , 150 ) ; 
            offset.y = randomRangeInt( -150 , 150 ) ; 

            offset.add( pos1 ) ;

            node.position = pos1 ; 

            // 
            tween( node ).sequence(
                tween( node ).to( 0.3 , { position : offset } ) , 
                tween( node ).delay( 0.2 ) , 
                tween( node ).to( 0.8 , { position : pos2 } ) , 
                tween( node ).call( ()=>{

                    node.destroy() ; 

                    onComplete?.() ; 

                } )
            ).start() ;
            
        } // end for

    }

}