import { instantiate, Node, RichText, Size, Sprite, UITransform } from "cc";
import { CfgArticleGrade, CfgArticleGradeItem } from "../../../../Common/Cfg/CfgArticleGrade";
import { BuffString } from "../../../../Game/World/1.Base/Buff/BuffString";
import { CfgArticle } from "../../../../Common/Cfg/CfgArticle";
import { Account } from "../../../../Common/Account/Account";
import { BagArticleGradeHelper } from "../../../../Game/Bag/Article/BagArticleGradeHelper";
import { CfgBuff } from "../../../../Common/Cfg/CfgBuff";
import { UiBagArticleDetail } from "./UiBagArticleDetail";
import { PfMgrIconBuff } from "../../../../Common/PfMgr/PfMgrIconBuff";
import { NodeSizeFit } from "../../../../SL/Cocos/Ui/NodeSizeFit";

// 
export class UiBagArticleDetailUnlocked {

    public detail : UiBagArticleDetail = null ; 
    public cfgArctile : CfgArticle = null ; 

    public root : Node ; 

    public content : Node ; 

    public items = new Array< UiBagArticleDetailUnlockedItem >() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.content = this.root.getChildByPath( "sv/view/content" ) ;

        this.pfItem = this.content.children[ 0 ] ;
        this.pfItem.active = false ; 

        this.pfItem2 = this.content.children[ 1 ] ;
        this.pfItem2.active = false ; 

        // 
        for( let i = this.content.children.length ; i > 2 ; --i ) {
            this.content.children[ i ].destroy() ; 
        } // end for

    }

    public refresh() {

        for( let uiItem of this.items ) {
            uiItem.root.destroy() ; 
        }
        this.items.splice( 0 , this.items.length ) ; 

        let grade = BagArticleGradeHelper.getGradeByLineId( this.cfgArctile.gradeLineId ) ;

        let line = CfgArticleGrade.data.get( this.cfgArctile.gradeLineId ) ;
        for( let i = 0 ; i < line.length ; ++i ) {
            let cfgGradeItem = line[ i ] ;
            if( cfgGradeItem.unlocked.length == 0 && cfgGradeItem.buffIds.length == 0 ) continue ; 

            let isUnlocked = cfgGradeItem.grade <= grade ; 
            
            if( isUnlocked == true ) {
                // 1
                isUnlocked = Account.ins.bag.unlocked.findIndex( ( e )=>{ return e == this.cfgArctile.id ; } ) != -1 ; 
            }

            let pfItem = isUnlocked ? this.pfItem : this.pfItem2 ; 

            let root = instantiate( pfItem ) ;
            root.active = true ; 
            root.setParent( this.content , false );

            let item = new UiBagArticleDetailUnlockedItem() ; 
            this.items.push( item ) ; 
            item.init( root ) ;
            item.cfgGradeItem = cfgGradeItem ; 
            item.isUnlocked = isUnlocked ; 
            item.refresh() ; 

        } // end for

        if( grade == line.length ) {
            // 
            this.detail.btnList.btnUpgrade.root.active = false ; 
        }

    }

    public pfItem : Node ; 
    public pfItem2 : Node ; 

}

export class UiBagArticleDetailUnlockedItem {

    public isUnlocked = true ; 
    public cfgGradeItem : CfgArticleGradeItem = null ;

    public root : Node ; 

    public txt : RichText ; 

    public icon = new UiBagArticleDetailUnlockedItemIcon() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.txt = this.root.getComponentInChildren( RichText ) ; 

        this.icon.init( this.root.getChildByPath( "Icon" ) ) ; 


    }

    public refresh() {

        let buffId : string = null ; 
        if( this.cfgGradeItem.unlocked.length > 0 ) {
            buffId = this.cfgGradeItem.unlocked[ 0 ] ; 
        }
        else {
            // 
            buffId = this.cfgGradeItem.buffIds.find( ( e )=>{ return e.indexOf( "MergeEnable" ) != -1 ; } ) ; 
        }
        if( buffId == null ) return ; 

        let str = BuffString.getDescription( buffId ) ;

        if( this.isUnlocked ) {
            // 
            str = "<outline color=black width=4><b>" + str + "</b></outline>" ;
        }
        else {
            let str2 = "<color=#AFFBFA>Lv.{1}</color>".replace( "{1}" , this.cfgGradeItem.grade.toString() ) ;
            str = "<outline color=black width=4><b>" + str2 + str + "</b></outline>" ;
        }

        this.txt.string = str ; 

        this.icon.refresh( buffId ) ; 


    }

}

// 
class UiBagArticleDetailUnlockedItemIcon {

    public root : Node ; 

    public iconBgList = Array< Node >() ;
    public icon : Sprite ; 
    private iconSize : Size ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon = node.getChildByPath( "val" ).getComponent( Sprite ) ;
        this.iconSize = this.icon.getComponent( UITransform ).contentSize.clone() ;

        for( let i = 1 ; ; i++ ) {

            let key = "bg" + i ;
            let t = this.root.getChildByPath( key ) ; 
            if( t == null ) break ;
            this.iconBgList.push( t ) ; 

        } // end for

    }

    public refresh( buffId : string ) {

        // 
        let spr = PfMgrIconBuff.ins.getIconById( buffId ) ;
        this.icon.spriteFrame = spr.spriteFrame ; 

        // Size
        NodeSizeFit.trimmed( this.icon , this.iconSize ) ;

        // 
        let cfgBuffItem = CfgBuff.data.get( buffId ) ;
        for( let t of this.iconBgList ){ t.active = false } ;
        let idx = cfgBuffItem.grade - 1 ; 
        if( this.iconBgList.length <= idx ) return ; 
        this.iconBgList[ idx ].active = true ; 



    }

    

}