import { Component, Label } from "cc";

// 
export class NumberRolling {

    public static exe(label: Label, duration: number, num1: number, num2: number ) : void 
    {

        let com: NumberRollingCom = null;

        com = label.getComponent(NumberRollingCom);
        if (com == null) {
            com = label.addComponent(NumberRollingCom);
        }

        com.enabled = true;
        com.label = label;
        com.num1 = num1;
        com.num2 = num2;
        com.setValue = this.setVal;
        com.duration = duration;

        com.onComplete = () => {
            com.enabled = false;
        };
        com.init();

    }

    private static setVal( n : Number ) : string 
    {
        return n.toString() ; 
    }

}


class NumberRollingCom extends Component {


    // 
    public label: Label = null;

    public num1: number = 0;

    public num2: number = 0;

    public duration: number = 0;

    public onComplete: () => void = null;

    public setValue: (val: number) => string = null;

    init(): void {

        this.diff = this.num2 - this.num1;
        this.timeCount = 0;

        this.update(0);

    }

    update(dt: number): void {

        this.timeCount += dt;
        let f = this.timeCount / this.duration;

        if (f >= 1) {
            this.showNumber(this.num2);
            this.enabled = false;
            if (this.onComplete != null) this.onComplete();
            return;
        }

        let n = this.diff * f;
        n = this.num1 + n;
        n = Math.floor(n);
        // this.label.string = n.toString();
        this.showNumber(n);

    }

    showNumber(n: number) {

        this.label.string = this.setValue(n);

    }

    private diff = 0;
    private timeCount = 0;

}