import { Node } from "cc";
import { CfgItemRewardItem, CfgItemRewardMgr } from "../../../../../Common/Cfg/CfgItemReward";
import { Account } from "../../../../../Common/Account/Account";
import { Label } from "cc";
import { Lang } from "../../../../../Common/Misc/Lang";
import { AccountPersist } from "../../../../../Common/Account/AccountPersist";
import { Input } from "cc";
import { UiGet } from "../../../Popup/UiGet";
import { UiMain } from "../../UiMain";
import { ItemGet } from "../../../../../Common/Item/ItemGet";
import { UiStamina } from "../../Margin/UiStamina";
import { UiMoney001 } from "../../Margin/UiMoney001";
import { UiMoney002 } from "../../Margin/UiMoney002";
import { UiToast } from "../../../../../Common/Ui/UiToast";
import { instantiate } from "cc";
import { tween } from "cc";
import { Vec3 } from "cc";

// 
export class UiWaveReward {

    public root : Node ; 

    public items = new Array< UiWaveRewardItem >() ; 

    public init( node : Node ) {

        this.root = node ; 

        let list = this.root.getChildByPath( "List" ) ; 

        let pfItem = list.children[ 0 ] ; 

        // 
        for( let i = list.children.length - 1 ; i > 0 ; --i ) { list.children[ i ].destroy() ; }

        let uiItem = new UiWaveRewardItem() ; 
        this.items.push( uiItem ) ; 
        uiItem.init( pfItem ) ;

        for( let i = 0 ; i < 3 ; ++i ) {

            let root = instantiate( pfItem ) ;
            root.setParent( pfItem.parent , false ) ; 

            let uiItem = new UiWaveRewardItem() ; 
            this.items.push( uiItem ) ; 
            uiItem.init( root ) ;

        } // end for

    }

    public refresh( levelIdx : number ) {

        let cfgList = new Array< CfgItemRewardItem >() ; 
        let mapping = new Map< CfgItemRewardItem , number >() ; 

        //  203
        for( let i = 0 ; i < 20 ; ++i ) {

            let id = "LevelWave_" + levelIdx + "_" + i ; 
            let cfg = CfgItemRewardMgr.data.get( id ) ;
            if( cfg == null ) continue ; 
            cfgList.push( cfg ) ; 

            mapping.set( cfg , i ) ;

        } // end for

        // 
        for( let i = cfgList.length ; i < this.items.length ; ++i ){
            this.items[ i ].root.active = false ; 
        }

        for( let i = 0 ; i < cfgList.length && i < this.items.length ; ++i ) {

            let cfg = cfgList[ i ] ; 
            let waveIdx = mapping.get( cfg ) ; 

            let uiItem = this.items[ i ] ;
            uiItem.cfg = cfg ; 
            uiItem.root.active = true ; 

            //   
            uiItem.levelIdx = levelIdx ; 
            uiItem.waveIdx = waveIdx ; 
            uiItem.refresh() ; 

        } // end for

    }

}

// 
export class UiWaveRewardItem {

    // 
    public levelIdx = 0 ;
    public waveIdx = 0 ;  
    public cfg : CfgItemRewardItem = null ; 

    // 1 2 3 
    public state = 0 ;

    public root : Node ; 

    // 
    public waveInfo : Label ; 

    // 
    public claimed : Node ; 
    // 
    public claimable : Node ; 
    // 
    public unclaimable : Node ; 


    public init( node : Node ) {

        this.root = node ; 

        this.claimed = this.root.getChildByPath( "Claimed" ) ; 
        this.unclaimable = this.root.getChildByPath( "Unclaimable" ) ; 
        this.claimable = this.root.getChildByPath( "Claimable" ) ; 
        this.initClaimable() ; 

        this.waveInfo = this.root.getChildByPath( "Wave" ).getComponent( Label ) ; 
        this.waveInfo.string = "" ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ;


    }

    public initClaimable() {

        // 
        let light = this.claimable.getChildByPath( "bg_light" ) ; 
        tween( light ).by( 5 , { eulerAngles : new Vec3( 0 , 0 , -360 ) } ).repeatForever().start() ; 

        // 
        let icon = this.claimable.getChildByPath( "Icon" ) ; 
        tween( icon ).sequence(
            tween( icon ).to( 0.3 , { eulerAngles : new Vec3( 0 , 0 , 10 ) } ) , 
            tween( icon ).to( 0.3 , { eulerAngles : new Vec3( 0 , 0 , -10 ) } ) , 
            tween( icon ).to( 0.3 , { eulerAngles : new Vec3( 0 , 0 , 10 ) } ) , 
            tween( icon ).to( 0.3 , { eulerAngles : new Vec3( 0 , 0 , -10 ) } ) , 
            tween( icon ).to( 0.3 , { eulerAngles : new Vec3( 0 , 0 , 0 ) } ) , 
            tween( icon ).delay( 1 ) , 
        ).repeatForever().start() ; 

    }

    public refresh() {

        // 
        let str = Lang.get( "Home_Battle_WaveReward_WaveInfo" ).replace( "{1}" , this.waveIdx.toString() ) ;
        this.waveInfo.string = str ; 

        
        this.state = this.getState() ; 
        this.unclaimable.active = this.claimable.active = this.claimed.active = false ; 
        if( this.state == 1 ) {
            this.claimed.active = true ; 
        }
        else if( this.state == 2 ) {
            this.claimable.active = true ; 
        }
        else if( this.state == 3 ) {
            this.unclaimable.active = true ; 
        }


    }

    // 
    private getState() : number {

        //   
        let isOver = false ; 
        let levelIdx = Account.ins.mainLevel.levelIdx ; 
        let waveIdx = Account.ins.mainLevel.waveIdx ; 
        if( this.levelIdx < levelIdx ) { isOver = true ; }
        else if( this.levelIdx > levelIdx ){ isOver = false ; }
        else { isOver = this.waveIdx <= waveIdx ; }

        if( isOver ) {

            // 
            let wave = Account.ins.waveReward.get( this.levelIdx ) ; 
            if( wave == null ) return 2 ;   // 
            return wave.has( this.waveIdx ) == true ? 1 : 2 ;

        }

        return 3 ; 

    }

    public onClick() {

        if( this.state == 1 ){
            // 
            UiToast.show( UiMain.ins.root , Lang.get( "Home_Battle_WaveReward_Claimed" ) ) ; 

            return ; 
        } 

        if( this.state == 3 ) {
            UiToast.show( UiMain.ins.root , "" ) ; 
            //    
            return ;
        }

        this.get() ; 


    }

    // 
    private get() {

        // 
        let uiGet = UiGet.create( UiMain.ins.root ) ;
        // uiGet.show() ; 
        uiGet.itemList.refresh( this.cfg.list ) ; 
        uiGet.itemList.showAnim( 0.1 ) ; 
        ItemGet.addList( this.cfg.list ) ;

        // 
        UiStamina.ins.refresh() ; 
        UiMoney001.ins.refresh() ; 
        UiMoney002.ins.refresh() ; 


        // 
        let item = Account.ins.waveReward.get( this.levelIdx ) ;
        if( item == null ) {
            item = new Set< number >() ;
            Account.ins.waveReward.set( this.levelIdx , item ) ; 
        }
        item.add( this.waveIdx ) ;
        AccountPersist.save() ; 

        // 
        this.refresh( ) ; 

    }

}