

// 
export class CfgArticleGrade {

    public static data = new Map< string , Array<CfgArticleGradeItem> >() ; 

}

export class CfgArticleGradeItem {

    //  1 
    public grade = 0 ; 

    //  Buffaura 
    public unlocked = new Array< string >() ;

    //  
    public attackDamage = 0 ; 

    //    
    public hpMaxIncrease = 0 ; 

    //   
    public hpRegenIncreasePercent = 0 ; 

    //   
    public hpRegenIncrease = 0 ; 

    // /
    public getMomeyPerWave = 0 ; 

    // buff
    public buffIds = new Array< string >() ; 

    // 
    public upgrade = new CfgArticleGradeItemUpgrade() ; 

}

// 
export class CfgArticleGradeItemUpgrade {

    // 
    public money1 = 0 ; 

    // 
    public itemId : string = null ; 
    public itemCount = 0 ; 

}

// 
export class CfgArticleGradeParser {

    public static parseJson( json : any ) {

        let str : string ; 

        let data = CfgArticleGrade.data ; 

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let levelId = row[ "Id" ] ;
            if( levelId == null || levelId == "" ) continue ; 

            // let levelIdx = Number( levelId ) ; 

            let list = data.get( levelId ) ;
            if( list == null ) {
                list = new Array< CfgArticleGradeItem >() ; 
                data.set( levelId , list );
            }

            let cfgItem = new CfgArticleGradeItem() ; 
            list.push( cfgItem ) ; 

            // 
            cfgItem.grade = list.length ;

            //  
            str = row[ "Unlocked" ] ; 
            if( str != null && str != "" ) {
                let strs = str.split( /[,\n]/ ) ; 
                for( let str2 of strs ) {
                    cfgItem.unlocked.push( str2 ) ; 
                } // end for
            }


            str = row[ "AttackDamage" ] ; 
            if( str != null && str != "" ) {
                cfgItem.attackDamage = Number( str );
            }
            
            str = row[ "HpMaxIncrease" ] ; 
            if( str != null && str != "" ) {
                cfgItem.hpMaxIncrease = Number( str );
            }

            str = row[ "HpRegenIncreasePercent" ] ; 
            if( str != null && str != "" ) {
                cfgItem.hpRegenIncreasePercent = Number( str );
            }

            str = row[ "HpRegenIncrease" ] ; 
            if( str != null && str != "" ) {
                cfgItem.hpRegenIncrease = Number( str );
            }
            

            str = row[ "GetMomeyPerWave" ] ; 
            if( str != null && str != "" ) {
                cfgItem.getMomeyPerWave = Number( str );
            }

            str = row[ "Buff" ] as string ; 
            if( str != null && str != "" ) {
                let strs = str.split( /[,\n]/ ) ;
                cfgItem.buffIds.push( ...strs );
            }
            
            str = row[ "UpgradeMoney1" ] ;
            if( str != null && str != "" ) {
                cfgItem.upgrade.money1 = Number( str ) ;
            }

            str = row[ "UpgradeFragment" ] ;
            if( str != null && str != "" ) {
                let strs = str.split( "," ) ; 
                cfgItem.upgrade.itemId = strs[ 0 ].trim() ;
                cfgItem.upgrade.itemCount = Number( strs[ 1 ] ) ;
            }

        } // end for

    }

}
