import { Node, Tween, tween, Vec3 } from "cc";

// 
export class SeqScaleShow {

    public static exe( nodes : Array< Node > , interval : number = 0.1 ) : Tween< Node > {

        this.stop( nodes ) ;

        let t = tween( nodes[ 0 ] ) ;
        for( let i = 0 ; i < nodes.length ; ++i ) {

            let node = nodes[ i ] ;

            // 0
            node.scale = Vec3.ZERO ; 

            t.call( ()=>{
                this.scale( node ) ;
            } );
            t.delay( interval ) ;

        } // end for

        t.start() ;

        return t ; 

    }

    public static scale( node : Node ) {

        node.active = true ; 

        node.scale = new Vec3( 0.2 , 0.2 , 0.2 ) ;
        tween( node ).sequence(
            tween( node ).to( 0.2 , { scale : new Vec3( 1.2 , 1.2 , 1.2 ) } ) , 
            tween( node ).to( 0.1 , { scale : new Vec3( 1.0 , 1.0 , 1.0 ) } ) , 
        ).start() ;

    }

    private static stop( nodes : Array< Node > ) {

        for( let node of nodes ) {
            Tween.stopAllByTarget( node ) ; 
        } // end for

    }

}