import { Node } from "cc";

// 
export class PfMgrNumber {

    public static ins = new PfMgrNumber() ; 

    public root : Node ; 
    
    public damage : Node ;  

    public damageCrit : Node ;  
    
    public init( node : Node ) {

        this.root = node ; 

        this.damage = this.root.getChildByPath( "Damage" ) ;

        this.damageCrit = this.root.getChildByPath( "DamageCrit" ) ;

    }
    
}