import { CfgItemRewardItem } from "../../../../Common/Cfg/CfgItemReward";
import { ItemData } from "../../../../Common/Item/ItemData";
import { ItemType } from "../../../../Common/Item/ItemType";


export class CfgDailyCheckin {

    public static data = new Array< Array< ItemData > >() ; 

}

export class CfgDailyCheckinParser {

    public static parseJson( json : any ) {

        let data = CfgDailyCheckin.data ; 

        for( let i = 0 ; i < json.length ; ++i ) {

            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new Array< ItemData >() ;
            data.push( cfgItem ) ; 

            let str : string = null ; 

            // 
            for( let i = 1 ; ; ++i ) {
                
                let key = "Item" + i ; 
                str = row[ key ] ;
                if( str == "" || str == null ) break ;

                let itemData = new ItemData() ; 
                cfgItem.push( itemData ) ; 
                itemData.id = str.trim() ; 

                key = "Item1Count" ; 
                itemData.count = Number( row[ key ] ) ; 


            } // end for

        } // end for

    }

}