import { Node, Size, Sprite, UITransform } from "cc";


/*
    
    

*/ 
export class NodeSizeFit {

    // Node  Node 
    public static set( node : Node , size : Size ) {

        let f = size.width / size.height ; 

        let uiTrans = node.getComponent( UITransform ) ; 
        let size2 = uiTrans.contentSize.clone() ; 
        if( f > 1 ) {
            size2.height = size2.width / f ; 
        }
        else if( f < 1 ) {
            size2.width = size2.height * f ;
        }
        uiTrans.setContentSize( size2 ) ;        

    }

    /*
        Node 
        nodeSize 
        sizeOrig 
    */ 
    public static set2( node : Node , nodeSize : Size , sizeOrig : Size ) {

        nodeSize = nodeSize.clone() ; 

        let f = sizeOrig.width / sizeOrig.height ; 

        let uiTrans = node.getComponent( UITransform ) ; 
        if( f > 1 ) {
            nodeSize.height = nodeSize.width / f ; 
        }
        else if( f < 1 ) {
            nodeSize.width = nodeSize.height * f ;
        }
        uiTrans.setContentSize( nodeSize ) ;        

    }

    /*
         Sprite SizeMode  Trimmed
        
    */ 
    public static trimmed( spr : Sprite , size : Size ) {

        spr.trim = true ; 
        spr.sizeMode = Sprite.SizeMode.TRIMMED ; 
        let size2 = spr.node.getComponent( UITransform ).contentSize ; 
        NodeSizeFit.set2( spr.node , size , size2 ) ; 

    }

}