import { Input, instantiate, Label, Node, Prefab } from "cc";
import { UiEffectScale } from "../../SL/Cocos/Ui/UiEffect";
import { SoundBtnClickSetter } from "../Audio/SoundBtnClickSetter";

export class UiDialog {

    public static pf : Prefab ; 

    public static create( parent : Node , str : string = null ) : UiDialog {

        let root = instantiate( this.pf ) ; 
        let ui = new UiDialog() ; 
        ui.init( root ) ;
        ui.root.setParent( parent , false ) ; 
        ui.txt.string = str ; 
        return ui ; 


    }

    public root : Node ;

    public title : Label ; 
    public txt : Label ; 
    public btnOK : Node ; 
    public btnClose : Node ; 

    public init( node : Node )  {

        this.root = node ; 

        this.btnClose = this.root.getChildByPath( "btnClose" ) ; 
        this.btnClose.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;

        this.title = this.root.getChildByPath( "Title" ).getComponent( Label ) ; 
        this.title.string = "" ; 

        this.txt = this.root.getChildByPath( "Text" ).getComponent( Label ) ; 
        this.txt.string = "" ; 

        this.btnOK = this.root.getChildByPath( "btnOK" ) ; 
        this.btnOK.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;

        SoundBtnClickSetter.exe( this.root ) ; 

    }

    public show() {

        UiEffectScale.show( this.root ) ; 

    }

}