import { Vec2 } from "cc";

// 
export class TileShapeDataParser {

    // [ 0 ] 
    public static exe( str : string ) : Array< Vec2 > {

        let coords = new Array< Vec2 >() ;
        let lines = str.split( "\n" ) ;
        for( let y = lines.length - 1 ; y >= 0 ; --y ) {
            let line = lines[ y ] ;
            for( let x = 0 ; x < line.length ; ++x ) {
                if( line.charAt( x ) == "0" ) continue ;
                let coord = new Vec2( x , lines.length - y - 1 ) ;
                coords.push( coord ) ;
            }
            
        } // end for

        return coords ;

    }

}