


export class CfgFarm {

    public static data = new Array< CfgFarmItem >() ; 

}

export class CfgFarmItem {

    public id : number ; 
    public unlock_level: number;
}


export class CfgFarmParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 

            let unlock_level = row["UnlockLevel"];
            if (unlock_level == null || unlock_level == "") continue;
            
            let cfgItem = new CfgFarmItem() ;
            cfgItem.id = id;
            cfgItem.unlock_level = unlock_level;
            CfgFarm.data.push( cfgItem ) ; 

            cfgItem.id = id ; 
            

        } // endf
    }

}