import { Label } from "cc";
import { Input } from "cc";
import { Node } from "cc";


/*
    Tab
    
*/ 
export class UiTabHeaderBottom
{

    // 
    public onSelectChange : ( item : UiTabHeaderItem )=>void = null ; 

    public root : Node ;

    // 
    public currentFlag : Node = null ; 

    // 
    public get items() : Array< UiTabHeaderItem > {
        return this.header.items ; 
    }

    public init(node : Node ) : void 
    {
        this.root = node;

        this.currentFlag = this.root.getChildByPath( "CurrentFlag" ) ;

        this.header.init( this.root.getChildByPath( "TabHeader" ) ) ;
        for( let i = 0 ; i < this.header.items.length ; ++i ) {
            let item = this.header.items[ i ] ; 
            // let idx = i ; 
            let name = item.root.name ; 
            item.root.off( Input.EventType.TOUCH_END ) ;
            item.root.on( Input.EventType.TOUCH_END , ()=>{ this.show( name ); } ) ; 
        } // end for

    }

    public show( name : string ) {

        if( this.header.currentItem?.root.name == name ) return ;

        this.header.showByName( name ) ;

        // 
        let currentItem = this.header.currentItem ;
        let pos = this.currentFlag.worldPosition.clone() ; 
        pos.x = currentItem.root.worldPosition.x ; 
        this.currentFlag.worldPosition = pos ; 

        this?.onSelectChange( currentItem ) ;

    }

    private header = new UiTabHeader() ; 

}


export class UiTabHeader
{
    // 
    public currentItem : UiTabHeaderItem = null ; 

    public root : Node ;

    public items = new Array< UiTabHeaderItem >();

    public init(node : Node ) : void 
    {
        this.root = node;
        let children = this.root.children;
        for ( let i = 0; i < children.length ; ++i)
        {
            let uiItem = new UiTabHeaderItem();
            let trans = children[ i ];
            uiItem.init( trans );
            this.items.push( uiItem ) ; 

            let idx = i;
            uiItem.root.on( Input.EventType.TOUCH_END , ()=>{ this.showByIndex(idx); } ) ; 

        } // end for

    }

    public showByName( name : string ) {

        let item = this.items.find( ( e )=>{ return e.root.name == name ; } ) ;
        if( item == null ) return ; 

        if( item == this.currentItem ) return ; 

        for ( let i = 0; i < this.items.length; ++i)
        {
            let itemTemp = this.items[i] ;
            if( itemTemp == item ) continue ; 
            itemTemp.show(false);

        } // end for
    
        this.currentItem = item ;
        this.currentItem.show(true);

    }

    //  Tab 
    public showByIndex( idx : number ) : void
    {
        for ( let i = 0; i < this.items.length; ++i)
        {
            let item = this.items[i] ;
            item.show(false);

        } // end for

        this.currentItem = this.items[idx] ;
        this.currentItem.show(true);

    }

}

export class UiTabHeaderItem
{

    public root : Node ; 

    // 
    public icon : Node ; 

    // 
    public txt : Label ; 

    public init( node : Node ) : void
    {
        this.root = node;
        this.icon = this.root.getChildByPath( "Icon" ) ;
        this.txt = this.root.getChildByPath( "Label" ).getComponent( Label ) ;

        this.show(false);

    }

    // 
    public show( val : boolean ) : void
    {
        //  
        let pos = this.icon.position.clone() ;
        pos.y = val == true ? 30 : 0 ;
        this.icon.position = pos ; 

    }

}