import { instantiate, randomRangeInt, Vec3 } from "cc";
import { Account } from "../../../../Common/Account/Account";
import { UiTray } from "../../../Bag/Main/UiTray";
import { CfgGameBagTrayAricleGen } from "../../../../Common/Cfg/CfgGameBagTrayAricleGen";
import { WeightProbability } from "../../../../SL/Common/Misc/WeightProbability";
import { PfMgrBagArticle } from "../../../../Common/PfMgr/PfMgr";
import { BagArticle } from "../../../Bag/Article/BagArticle";
import { CfgArticleMgr } from "../../../../Common/Cfg/CfgArticle";
import { CfgShape } from "../../../../Common/Cfg/CfgShape";

// 
export class BagTrayAricleGen {

    public static ins = new BagTrayAricleGen() ; 

    /*
        
        22
    */ 
    public genFirst() {

        //  
        UiTray.ins.clear() ; 

        let bagExpandCount = 0 ; 
        let count = 0 ; 

        //  2  2 
        for( let i = 0 ; i < 2 ; ++i ) {

            let id = this.rndDeployWeapon( 2 ) ;
            if( id == null ) continue ; 
            UiTray.ins.addArticleById( id ) ;
            ++count ; 
            
        } // end for

        // 3
        for( let i = count ; i < 3 ; ++i ) {

            let id : string = null ; 

            // 
            if( bagExpandCount < 1 ) {
                id = this.rnd2() ; 
                if( id.startsWith( "BagExpand" ) ) { bagExpandCount++ ; }
            } 
            else {
                id = this.rndDeploy() ; 
            }

            UiTray.ins.addArticleById( id ) ;

        } // end for

        this.addAdTag() ;     


    }

    public gen() {

        let bagExpandCount = 0 ; 

        //  
        UiTray.ins.clear() ; 

        for( let i = 0 ; i < 3 ; ++i ) {

            let id : string = null ; 

            // 
            if( bagExpandCount < 1 ) {
                id = this.rnd2() ; 
                if( id.startsWith( "BagExpand" ) ) { bagExpandCount++ ; }
            } 
            else {
                id = this.rndDeploy() ; 
            }

            UiTray.ins.addArticleById( id ) ;

        } // end for

        this.addAdTag() ; 

    }

    // 
    public addAdTag() {

        // 
        let canAddAdTag = ( a : BagArticle )=>{

            // 3  3
            if( a.cfg.mergeGrade >= 3 ) return true ; 

            if( a.isBagExpand == true && a.cfgShape.coords.length >= 3 ) return true ; 

            return false ; 

        } ; 

        let list = UiTray.ins.articles ;
        for( let i = 0 ; i < list.length ; ++i ) {
            let a = list[ i ] ;

            if( canAddAdTag( a ) == false ) continue ;

            let id = "AdTag/" + a.cfgShape.id ;
            let pf = PfMgrBagArticle.bagArticle.getChildByPath( id ) ; 
            if( pf == null ) continue ; 
            let adTag = instantiate( pf ) ;
            a.view.adTag = adTag ;
            adTag.setParent( a.view.root , false );
            adTag.position = Vec3.ZERO ; 
            
        } // end for

    }

    // 2
    public genGrade2() {


        this.BagExpandCount = 0 ; 

        //  
        UiTray.ins.clear() ; 

        // 2
        let id = this.rndDeployGrade( 2 ) ; 
        UiTray.ins.addArticleById( id ) ;


        for( let i = 0 ; i < 2 ; ++i ) {

            let id = this.rnd2() ; 
            if( id.startsWith( "BagExpand" ) ) {
                // 

                // 
                if( this.BagExpandCount >= 1 ) {
                    i-- ; 
                    continue ; 
                }

                this.BagExpandCount++ ;
            }
            else{
                id = this.rndGrade( id ) ; 
            }
            
            UiTray.ins.addArticleById( id ) ;

        }

    }

    //  ID 
    private rnd1() : string {

        let ready = Account.ins.bag.deploy ;
        let idx = randomRangeInt( 0 , ready.length ) ; 
        let id = ready[ idx ] ; 
        return id ; 

    }


    //   
    private rnd2() : string {

        let w = new WeightProbability() ;

        // 
        let list = Account.ins.bag.deploy ; 
        for( let id of list ) {
            let cfgItem = CfgGameBagTrayAricleGen.data.get( id ) ; 
            if( cfgItem == null ) continue ; 

            w.add( id ,  cfgItem.pw ) ; 

        } // end for
        
        // 
        let common = CfgGameBagTrayAricleGen.common ; 
        for( let i = 0 ; i < common.length ; ++i ) {
            let cfgItem = common[ i ] ;
            w.add( cfgItem.id ,  cfgItem.pw ) ; 
        } // end for

        let str = w.get() ; 

        return str ; 

    }

    //   2
    private rndDeployGrade( grade : number ) : string {

        let id = this.rndDeploy() ; 

        let idArticle = id.split( "_" )[ 0 ] + "_" + grade ;

        return idArticle ; 

    }

    //  
    private rndDeploy() : string {

        let w = new WeightProbability() ;

        // 
        let ready = Account.ins.bag.deploy ; 
        for( let id of ready ) {
            let cfgItem = CfgGameBagTrayAricleGen.data.get( id ) ; 
            if( cfgItem == null ) continue ; 

            w.add( id ,  cfgItem.pw ) ; 

        } // end for

        let str = w.get() ; 

        return str ; 

    }

    /*
           
    */ 
    private rndDeployWeapon( cellLenMin : number = 2 ) : string {

        let w = new WeightProbability() ;

        let ready = Account.ins.bag.deploy ; 
        for( let id of ready ) {

            // 2
            let cfgArtile = CfgArticleMgr.data.get( id ) ; 
            if( cfgArtile.tower.atkDamage <= 0 ) continue ; 
            if( CfgShape.data.get( cfgArtile.bagShapeId ).coords.length < cellLenMin ) continue ; 

            let cfgItem = CfgGameBagTrayAricleGen.data.get( id ) ; 
            if( cfgItem == null ) continue ; 

            w.add( id ,  cfgItem.pw ) ; 

        } // end for

        let str = w.get() ; 
        return str ; 

    }

    // ID
    private rndGrade( id : string ) : string {

        let w = new WeightProbability() ;

        let cfgItem = CfgGameBagTrayAricleGen.data.get( id ) ; 

        for( let i = 0 ; i < cfgItem.grade.length ; ++i ) {

            let pw = cfgItem.grade[ i ] ; 
            let k = ( i + 1 ).toString() ; 
            w.add( k , pw ) ; 

        }

        let strGrade = w.get() ; 
        
        let idArticle = id.split( "_" )[ 0 ] + "_" + strGrade ;

        return idArticle ; 

    }

    private BagExpandCount = 0 ; 


}