import { Node, find } from "cc";
import { UiDialog } from "../../Ui/UiDialog";
import * as i18n from 'db://i18n/LanguageData';

// 
export class AdRewardVideoFailHandler {

    public static exe( err : string ) : boolean {

        let parent = find( "Canvas" ) ; 

        if( err != null ) {

            // let str = "" + err ; 
            let str = i18n.t( "Ad_Error" ) + err ; 
            let ui = UiDialog.create( find( "Canvas" ) ) ;
            ui.title.string = "" ; 
            ui.txt.string = str ;

            return true ;
        }

        // if( err == null ) {

        //     let str = "" ; 
        //     let ui = UiDialog.show( str ) ;

        //     return true ; 
        // }

        return false ; 

    }

}