
import { Affix, AffixType } from "../../Game/World/1.Base/Buff/Affix";
import { AuraScopeType } from "../../Game/World/1.Base/Buff/AuraScopeType";
import { BulletRotationType } from "../../Game/World/1.Base/Bullet/Bullet";
import { FrameSecConvert } from "../Misc/FrameSecConvert";

export class CfgBuff {

    public static data = new Map< string , CfgBuffItem >() ;  

}

export class CfgBuffItem {

    public id : string ; 

    //  
    public affixType = AffixType.None ; 
    //  
    public affixArgN : number = 0 ;
    public affixArgNList : Array< number > = null ; 
    //  
    public affixArgStr : string ; 

    // 
    public duration : number = 0 ;

    // -1 0 1 1
    public interval = -2 ; 

    // 
    public grade = 1 ;

    // 
    public specified : Set< string > = null ; 

    // 
    public isAura = false ; 

    //  
    public auraOverlay = true ; 

    //  
    public auraScopeType = AuraScopeType.Dir8 ; 

    // 
    public mergeGradeFactor : Array< number > = null ; 

    // 
    public unlockCount = -1 ; 

}

// 
export class CfgBuffParser {

    public static parseJson( json : any ) {

        let str : string = null ; 

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgBuffItem() ;
            CfgBuff.data.set( id , cfgItem ) ;
            cfgItem.id = id ;

            str = row[ "Type" ] ; 
            if( str != null && str != "" ) {
                cfgItem.affixType = str as AffixType ; 
            }

            str = row[ "ArgN" ] ; 
            if( str != null && str != "" ) {

                if( typeof str === "number" ) {
                    cfgItem.affixArgN = str ; 
                }
                else {

                    let strs = str.split( "," ) ;
                    if( strs.length == 1 ) {
                        cfgItem.affixArgN = Number( str ) ; 
                    }
                    else {
                        cfgItem.affixArgN = Number( strs[ 0 ] ) ; 
                        cfgItem.affixArgNList = new Array< number >() ; 
                        for( let str of strs ) {
                            cfgItem.affixArgNList.push( Number( str ) ) ;
                        } // end for
                    }

                }

            }

            str = row[ "ArgStr" ] ; 
            if( str != null && str != "" ) {
                cfgItem.affixArgStr = str ; 
            }

            str = row[ "Duration" ] ; 
            if( str != null && str != "" ) {
                cfgItem.duration = FrameSecConvert.toFrame( Number( str ) ) ; 
            }

            str = row[ "Interval" ] ; 
            if( str != null && str != "" ) {
                cfgItem.interval = FrameSecConvert.toFrame( Number( str ) ) ; 
            }

            str = row[ "Grade" ] ; 
            if( str != null && str != "" ) {
                cfgItem.grade = Number( str ) ; 
            }

            str = row[ "Specified" ] ; 
            if( str != null && str != "" && str != "All" ) {
                cfgItem.specified = new Set< string >() ; 
                let strs = str.split( /[,\n]/ ).filter( Boolean ) ;
                for( let t of strs ) {
                    t = t.trim() ; 
                    cfgItem.specified.add( t ) ;
                } // end for

            }

            str = row[ "IsAura" ] ; 
            if( str != null && str != "" ) {
                cfgItem.isAura = str == "true" ? true : false ; 
            }

            str = row[ "AuraOverlay" ] ; 
            if( str != null && str != "" ) {
                cfgItem.auraOverlay = str == "true" ; 
            }

            str = row[ "AuraScope" ] ; 
            if( str != null && str != "" ) {
                cfgItem.auraScopeType = str as AuraScopeType ; 
            }


            str = row[ "MergeGradeFactor" ] ; 
            if( str != null && str != "" ) {
                let strs = str.split( "," ) ;
                cfgItem.mergeGradeFactor = new Array< number >() ;
                for( let str2 of strs ) {
                    cfgItem.mergeGradeFactor.push( Number( str2 ) ) ; 
                } // end for
                
            }

            str = row[ "UnlockCount" ] ; 
            if( str != null && str != "" ) {
                cfgItem.unlockCount = Number( str ) ; 
            }



        } // endf

    }

}
