import { math, Rect, Vec3, view } from "cc";
import { Bullet, BulletRotationType } from "./Bullet";
import { BulletMgr } from "./BulletMgr";
import { MoveCom } from "../Role/Move/MoveCom";
import { BulletMove } from "./BulletMoveSys";

export class BulletSys {

    public static update() {

        let list = BulletMgr.ins.data ; 
        for( let i = list.length - 1 ; i >= 0 ; --i ) {

            let bullet = list[ i ] ;
           
            this.outOfBounds( bullet ) ; 

            this.lifecycle( bullet ) ; 
            
            if( bullet.life.hp.current <= 0 ) {
                this.die( bullet ) ;
                continue ; 
            } 

            BulletMove.update( bullet ) ; 

        } // end fpr

    }

    // 
    private static lifecycle( bullet : Bullet ) {

        if( bullet.Lifecycle > 0 ) {
            bullet.Lifecycle-- ; 
            if( bullet.Lifecycle == 0 ) {
                bullet.life.hp.current = 0 ; 
            }
        }

    }

    // 
    public static outOfBounds( bullet : Bullet ) {

        if( this.rectBounds == null ) {
            let size = view.getVisibleSize() ; 
            this.rectBounds = new Rect() ; 
            this.rectBounds.xMin = ( size.width + 200 ) * -1 ; 
            this.rectBounds.xMax = size.width + 200 ; 
            this.rectBounds.yMin = ( size.height + 200 ) * -1 ; 
            this.rectBounds.yMax = size.height + 200 ; 
        }


        let pos = bullet.view.root.worldPosition ; 
        if( pos.x < this.rectBounds.xMin || pos.x > this.rectBounds.xMax || 
            pos.y < this.rectBounds.yMin || pos.y > this.rectBounds.yMax 
            )
        {
            // 
            bullet.life.hp.current = 0 ; 
        }

    }

    public static rectBounds : Rect ; 

    // 
    public static die( bullet : Bullet ) {

        BulletMgr.ins.remove( bullet ) ; 

        bullet.view.root.destroy() ; 

    }

}

