import { Input, instantiate, Label, Node, Vec3 } from "cc";
import { UiBagItem } from "./UiBagItem";
import { Account } from "../../../Common/Account/Account";
import { UiBagArticleDetail } from "./ArticleDetail/UiBagArticleDetail";
import { UiMain } from "../Main/UiMain";
import { UiBagPrefab } from "./UiBagPrefab";
import { CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { SoundBtnClickSetter } from "../../../Common/Audio/SoundBtnClickSetter";
import { UiHybridGuide } from "../Popup/UiHybridGuide";
import { CfgGeneral } from "../../../Common/Cfg/CfgGeneral";
import { Lang } from "../../../Common/Misc/Lang";

// 
export class UiBagDeploy {

    public root : Node ; 

    public items = new Array< UiBagItem >() ;

    public count : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.count = this.root.getChildByPath( "Title/Count" ).getComponent( Label ) ; 

        this.list = this.root.getChildByPath( "List" ) ; 

        let btnHybridGuide = this.root.getChildByPath( "btnHybridGuide" ) ;
        btnHybridGuide.on( Input.EventType.TOUCH_END , ()=>{
            UiHybridGuide.create( UiMain.ins.root ).show() ;
        } ) ;

        this.refresh() ; 

    }

    public refresh() {

        this.items.splice( 0 , this.items.length ) ; 
        this.list.removeAllChildren() ; 

        // let list = [ "001_1" , "010_1" , "013_1" ] ;
        let list = Account.ins.bag.deploy ; 

        for( let i = 0 ; i < list.length ; ++i ) {

            let id = list[ i ] ; 

            // 
            let q = CfgArticleMgr.data.get( id ).quality ; 
            let pf = UiBagPrefab.ins.items[ q - 1 ] ; 
            let uiItem = new UiBagItem() ; 
            this.items.push( uiItem ) ;
            uiItem.init( instantiate( pf ) ) ;
            uiItem.root.active = true ; 
            uiItem.root.setParent( this.list , false ) ;
            uiItem.articleId = id ;
            uiItem.refresh() ; 

            uiItem.root.on( Input.EventType.TOUCH_END , ()=>{
                this.onClick_Item( uiItem ) ;
            } ) ;

        } // end for
        
        // 
        for( let i = list.length ; i < 8 ; ++i ) {

            let empty = instantiate( UiBagPrefab.ins.itemEmpty ) ; 
            empty.setParent( this.list , false ) ;

            let isUnlocked = Account.ins.bag.deployCellUnlocked.has( i ) ; 

            let locked = empty.getChildByPath( "Locked" ) ; 

            if( isUnlocked == true ) {
                locked.active = false ; 
            }
            else {
                locked.active = true ; 

                let txt = locked.getComponentInChildren( Label ) ; 
                let levelIdx = CfgGeneral.ins.bagDeployCellUnlocked.get( i ) ; 
                txt.string = Lang.get( "Home_Bag_UnlockByMainLevel" ).replace( "{1}" , levelIdx.toString() ) ;

            }

            

        }

        this.count.string = list.length + "/8" ;

        SoundBtnClickSetter.exe( this.root ) ;

    }

    private onClick_Item( uiItem : UiBagItem ) {

        let uiDetail = UiBagArticleDetail.create( UiMain.ins.root ) ;
        uiDetail.articleId = uiItem.articleId ; 
        uiDetail.name.string = uiItem.name.string ; 
        uiDetail.refresh() ; 
        uiDetail.show() ; 
        uiDetail.btnList.setShowModeAsDeploy() ; 

        let item = uiDetail.root.getChildByPath( "Item" ) ; 
        let itemRoot = instantiate( uiItem.root ) ;
        itemRoot.scale = new Vec3( 1.2 , 1.2 , 1.2 ) ; 
        itemRoot.setParent( item.parent , false ) ;
        itemRoot.position = item.position ; 
        item.destroy() ; 
        let uiItem2 = new UiBagItem() ; 
        uiDetail.item = uiItem2 ; 
        uiItem2.init( itemRoot ) ; 
        uiItem2.name.node.active = false ; 
        uiItem2.articleId = uiItem.articleId ; 
        uiItem2.refresh() ; 
        

    }

    private list : Node ; 

}

