import { Role } from "../../Role/Base/Role";
import { TowerMgr } from "../../Tower/Entity/TowerMgr";
import { AffixType } from "../Affix";
import { Buff } from "../Buff";


//  30%20%
export class Buff_HpRegenIncreasePercentBelowPercentPerWave {

    public static tryExe( role : Role ) {

        // 
        let f = 0 ;
        for( let tower of TowerMgr.ins.data ) {
            for( let buff of tower.buff.list ) {
                if( buff.affix.type == AffixType.HpRegenIncreasePercentBelowPercentPerWave && 
                    buff.extra != true
                 ) {

                    let p = role.life.hp.current / role.life.hp.max ;
                    if( p < buff.affix.argNList[ 1 ] ) {
                        f += buff.affix.argNList[ 0 ] ;

                        // 
                        buff.extra = true ; 

                    } 

                }

            } // end for

        } // end for

        if( f <= 0 ) return ;

        let val = role.life.hp.max * f ;
        role.life.add( val ) ;

        role.view.hpBar.refresh() ; 

    }

    //  
    public static resetAllTowers() {

        for( let tower of TowerMgr.ins.data ) {
            for( let buff of tower.buff.list ) {
                this.resetBuff( buff ) ;
            } // end for
        } // end for

    }

    public static resetBuff( buff : Buff ) {

        if( buff.affix.type != AffixType.HpRegenIncreasePercentBelowPercentPerWave ) return ; 

        buff.extra = false ; 

    }

}
