import { UITransform } from "cc";
import { Size } from "cc";
import { BlockInputEvents } from "cc";
import { tween } from "cc";
import { Vec3 } from "cc";
import { Rect } from "cc";
import { Node } from "cc";

export class UiGuideCover {

    public root : Node ; 

    public rootBlockInput : BlockInputEvents ; 

    public mask : Node ; 
    public maskUiTrans : UITransform ; 

    public init( node : Node ) {

        this.root = node ; 

        // 
        this.root.getComponent( UITransform ).setContentSize( 9999 , 9999 ) ;
        this.root.position = Vec3.ZERO ; 

        // 
        this.rootBlockInput = this.root.getComponent( BlockInputEvents ) ; 
        if( this.rootBlockInput == null ) {
            this.rootBlockInput = this.root.addComponent( BlockInputEvents ) ; 
        }

        this.mask = this.root.getChildByPath( "Mask" ) ; 
        this.maskUiTrans = this.mask.getComponent( UITransform ) ;

    }

    // 
    public showAnim( posWorld : Vec3 , size : Size , duration = 0.5 ) {

        this.rootBlockInput.enabled = true ; 

        tween( this.mask ).parallel( 

            tween( this.mask ).to( duration , { worldPosition : posWorld } ) , 
            tween( this.maskUiTrans ).to( duration , { contentSize : size } ) , 

         )
         .call( ()=>{
            this.rootBlockInput.enabled = false ; 
         } )
         .start() ;

    }

}