import { math, Node, Rect, size, Size, Tween, tween, UITransform, Vec2, Vec3, Widget } from "cc";
import { UiBagGrid } from "./UiBagGrid";
import { GridHelper } from "./GridHelper";
import { TweenHelper } from "../../../Common/Misc/TweenHelper";

// 
export class UiBagGridBg {

    public static ins = new UiBagGridBg() ;

    public rect : Rect ; 

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

    }

    // 
    public entirety( duration = 0 ) {

        let rect = GridHelper.getBoundingRectEntirety() ; 
        this.setBg( rect , duration ) ; 

    }

    public entiretyAnim( duration = 0.5 ) {
        this.entirety( duration ) ;
    }

    // 
    public unlocked( duration = 0 ) {

        let rect = GridHelper.getBoundingRectUnlocked() ; 
        this.setBg( rect , duration ) ; 

    }

    public unlockedAnim( duration = 0.5 ) {
        this.unlocked( duration ) ;
    }

    //  
    private setBg( rect : Rect , duration = 0 ) {

        this.rect = rect ; 

        let pos = new Vec3( rect.center.x , rect.center.y ) ; 
        let uitrans = this.root.getComponent( UITransform ) ;
        if( duration == 0 ) {
            // 
            this.root.position = pos ; 
            this.setBgChildrenAlignMode( Widget.AlignMode.ALWAYS ) ;
            uitrans.setContentSize( rect.size ) ;
            this.setBgChildrenAlignMode( Widget.AlignMode.ON_WINDOW_RESIZE ) ;
            
        }
        else {
            // 

            this.setBgChildrenAlignMode( Widget.AlignMode.ALWAYS ) ;
            TweenHelper.contentSize( uitrans , rect.size , duration ) ; 

            Tween.stopAllByTarget( this.root ) ;
            tween( this.root ).to( duration , { position : pos } ).call( ()=>{
                // console.log( "" ) ; 
                this.setBgChildrenAlignMode( Widget.AlignMode.ON_WINDOW_RESIZE ) ;
            } ).start() ;
            
            
        }

    }

    private setBgChildrenAlignMode( mode = Widget.AlignMode.ON_WINDOW_RESIZE ) {
        let list = this.root.getComponentsInChildren( Widget ) ;
        for( let w of list ) {
            w.alignMode = mode ; 
        }
    }

}

