import { Node, Sprite } from "cc";

export class PfMgrIcon {

    public static ins = new PfMgrIcon() ; 

    public root : Node ; 

    //  001
    public money001 : Sprite ; 

    //  001
    public money002 : Sprite ; 

    // 
    public stamina : Sprite ; 

    public init( node : Node ) {

        this.root = node ; 

        this.money001 = this.root.getChildByPath( "Money/Money001" ).getComponent( Sprite ) ; 
        this.money002 = this.root.getChildByPath( "Money/Money002" ).getComponent( Sprite ) ; 

        this.stamina = this.root.getChildByPath( "Stamina" ).getComponent( Sprite ) ; 

    }

}