import { Node, Sprite } from "cc";

export class UiBar {

    public root : Node ; 

    public bar : Sprite ; 

    public init( root : Node ) {
        
        this.root = root ;
        this.bar = this.root.getChildByPath( "bar" ).getComponent( Sprite ) ;

    }

    // 
    public setProgress( current : number , max : number ) {
        this.bar.fillRange = current / max ;
    }

}