import { instantiate, Layout, Node } from "cc";
import { UiFeatureSelItem } from "./UiFeatureSelItem";
import { FeatureExtract } from "./FeatureExtract";
import { UiFeatureSel } from "./UiFeatureSel";
import { SoundBtnClickSetter } from "../../../../Common/Audio/SoundBtnClickSetter";
import { Button } from "cc";

// 
export class UiFeatureSelItemList {

    public featureSel : UiFeatureSel = null ; 

    public root : Node ; 

    public items = new Array< UiFeatureSelItem >() ;

    private layout : Layout ; 

    private pfItem : Node = null ; 

    public init( node : Node ) {

        this.root = node ; 

        this.layout = this.root.getComponent( Layout ) ; 

        this.pfItem = this.root.getChildByName( "Item" ) ; 

        // 
        let uiItem = new UiFeatureSelItem() ; 
        uiItem.init( this.pfItem ) ;
        this.items.push( uiItem ) ; 

    }

    public refresh() {

        let list = FeatureExtract.exe() ; 

        this.setItemCount( list.length ) ; 
        
        for( let i = 0 ; i < list.length ; ++i ) {

            let cfgItem = list[ i ] ;

            let uiItem = this.items[ i ] ; 
            uiItem.featureSel = this.featureSel ; 
            uiItem.refresh( cfgItem ) ;
            uiItem.root.setSiblingIndex( i ) ;

            // 
            uiItem.root.getComponent( Button ).enabled = true ; 

        } // end for

        this.layout.enabled = true ; 
        this.layout.updateLayout( true ) ; 

        SoundBtnClickSetter.exe( this.root ) ;


    }

    public setItemCount( n : number ) {

        let diff = n - this.items.length ; 

        // 
        for( let i = 0 ; i < diff ; ++i ) {

            let node = instantiate( this.pfItem ) ; 
            node.active = true ; 
            node.setParent( this.root ) ;
            let uiItem = new UiFeatureSelItem() ; 
            uiItem.init( node ) ;
            this.items.push( uiItem ) ; 

        } // end for

        // 
        for( let i = this.items.length + diff ; i < this.items.length ; ++i ) {
            this.items[ i ].root.active = false ; 
        } // end for

    }



}
