import { director, Input, instantiate, Node, Prefab, resources } from "cc";
import { SceneTransition } from "../../../../Common/Ui/UiLoadingScene";
import { UiEffectPopup } from "../../../../SL/Cocos/Ui/UiEffect";
import { UiItemList } from "../../../../Common/Item/UiItemList";
import { SoundBtnClickSetter } from "../../../../Common/Audio/SoundBtnClickSetter";

// UI
export class UiWin {

    public static pf : Prefab ; 

    public static loadPf() {

        if( this.pf != null ) return ; 

        resources.load( "Game/Ui/Settle/Win" , Prefab , ( err , data )=>{
            this.pf = data ;
        } ) ;

    }

    public static create( parent : Node ) : UiWin {

        let root = instantiate( this.pf ) ;
        let ui = new UiWin() ;
        ui.init( root ) ; 
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }


    public root : Node ;

    public btnOK : Node ; 

    public btnShare : Node ; 

    // 
    public itemList = new UiItemList() ; 

    public init( node : Node ) {

        this.root = node ; 

        SoundBtnClickSetter.exe( this.root ) ;

        this.btnOK = this.root.getChildByPath( "BtnList/btnOK" ) ;
        this.btnOK.on( Input.EventType.TOUCH_END , this.onClick_btnOK , this ) ;

        this.btnShare = this.root.getChildByPath( "BtnList/btnShare" ) ;
        this.btnShare.on( Input.EventType.TOUCH_END , this.onClick_btnShare , this ) ;

        this.itemList.init( this.root.getChildByPath( "Reward" ) ) ; 
        

    }

    public show() {
        this.root.active = true ; 
        UiEffectPopup.show( this.root ) ;
        
    }

    private onClick_btnOK() {

        SceneTransition.exe( "Home" ) ; 

    }

    private onClick_btnShare() {

    }

}