import { Vec2 } from "cc";
import { TileShapeDataParser } from "./TileShapeDataParser";
import { MergeDefinition } from "../../Game/Bag/Merge/MergeDefinition";
import { FrameSecConvert } from "../Misc/FrameSecConvert";
import { AttackTargetSelectionType } from "../../Game/World/1.Base/Tower/Entity/AttackCom";
import { CfgTowerItem, CfgTowerParser } from "./CfgTower";

// 
export class CfgArticleMgr {

    public static data = new Map< string , CfgArticle >() ; 

}

// 
export class CfgArticle {

    public id : string ; 

    // 
    public dataOrig : any ; 

    // 
    public bagShapeId : string ;

    // 
    public mergeGrade = -1 ; 

    // ID
    public gradeLineId : string ; 

    // 
    public tower = new CfgTowerItem() ; 

    // 
    public quality = 1 ; 

}



// 
export class CfgArticleParser {

    public static parseJson( json : any ) {

        MergeDefinition.ins = new MergeDefinition() ; 

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgArticle() ;
            CfgArticleMgr.data.set( id , cfgItem ) ;
            cfgItem.id = id ;

            let str : string = null ; 

            cfgItem.dataOrig = row ; 

            cfgItem.bagShapeId = row[ "BagShapeId" ] ; 
            
            str = row[ "MergeGrade" ] ; 
            if( str != null && str != "" ) {
                cfgItem.mergeGrade = Number( str ) ; 
            }

            cfgItem.gradeLineId = row[ "GradeLineId" ] ; 

            str = row[ "Quality" ] ; 
            if( str != null && str != "" ) {
                cfgItem.quality = Number( str ) ;
            }

            this.handleMergeDefinition( id , row ) ;

            CfgTowerParser.parseTowerItem( cfgItem.tower , row ) ; 

        } // endf

    }



    private static handleMergeDefinition( id : string , row : any ) {

        let str = row[ "MergeDefinition" ] as string ; 
        if( str == null || str == "" ) return ; 

        let str2 = row[ "MergeHybrid" ] ; 
        let isHybrid = str2 == "true" ? true : false ; 

        if( isHybrid ) {

            MergeDefinition.ins.allowData.set( id , false ) ; 

        }

        let strs = str.split( "," ) ;
        for( let str2 of strs ) {
            let kv = str2.split( "->" ) ; 
            let id2 = kv[ 0 ].trim() ; 
            let id3 = kv[ 1 ].trim() ; 

            MergeDefinition.ins.addDefinition( id , id2 , id3 , isHybrid ) ;

        } // end for

    }

}