import { Label, Node, RichText, Sprite } from "cc";
import { FrameSecConvert } from "../../../../Common/Misc/FrameSecConvert";
import { BagArticleGradeHelper } from "../../../../Game/Bag/Article/BagArticleGradeHelper";
import { CfgArticle } from "../../../../Common/Cfg/CfgArticle";
import { Lang } from "../../../../Common/Misc/Lang";
import { CfgArticleGradeItem } from "../../../../Common/Cfg/CfgArticleGrade";
import { CfgBuff } from "../../../../Common/Cfg/CfgBuff";
import { AffixType } from "../../../../Game/World/1.Base/Buff/Affix";

// 
export class UiBagArticleDetailBaseAttribute {

    public cfgArctile : CfgArticle = null ; 

    public root : Node ; 

    public cd = new UiBagArticleDetailBaseAttributeItem() ; 
    public target = new UiBagArticleDetailBaseAttributeItem() ; 
    public attackDamage = new UiBagArticleDetailBaseAttributeItem() ; 
    public hpMaxIncrease =  new UiBagArticleDetailBaseAttributeItem() ; 
    public HpRegen =  new UiBagArticleDetailBaseAttributeItem() ; 
    public AttackDamageAura =  new UiBagArticleDetailBaseAttributeItem() ; 
    public Money001 =  new UiBagArticleDetailBaseAttributeItem() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.target.init( this.root.getChildByPath( "Target" ) ); 
        this.target.root.active = false ; 

        this.cd.init( this.root.getChildByPath( "Cd" ) ) ;
        this.cd.root.active = false ; 

        this.attackDamage.init( this.root.getChildByPath( "Attack" ) ) ;
        this.attackDamage.root.active = false ; 

        this.hpMaxIncrease.init( this.root.getChildByPath( "HpMaxIncrease" ) ) ;
        this.hpMaxIncrease.root.active = false ; 

        this.HpRegen.init( this.root.getChildByPath( "HpRegen" ) ) ;
        this.HpRegen.root.active = false ; 

        this.AttackDamageAura.init( this.root.getChildByPath( "AttackAura" ) ) ;
        this.AttackDamageAura.root.active = false ; 

        this.Money001.init( this.root.getChildByPath( "Money001" ) ) ;
        this.Money001.root.active = false ; 

    }

    private cfgCurrent : CfgArticleGradeItem = null ; 
    private cfgNext : CfgArticleGradeItem = null ; 

    public refresh() {

        this.cfgCurrent = BagArticleGradeHelper.getCfgCurrent( this.cfgArctile.id ) ;
        this.cfgNext = BagArticleGradeHelper.getCfgNext( this.cfgArctile.id ) ; 

        this.refreshAttack() ; 
        this.refreshMisc() ; 

    }

    public refreshAttack() {

        let val = this.cfgCurrent.attackDamage ; 
        if( val == 0 ) return ; 

        this.attackDamage.root.active = true ; 

        // 
        this.attackDamage.val.string = "<b>" + val.toString() + "</b>" ; 

        //  
        if( this.cfgNext != null ) {
            let diff = this.cfgNext.attackDamage - this.cfgCurrent.attackDamage ; 
            if( diff > 0 ) {
                this.attackDamage.val.string += "<b><color=Green>+" + diff + "</color></b>" ; 
            }
        }

        // 
        this.target.root.active = true ; 
        this.target.val.string = "<b>" + Lang.get( "AttackTargeSelType_" + this.cfgArctile.tower.atkTargeSelType ) + "</b>" ; 

        //  CD 
        this.cd.root.active = true ; 
        this.cd.val.string = "<b>" + FrameSecConvert.toSecond( this.cfgArctile.tower.atkCd ).toString() + "</b>" ; 

    }

    // 
    public refreshMisc() {
     
        let str1 = "<b>{1}</b>" ; 
        let str2 = "<b>{1}%</b>" ; 

        let cfgItems = BagArticleGradeHelper.getCfgCurrentItems( this.cfgArctile.id ) ;

        for( let cfg of cfgItems ) {

            let ids = cfg.buffIds ; 
            for( let id of ids ) {
                let cfgBuffItem = CfgBuff.data.get( id ) ;
                
                switch( cfgBuffItem.affixType ) {
                    
                    case AffixType.HpMaxIncrease : {
                        this.hpMaxIncrease.root.active = true ; 
                        this.hpMaxIncrease.val.string = str1.replace( "{1}" , cfgBuffItem.affixArgN.toString() ) ; 
                    }
                    break ;

                    case AffixType.HpRegenIncrease : {
                        this.HpRegen.root.active = true ; 
                        this.HpRegen.val.string = str1.replace( "{1}" , cfgBuffItem.affixArgN.toString() ) ; 
                    }
                    break ;

                    case AffixType.HpRegenIncreasePercent : {
                        this.HpRegen.root.active = true ; 
                        let n = cfgBuffItem.affixArgN * 100 ;
                        this.HpRegen.val.string = str2.replace( "{1}" , n.toString() ) ; 
                    }
                    break ;

                    case AffixType.GetMomeyPerWave : {
                        this.Money001.root.active = true ; 
                        this.Money001.val.string = str1.replace( "{1}" , cfgBuffItem.affixArgN.toString() ) ; 
                    }
                    break ;

                    case AffixType.AuraAttackDamageIncreasePercent : {
                        this.AttackDamageAura.root.active = true ; 
                        let n = cfgBuffItem.affixArgN * 100 ;
                        this.AttackDamageAura.val.string = str2.replace( "{1}" , n.toString() ) ; 
                    }
                    break ; 

                }

            } // end for

        } // end for

    }

}

// 
class UiBagArticleDetailBaseAttributeItem {

    public root : Node ; 

    public name : Label ; 

    public icon : Sprite ; 

    public val : RichText ; 

    public init( node : Node ) {

        this.root = node ; 

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;

        this.val = this.root.getChildByPath( "Val" ).getComponent( RichText ) ;

    }

}