import { Random } from "./Random";


export class WeightProbabilityItem {

    // 
    public name: string;

    // 
    public probability: number;

}

// 
export class WeightProbability {

    // 
    public init(probabilities: Array<WeightProbabilityItem>): void {
        
        // 
        this.weightCount = 0;
        this.data.splice(0, this.data.length);

        for (let i = 0; i < probabilities.length; ++i) {
            let item = probabilities[i];

            let item2 = {};
            item2["min"] = this.weightCount;
            item2["max"] = this.weightCount + item.probability;
            item2["id"] = item.name ;
            this.data.push(item2);

            this.weightCount += item.probability;

        } // end for

    }

    public add( id : string , w : number ) {

        let item = {};
        item["min"] = this.weightCount;
        item["max"] = this.weightCount + w ;
        item["id"] = id ;
        this.data.push(item);

        this.weightCount += w ;

    }

    // 
    public get(): string {

        let ret: string = null;

        let n = WeightProbability.rangeInt(0, this.weightCount - 1);
        // 
        for (let i = 0; i < this.data.length; ++i) {

            let item = this.data[i];
            if (item["min"] <= n && n < item["max"]) {
                ret = item["id"] ;
            }

        } // end for

        return ret;

    }

    // 
    public clear() {
        this.data.splice( 0 , this.data.length ) ;
        this.weightCount = 0;
    }

    public static rangeInt(min : number , max : number ) : number
    {
        return Math.floor(Math.random() * ( max + 1 - min  ) ) + min ;

    }

    private weightCount = 0;

    private data = new Array();

}