
import { AttackTargetSelectionType } from "../../Game/World/1.Base/Tower/Entity/AttackCom";
import { FrameSecConvert } from "../Misc/FrameSecConvert";
import { SliderJoint2D } from "cc";

// 
export class CfgTowerItem {

    // ID
    public atkBulletId : string ; 

    // 
    public atkDamage : number = 0 ; 

    // CD
    public atkCd : number = 0 ; 

    // 
    public atkCritProbability : number = 0 ; 
    // 
    public atkCritMultiple : number = 2 ; 

    // 
    public atkTargeSelType = AttackTargetSelectionType.None ; 

    // 
    public atkBulletScatterCount = 0 ; 

    // Buff
    public buffs = new Array< string >() ; 


}


export class CfgTowerParser {

    public static parseTowerItem( cfgItem : CfgTowerItem , row : any ) {

        let str : string ; 

        str = row[ "AttackBulletId" ] ; 
        if(str != null && str != "" ) {
            cfgItem.atkBulletId = str ; 
        }

        str = row[ "AttackValue" ] ; 
        if(str != null && str != "" ) {
            cfgItem.atkDamage = Number( str ) ; 
        }
        
        str = row[ "AttackCd" ] ; 
        if(str != null && str != "" ) {
            cfgItem.atkCd = FrameSecConvert.toFrame( Number( str ) ) ; 
        }
        
        // 
        str = row[ "AttackTargetSelectionType" ] ; 
        if( str != null && str != "" ) {
            cfgItem.atkTargeSelType = str as AttackTargetSelectionType ; 
        }
        else {
            cfgItem.atkTargeSelType = AttackTargetSelectionType.First ; 
        }

        str = row[ "AttackCritProb" ] ; 
        if( str != null && str != "" ) {
            cfgItem.atkCritProbability = Number( str ) * 100 ; 
        }
        str = row[ "AttackCritMultiple" ] ; 
        if( str != null && str != "" ) {
            cfgItem.atkCritMultiple = Number( str ) ; 
        }
        
        str = row[ "AttackBulletScatterCount" ] ; 
        if( str != null && str != "" ) {
            cfgItem.atkBulletScatterCount = Number( str ) ; 
        }

        str = row[ "Buffs" ] ; 
        if( str != null && str != "" ) {
            let strs = str.split( /[,\n]/ ) ;
            for( let i = 0 ; i < strs.length ; ++i ) {
                let str2 = strs[ i ] ;
                cfgItem.buffs.push( str2.trim() ) ;
            } // end for
            
        }



    }

}