
// 
export class MergeDefinition {

    public static ins : MergeDefinition = null ; 

    // 
    public addDefinition( id1 : string , id2 : string , id3 : string , isHybrid = false ) {

        let orig = new MergeDataItem( id1 , id2 , id3 , isHybrid ) ; 
        this.orig.push( orig ) ; 

        if( id1 == id2 ) {
            this.addDefinitionSingle( id1 , id2 , id3 , this.data ) ; 
            return ;
        }

        //  2
        let data = isHybrid == false ? this.data : this.hybrid ; 
        this.addDefinitionSingle( id1 , id2 , id3 , data ) ; 
        this.addDefinitionSingle( id2 , id1 , id3 , data ) ; 

    }

    //  
    private addDefinitionSingle( id1 : string , id2 : string , id3 : string , 
        data : Map< string , Map< string , string > > ) {

        let mapping2 = data.get( id1 ) ;
        if( mapping2 == null ) {
            mapping2 = new Map< string , string >() ; 
            data.set( id1 , mapping2 ) ;
        }

        mapping2.set( id2 , id3 ) ; 

    }

    // ID
    public getMergedId( id1 : string , id2 : string ) : string {

        let mapping2 = this.data.get( id1 ) ;
        if( mapping2 == null ) return null ; 

        return mapping2.get( id2 ) ;

    }

    // ID 
    public getMergedIdhybrid( id1 : string , id2 : string ) {

        // 
        let ret = this.allowData.get( id1 ) ; 
        if( ret == false ) return null ; 

        ret = this.allowData.get( id2 ) ; 
        if( ret == false ) return null ; 

        let mapping2 = this.hybrid.get( id1 ) ;
        if( mapping2 == null ) return null ; 

        return mapping2.get( id2 ) ;

    }

    // 
    public orig = new Array< MergeDataItem >() ; 

    //   
    public data = new Map< string , Map< string , string > >() ; 

    //    id1 -> id2 , id2 -> id1 
    public hybrid = new Map< string , Map< string , string > >() ; 

    // 
    public allowData = new Map< string , boolean >() ;  

}

// 
export class MergeDataItem {

    public id1 : string ; 

    public id2 : string ; 

    public id3 : string ; 

    public isHybrid = false ; 

    public constructor( id1 : string = null , id2 : string = null , id3 : string = null , isHybrid = false ) {
        this.id1 = id1 ; 
        this.id2 = id2 ; 
        this.id3 = id3 ; 
        this.isHybrid = isHybrid ; 
    }

}