import { resources } from "cc";
import { BagArticle } from "../../Bag/Article/BagArticle";
import { UiBagBottom } from "../../Bag/Main/UiBagBottom";
import { UiTray } from "../../Bag/Main/UiTray";
import { UiBagTouch } from "../../Bag/Touch/UiBagTouch";
import { GameArg } from "../GameArg";
import { Prefab } from "cc";
import { instantiate } from "cc";
import { UiMain } from "../../Ui/Main/UiMain";
import { UiBagGrid } from "../../Bag/Grid/UiBagGrid";
import { Vec2 } from "cc";
import { tween } from "cc";
import { Vec3 } from "cc";
import { Node } from "cc";
import { UITransform } from "cc";
import { BlockInputEvents } from "cc";
import { Tween } from "cc";
import { UiGuideCover } from "../../../Common/Guide/UiGuideCover";
import { Input } from "cc";
import { SoundBtnClickSetter } from "../../../Common/Audio/SoundBtnClickSetter";
import { BagArticleGridPlaceByPos } from "../../Bag/Article/BagArticleGridPlaceByPos";
import { UiTextBox } from "./UiTextBox";
import { Lang } from "../../../Common/Misc/Lang";
import { languages } from "../../../../resources/i18n/en";
import { GuideData } from "./GuideData";


// 
export class Guide {

    // 
    public static pf : Node ; 
    public static hand : Node ; 

    public static textBox : UiTextBox = null ;  

    //  
    public static cover : UiGuideCover ; 

    public static blockInput : Node ; 



    // 
    public static exe1() {

        let b = GameArg.ins.mainIdx == 1 ; 
        if( b == false ) return ; 

        GuideData.ins = new GuideData() ; 
        GuideData.ins.load() ; 
        if( GuideData.ins.hasRecord1() == true ) {
            GuideData.ins = null ; 
            return ; 
        }


        // 
        UiBagBottom.ins.hide( ) ; 

        // 
        UiTray.ins.clear() ; 
        // 2 
        UiTray.ins.addArticleById( "004_1" ) ;
        UiTray.ins.addArticleById( "005_1" ) ;
        UiTray.ins.addArticleById( "002_1" ) ;
        
        // 
        let blockInput = new Node( "BlockInput" ) ; 
        this.blockInput = blockInput ; 
        blockInput.addComponent( UITransform ).setContentSize( 99999 , 99999 ) ; 
        blockInput.addComponent( BlockInputEvents ) ; 
        blockInput.setParent( UiMain.ins.root , false ) ;
        blockInput.position = Vec3.ZERO ; 

        // 
        let list = [ 
            "Game/Misc/Guide" , 
            "Common/Ui/GuideCover" , 
         ] ;
        resources.load( list , Prefab , ( err , data )=>{

            // blockInput.destroy() ; 

            // 
            let root = instantiate( data[ 0 ] ) ; 
            this.pf = root ; 
            let hand = this.hand = root.getChildByPath( "Hand" ) ; 
            hand.setParent( UiMain.ins.root , false ) ; 

            let rootCover = instantiate( data[ 1 ] ) ; 
            this.cover = new UiGuideCover() ; 
            this.cover.init( rootCover ) ; 
            this.cover.root.setParent( UiMain.ins.root , false ) ; 
            this.cover.root.active = false ; 

            // 
            this.textBox = new UiTextBox() ; 
            this.textBox.init( root.getChildByPath( "Text" ) ) ; 
            this.textBox.root.active = false ; 
            this.textBox.root.setParent( UiMain.ins.root , false ) ;


            this.guideStep0( root.getChildByPath( "Ui1" ) ) ; 

            // this.guideRefresh() ; 
            
        } ) ; 

    }

    // 
    private static guideStep0( root : Node ) {

        root.setParent( UiMain.ins.root , false ) ; 
        root.position = Vec3.ZERO ; 

        let cover = root.getChildByPath( "Cover" ) ; 
        cover.getComponent( UITransform ).setContentSize( 9999 , 9999 ) ; 
        cover.on( Input.EventType.TOUCH_END , ()=>{
            root.destroy() ; 
            this.guideStep1() ; 
        } ) ; 

        // 
        let prompt = root.getChildByPath( "Prompt" ) ; 
        tween( prompt ).sequence(
            tween( prompt ).to( 0.5 , { scale : new Vec3( 1.2 , 1.2 , 1.2 ) } ) , 
            tween( prompt ).to( 0.5 , { scale : new Vec3( 1.0 , 1.0 , 1.0 ) } )
        ).repeatForever().start() ; 

    }

    // 
    private static guideStep1() {

        this.textBox.root.active = true ; 
        this.textBox.root.position = new Vec3( 0 , 600 ) ; 
        this.textBox.label.string = Lang.get( "Game_Guide_3" ) ; 

        let blockInput = this.blockInput ; 
        blockInput.active = false ; 

        // 
        this.guideTrayToGrid( UiTray.ins.articles[ 0 ] , new Vec2( 1 , 2 ) , ()=>{

            // 
            blockInput.active = true ; 
            tween( UiMain.ins.root ).delay( 0.2 ).call( ()=>{

                blockInput.active = false ; 
                this.guideTrayToGrid( UiTray.ins.articles[ 0 ] , new Vec2( 2 , 2 ) , ()=>{

                    // 
                    blockInput.active = true ; 
                    tween( UiMain.ins.root ).delay( 0.2 ).call( ()=>{

                        blockInput.active = false ; 
                        this.guideTrayToGrid( UiTray.ins.articles[ 0 ] , new Vec2( 2 , 3 ) , ()=>{
    
                            blockInput.active = true ; 
                            this.hand.active = false ; 

                            // 
                            UiBagTouch.ins.root.active = false ; 


                            this.guideBtnRefresh() ; 

                        } ) ; 

                    } ).start() ; 

                } ) ; 

            } ).start() ; 


        } ) ; 

    }

    //  
    private static guideBtnRefresh() {

        this.textBox.root.active = true ; 
        this.textBox.root.position = new Vec3( 0 , -600 ) ; 
        this.textBox.label.string = Lang.get( "Game_Guide_4" ) ; 

        // 
        this.blockInput.active = false ; 

        let btn = UiBagBottom.ins.btnRefresh ; 
        // btn.active = true ; 

        // 
        let btn2 = instantiate( btn ) ; 
        btn2.active = true ; 
        btn2.setParent( UiMain.ins.root , true ) ;
        btn2.worldPosition = btn.parent.worldPosition ; 
        SoundBtnClickSetter.exe( btn2 ) ;
        btn2.on( Input.EventType.TOUCH_END , ()=>{
            btn2.destroy() ; 
            this.cover.root.active = false ; 

            this.textBox.root.active = false ; 

            // 
            UiTray.ins.clear() ; 
            // 2 
            UiTray.ins.addArticleById( "004_1" ) ;
            UiTray.ins.addArticleById( "006_1" ) ;
            UiTray.ins.addArticleById( "003_1" ) ;

            this.blockInput.active = true ; 
            tween( UiMain.ins.root ).delay( 0.5 ).call( ()=>{
                this.blockInput.active = false ; 
                this.guideStep2() ; 

            } ).start() ; 
            

        } ) ; 

        this.coverBtn( btn2 ) ; 

    }

    // 
    private static guideStep2() {

        this.textBox.root.active = true ; 
        this.textBox.root.position = new Vec3( 0 , 600 ) ; 
        this.textBox.label.string = Lang.get( "Game_Guide_5" ) ; 

        let blockInput = this.blockInput ; 
        blockInput.active = false ; 

        UiBagTouch.ins.root.active = true ; 

        this.guideTrayToGrid( UiTray.ins.articles[ 0 ] , new Vec2( 1 , 2 ) , ()=>{

            // 
            blockInput.active = true ; 
            tween( UiMain.ins.root ).delay( 0.2 ).call( ()=>{

                blockInput.active = false ; 
                this.guideTrayToGrid( UiTray.ins.articles[ 0 ] , new Vec2( 1 , 4 ) , ()=>{

                    // 
                    blockInput.active = true ; 
                    tween( UiMain.ins.root ).delay( 0.2 ).call( ()=>{

                        blockInput.active = false ; 
                        this.guideTrayToGrid( UiTray.ins.articles[ 0 ] , new Vec2( 3 , 2 ) , ()=>{
    
                            blockInput.active = true ; 
                            this.hand.active = false ; 
                            this.textBox.root.active = false ; 

                            // 
                            UiBagTouch.ins.root.active = false ; 


                            this.guideBtnStart() ; 

                        } ) ; 

                    } ).start() ; 

                } ) ; 

            } ).start() ; 

        } ) ;

    }

    // 
    private static guideBtnStart() {

        this.textBox.root.active = true ; 
        this.textBox.root.position = new Vec3( 0 , -600 ) ; 
        this.textBox.label.string = Lang.get( "Game_Guide_6" ) ; 

        // 
        this.blockInput.active = false ; 

        let btn = UiBagBottom.ins.btnStart ; 

        // 
        let btn2 = instantiate( btn ) ; 
        btn2.active = true ; 
        btn2.setParent( UiMain.ins.root , true ) ;
        btn2.worldPosition = btn.parent.worldPosition ; 
        SoundBtnClickSetter.exe( btn2 ) ;
        btn2.on( Input.EventType.TOUCH_END , ()=>{
            btn2.destroy() ; 
            this.textBox.root.active = false ; 
            this.complete() ;
            btn.emit( Input.EventType.TOUCH_END );
            
        } ) ; 

        this.coverBtn( btn2 ) ; 

    }

    private static complete() {

        // 
        UiBagTouch.ins.onBeforeArticlePlace = null ; 
        UiBagTouch.ins.onBeforeArticleSelect = null ; 
        UiBagTouch.ins.onCompleteArticlePlace = null ; 

        // 
        this.blockInput.destroy() ; 
        this.cover.root.destroy() ; 
        this.hand.destroy() ; 

        // 
        GuideData.ins.record1() ; 
        GuideData.ins.save() ; 
        GuideData.ins = null ; 

    }

    // 
    private static coverBtn( btn : Node ) {

        btn.active = true ; 

        // 
        let size = btn.getComponent( UITransform ).contentSize.clone() ; 

        // 
        let size1 = size.clone() ;
        size1.width *= 15 ; size1.height *= 15 ; 
        size1.set( 3000 , 3000 ) ; 

        // 
        this.cover.root.active = true ; 
        this.cover.mask.worldPosition = btn.worldPosition ; 
        this.cover.maskUiTrans.setContentSize( size1 ) ; 
        this.cover.showAnim( btn.worldPosition , size , 0.5 ) ;

    }

    // 
    private static guideTrayToGrid( article : BagArticle , coordTarget : Vec2 , onComplete : ()=>void ) {

        let idx = UiTray.ins.articles.indexOf( article ) ; 

        let articleTarget = UiTray.ins.articles[ idx ] ; 
        let pos1 = articleTarget.view.root.worldPosition ; 
        let cell = UiBagGrid.ins.getCell( coordTarget ) ; 
        let pos2 = cell.root.worldPosition ; 
        
        this.hand.active = true ; 
        this.handMove( this.hand , pos1 , pos2 ) ; 

        // 
        UiBagTouch.ins.onBeforeArticleSelect = ( article : BagArticle )=>{
            return articleTarget == article ; 
        } ;

        // 
        UiBagTouch.ins.onBeforeArticlePlace = ( article : BagArticle )=>{

            let cells = BagArticleGridPlaceByPos.getCellsPlace( article ) ; 

            let b = cells != null && 
                cells.find( ( e )=>{ return e.coord.equals( coordTarget ) ; } ) != null ;
            if( b == false ) {

                // 

                // 
                UiTray.ins.insertArticleByIndex( article , idx ) ; 
                UiBagTouch.ins.towerAuraHint.clear( ) ; 

                return false ;
            }

            // 
            tween( UiMain.ins.root ).delay( 0.01 ).call( onComplete ).start( ) ;

            return true ;

        } ;

    }

    // 
    private static handMove( hand : Node , pos1 : Vec3 , pos2 : Vec3  ) {

        let duration = pos1.clone().subtract( pos2 ).length() / 400 ;

        // 
        let pos12 = pos1.clone() ; 

        hand.worldPosition = pos12 ; 

        Tween.stopAllByTarget( hand ) ; 

        tween( hand ).sequence(

            tween( hand ).call( ()=>{
                hand.worldPosition = pos12 ; 
            } ) , 

            tween( hand ).to( duration , { worldPosition : pos2 } ) , 

            tween( hand ).delay( 0.1 ) , 

        ).repeatForever().start() ; 

    }



}

