import { AffixType } from "../../Game/World/1.Base/Buff/Affix";
import { FrameSecConvert } from "../Misc/FrameSecConvert";


export class CfgGameBagTrayAricleGen {

    // 
    public static data = new Map< string , CfgGameBagTrayAricleGenItem >() ; 

    // 
    public static common = Array< CfgGameBagTrayAricleGenItem >() ;

}

export class CfgGameBagTrayAricleGenItem {

    public id : string ; 

    // public isCommon = false ; 

    // 
    public pw : number ; 

    public grade : Array< number > ; 

}


export class CfgGameBagTrayAricleGenParser {

    public static parseJson( json : any ) {

        let str : string = null ; 
        
        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgGameBagTrayAricleGenItem() ;

            str = row[ "isCommon" ] ; 
            if( str != null && str != "" ) {
                CfgGameBagTrayAricleGen.common.push( cfgItem ) ;
            }
            else {
                CfgGameBagTrayAricleGen.data.set( id , cfgItem ) ; 
            }

            cfgItem.id = id ;

            cfgItem.pw = Number( row[ "Weight" ] ) ; 

            // 
            cfgItem.grade = new Array< number >() ; 
            for( let i = 1 ; i < 10 ; ++i ) {
                let key = "MergeGrade" + i ; 
                let str = row[ key ] ; 
                let p = 0 ; 
                if( str != null && str != "" ) {
                    p = Number( str ) ;
                }
                cfgItem.grade.push( p ) ; 
                
            }


        } // endf

    }

}