import { Label, Node, Sprite } from "cc";
import { UiBagItem } from "./UiBagItem";
import { CfgArticleUnlock } from "../../../Common/Cfg/CfgArticleUnlock";

// 
export class UiBagItemLocked extends UiBagItem {

    // 
    public unlockInfo : Label ; 

    public override init( node : Node ) : void {

        super.init( node ) ; 

        this.unlockInfo = this.root.getChildByPath( "UnlockInfo" ).getComponent( Label ) ;

    }

    public override refresh() : void {
        super.refresh() ;

        // 
        let shape = this.shape.getChildByPath( "Shape" ) ; 
        for( let node of shape.children ) {
            node.getComponent( Sprite ).grayscale = true ; 
            
        }

        this.refreshUnlockInfo() ; 

    }

    public refreshUnlockInfo() {

        // 
        let cfgArticleUnlockItem = CfgArticleUnlock.data.get( this.articleId ) ; 
        if( cfgArticleUnlockItem != null ) {

            let str = "{1}" ; 
            str = str.replace( "{1}" , cfgArticleUnlockItem.levelIdx.toString() ) ; 
            this.unlockInfo.string = str ; 

            return ;


        }

    }

    
}