import { Input, Layout, Node, RichText, Size, Sprite, tween, UITransform, Vec3 } from "cc";
import { Lang } from "../../../../Common/Misc/Lang";
import { PfMgrIconBuff } from "../../../../Common/PfMgr/PfMgrIconBuff";
import { CfgFeatureItem } from "../../../../Common/Cfg/CfgFeature";
import { GamePause } from "../../../World/2.Td/Flow/GamePause";
import { UiFeatureSel } from "./UiFeatureSel";
import { FeatureAcquire } from "./FeatureAcquire";
import { BuffString } from "../../../World/1.Base/Buff/BuffString";
import { CfgBuff, CfgBuffItem } from "../../../../Common/Cfg/CfgBuff";
import { NodeSizeFit } from "../../../../SL/Cocos/Ui/NodeSizeFit";
import { UiBgQuality } from "../../../../Common/Ui/UiBgQuality";
import { Button } from "cc";

// 
export class UiFeatureSelItem {

    public grade = 1 ; 

    public featureSel : UiFeatureSel = null ; 

    public cfgItem : CfgFeatureItem ; 

    public root : Node ; 

    public icon : Sprite ; 
    private iconSize : Size ; 
    
    public descr : RichText ; 
    
    public bg = new UiBgQuality() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon = this.root.getChildByPath( "Icon/val" ).getComponent( Sprite ) ; 
        this.iconSize = this.icon.getComponent( UITransform ).contentSize.clone() ;
        

        this.descr = this.root.getChildByPath( "Descr" ).getComponent( RichText ) ;

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ; 
        
        this.bg.init( this.root.getChildByPath( "bg" ) ) ;

    }

    public refresh( item : CfgBuffItem ) {

        this.cfgItem = item ; 

        // 
        this.refreshIcon( item ) ; 

        // 
        this.refreshDescr( item ) ; 

        // 
        this.bg.show( item.grade ) ;

    }

    private refreshIcon( item : CfgBuffItem ) {

        // this.icon.node.active = false ; return ;

        this.icon.node.active = true ; 

        // 
        let spr = PfMgrIconBuff.ins.getIconById( item.id ) ;
        this.icon.spriteFrame = spr.spriteFrame ; 
        
        // Size
        NodeSizeFit.trimmed( this.icon , this.iconSize ) ;
        
    }

    private refreshDescr( item : CfgBuffItem ) {

        let str = BuffString.getDescription( item.id ) ;
        // 
        str = "<outline color=black width=4> <b>" + str + "</b> </outline>" ;
        this.descr.string = str ; 

    }


    private onClick( ) {

        // 
        let btn = this.root.getComponent( Button ) ; 
        if( btn.enabled == false ) return ; 
        btn.enabled = false ; 

        this.playAnimSel( ()=>{

            // UI
            // this.featureSel.root.destroy() ; 
            this.featureSel.root.active = false ; 

            // 
            GamePause.resume() ; 

            FeatureAcquire.exe( [ this.cfgItem.id ] ) ;

        } ) ;

        
    }

    // 
    private playAnimSel( onComplete : ()=>void ) {

        this.featureSel.title.active = false ; 
        this.featureSel.btnList.root.active = false ; 

        // 
        this.featureSel.itemList.root.getComponent( Layout ).enabled = false ; 

        // 
        for( let item of this.featureSel.itemList.items ) {
            if( item == this ) { 
                continue ; 
            }
            item.root.active = false ; 
        }

        // 
        tween( this.root ).sequence(
            tween( this.root ).to( 0.2 , { scale : new Vec3( 1.2 , 1.2 , 1.2 ) } ) , 
            tween( this.root ).to( 0.1 , { scale : new Vec3( 0 , 0 , 0 ) } ) , 
            tween( this.root ).call( ()=>{

                onComplete?.() ;

                // UI
                // this.featureSel.root.destroy() ; 
                this.featureSel.root.active = false ; 

                // 
                GamePause.resume() ; 

            } ) ,
        ).start() ;

    }
    
}