import { find, Node } from "cc";
import { UiTop } from "./UiTop";
import { UiPlayerGrade } from "./UiPlayerGrade";
import { UiFeature } from "./Feature/UiFeature";
import { UiFeatureSel } from "../Popup/FeatureSel/UiFeatureSel";
import { SoundBtnClickSetter } from "../../../Common/Audio/SoundBtnClickSetter";

export class UiMain {

    public static ins : UiMain = null ; 

    public root : Node ; 

    public init() {

        this.root = find( "Canvas" ) ; 

        // UiKeyboard.ins = new UiKeyboard() ; 
        // UiKeyboard.ins.init() ; 

        UiFeatureSel.ins = UiFeatureSel.create( UiMain.ins.root ) ; 
        UiFeatureSel.ins.root.active = false ; 

        new UiTop().init( this.root.getChildByPath( "Ui/Top" ) );
        
        UiPlayerGrade.ins = new UiPlayerGrade() ; 
        UiPlayerGrade.ins.init( find( "Canvas/Ui/Bottom/Grade" ) ) ; 
        UiPlayerGrade.ins.refresh() ; 
        
        UiFeature.ins = new UiFeature() ; 
        UiFeature.ins.init( find( "Canvas/Ui/Bottom/Feature" ) ) ; 
        UiFeature.ins.refresh() ; 

        // 
        SoundBtnClickSetter.exe( this.root ) ; 

    }

}