import { Lang } from "../../../../Common/Misc/Lang";


export class LevelString {

    public static getName( id : string ) : string {

        let key = "MainLevel_Name_" + id ; 
        let str = Lang.get( key ) ; 
        return str ; 

    }

    public static getMainLevelName( idx : number ) : string {

        let key = "MainLevel_Name_" + ( idx + 1 ) ; 
        let str = Lang.get( key ) ; 
        return str ; 

    }

}