
import { AccountPersist } from '../../Common/Account/AccountPersist';
import { ResLoad } from './ResLoad';
import { director, find, Label } from 'cc';
import { Lang } from '../../Common/Misc/Lang';
import { UiKeyboard } from './UiKeyboard';
import { Account } from '../../Common/Account/Account';
import { UiToast } from '../../Common/Ui/UiToast';
import { UiWin } from '../../Game/Ui/Popup/Settle/UiWin';
import { UiLose } from '../../Game/Ui/Popup/Settle/UiLose';
import { AdInit } from '../../Common/Ad/Misc/AdInit';
import { WxHelper } from '../../SL/Cocos/Wx/WxHelper';
import { UiBar } from '../../Game/Ui/Bar/UiBar';
import { PlatformInit } from '../../Common/Platform/PlatformInit';
import { StaminaRecoveryPersist } from '../../Common/Misc/StaminaRecovery';
import { AudioSettingsPersist } from '../../Common/Audio/AudioSettingsPersist';
import { DateTimeMgr } from '../../SL/Common/Misc/DateTimeMgr';
import { DailyCheckinDataPersist } from '../../Home/Ui/Popup/DailyCheckin/DailyCheckinData';
import { assetManager } from 'cc';
import { log } from 'cc';
import { TextAsset } from 'cc';
import { JsonAsset } from 'cc';
import { Test } from './Test';


export class Init {

    public static exe() {

        /*
            
            https://docs.cocos.com/creator/3.8/manual/zh/advanced-topics/dynamic-atlas.html
        */
        // macro.CLEANUP_IMAGE_CACHE = false;
        // DynamicAtlasManager.instance.enabled = true;
        
        // 
        DateTimeMgr.ins = new  DateTimeMgr() ; 
        DateTimeMgr.ins.dtStart = new Date( Date.now() ) ;

        Lang.init() ;   

        let _wxHelper=new WxHelper();
        WxHelper.enableShareMenu() ; 
        _wxHelper.initShare();
        _wxHelper.CheckIsShare();

        UiKeyboard.ins = new UiKeyboard() ; 
        UiKeyboard.ins.init() ; 

        // 
        let ProgressBar = new UiBar() ;
        ProgressBar.init( find( "Canvas/Progress" ) );
        ProgressBar.bar.fillRange = 0 ;
        let txt = ProgressBar.root.getComponentInChildren( Label ) ; 

        // 
        ResLoad.onProgress = ( val : number )=>{

            // 
            let val2 = Math.floor( val * 100 ) ;
            txt.string = val2 + " %" ; 
            
            // 
            ProgressBar.bar.fillRange = val / 1 ; 
            
        } ;
        ResLoad.exe( this.onCompleteResLoad.bind( this ) ) ;
        
        /*
            
            
        */ 
        UiWin.loadPf() ; 
        UiLose.loadPf() ; 
        UiToast.loadPf() ; 

        PlatformInit.exe() ; 

        Test.exe() ; 
        
    }

    private static onCompleteResLoad() {

        AdInit.exe() ; 

        director.loadScene( "Home" );

        Account.ins = new Account() ; 
        Account.ins.init() ;
        
        AccountPersist.load() ; 
        StaminaRecoveryPersist.load() ; 
        AudioSettingsPersist.load() ; 
        DailyCheckinDataPersist.load() ; 

    }


}