import { Vec3 } from "cc";
import { WorldNode } from "../../1.Base/Support/Misc/WorldNode";


// 
export class EnemyMovePath {

    public static ins = new EnemyMovePath() ; 

    public data = new Array< Vec3 >() ;

    public init( ) {

        this.data.splice( 0 , this.data.length ) ; 

        let node = WorldNode.ins.root.getChildByPath( "Cfg/Path" ) ; 
        for( let i = 0 ; i < node.children.length ; ++i ) {

            let point = node.children[ i ] ; 

            this.data.push( point.position ) ; 

        } // end for

        node.destroy() ; 

    }

}