import { StringUtil } from "../../../../SL/Common/StringUtil";
import { FrameSecConvert } from "../../../../Common/Misc/FrameSecConvert";

//   
export class CfgLevelWaveMgr {

    // 
    public static data = new Map< string , LevelData >() ; 

    // 
    public static main = Array< LevelData >( null ) ;

}

// 
export class LevelData {

    public id : string ; 

    // 
    public waves = new Array< WaveData >( null ) ; 

}

// 
export class WaveData {

    // 
    public rewards = new Array< string >() ; 

    // 
    public items = new Array< WaveItem >() ; 
 
}

// 
export class WaveItem {

    // 
    public triggerTime : number ; 

    // ID
    public genRoleId : string ; 

   
    // 
    public hpMaxFactor = 1 ; 
 
    // 
    public attackFactor = 1 ; 

}


// 
export class CfgLevelWaveParser {

    public static parseJson( json : any ) {

        let data = CfgLevelWaveMgr.data ; 

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let levelId = row[ "Id" ].toString() ;
            if( levelId == null || levelId == "" ) continue ; 

            // let levelIdx = Number( levelId ) ; 

            let levelData = data.get( levelId ) ;
            if( levelData == null ) {
                levelData = new LevelData() ; 
                levelData.id = levelId ; 
                data.set( levelId , levelData );
            }

            let wave = this.parseWave( row ) ; 
            levelData.waves.push( wave ) ; 

        } // end for

        // 
        for( let i = 1 ; ; ++i ) {

            let id = i.toString() ;

            let levelData = data.get( id ) ;
            if( levelData == null ) break ; 

            CfgLevelWaveMgr.main.push( levelData ) ; 

        } // end for

    }

    // 
    private static parseWave( row : any ) : WaveData {

        let wave = new WaveData() ; 

        let hpMaxFactor = 1 ;
        let str = row[ "HpMaxFactor" ] ; 
        if( str != null && str != "" ) {
            hpMaxFactor = Number( str ) ; 
        }

        let attackFactor = 1 ; 
        str = row[ "AttackFactor" ] ; 
        if( str != null && str != "" ) {
            attackFactor = Number( str ) ; 
        }

        for( let i = 1 ; ; ++i ) {

            // ID
            let keyId = "G" + i + "_Id" ; 
            let id = row[ keyId ] as string ; 
            if( id == null || id == "" ) break ;
            
            // 
            let keyDelay = "G" + i + "_Delay" ; 
            str = row[ keyDelay ] ; 
            let delay = FrameSecConvert.toFrame( Number( str ) ) ;

            // 
            let key = "G" + i + "_Count" ; 
            str = row[ key ] ; 
            let count = Number( str ) ; 
 
            // 
            key = "G" + i + "_Interval" ; 
            str = row[ key ] ; 
            let interval = FrameSecConvert.toFrame( Number( str ) ) ;

            for( let i = 0 ; i < count ; ++i ) {

                let item = new WaveItem() ; 
                wave.items.push( item ) ; 

                item.genRoleId = id ; 
                item.triggerTime = delay + i * interval ; 
                item.hpMaxFactor = hpMaxFactor ; 
                item.attackFactor = attackFactor ; 

            } // end for


        } // end for

        // 
        wave.items.sort( ( a , b )=>{
            if( a < b ) return -1;
            else if( a > b  ) return 1 ;
            return 0 ; 
        } )

        return wave ; 

    }

    

}