import { Vec3 } from "cc";
import { MoveCom } from "../Role/Move/MoveCom";
import { Bullet } from "./Bullet";


// 
export class BulletMoveCalc {

    public static exe( move : MoveCom , dir : Vec3 ) {

        // 
        move.direction.set( dir ) ;
        move.direction.normalize() ; 

        // 
        let offset = move.offset ; 
        offset.set( move.direction ) ;
        offset.multiplyScalar( move.speed ) ; 

    }

    // 
    public static target( bullet : Bullet ) {

        let targetRole = bullet.target.targetRole ; 
        if( targetRole == null ) return ; 

        let target = targetRole.view.collider.node ;
        let v = target.worldPosition.clone().subtract( bullet.view.root.worldPosition ) ; 
        BulletMoveCalc.exe( bullet.move , v ) ;

    }

}