import { randomRangeInt, Vec3 } from "cc";
import { BagArticle } from "../../../../Bag/Article/BagArticle";
import { GridTraverse } from "../../../../Bag/Grid/GridTraverse";
import { BagArticleGridPlace } from "../../../../Bag/Article/BagArticleGridPlace";
import { EffectFactory } from "../../../../Bag/Merge/EffectFactory";
import { TowerAllUpdate } from "../../Tower/Misc/TowerAllUpdate";
import { TowerFactory } from "../../Tower/Entity/TowerFactory";
import { BagArticleFactory } from "../../../../Bag/Article/BagArticleFactory";
import { TowerMgr } from "../../Tower/Entity/TowerMgr";

export class Buff_RndArticleMergeGradeUpgrade {

    public static exe() {

        let article = this.getRndAritcle() ;
        if( article == null ) return ; 

        this.replace( article ) ;

        //  Buff 
        TowerAllUpdate.updateAll() ;         

    }

    // 
    public static replace( article : BagArticle ) : BagArticle {

        // 
        let cells = article.cells.slice() ;
        BagArticleGridPlace.unbind( article ) ; 
        article.view.root.destroy() ; 
        if( article.tower != null ) {
            TowerMgr.ins.remove( article.tower ) ;
        }


        // 
        let id = article.cfg.id ; 
        let id2 = id.split( "_" )[ 0 ] + "_" + ( article.cfg.mergeGrade + 1 ) ; 
        let article2 = BagArticleFactory.create( id2 ) ; 
        article2.view.root.setParent( article.view.root.parent ) ;
        article2.view.root.position = article.view.root.position ; 
        BagArticleGridPlace.bind( article2 , cells ) ;
        let tower2 = TowerFactory.create( article2 ) ; 
        TowerMgr.ins.data.push( tower2 ) ;


        // 
        let effect = EffectFactory.create( "Merge" ) ;
        effect.setParent( article2.view.root , false ) ;
        effect.position = Vec3.ZERO ; 


        return article2 ; 

    }

    // 
    public static getRndAritcle() : BagArticle {

        let list = new Array< BagArticle >() ;
        GridTraverse.exe( ( cell )=>{
            let a = cell.article ; 
            if( a == null || a.cfg.mergeGrade >= 4 ) return ; 
            list.push( a ) ;
        } ) ;

        if( list.length == 0 ) {
            // 
            return null ;
        }

        // 4
        let idx = randomRangeInt( 0 , list.length ) ; 
        return list[ idx ] ;

    }

}