import { BulletMoveType, BulletRotationType } from "../../Game/World/1.Base/Bullet/Bullet";
import { FrameSecConvert } from "../Misc/FrameSecConvert";

export class CfgBullet {

    public static data = new Map< string , CfgBulletItem >() ;  

}

export class CfgBulletItem {

    public id : string ; 

    public viewId : string ; 

    // 
    public moveSpeed : number ;

    // 
    public passCount : number = 0 ;

    public rotationType = BulletRotationType.None ; 

    public bounceRect = 0 ; 
    public bounceEnemy = 0 ; 
    public bounceRectEnemy = 0 ; 
    public BounceBetweenEnemies = 0 ; 

    // 
    public moveType = BulletMoveType.Linear ; 

    // buff
    public buffId : string = null ; 

}

// 
export class CfgBulletParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgBulletItem() ;
            CfgBullet.data.set( id , cfgItem ) ;
            cfgItem.id = id ;

            let speed = Number( row[ "MoveSpeed" ] ) ; 
            cfgItem.moveSpeed = FrameSecConvert.toSecond( speed ) ; 

            let str : string = null ;

            str = row[ "RotationType" ] ; 
            if( str != null && str != "" ) {
                cfgItem.rotationType = str as BulletRotationType ; 
            }
            
            str = row[ "Pass" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.passCount = Number( str ) ; 
            }
            
            str = row[ "BounceRect" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.bounceRect = Number( str ) ; 
            }

            str = row[ "BounceEnemy" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.bounceEnemy = Number( str ) ; 
            }

            str = row[ "BounceRectEnemy" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.bounceRectEnemy = Number( str ) ; 
            }
            
   
            str = row[ "BounceBetweenEnemies" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.BounceBetweenEnemies = Number( str ) ; 
            }

            str = row[ "ViewId" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.viewId = str ; 
            }

            str = row[ "MoveType" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.moveType = str.trim() as BulletMoveType ;
            }
            

            str = row[ "BuffId" ] ; 
            if( str != null && str != ""  ) {
                cfgItem.buffId = str ; 
            }

        } // endf

    }

}
