import { Node } from "cc";
import { RoleMgr } from "../../Role/Base/RoleMgr";
import { PlayerRole } from "../../../2.Td/Player/PlayerRole";
import { CurrentMax } from "../SimpleStruct/CurrentMax";
import { FrameSecConvert } from "../../../../../Common/Misc/FrameSecConvert";

// 
export class ZOrderCtrl {

    public static ins = new ZOrderCtrl() ; 

    public interval = new CurrentMax() ;

    public constructor() {
        // this.interval.max = FrameSecConvert.toFrame( 0.5 ) ;
        this.interval.max = 2 ;
    }

    public update() {

        // 
        this.interval.current-- ;
        if( this.interval.current > 0 ) return ; 
        this.interval.setCurrentAsMax() ; 

        // 
        this.list.splice( 0 , this.list.length ) ; 
        
        // 
        let allenemies = RoleMgr.ins.allEnemies ;
        for( let i = 0 ; i < allenemies.length ; ++i ) {
            let role = allenemies[ i ] ;
            this.list.push( role.view.root ) ;
        }
        this.list.push( PlayerRole.ins.main.view.root ) ;

        //  Y 
        this.list.sort( ( a , b )=>{
            let y1 = a.position.y ; 
            let y2 = b.position.y ;
            if( y1 < y2 ) return 1 ;
            else if ( y1 > y2 ) return -1 ;
            return 0 ;
        } ) ;

        // 
        for( let i = 0 ; i < this.list.length ; ++i ) {
            let node = this.list[ i ] ;
            node.setSiblingIndex( i ) ;

        }

    }

    private list = new Array< Node >() ;

}