import { Input, Label, Node } from "cc";
import { Account } from "../../../../../Common/Account/Account";
import { CfgLevelWaveMgr } from "../../../../../Game/World/2.Td/Level/CfgLevelWaveMgr";
import { UiLevelThumbnail } from "./UiLevelThumbnail";
import { UiLevelBtnList } from "./UiLevelBtnList";
import { UiLevelArrow } from "./UiLevelArrow";
import { UiWaveReward } from "./UiWaveReward";
import { UiLevelInfo } from "./UiLevelInfo";


export class UiLevel {

    public static ins : UiLevel = null ; 

    // 
    public selLevelIdx = 0 ; 

    public root : Node ; 

    public info = new UiLevelInfo() ; 

    // 
    public arrow = new UiLevelArrow() ; 

    // 
    public btnList = new UiLevelBtnList() ; 

    // 
    public thumbnail = new UiLevelThumbnail() ; 

    public waveReward = new UiWaveReward() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.btnList.uiLevel = this ; 
        this.btnList.init( this.root.getChildByPath( "BtnList" ) ) ;

        this.info.uiLevel = this ; 
        this.info.init( this.root.getChildByPath( "Info" ) ) ; 

        this.arrow.uiLevel = this ; 
        this.arrow.init( this.root.getChildByPath( "Arrow" ) ) ;
        
        this.thumbnail.init( this.root.getChildByPath( "Thumbnail" ) ) ; 

        this.waveReward.init( this.root.getChildByPath( "WaveReward" ) ) ; 

        this.setSelLevelIdx( Account.ins.mainLevel.levelIdx ) ;
        this.refresh() ; 

    }

    public setSelLevelIdx( idx : number ) {

        if( idx >= CfgLevelWaveMgr.main.length ) {
            // 
            idx = CfgLevelWaveMgr.main.length - 1 ; 
        }

        this.selLevelIdx = idx ; 

    }

    public refresh() {

        this.arrow.refresh() ; 

        this.info.refresh() ; 

        // 
        this.btnList.refresh( this.selLevelIdx ) ;

        // 
        this.thumbnail.refresh( this.selLevelIdx ) ; 

        // 
        this.waveReward.refresh( this.selLevelIdx ) ; 
        
    }


}



