import { sys } from "cc";

// 
export class GuideData {

    public static ins : GuideData = null ; 

    // 1
    public record1() {

        // 
        this.data.set( "Record1" , "2024.11.11" ) ; 

    }

    // 1
    public hasRecord1() {

        let str = this.data.get( "Record1" ) ;
        if( str == null || str == "" ) return false ; 
        return str == "2024.11.11" ; 

    }

    public load() {

        let str = sys.localStorage.getItem( "GameGuide" ) ;
        if( str == null || str == "" ) return ; 
        
        let json = JSON.parse( str ) ;
        for( let k in json ) {
            this.data.set( k , json[ k ] ) ; 
        } // end for

    }

    public save() {

        let json = {} ; 
        for( let kv of this.data ) {
            json[ kv[0] ] = kv[1] ;
        } ;
        let str = JSON.stringify( json ) ;

        sys.localStorage.setItem( "GameGuide" , str ) ;

    }

    private data = new Map< string , string >() ; 

}