import { instantiate, Node, Size, Tween, tween, UITransform, Vec2, Vec3 } from "cc";
import { CfgArticle, CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { PfMgrBagArticle } from "../../../Common/PfMgr/PfMgr";
import { BagArticle } from "../Article/BagArticle";
import { BagArticleGridPlace } from "../Article/BagArticleGridPlace";
import { BagArticleFactory } from "../Article/BagArticleFactory";

// 
export class UiTray {

    public static ins : UiTray = null ; 

    public articles = new Array< BagArticle >() ; 

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

    }

    public addArticleById( id : string , idx = -1 ) {

        let article = BagArticleFactory.create( id );

        article.view.root.setParent( this.root , false ) ;
        article.view.root.position = Vec3.ZERO ; 

        if( idx == -1 ) {
            this.articles.push( article ) ;
        }
        else {
            this.articles.splice( idx , 0 , article ) ;
        }
        
        
        this.arrange() ; 

    }

    //  pos 
    public insertArticle( article : BagArticle ) {

        // 
        BagArticleGridPlace.unbind( article ) ;

        article.view.root.setParent( this.root , true ) ;

        let idx = this.articles.indexOf( article ) ; 
        if( idx == -1 ) {
            this.articles.push( article ) ;
        }

        //  X 
        this.articles.sort( ( a , b )=>{
            let x1 = a.view.root.worldPosition.x ;
            let x2 = b.view.root.worldPosition.x ;
            // if( x1 < x2 ) return -1 ;
            // else if( x1 > x2 ) return 1 ;
            // else return 0 ;
            return x1 - x2 ;
        } ) ;


        this.arrange() ; 

    }

    // 
    public insertArticleByIndex( article : BagArticle , idx : number ) {

        // 
        BagArticleGridPlace.unbind( article ) ;

        article.view.root.setParent( this.root , true ) ;

        let idx2 = this.articles.indexOf( article ) ; 
        if( idx2 != -1 ) {
            this.articles.splice( idx2 , 1 ) ; 
        }

        // 
        this.articles.splice( idx , 0 , article ) ; 


        this.arrange() ; 

    }

    public removeAndArrange( article ) {

        this.remove( article ) ; 

        this.arrange() ;

    }

    public remove( article ) {

        let idx = this.articles.indexOf( article );
        if( idx == -1 ) return ; 
        this.articles.splice( idx , 1 ) ; 

    }


    // 
    public arrange() {

        let interval = 20 ; 
        // 
        let w = 0 ; 
        for( let article of this.articles ) {

            // let size = article.view.root.getComponent( UITransform ).contentSize ; 
            // let w2 = article.view.main.getComponent( UITransform ).getBoundingBox().width ; 
            // let w2 = article.view.root.getComponent( UITransform ).getBoundingBoxToWorld().width ; 
            let w2 = article.view.size.width ; 
            w += w2 ; 
        }
        // 
        w += ( this.articles.length - 1 ) * interval ; 


        // 
        let xStart = w / 2 * -1 ; 
        for( let article of this.articles ) {

            // let w2 = article.view.root.getComponent( UITransform ).contentSize.width ; 
            // let w2 = article.view.main.getComponent( UITransform ).getBoundingBox().width ; 
            // let w2 = article.view.root.getComponent( UITransform ).getBoundingBoxToWorld().width ; 
            let w2 = article.view.size.width ; 
            let pos = article.view.root.position.clone() ;
            pos.x = xStart + w2 / 2 ;
            pos.y = 0 ; 

            xStart += w2 + interval ; 

            // 
            // article.root.position = pos ; 
            // 
            Tween.stopAllByTarget( article.view.root ) ;
            tween( article.view.root ).to( 0.2 , { position : pos } ).start() ; 

        } // end for

    }

    public clear() {

        for( let article of this.articles ) {
            article.view.root.destroy() ; 
        }
        this.articles.splice( 0 , this.articles.length ) ;

    }

    
}




