// 
export class CfgLoad {

    public exe( onComplete : ()=>void ) {

        // 
        if( this.flag == true ) {
            onComplete() ; 
            return ; 
        }
        this.flag = false ;        

        //  resources 
        this.pathMode = 1 ; 
        let loader = new LoaderResources() ; 
        loader.onComplete = onComplete ;
        
        //    oss 
        // this.pathMode = 2 ; 
        // let loader = new LoaderRemote() ; 
        // loader.onComplete = onComplete ;


        this.common( loader ) ;
        this.home( loader ) ;
        this.game( loader ) ; 

        loader.exe( ) ;

    }

    /*
        1 Resources 
        2  URL
    */ 
    private pathMode = 1 ;

    // ID
    private getPath( key : string ) {

        let ret = key ; 
        switch( this.pathMode ) {
            case 1 : {
                ret = "Common/Cfg/" + key ;
            }
            break ; 

            case 2 : {
                ret = "https://bagtd.oss-cn-beijing.aliyuncs.com/test001/" + key + ".json" ;
            }
            break ; 
        }

        return ret ; 
        

    }

    // 
    private common( loader : LoaderBase ) {

        loader.add( this.getPath( "General" ) , ( data : JsonAsset )=>{
            CfgGeneralParser.parseJson( data.json ) ;
        } ) ;

        //  
        loader.add( this.getPath( "Item" ) , ( data : JsonAsset )=>{
            CfgItemParser.parseJson( data.json ) ;
        } ) ;

        //  
        loader.add( this.getPath( "ItemDraw" ) , ( data : JsonAsset )=>{
            CfgItemDrawParser.parseJson( data.json ) ;
        } ) ;


    }

    // 
    private home( loader : LoaderBase ) {

        // 
        loader.add( this.getPath( "ArticleUnlock" ) , ( data : JsonAsset )=>{
            CfgArticleUnlockParser.parseJson( data.json ) ;
        } ) ;

        // 
        loader.add( this.getPath( "ItemReward" ) , ( data : JsonAsset )=>{
            CfgItemRewardParser.parseJson( data.json ) ;
        } ) ;
        
        // 
        loader.add( this.getPath( "DailyCheckin" ) , ( data : JsonAsset )=>{
            CfgDailyCheckinParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "Shop" ) , ( data : JsonAsset )=>{
            CfgShopParser.parseJson( data.json ) ;
        } ) ;

        

    }

    // 
    private game( loader : LoaderBase ) {

        // 
        loader.add( this.getPath( "LevelGeneral" ) , ( data : JsonAsset )=>{
            CfgLevelGeneralParser.parseJson( data.json ) ;
        } ) ;

        //  
        loader.add( this.getPath( "LevelWave" ) , ( data : JsonAsset )=>{
            CfgLevelWaveParser.parseJson( data.json ) ;
        } ) ;

        // 
        loader.add( this.getPath( "Article" ) , ( data : JsonAsset )=>{
            CfgArticleParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "Bullet" ) , ( data : JsonAsset )=>{
            CfgBulletParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "Role" ) , ( data : JsonAsset )=>{
            CfgRoleParser.parseJson( data.json ) ; 
        } ) ;

        loader.add( this.getPath( "Upgrade" ) , ( data : JsonAsset )=>{
            CfgUpgradeParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "Feature" ), ( data : JsonAsset )=>{
            CfgFeatureParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "Buff" ) , ( data : JsonAsset )=>{
            CfgBuffParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "Shape" ) , ( data : JsonAsset )=>{
            CfgShapeParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "ArticleGrade" ) , ( data : JsonAsset )=>{
            CfgArticleGradeParser.parseJson( data.json ) ;
        } ) ;

        loader.add( this.getPath( "GameBagTrayAricleGen" ) , ( data : JsonAsset )=>{
            CfgGameBagTrayAricleGenParser.parseJson( data.json ) ;
        } ) ;


    }

    // 
    public flag = false ; 

}



