import { Vec2 } from "cc";
import { UiBagGrid } from "./UiBagGrid";
import { UiBagCell } from "./UiBagCell";

export class CellExpand {

    // 4
    public static exe4( coord : Vec2 ) : Array< UiBagCell > {

        let list = new Array< UiBagCell >() ;

        let coord2 = coord.clone() ;
        coord2.y += 1 ;
        let cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }

        coord2 = coord.clone() ;
        coord2.x += 1 ;
        cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }
        
        coord2 = coord.clone() ;
        coord2.y -= 1 ;
        cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }

        coord2 = coord.clone() ;
        coord2.x -= 1 ;
        cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }

        return list ; 

    }

    // 8
    public static exe8( coord : Vec2 ) : Array< UiBagCell > {

        // let list = new Array< UiBagCell >() ;

        let list = this.exe4( coord ) ;

        let coord2 = coord.clone() ;
        coord2.x += 1 ;
        coord2.y += 1 ;
        let cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }

        coord2 = coord.clone() ;
        coord2.x += 1 ;
        coord2.y += -1 ;
        cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }
        
        coord2 = coord.clone() ;
        coord2.x += -1 ;
        coord2.y += -1 ;
        cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }

        coord2 = coord.clone() ;
        coord2.x += -1 ;
        coord2.y += 1 ;
        cell = UiBagGrid.ins.getCell( coord2) ; 
        if( cell != null ) {
            list.push( cell ) ;
        }

        return list ; 

    }

}