

export class CfgLevelGeneralMgr {

    public static data = new Map< string , CfgLevelGeneralItem >() ; 

}

// 
export class CfgLevelGeneralItem {

    // ID
    public id : string ; 

    // ID
    public mapId : string ; 


}

// 
export class CfgLevelGeneralParser {

    public static parseJson( json : any ) {

        let data = CfgLevelGeneralMgr.data ; 

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ].toString() ;
            if( id == null || id == "" ) continue ; 

            // let levelIdx = Number( levelId ) ; 
            let cfgItem = new CfgLevelGeneralItem() ; 
            data.set( id , cfgItem ) ;
            cfgItem.id = id ; 

            cfgItem.mapId = row[ "MapId" ] ; 

        } // end for

    }

}