import { ItemData } from "db://assets/Script/Common/Item/ItemData";
import { ItemType } from "db://assets/Script/Common/Item/ItemType";

export class CfgShop {

    public static data = new Map< string , CfgShopItem > ; 

    //  
    public static daliyFixed = new Array< CfgShopItem >() ; 
    //  
    public static daliy = new Array< CfgShopItem >() ; 

    // 
    public static money001 = new Array< CfgShopItem >() ; 

}

export class CfgShopItem {

    public id : string ; 

    // Key
    public nameLangId : string ; 

    // 
    public target : ItemData ; 

    // 
    public cost : ItemData ; 

    //  ID
    public adDaliyLimitId : string = null ; 
    // 
    public adDaliyLimitTotal = 0 ; 

}

export class CfgShopParser {

    public static parseJson( json : any ) {

        let data = CfgShop.data ; 

        for( let i = 0 ; i < json.length ; ++i ) {

            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 

            let item = new CfgShopItem() ; 
            data.set( id , item )  ; 

            item.id = id ; 

            item.nameLangId = row[ "NameLangId" ] ; 

            item.target = new ItemData( ) ; 
            item.target.id = row[ "ItemId" ] ; 
            item.target.count = Number( row[ "ItemCount" ] ) ; 
            
            item.cost = new ItemData( ) ; 
            item.cost.id = row[ "CostItemId" ] ; 
            item.cost.count = Number( row[ "CostItemCount" ] ) ; 

            let str = row[ "AdDaliyLimitId" ] ; 
            if( str != null && str != "" ) {

                item.adDaliyLimitId = str ; 

                str = row[ "AdDaliyLimitTotal" ] ; 
                item.adDaliyLimitTotal = Number( str ) ; 

            }

            // 
            if( id.startsWith( "Money001" ) ) {
                CfgShop.money001.push( item ) ;
            }
            else if( id.startsWith( "DaliyFixed" ) ) {
                CfgShop.daliyFixed.push( item ) ;
            }
            else if( id.startsWith( "Daliy" ) ) {
                CfgShop.daliy.push( item ) ;
            }
            

        } // end for

    }

}