import { find, instantiate, Prefab, resources, Vec3 } from "cc";
import { WorldNode } from "../../World/1.Base/Support/Misc/WorldNode";
import { World } from "../../../Home/World/World";

// 
export class LevelLoad {

    // 
    public static pfMap : Prefab ;

    public static load( ) {

        this.load2() ; 

    }

    // 
    public static load2() {

        //  
        let layer0 = find( "Canvas/World/Layer0" ) ; 
        if( layer0 != null ) {
            layer0.destroy() ; 
        }

        let node = instantiate( this.pfMap ) ;
        node.name = "Layer0" ; 
        node.setParent( find( "Canvas/World" ) , false ) ; 
        node.position = Vec3.ZERO ; 
        node.setSiblingIndex( 0 ) ;

    }

}