import { Vec3 } from "cc";
import { PfLevelThumbnail } from "./PfLevelThumbnail";
import { Sprite } from "cc";
import { Account } from "../../../../../Common/Account/Account";
import { Color } from "cc";
import { Node } from "cc";
import { instantiate } from "cc";
import { CfgLevelGeneralMgr } from "db://assets/Script/Game/World/2.Td/Level/CfgLevelGeneralMgr";
import { tween } from "cc";

// 
export class UiLevelThumbnail {

    // 
    public isMoveCompleted = false ; 

    // 
    public idx = -1 ; 

    public root : Node ;

    // 
    public current : Node = null ; 

    public init( node : Node ) {

        this.root = node ; 

    }

    public refresh( idx : number ) {

        if( this.idx == idx ) return ; 

        if( this.idx == -1 ) {
            //  

            // 
            this.root.removeAllChildren() ; 
            this.current = this.create( idx ) ; 
            this.isMoveCompleted = true ; 
        }
        else if( this.idx < idx  ) {
            this.isMoveCompleted = false ; 
            let node2 = this.create( idx ) ; 
            this.move( node2 , true ) ;
        }
        else {
            this.isMoveCompleted = false ; 
            let node2 = this.create( idx ) ; 
            this.move( node2 , false ) ; 

        }

        this.idx = idx ; 

    }

    // 
    private create( idx : number ) : Node {

        // IDID
        let cfg = CfgLevelGeneralMgr.data.get( idx.toString() ) ;
        if( cfg == null ) return ; 
        let id = cfg.mapId ; 

        // 
        let pf = PfLevelThumbnail.ins.root.getChildByPath( id ) ;
        if( pf == null ) return ; 
        let node = instantiate( pf ) ;
        node.setParent( this.root , false ) ; 
        node.position = Vec3.ZERO ; 


        let sprites = node.getComponentsInChildren( Sprite ) ; 
        // 
        if( idx < Account.ins.mainLevel.levelIdx ) {
            // 
            for( let spr of sprites ) {
                spr.color = Color.WHITE ; 
            } // end for
        }
        else if( idx == Account.ins.mainLevel.levelIdx ) {
            // 
            for( let spr of sprites ) {
                spr.color = Color.WHITE ; 
            } // end for
        }
        else {
            // 
            for( let spr of sprites ) {
                spr.color = Color.GRAY ; 
            } // end for

        }

        return node ; 

    }

    public move( target : Node , isLeft : boolean ) {

        if( this.current == null && target == null ) {
            //  
            this.isMoveCompleted = true ; 
            return ; 
        }

        // 
        let len = 1200 ; 
        let pos1 = new Vec3() ; 
        let pos2 = new Vec3() ; 
        if( isLeft ) {
            pos1.x = -len ; 
            
            pos2.x = len ;
        }
        else {
            pos1.x = len ; 
            pos2.x = -len ;
        }

        let d = 0.3 ; 

        // 
        let current = this.current ;
        if( current != null ) {
            tween( current ).to( d , { position : pos1 } ).call( ()=>{
                this.isMoveCompleted = true ; 
                current.destroy() ; 
                current = null ; 
            } ).start() ;
        }

        // 
        this.current = target ; 
        if( target != null ) {
            target.position = pos2 ; 
            // 
            tween( target ).to( d , { position : Vec3.ZERO } ).call( ()=>{
                this.current = target ; 
                this.isMoveCompleted = true ; 
            } ).start() ; 
    
        }

    }

}