
import { BagArticle } from "../Article/BagArticle";
import { UiBagBottom } from "../Main/UiBagBottom";
import { UiBagExpandOp } from "../Main/UiBagExpandOp";
import { UiBagGrid } from "./UiBagGrid";
import { UiBagGridBg } from "./UiBagGridBg";

// 
export class GridModeTransition {

    public static mode = 1 ;

    // 
    public static init() {

        UiBagGridBg.ins.unlocked() ;
        UiBagGrid.ins.alignBgCenter( 0 ) ; 
        UiBagExpandOp.ins.hide() ; 
        UiBagBottom.ins.show() ; 

        this.mode = 1 ; 
    }

    // 
    public static toExpandable() {

        if( this.mode == 2 ) return ;
        this.mode = 2 ; 

        UiBagGrid.ins.showExpandable() ; 
        UiBagGrid.ins.alignBgCenter() ; 
        UiBagExpandOp.ins.show() ; 
        UiBagBottom.ins.hide() ; 
        

    }


    // 
    public static toNormal() {

        if( this.mode == 1 ) return ;
        this.mode = 1 ; 

        UiBagGrid.ins.showNormal() ; 
        UiBagGrid.ins.alignBgCenter() ; 
        UiBagExpandOp.ins.hide() ; 
        UiBagBottom.ins.show() ; 

    }



}