import { CurrentMax } from "../SimpleStruct/CurrentMax";

export class FrameCall {

    public static ins : FrameCall = null ; 

    // delay  0 
    public once( fn : ()=>void , delay : number = 0 ) {

        let item = new FrameCallDataItem() ; 
        item.interval.setCurrentAndMax( delay ) ; 
        item.times = 1 ;
        item.fn = fn ; 
        this.items.push( item ) ; 

    }


    public update() {

        for( let i = this.items.length - 1 ; i >= 0 ; --i ) {

            let item = this.items[ i ] ;

            item.interval.current-- ;
            if( item.interval.current > 0 ) continue ; 

            item.fn?.() ; 

            item.times-- ; 
            if( item.times <= 0 ) {
                // 
                this.items.splice( i , 1 ) ;
            }

            item.interval.setCurrentAsMax() ; 

        } // end for

    }

    public items = new Array< FrameCallDataItem >() ; 
}

// 
export class FrameCallDataItem {

    // 
    public interval = new CurrentMax() ;

    // 
    public times = 1 ; 

    // 
    public fn : ()=>void = null ; 

}