import { PlayerRole } from "../../../2.Td/Player/PlayerRole";
import { AffixType } from "../../Buff/Affix";
import { Buff } from "../../Buff/Buff";
import { Tower } from "../Entity/Tower";

export class TowerSysBuff {

    public static update( tower : Tower ) {

        let list = tower.buff.list ; 
        for( let i = list.length - 1 ; i >= 0 ; --i ) {

            let buff = list[ i ] ;

            // buff.duration.current-- ; 
            // if( buff.duration.current < 0 ) {
            //     // 
            //     BuffOp.roleRemove( tower , i ) ; 
            //     continue ; 
            // }

            let interval = buff.interval ;
            if( interval != null && interval.max >= 0 ) {
                interval.current-- ; 
                if( interval.current == -1 ) {

                    interval.setCurrentAsMax() ; 

                    // 
                    this.trigger( tower , buff ) ; 

                }
            }

        } // end for

    }

    public static trigger( tower : Tower , buff : Buff ) {

        switch( buff.affix.type ) {

            case AffixType.HpRegenIncrease : {
                //  
                let role = PlayerRole.ins.main ; 
                role.life.add( buff.affix.argN ) ;
                role.view.hpBar.refresh() ; 
            }
            break ; 

            case AffixType.HpRegenIncreasePercent : {
                
                // 
                let role = PlayerRole.ins.main ; 
                let hp = role.life.hp ; 
                let val = hp.max * buff.affix.argN ; 
                role.life.add( val ) ;
                role.view.hpBar.refresh() ; 

            }
            break ; 

        }

    }


}