import { Node, Vec3, tween } from "cc";

/*

    
    https://blog.csdn.net/hangsky1990/article/details/129890209
*/
export class BezierMove {

    /*
        end 
        ctrl 
    */ 
    public static exe2Order( node : Node , duration : number , end : Vec3 , ctrl : Vec3 , onComplete : ()=>void = null ) { //node

        let startPos = node.position //
        //
        let twoBezier = (t: number, p1: Vec3, cp: Vec3, p2: Vec3) => {
          let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
          let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
          return new Vec3(x, y, 0);
        };

        let t = tween(node.position)
          .to( duration , end, { 
            onUpdate: (target: Vec3, ratio: number) => {
              node.position = twoBezier(ratio, startPos, ctrl, end); 
            }
        }) ;

        if( onComplete != null ) {
            t.call( ()=>{
                onComplete() ; 
            } ) ;
        }

        t.start() ; 

      }


}


