import { sys } from "cc";
import { Account } from "../Account/Account";
import { AccountPersist } from "../Account/AccountPersist";

// 
export class StaminaRecovery
{
    public static ins = new StaminaRecovery();


    /*
        
        180030
    */
    public cycle = 20 * 60 ; 


    // 
    public time = -1 ;

    // 
    public state = 0;

    // 
    public countdown = 0;


    public exe() : void
    {
        //Debug.Log( "" + state );

        let stamina = Account.ins.stamina ; 

        switch( this.state )
        {
            case 0:
            {
                // 
                if ( stamina.current >= stamina.max )
                {
                    // 
                    return;
                }

                // 
                this.time = Math.floor( Date.now() / 1000 ) ;
                this.state = 1;

                StaminaRecoveryPersist.save();

            }
            break;

            case 1:
            {
                // 

                var now = Math.floor( Date.now() / 1000 ) ;
                var diff = now - this.time;
                var diffSce = diff ;

                this.countdown = this.cycle - diffSce;
                if ( this.countdown < 0 ) this.countdown = 0; 

                if ( diffSce < this.cycle )
                {
                    //Debug.Log( "" + this.cooldown );
                    return;
                }

                // 
                var n = Math.floor( diffSce / this.cycle ) ;
                stamina.current += n;
                // 
                if ( stamina.current > stamina.max ) stamina.current = stamina.max;
                AccountPersist.save();

                if ( stamina.current >= stamina.max )
                {   // 
                    this.state = 0 ; 
                }
                else
                {   // 
                    this.time = now ;
                }

                StaminaRecoveryPersist.save();

            }
            break;

        }

    }

}


// 
export class StaminaRecoveryPersist
{
    public static load() : void 
    {
        let recovery = StaminaRecovery.ins ; 

        let str = sys.localStorage.getItem( "StaminaRecovery" ) ; 
        if( str == null || str == "" ) return ;
        let json = JSON.parse( str ) ;
        let str2 = json[ "State" ] ;
        recovery.state = Number( str2 ) ; 
        str2 = json[ "Time" ] ;
        recovery.time = Number( str2 ) ; 

    }

    public static save() : void
    {
        let recovery = StaminaRecovery.ins;

        let json = {} ;
        json[ "State" ] = recovery.state ; 
        json[ "Time" ] = recovery.time ; 
        let str = JSON.stringify( json ) ;
        sys.localStorage.setItem( "StaminaRecovery" , str ) ; 

    }

}