import { instantiate, Node, sp } from "cc";
import { EffectPfMgr } from "../../World/1.Base/Support/Misc/EffectPfMgr";

export class EffectFactory {

    public static create( id : string , destroyIfComplete = true ) : Node {        

        let pf = EffectPfMgr.ins.root.getChildByPath( id ) ;
        if( pf == null ) return null ; 
        
        let node = instantiate( pf ) ;
        let sk = node.getComponent( sp.Skeleton ) ;
        if( sk == null ) {
            sk = node.getComponentInChildren( sp.Skeleton ) ; 
        }

        if( destroyIfComplete ) {

            sk?.setCompleteListener( ()=>{
                node.destroy() ; 
            } ) ;

        }

        return node ; 

    }


}