import { Input, Node } from 'cc';
import { UiSettings } from '../../../../../Common/Ui/UiSettings';
import { UiMain } from '../../UiMain';
import { UiDialog } from '../../../../../Common/Ui/UiDialog';
import { UiDailyCheckin } from '../../../Popup/DailyCheckin/UiDailyCheckin';

export class UiRight {

    public static ins=new UiRight();

    public root:Node;

    //
    public btnDailyCheckin:Node;

    //
    public btnSettings:Node;

    public init(node:Node){

        this.root = node;

        this.btnDailyCheckin = this.root.getChildByPath("btnDailyCheckin");

        this.btnSettings = this.root.getChildByPath("btnSettings");
        this.btnSettings.on( Input.EventType.TOUCH_END , ()=>{
            UiSettings.create( UiMain.ins.root ).show() ;
        } );

        this.btnDailyCheckin.on(Input.EventType.TOUCH_END,()=>{
            // UiDialog.create( UiMain.ins.root , "..." ).show() ;
            UiDailyCheckin.create( UiMain.ins.root ).show() ;
        });
    }

}



