import { CfgBuff } from "../../../../Common/Cfg/CfgBuff";
import { CurrentMax } from "../Support/SimpleStruct/CurrentMax";
import { Affix } from "./Affix";
import { Buff } from "./Buff";

export class BuffFactory {

    public static create( id : string ) : Buff {

        let buff = new Buff() ; 

        let cfg = CfgBuff.data.get( id ) ;
        if( cfg == null ) {

            console.error( "BuffFactory.create " + "id:" + id ) ;

            return ;
        }

        buff.cfg = cfg ; 

        buff.affix = new Affix() ; 
        
        buff.affix.type = cfg.affixType ; 
        buff.affix.argN = cfg.affixArgN ; 
        if( cfg.affixArgNList != null ) {
            buff.affix.argNList = new Array< number >() ;
            buff.affix.argNList.push( ...cfg.affixArgNList ) ;
        }
        
        buff.affix.argStr = cfg.affixArgStr ; 

        buff.duration = new CurrentMax() ; 
        buff.duration.setCurrentAndMax( cfg.duration ) ; 

        buff.interval = new CurrentMax() ; 
        buff.interval.setCurrentAndMax( cfg.interval ) ;

        return buff ; 

    }

}