import { Input, Node, tween, Vec3 } from "cc";
import { BagArticleGridPlace } from "../Article/BagArticleGridPlace";
import { GridModeTransition } from "../Grid/GridModeTransition";
import { GridTraverse } from "../Grid/GridTraverse";
import { BagArticle } from "../Article/BagArticle";
import { CellExpand } from "../Grid/CellExpand";
import { UiTray } from "./UiTray";
import { UiToast } from "../../../Common/Ui/UiToast";
import { UiMain } from "../../Ui/Main/UiMain";
import { Lang } from "../../../Common/Misc/Lang";
import { UiBagTouch } from "../Touch/UiBagTouch";

// 
export class UiBagExpandOp {

    public static ins = new UiBagExpandOp() ;

    public root : Node ; 

    public btnOK : Node ; 

    public prompt : Node ; 

    public init( node : Node ) {

        this.root = node ; 
        this.root.active = true ; 

        this.btnOK = this.root.getChildByPath( "btnOK" ) ;
        // this.btnOK.active = false ; 
        this.btnOK.on( Input.EventType.TOUCH_END , this.onClick_OK , this ) ; 

        this.prompt = this.root.getChildByPath( "Prompt" ) ;
        tween( this.prompt ).sequence(
            tween( this.prompt ).to( 0.9 , { scale : new Vec3( 1.2 , 1.2 , 1.2 ) } ) , 
            tween( this.prompt ).to( 0.9 , { scale : new Vec3( 1.0 , 1.0 , 1.0 ) } ) , 
        ).repeatForever().start() ;


    }

    public show() {
        this.root.active = true ; 
    }

    public hide() {
        this.root.active = false ; 
    }

    private onClick_OK() {

        GirdExpandCtrl.exe() ; 

        UiBagTouch.ins.limit.clear() ; 

    }


}

// 
export class GirdExpandCtrl {

    // 
    public static exe() {

        this.exe2() ; 

    }

    private static exe1() {

        GridTraverse.exe( ( cell )=>{

            let a = cell.article ;
            if( a == null ) return ; 

            if( a.isBagExpand == false ) return ;

            for( let cell of a.cells ) {
                cell.unlocked = true ; 

                cell.bg.active = true ; 
                cell.hintPlaceable.active = false ; 
                cell.hintExpandable.active = false ; 

            }

            a.view.root.destroy() ; 
            BagArticleGridPlace.unbind( a ) ;

        } ) ;

        // 
        GridModeTransition.toNormal() ; 

    }

    private static exe2() {

        GridTraverse.exe( ( cell )=>{

            let a = cell.article ;
            if( a == null ) return ; 

            if( a.isBagExpand == false ) return ;

            if( this.isNextToUnlocked( a ) == true ) {
                this.articleToUnlocked( a ) ;
            }
            else {
                // 
                BagArticleGridPlace.unbind( a ) ;
                UiTray.ins.insertArticle( a ) ; 

                UiToast.show( UiMain.ins.root , Lang.get( "Game_Prompt_CannotPlaceBagExpand" ) ) ; 
            }

        } ) ;

        // 
        GridModeTransition.toNormal() ; 

    }

    // 
    private static articleToUnlocked( a : BagArticle ) {

        for( let cell of a.cells ) {
            cell.unlocked = true ; 

            cell.bg.active = true ; 
            cell.hintPlaceable.active = false ; 
            cell.hintExpandable.active = false ; 

        }

        a.view.root.destroy() ; 
        BagArticleGridPlace.unbind( a ) ;

    }

    // 
    private static isNextToUnlocked( a : BagArticle ) : boolean {

        for( let cell of a.cells ) {
            let list = CellExpand.exe4( cell.coord ) ; 
            let idx = list.findIndex( ( e )=>{ return e.unlocked == true } ) ;
            if( idx != -1 ) {
                return true ; 
            }

        } // end for

        return false ; 

    }


}