import { FrameSecConvert } from "../Misc/FrameSecConvert";

export class CfgRole {

    public static data = new Map< string , CfgRoleItem >() ;  

}

export class CfgRoleItem {

    public id : string ; 

    // ID
    public viewId : string ; 

    // 
    public dataOrig : any ; 

    // 

    // 
    public attackDamage : number ; 
    // CD
    public attackCd : number ; 


    // 
    public moveSpeed : number ; 

    // 
    public exp : number ; 

    // 
    public lifeHpMax : number ; 

    //  ID
    public hpBarId : string = null ; 

}

export class CfgRoleParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 

            let cfg = new CfgRoleItem() ; 

            cfg.id = id ; 

            CfgRole.data.set( id , cfg ) ; 

            let str = row[ "View" ] ;
            if( str != null && str != "" ) {
                cfg.viewId = str ;
            }

            cfg.dataOrig = row ; 
            
            cfg.moveSpeed = FrameSecConvert.toSecond( Number( row[ "MoveSpeed" ] ) ) ; 

            cfg.exp = Number( row[ "Exp" ] ) ;

            cfg.lifeHpMax = Number( row[ "LifeHpMax" ] ) ; 

            str = row[ "AttackDamage" ] ; 
            if( str != null && str != "" ) {
                cfg.attackDamage = Number( str ) ;
            }

            str = row[ "AttackCd" ] ; 
            if( str != null && str != "" ) {
                cfg.attackCd = FrameSecConvert.toFrame( Number( str ) ) ;
            }

            str = row[ "HpBarId" ] ; 
            if( str != null && str != "" ) {
                cfg.hpBarId = str ; 
            }

        } // end for

    }

}