import { Rect, Size, Vec2, Vec3 } from "cc";
import { UiBagGrid } from "./UiBagGrid";
import { UiBagCfg } from "../Main/UiBagCfg";

export class GridHelper {

    //  
    public static getBoundingRectShapeUnlocked() : Rect {

        let rect = new Rect() ; 

        // 
        let coord1 = new Vec2( 9999 , 9999 ) ; 
        // 
        let coord2 = new Vec2( -1 , -1 ) ; 

        let grid = UiBagGrid.ins.grid ;
        let girdSize = UiBagGrid.ins.gridSize ; 
        for( let y = 0 ; y < girdSize.height ; ++y ) {
            for( let x = 0 ; x < girdSize.width ; ++x ) {

                let cell = grid[ y ][ x ] ; 
                if( cell.unlocked == false ) continue ; 

                if( x < coord1.x ) {
                    coord1.x = x ;
                }

                if( y < coord1.y ) {
                    coord1.y = y ; 
                }

                if( x > coord2.x ) {
                    coord2.x = x ; 
                }

                if( y > coord2.y ) {
                    coord2.y = y ;
                }

            } // end for
        } // end for

        rect.xMin = coord1.x ; 
        rect.yMin = coord1.y ; 
        rect.xMax = coord2.x ; 
        rect.yMax = coord2.y ; 

        return rect ; 

    }

    //  
    public static getBoundingRectUnlocked() : Rect {

        let rect = this.getBoundingRectShapeUnlocked() ;

        let grid = UiBagGrid.ins.grid ;
        // 
        let coord1 = new Vec2( rect.xMin , rect.yMin ) ; 
        // 
        let coord2 = new Vec2( rect.xMax , rect.yMax ) ; 

        // 
        // let cellSize = new Size( 80 , 80 ) ; 
        let cellSize = UiBagCfg.ins.cellSize ; 

        // 
        let pos = new Vec2() ; 
        let cell1 = grid[ rect.yMin ][ rect.xMin ] ; 
        let cell2 = grid[ rect.yMax ][ rect.xMax ] ; 
        let pos2 = cell1.root.position.clone().add( cell2.root.position ).divide3f( 2 , 2 , 2 ) ;
        pos.x = pos2.x ; pos.y = pos2.y ; 

        // 
        let size = new Size() ;
        size.width = ( coord2.x - coord1.x + 1 ) * cellSize.width ;
        size.height = ( coord2.y - coord1.y + 1 ) * cellSize.height ; 
        size.width += 40 ; 
        size.height += 40 ; 

        let rectRet = new Rect() ; 
        rectRet.size = size ; 
        rectRet.center = pos ; 
        return rectRet ; 

    }

    // 
    public static getBoundingRectEntirety() : Rect {

        // let cellSize = new Size( 80 , 80 ) ; 
        let cellSize = UiBagCfg.ins.cellSize ; 

        // 
        let gridSize = UiBagGrid.ins.gridSize ; 
        let size = new Size() ; 
        size.width = gridSize.width * cellSize.width ; 
        size.height = gridSize.height * cellSize.height ;

        // 
        size.width += 40 ; 
        size.height += 40 ; 

        let rectRet = new Rect() ; 
        rectRet.size = size ; 
        rectRet.center = Vec2.ZERO ; 
        return rectRet ; 

    }
}