import { Node } from "cc";
import { Label } from "cc";
import { UiItemIcon } from "db://assets/Script/Common/Item/UiItemIcon";
import { Lang } from "db://assets/Script/Common/Misc/Lang";
import { CfgShopItem } from "./CfgShop";
import { ItemData } from "db://assets/Script/Common/Item/ItemData";
import { Input } from "cc";
import { UiGet } from "../../../Popup/UiGet";
import { UiMain } from "../../UiMain";
import { ItemSub } from "db://assets/Script/Common/Item/ItemSub";
import { ItemGet } from "db://assets/Script/Common/Item/ItemGet";
import { Ad } from "db://assets/Script/Common/Ad/Ad";
import { UiBgQuality } from "db://assets/Script/Common/Ui/UiBgQuality";
import { CfgItem, CfgItemMgr } from "db://assets/Script/Common/Cfg/CfgItem";
import { DailyLimit } from "db://assets/Script/Common/Misc/DailyLimit";
import { UiToast } from "db://assets/Script/Common/Ui/UiToast";
import { SoundBtnClickSetter } from "db://assets/Script/Common/Audio/SoundBtnClickSetter";
import { UiDialog } from "../../../../../Common/Ui/UiDialog";

export class UiShopItem {

    public cfg : CfgShopItem = null ; 

    public onGetItems : ()=>void = null ; 

    public root : Node ; 

    // 
    public icon : UiItemIcon ; 

    // 
    public count : Label ; 

    // 
    public bg : UiBgQuality ; 

    // 
    public name : Label ; 
    
    // 
    public cost : UiCost ; 

    // 
    public costAd : Node ; 
    public costAdLabel : Label ; 

    // 
    public soldOut : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        // SoundBtnClickSetter.exe( this.root ) ; 
        
        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ; 

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ; 

        this.icon = new UiItemIcon() ; 
        this.icon.init( this.root.getChildByPath( "Icon" ) ) ; 

        this.count = this.root.getChildByPath( "Count" ).getComponent( Label ) ; 

        let t = this.root.getChildByPath( "Icon/bg" ) ; 
        if( t != null ) {
            this.bg = new UiBgQuality() ; 
            this.bg.init( t ) ;
        }

        this.cost = new UiCost() ; 
        this.cost.init( this.root.getChildByPath( "Cost" ) ) ; 

        this.costAd = this.root.getChildByPath( "CostAd" ) ; 
        this.costAd.active = false ; 

        this.costAdLabel = this.costAd.getChildByPath( "val" ).getComponent( Label ) ; 

        this.soldOut = this.root.getChildByPath( "SoldOut" ) ;
        if( this.soldOut ) {
            this.soldOut.active = false ; 
        }
        

    }

    public refresh() {

        this.refreshGeneral() ; 

        this.refreshCost() ; 

    }

    // 
    private refreshGeneral() {

        // 
        this.name.string = Lang.get( this.cfg.nameLangId ) ; 

        // 
        this.icon.refresh( this.cfg.target.id ) ; 

        // 
        this.count.string = "x" + this.cfg.target.count ; 

        //  
        if( this.bg != null ) {
            let cfg2 = CfgItemMgr.data.get( this.cfg.target.id ) ;
            if( cfg2 != null ) {
                this.bg.show( cfg2.quality ) ;
            }
        }

    }

    // 
    private refreshCost() {

        let isAd = this.cfg.adDaliyLimitId != null ; 

        this.cost.root.active = !isAd ; 
        this.costAd.active = isAd ; 

        if( isAd ) {
            // 

            if( this.adDl == null ) {
                let id = "DailyLimit_" + this.cfg.adDaliyLimitId ; 
                this.adDl = new DailyLimit( id , this.cfg.adDaliyLimitTotal ) ; 
                this.adDl.load() ; 
            }

            // 
            this.refreshAdLeft() ; 

        }
        else {
            // 
            this.cost.refresh( this.cfg.cost ) ;

        }

    }

    // 
    public refreshAdLeft() {

        // 
        let str = Lang.get( "Shop_Free" ) + "(" + this.adDl.current + ")" ; 
        this.costAdLabel.string = str ; 

    }

    public onClick() {

        if( this.cfg.adDaliyLimitId ) {

            // 
            this.ad( ) ; 
            return ; 

        }

        // 
        let ui = UiDialog.create( UiMain.ins.root , "" ) ;
        ui.show() ; 
        ui.btnOK.on( Input.EventType.TOUCH_END , ()=>{

            if( ItemSub.exe( [ this.cfg.cost ] ) == false ) return ; 
            this.getItems() ; 

        } ) ; 


    }

    // 
    private ad() {

        // 
        if( this.adDl.current <= 0 ) {

            UiToast.show( UiMain.ins.root , Lang.get( "AdLeftNotEnough" ) ) ; 
            return ; 
        }


        let onSuccess = ()=>{

            this.getItems() ; 

            this.adDl.decrement() ; 
            this.adDl.save() ; 
            this.refreshAdLeft() ; 

        } ;
        let onCancel = ()=>{

        } ;
        let onError = ()=>{

        } ; 
        Ad.ins.rewardShow( onSuccess , onCancel , onError ) ;

    }

    private getItems() {

        // 
        let uiGet = UiGet.create( UiMain.ins.root ) ; 
        uiGet.itemList.refresh( [ this.cfg.target ] ) ;
        uiGet.itemList.showAnim() ; 

        // 
        ItemGet.addList( [ this.cfg.target ] ) ; 

        this.onGetItems?.() ;

    }

    // 
    public adDl : DailyLimit = null ;

}



// 
export class UiCost {

    public root : Node ; 

    public icon : UiItemIcon ; 

    public count : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon = new UiItemIcon() ; 
        this.icon.init( this.root ) ; 

        this.count = this.root.getChildByPath( "Count" ).getComponent( Label ) ;

    }

    public refresh( itemData : ItemData ) {

        this.icon.refresh( itemData.id ) ;
        this.count.string = itemData.count.toString() ; 

    }



}


