import { Input, Node, game } from "cc";

// 
export class ButtonInterval {

    // 
    public static set( node : Node , fn : ()=>void , interval = 500 ) {

        let t = game.totalTime ; 
        node.on( Input.EventType.TOUCH_END , ()=>{

            let t2 = game.totalTime ; 
            let diff = t2 - t ; 
            if( diff < interval ) { return ; } 
            t = t2 ; 

            fn() ; 

        } ) ; 

    }

}