import { isValid } from "cc";
import { UiStamina } from "../../Home/Ui/Main/Margin/UiStamina";
import { Account } from "../Account/Account";
import { AccountPersist } from "../Account/AccountPersist";
import { ItemData } from "./ItemData";
import { ItemType } from "./ItemType";
import { UiMoney002 } from "../../Home/Ui/Main/Margin/UiMoney002";
import { UiMoney001 } from "../../Home/Ui/Main/Margin/UiMoney001";

// 
export class ItemGet {

    // 
    public static addList( items : Array< ItemData > ) {

        for( let item of items ) {

            let id = item.id ; 
            switch( id ) {

                case ItemType.Money001 : {

                    Account.ins.money.money001 += item.count ; 

                    if( isValid( UiMoney001.ins.root ) ) {
                        UiMoney001.ins.refresh() ; 
                    }
                }
                break ; 

                case ItemType.Money002 : {

                    Account.ins.money.money002 += item.count ; 

                    if( isValid( UiMoney002.ins.root ) ) {
                        UiMoney002.ins.refresh() ; 
                    }

                }
                break ; 

                case ItemType.Stamina : {

                    Account.ins.stamina.current += item.count ; 

                    if( isValid( UiStamina.ins.root ) ) {
                        UiStamina.ins.refresh() ; 
                    }

                }
                break ; 

                default : {

                    let n = Account.ins.items.get( id ) ; 
                    if( n == null ) {
                        n = 0 ;
                    }
                    n += item.count ; 
                    Account.ins.items.set( id , n ) ;  

                }
                break ; 

            }

        } // end for
        AccountPersist.save() ; 

    }


    
}