import { find, Input, instantiate, Node, Prefab } from "cc";
import { UiBagGrid } from "../Grid/UiBagGrid";
import { UiBagTouch } from "../Touch/UiBagTouch";
import { UiArticleDetail } from "../ArticleDetail/UiArticleDetail";



//  UI
export class UiBag {

    public static ins : UiBag ; 

    public static createIns( node : Node ) {

        let ui = new UiBag() ; 
        this.ins = ui ; 
        ui.init( node ) ;

    }


    public static pf : Prefab ;

    public static create( parent : Node = null ) : UiBag {

        let root = instantiate( this.pf ) ;

        let ui = new UiBag() ; 
        ui.init( root ) ;
        ui.root.setParent( parent , false ) ; 

        return ui ; 

    }

 
    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        UiBagGrid.ins = new UiBagGrid() ; 
        UiBagGrid.ins.init( this.root.getChildByPath( "Grid" ) ) ;

        UiBagTouch.ins = new UiBagTouch() ; 
        UiBagTouch.ins.init( this.root.getChildByPath( "Touch" ) ) ; 

        // UiTray.ins = new UiTray() ; 
        // UiTray.ins.init( this.root.getChildByPath( "Tray" ) ) ; 

        // UiBagBottom.ins = new UiBagBottom() ; 
        // UiBagBottom.ins.init( this.root.getChildByPath( "Bottom" ) ) ;

        UiArticleDetail.ins = new UiArticleDetail() ; 
        UiArticleDetail.ins.init( this.root.getChildByPath( "ArticleInfo" ) ) ; 
        
        // UiBagExpandOp.ins = new UiBagExpandOp() ; 
        // UiBagExpandOp.ins.init( this.root.getChildByPath( "BagExpandOp" ) ) ; 

        // GridModeTransition.init() ; 
        
    }

}

