export class Init {

    public static exe() {

        LevelLoad.load( ) ;

        WorldNodeInit.exe() ; 

        // 
        let id = GameArg.ins.levelId ; 
        LevelCtrl.ins = new LevelCtrl() ; 
        LevelCtrl.ins.levelData = CfgLevelWaveMgr.data.get( id ) ; 
      
        FrameCall.ins = new FrameCall() ; 

        FeatureExtractPool.ins = new FeatureExtractPool() ; 
        FeatureExtractPool.ins.init() ; 

        PlayerInit.exe() ; 

        UiMain.ins = new UiMain() ; 
        UiMain.ins.init() ; 
        
        CollisionHandler.init() ; 

        EnemyMovePath.ins.init() ; 

        RoleMgr.ins = new RoleMgr() ; 

        BulletMgr.ins = new BulletMgr() ; 

        // 
        // DenFactory.create( WorldNode.ins.root.getChildByPath( "Den" ) ) ;
        PlayerRoleInit.init() ; 

        BulletBounceRect.ins.init() ; 

        this.initBag() ; 

        Stats.ins = new Stats() ; 

        Test.exe() ; 
        
        Music.ins.game() ; 

        Guide.exe1() ; 

        // 
        director.getScene().on( Node.EventType.NODE_DESTROYED , ()=>{
            console.log( "" ) ;
            Deinit.exe() ; 
        } ) ;

    }


    public static initBag() {

        // 
        UiBag.createIns( find( "Canvas/World/Bag" ) ) ;

        // 
        UiBagBottom.ins = new UiBagBottom() ; 
        UiBagBottom.ins.init( UiMain.ins.root.getChildByPath( "Ui/BagBottom" ) ) ;     

        // 
        UiTray.ins = new UiTray() ; 
        UiTray.ins.init( UiMain.ins.root.getChildByPath( "Ui/BagTray" ) ) ; 

        // 
        UiBagExpandOp.ins = new UiBagExpandOp() ; 
        UiBagExpandOp.ins.init( UiMain.ins.root.getChildByPath( "Ui/BagExpandOp" ) ) ; 
   
        //  
        for( let kv of MergeDefinition.ins.allowData ) {
            MergeDefinition.ins.allowData.set( kv[0] , false ) ; 
        }

        GridModeTransition.init() ; 
        

        // 
        BagView.enterDirect() ; 

        let uiBottom = UiBagBottom.ins ; 
        uiBottom.btnStart.on( Input.EventType.TOUCH_END , ()=>{

            // 
            let hasArticle = false ; 
            GridTraverse.exe2( ( cell )=>{
                if( cell.article != null ) {
                    hasArticle = true ; 
                    return false ; 
                }
                return true ; 
            } ) ;
            if( hasArticle == false ) {
                UiToast.show( UiMain.ins.root , Lang.get( "Game_Prompt_Deploy" ) ) ; 
                return ; 
            }
           

            BagView.leave() ; 

            GameStart.start() ; 

        } ) ;
        

        // uiBottom.btnRefresh.off( Input.EventType.TOUCH_END ) ; 
        uiBottom.btnRefresh.on( Input.EventType.TOUCH_END , ()=>{

            let m = Player.ins.money001 - 15 ; 
            if( m < 0 ) {
                // 

                let dial = UiDialog.create( UiMain.ins.root ) ; 
                dial.txt.string = "\n30" ; 
                dial.show() ; 
                dial.btnOK.on( Input.EventType.TOUCH_END , ()=>{
                    // 

                    Ad.ins.rewardShow( ()=>{

                        Player.ins.money001 += 30 ;
                        UiMoney001.ins.refresh() ; 

                        // 
                        MoneyFly.money001Fly2( UiMain.ins.root , Vec3.ZERO , UiMoney001.ins.icon ) ; 

                    } ) ;


                } ) ;

                return ; 
            }
            Player.ins.money001 = m ;
            UiMoney001.ins.refresh() ; 

            BagTrayAricleGen.ins.gen() ; 

        } ) ; 
        

    }

}



export class Deinit {

    public static exe() {

        RoleMgr.ins = null ; 

        BulletMgr.ins = null ; 

        TimeScale.set( 1 ) ; 

    }

}