import { Collider2D, Contact2DType, instantiate } from "cc";
import { Bullet, BulletBounce } from "./Bullet";
import { BulletPfMgr } from "./BulletPfMgr";
import { CollisionBullet } from "../Collision/CollisionBullet";
import { CfgBullet } from "../../../../Common/Cfg/CfgBullet";
import { FrameSecConvert } from "../../../../Common/Misc/FrameSecConvert";

export class BulletFactory {

    public static create( id : string ) {

        let bullet = new Bullet() ; 
        bullet.id = id ; 

        bullet.Lifecycle = FrameSecConvert.toFrame( 60 * 5 ) ; 

        BulletCfgSet.exe( bullet ) ;

        BulletViewSet.exe( bullet ) ; 

        return bullet ; 

    }

}


// 
export class BulletCfgSet {

    public static exe( bullet : Bullet ) {

        let cfg = CfgBullet.data.get( bullet.id ) ; 

        bullet.cfg = cfg ; 

        bullet.view.rotationType = cfg.rotationType ; 

        bullet.life.hp.setCurrentAndMax( 1 ) ; 

        bullet.move.speed = cfg.moveSpeed ; 

        bullet.passCount = cfg.passCount ; 
        
        bullet.bounce = new BulletBounce() ; 
        bullet.bounce.bounceRect = cfg.bounceRect ; 
        bullet.bounce.bounceEnemy = cfg.bounceEnemy ; 
        bullet.bounce.bounceRectEnemy = cfg.bounceRectEnemy ; 
        bullet.bounce.bounceBetweenEnemies.count = cfg.BounceBetweenEnemies ; 
        
        bullet.target.moveType = cfg.moveType ; 

        if( cfg.buffId != null ) {
            bullet.buffIds.push( cfg.buffId ) ; 
        }
        

    }

}


// 
export class BulletViewSet {

    public static exe( bullet : Bullet ) {

        let cfg = CfgBullet.data.get( bullet.id ) ; 

        let id = cfg.viewId != null ? cfg.viewId : bullet.id ; 

        let pf = BulletPfMgr.ins.root.getChildByPath( id ) ;
        bullet.view.root = instantiate( pf ) ;

        this.collsion( bullet ) ;

    }

    // 
    private static collsion( bullet : Bullet ) {

        let root = bullet.view.root ; 
        let collider = root.getComponentInChildren(Collider2D);
        if( collider == null ) return ;

        // 
        collider[ "Bullet" ] = bullet ; 

        collider.on(Contact2DType.BEGIN_CONTACT, CollisionBullet.onBeginContact , CollisionBullet );

    }

}