import { Game, UITransform, Vec3 } from "cc";
import { UiMoney001 } from "../../../Ui/Main/UiMoney001";
import { AffixType } from "../../1.Base/Buff/Affix";
import { TowerMgr } from "../../1.Base/Tower/Entity/TowerMgr";
import { BagView } from "../Bag/BagView";
import { Player } from "../Player/Player";
import { LevelCtrl } from "./LevelCtrl";
import { UiMain } from "../../../Ui/Main/UiMain";
import { MoneyFly } from "../../../../Common/Misc/MoneyFly";
import { UiWin } from "../../../Ui/Popup/Settle/UiWin";
import { UiWaveInfo } from "../../../Ui/Main/UiWaveInfo";
import { GameArg } from "../../../Init/GameArg";
import { Account } from "../../../../Common/Account/Account";
import { AccountPersist } from "../../../../Common/Account/AccountPersist";
import { GamePause } from "../Flow/GamePause";
import { BulletMgr } from "../../1.Base/Bullet/BulletMgr";
import { MergeHybridHintMgr } from "../../../Bag/Merge/MergeHybridHint";
import { BagArticleSpeak } from "../../../Bag/Article/BagArticleSpeak";
import { ItemDraw } from "../../../../Common/Item/ItemDraw";
import { ItemGet } from "../../../../Common/Item/ItemGet";

// 
export class WaveSettle {

    public static exe() {

        GamePause.pause() ; 

        this.bulletClear() ; 

        UiWaveInfo.ins.refresh() ; 

        this.towerBuff() ; 

        if( LevelCtrl.ins.currentWaveIdx >= LevelCtrl.ins.levelData.waves.length ) {
            // 
            this.levelSettle() ; 
            return ; 
        }

        // 
        BagView.enter() ; 

        // 
        MergeHybridHintMgr.ins.exe() ; 
        
        // 
        BagArticleSpeak.clearMemory() ; 

    }

    // 
    private static bulletClear() {

        let data = BulletMgr.ins.data ; 
        for( let i = 0 ; i < data.length ; ++i ) {
            let bullet = data[ i ] ;
            bullet.view.root.destroy() ; 
        }
        data.splice( 0 , data.length ) ; 

    }

    // 
    public static levelSettle() {

        let uiWin = UiWin.create( UiMain.ins.root ) ; 
        uiWin.show() ; 
        let id = "Level_" + GameArg.ins.levelId + "_Win" ; 
        let items = ItemDraw.exe( id ) ; 
        uiWin.itemList.refresh( items ) ; 
        uiWin.itemList.showAnim() ; 
        ItemGet.addList( items ) ; 

        if( GameArg.ins.mainIdx >= 0 ) {
            // 
            let idxNext = GameArg.ins.mainIdx + 1 ;
            Account.ins.mainLevel.levelIdx = idxNext ; 
            Account.ins.mainLevel.waveIdx = 0 ; 

            AccountPersist.save() ; 

        }

    }

    //  Tower Buff
    public static towerBuff() {

        for( let tower of TowerMgr.ins.data ) {

            // 
            let moneyAdded = 0 ; 

            for( let buff of tower.buff.list ) {
                switch( buff.affix.type ) {
                    case AffixType.GetMomeyPerWave : {
                        moneyAdded += buff.affix.argN ; 
                    }
                }

            } // end for

            if( moneyAdded > 0 ) {

                Player.ins.money001 += moneyAdded ; 
                UiMoney001.ins.refresh() ; 

                let parent = UiMain.ins.root ; 

                let pos1 = tower.article.view.root.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ;
                parent.getComponent( UITransform ).convertToNodeSpaceAR( pos1 , pos1 ) ;

                // 
                let pos2 = UiMoney001.ins.icon.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ;
                parent.getComponent( UITransform ).convertToNodeSpaceAR( pos2 , pos2 ) ;

                MoneyFly.money001Fly( parent , pos1 , pos2 ) ; 

            }

        } // end for

    }

}