import { sys } from "cc";
import { Sound } from "./Sound";
import { Music } from "./Music";

// 
export class AudioSettingsPersist {

    public static load() {

        this.loadMusic() ;
        this.loadSound() ;

    }

    public static save() {
        this.saveMusic() ;
        this.saveSound() ; 
    }


    public static loadMusic() {
        let val = sys.localStorage.getItem( "AudioMusicEnabled" ) ;
        if( val == null || val == "" ) return ;
        Music.ins.enabled = val == "1" ? true : false ;
    }

    public static saveMusic() {
        sys.localStorage.setItem( "AudioMusicEnabled" , Music.ins.enabled == true ? "1" : "0" ) ;
    }

    public static loadSound() {
        let val = sys.localStorage.getItem( "AudioSoundEnabled" ) ;
        if( val == null || val == "" ) return ;
        Sound.ins.enabled = val == "1" ? true : false ;
    }

    public static saveSound() {
        sys.localStorage.setItem( "AudioSoundEnabled" , Sound.ins.enabled == true ? "1" : "0" ) ;
    }

}