import { Label, Node } from "cc";
import { Player } from "../../World/2.Td/Player/Player";
import { CfgUpgrade } from "../../../Common/Cfg/CfgUpgrade";
import { UiBar } from "../Bar/UiBar";

// 
export class UiPlayerGrade {

    public static ins : UiPlayerGrade ; 

    public root : Node ; 

    public val : Label ; 

    public bar = new UiBar() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.val = this.root.getChildByPath( "val" ).getComponent( Label ) ; 

        this.bar.init( this.root.getChildByPath( "Bar" ) ) ; 

    }

    public refresh() {

        let g = Player.ins.grade ; 

        this.val.string = "Lv." + g.grade ; 

        let expNeeded = CfgUpgrade.data[ g.grade ] ; 
        this.bar.setProgress( g.exp , expNeeded ) ; 


    }

}