import { instantiate, UITransform, Vec3 } from "cc";
import { Role } from "../Role/Base/Role";
import { RoleAttributeUpdate } from "../Role/Misc/RoleAttributeUpdate";
import { EffectPfMgr } from "../Support/Misc/EffectPfMgr";
import { Affix, AffixType } from "./Affix";
import { Buff } from "./Buff";
import { BuffFactory } from "./BuffFactory";
import { RoleAnimSpine } from "../Role/Misc/RoleAnimSpine";

// Buff 
export class BuffOp {

    //   Buff
    public static roleAdd( role : Role , id : string ) : [ boolean , Buff ] {

        let buffNew = BuffFactory.create( id ) ;

        let buff = role.buff.list.find( ( e )=>{ return e.affix.type == buffNew.affix.type ; } ) ;
        if( buff == null ) {
            // 

            this.roleAddNew( role , buffNew ) ; 

            return [ true , buffNew ] ; 
        }

        // 

        // 
        buff.duration.max = buffNew.duration.max ; 
        buff.duration.setCurrentAsMax() ; 

        buff.interval.max = buffNew.interval.max ; 

        return [ false , buff ] ; 

    }

    //  buff
    private static roleAddNew( role : Role , buff : Buff ) {

        role.buff.list.push( buff ) ;

        switch( buff.affix.type ) {

            // 
            case AffixType.MoveSpeedDecreasePercent : {
                // 
                RoleAttributeUpdate.exe( role ) ;

                let effect = instantiate( EffectPfMgr.ins.root.getChildByPath( "BuffMoveSpeedDecrease" ) ) ;
                effect.setParent( role.view.root , false ) ;
                effect.position = Vec3.ZERO ; 
                effect.setSiblingIndex( 0 ) ; 

            }
            break ; 

            case AffixType.AttackSpeedIncreasePercent : {
                // 
                RoleAttributeUpdate.exe( role ) ;
            }
            break ;


            // 
            case AffixType.Burn : {

                let node = instantiate( EffectPfMgr.ins.root.getChildByPath( "BuffBurn" ) ) ;
                node.setParent( role.view.root , false ) ;
                node.position = Vec3.ZERO ; 
            }
            break ; 

            // 
            case AffixType.Poisoning : 
            case AffixType.PoisoningPercent : 
            {

                let node = instantiate( EffectPfMgr.ins.root.getChildByPath( "BuffPoisoning" ) ) ;
                node.setParent( role.view.root , false ) ;
                node.position = Vec3.ZERO ; 
            }
            break ; 

            // 
            case AffixType.Frozen : {

                // 
                role.move.lockCount++ ; 
                // 
                role.attack.lockCount++ ; 

                // 
                role.view.skeleton.paused = true ; 

                // 
                let node = instantiate( EffectPfMgr.ins.root.getChildByPath( "BuffFrozen" ) ) ;
                node.setParent( role.view.root , false ) ;
                node.position = Vec3.ZERO ; 
                let uitransEffect = node.getChildByPath( "Main" ).getComponent( UITransform ) ;
                let rectEffect = uitransEffect.getBoundingBox() ;

                let uiTransRole = role.view.main.getComponent( UITransform ) ; 
                let rectRole = uiTransRole.getBoundingBox() ;
                rectRole.height += 20 ; 

                let f = rectRole.height / rectEffect.height ;

                rectEffect.width *= f ;
                rectEffect.height *= f ; 
                uitransEffect.contentSize = rectEffect.size ; 


            }
            break ; 

            // 
            case AffixType.Stun : {

                // 
                role.move.lockCount++ ; 
                // 
                role.attack.lockCount++ ; 

                // 
                RoleAnimSpine.playIdle( role.view.skeleton ) ;

                // 
                let h = role.view.main.getComponent( UITransform ).contentSize.height ; 
                let node = instantiate( EffectPfMgr.ins.root.getChildByPath( "BuffStun" ) ) ;
                node.setParent( role.view.root , false ) ;
                node.position = new Vec3( 0 , h + 30 , 0 ) ; 


            }
            break ; 

        }

    }

    // BuffId
    public static roleRemove( role : Role , idx : number ) {

        let buff = role.buff.list[ idx ] ; 
        role.buff.list.splice( idx , 1 ) ; 

        switch( buff.affix.type ) {

            case AffixType.MoveSpeedDecreasePercent : {

                // 
                RoleAttributeUpdate.exe( role ) ;

                // 
                let effect = role.view.root.getChildByPath( "BuffMoveSpeedDecrease" );
                effect?.destroy() ; 

            }
            break ; 

            case AffixType.AttackSpeedIncreasePercent : {
                // 
                RoleAttributeUpdate.exe( role ) ;
            }
            break ;



            case AffixType.Burn : {
                // 

                // 
                let effect = role.view.root.getChildByPath( "BuffBurn" );
                effect?.destroy() ; 

            }
            break ; 

            case AffixType.Poisoning : 
            case AffixType.PoisoningPercent : 
            {

                // 
                let effect = role.view.root.getChildByPath( "BuffPoisoning" );
                effect?.destroy() ; 

            }
            break ; 

            case AffixType.Frozen : {

                // role.move.enabled = true ; 
                role.move.lockCount-- ; 
                role.attack.lockCount-- ; 

                role.view.skeleton.paused = false ; 

                // 
                let effect = role.view.root.getChildByPath( "BuffFrozen" );
                effect?.destroy() ; 
            }
            break ; 

            case AffixType.Stun : {

                // role.move.enabled = true ; 
                role.move.lockCount-- ; 
                role.attack.lockCount-- ; 

                // 
                let effect = role.view.root.getChildByPath( "BuffStun" );
                effect?.destroy() ; 

            }
            break ; 

        }

    }

}