import { Input, instantiate, Node, tween, Vec3 } from "cc";
import { Account } from "../../../Common/Account/Account";
import { AccountPersist } from "../../../Common/Account/AccountPersist";
import { UiBag } from "./UiBag";

// 
export class ReplaceModeEnter {

    // 
    public static enter( idUnlocked : string ) {

        // 
        UiBag.ins.sv.active = false ; 
        UiBag.ins.replace.root.active = true ; 

        // UI
        let uiItem = UiBag.ins.unlocked.items.find( ( e )=>{ return e.articleId == idUnlocked ; } ) ; 
        let node = instantiate( uiItem.root ) ; 
        node.setParent( UiBag.ins.replace.root , false ) ;
        UiBag.ins.replace.item.active = false ; 
        node.position = UiBag.ins.replace.item.position ; 

        //  
        let items = UiBag.ins.deploy.items ; 
        for( let i = 0 ; i < items.length ; ++i ) {
            let uiItem = items[ i ] ;
            let root = uiItem.root ;
            this.deployItemAnim( root ) ;

             let idDeploy = uiItem.articleId ; 

             uiItem.root.off( Input.EventType.TOUCH_END ) ; 
             uiItem.root.on( Input.EventType.TOUCH_END , ()=>{
                this.swap( idUnlocked , idDeploy ) ; 
             } ) ;

        } // end for

    }

    private static deployItemAnim( root : Node ) {

        // 
        // z 0 
        let val1 = 1.1 ;
        let val2 = 0.9 ; 
        tween( root ).sequence( 
            tween( root ).to( 0.8 , { scale : new Vec3( val1 , val1 , val1 ) } ) , 
            tween( root ).to( 0.8 , { scale : new Vec3( val2 , val2 , val2 ) } ) , 
         ).repeatForever().start() ;

    }

    // 
    private static swap(  idUnlocked : string , idDeploy : string ) {

        // 
        let idx = Account.ins.bag.deploy.indexOf( idDeploy ) ; 
        Account.ins.bag.deploy[ idx ] = idUnlocked ; 

        // 
        let unlocked = Account.ins.bag.unlocked ;
        let idx1 = unlocked.indexOf( idUnlocked ) ;
        let idx2 = unlocked.indexOf( idDeploy ) ;
        let t = unlocked[ idx1 ] ;
        unlocked[ idx1 ] = unlocked[ idx2 ] ; 
        unlocked[ idx2 ] = t ; 

        AccountPersist.save() ; 

        // 
        UiBag.ins.sv.active = true ; 
        UiBag.ins.replace.root.active = false ; 

        // 
        UiBag.ins.deploy.refresh() ; 
        UiBag.ins.unlocked.refresh() ; 

    }

}