import { _decorator, Component, Node } from 'cc';
import { ItemType } from '../Item/ItemType';



export class CfgItem{

    // ID 
    public id : string;

    // 
    public quality : number;

}

export class CfgItemMgr{

    public static data = new Map< string , CfgItem >() ;

}

export class CfgItemParser{

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {

            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgItem() ;

            CfgItemMgr.data.set( id , cfgItem ) ;

            cfgItem.id = id ;

            let str : string = null ; 

            str = row[ "Quality" ] ; 
            if( str != null && str != "" ) {

                cfgItem.quality = Number(str) ; 

            }

        } 

    }
}

