import { Input, instantiate, Node, Prefab } from "cc";
import { UiBagDeploy } from "./UiBagDeploy";
import { UiBagUnlocked } from "./UiBagUnlocked";
import { UiBagLocked } from "./UiBagLocked";
import { UiBagPrefab } from "./UiBagPrefab";
import { UiBagReplace } from "./UiBagReplace";

export class UiBag {

    public static ins = new UiBag() ; 

    public static pf : Prefab ; 

    public static create() {

        let root = instantiate( this.pf ) ; 
        this.ins.init( root ) ;

    }


    public root : Node ; 

    public deploy : UiBagDeploy ; 
    public unlocked : UiBagUnlocked ; 
    public locked : UiBagLocked ; 
    public replace : UiBagReplace ; 

    public sv : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        let btnBack = this.root.getChildByPath( "btnBack" ) ;
        btnBack.on( Input.EventType.TOUCH_END , ()=>{
            this.root.active = false ; 
        } ) ;

        UiBagPrefab.ins = new UiBagPrefab() ; 
        UiBagPrefab.ins.init( this.root.getChildByPath( "Prefab" ) ) ;

        // 
        this.deploy = new UiBagDeploy() ; 
        this.deploy.init( this.root.getChildByPath( "Deploy" ) ) ;
        
        this.sv = this.root.getChildByPath( "sv" ) ; 
        this.sv.active = true ; 

        // /
        this.unlocked = new UiBagUnlocked() ; 
        this.unlocked.init( this.root.getChildByPath( "sv/view/content/Unlocked" ) ) ;
     
        // /
        this.locked = new UiBagLocked() ; 
        this.locked.init( this.root.getChildByPath( "sv/view/content/Locked" ) ) ;

        this.replace = new UiBagReplace() ; 
        this.replace.init( this.root.getChildByPath( "Replace" ) ) ;
        this.replace.root.active = false ; 

    }

    public show() {

        this.root.active = true ; 

        this.refresh() ; 

    }

    public refresh() {

        this.deploy.refresh() ; 
        this.unlocked.refresh() ; 
        this.locked.refresh() ; 

    }

}

