import { _decorator, Component, Node } from 'cc';
import { ItemData } from '../Item/ItemData';


export class CfgItemRewardMgr{

    public static data = new Map< string , CfgItemRewardItem >() ;

}

export class CfgItemRewardItem {

    // ID 
    public id : string;

    // 
    public list = new Array< ItemData > ;

}


export class CfgItemRewardParser{

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {

            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgItemRewardItem() ;

            CfgItemRewardMgr.data.set( id , cfgItem ) ;

            cfgItem.id = id ;

            let str : string = null ; 

            for( let i = 1 ; i < 20 ; ++i ) {
                
                let key = "Item" + i ; 
                str = row[ key ] ;
                if( str == "" || str == null ) break ;

                let strs = str.split( "," ) ; 
                let itemData = new ItemData() ; 
                cfgItem.list.push( itemData ) ; 
                itemData.id = strs[ 0 ].trim() ; 
                itemData.count = Number( strs[ 1 ] ) ; 

            } // end for

        } // end for

    }
}

