import { _decorator, Component, Node } from 'cc';

const { ccclass, property } = _decorator;


export class CfgDaySign {

    public id : string ;

    public DayGifts: Array<number> = new Array<number>();
}

export class CfgDaySignMgr {

    public static data = new Map<string, CfgDaySign>();
}

export class CfgDaySignParser {

    public static parseJson(json: any) {
        for (let i = 0; i < json.length; ++i) {

            let row = json[i];

            let id = row["Id"];
            if (id == null || id == "") continue;

            let cfgItem = new CfgDaySign();

            CfgDaySignMgr.data.set(id, cfgItem);
            cfgItem.id = id;

            let str = row["DayGifts"];

            if (str && typeof str === "string") {
                let strs = str.split(/[|\n]/);
                for (let s of strs) {
                    let num = Number(s.trim());
                    if (!isNaN(num)) {
                        cfgItem.DayGifts.push(num);
                    }
                }
            }
        }
    }
}


