
import { AffixType } from "../../Buff/Affix";
import { Buff } from "../../Buff/Buff";
import { BuffFactory } from "../../Buff/BuffFactory";
import { Tower } from "../Entity/Tower";
import { TowerMgr } from "../Entity/TowerMgr";
import { TowerAuraScopeExpand } from "./TowerAuraScopeExpand";


// Buff
export class TowerAuraHandler {

    //  
    public static aura() {

        for( let i = 0 ; i < TowerMgr.ins.data.length ; ++i ) {
            let tower = TowerMgr.ins.data[ i ] ;
            this.auraEmit( tower ) ;
        } // end for

    }

    // 
    private static auraEmit( tower : Tower ) {

        // 
        let buffOverlapEnable = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.OverlapEnable ; } ) ;


        for( let buff of tower.aura.list ) {

            /*
                 buff 
                
                buffbuff
                
            */ 

            let buff2 = BuffFactory.create( buff.cfg.id ) ;
            buff2.isNative = false ; 

            switch( buff.affix.type ) {
                case AffixType.AuraAttackDamageIncreasePercent : 
                case AffixType.AuraAttackSpeedIncreasePercent : 
                case AffixType.AuraAttackCritProbabilityIncrease : 
                {

                    let ret = TowerAuraScopeExpand.exe( buff.cfg.auraScopeType , tower.article.cells ) ; 
                    this.buffAdd( ret.towers , buff2 , tower , buffOverlapEnable ) ;

                }
                break ; 

                case AffixType.AuraAttackDamageIncreaseAllPercent : 
                {

                    // 
                    let towers = TowerMgr.ins.data ; 
                    this.buffAdd( towers , buff2 , tower ) ;

                }
                break ;

            }

        } // end for

    }

    private static buffAdd( towers : Array< Tower > , buff : Buff , tower : Tower , buffOverlapEnable : Buff = null ) {

        for( let tower2 of towers ) {

            if( buff.cfg.auraOverlay == false ) {
                // 

                if( buffOverlapEnable != null ) {
                    // Buff


                }
                else {

                    let buff2 = tower2.buff.list.find( ( e )=>{ return e.cfg.id == buff.cfg.id ; } ) ; 
                    if( buff2 != null ) {
                        continue ; 
                    }

                }

            }

            // Buff
            tower2.buff.list.push( buff ) ; 

            let str = tower2.article.cfg.id + " Buff " + "" + tower.article.cfg.id ; 
            console.log( str )  ;

        }

    }


}

