import { instantiate, Vec3 } from "cc";
import { UiBagCfg } from "../Main/UiBagCfg";
import { UiBagGrid } from "./UiBagGrid";

// 
export class BagGridDraw {

    public static exe( ) {

        this.drawCell() ; 

    }

    //  
    public static drawCell() {

        // 

        let cellPf = UiBagGrid.ins.cellPf ;

        let size = UiBagGrid.ins.gridSize ; 
        let grid = UiBagGrid.ins.grid ; 

        // 
        // let cellSize = new Size( 80 , 80 ) ;
        let cellSize = UiBagCfg.ins.cellSize ; 

        let posStart = new Vec3( ) ;
        posStart.x = size.width * cellSize.width / -2 + cellSize.width / 2 ;
        posStart.y = size.height * cellSize.height / -2 + cellSize.height / 2

        for( let yCoord = 0 ; yCoord < size.height ; ++yCoord ) {

            for( let xCoord = 0 ; xCoord < size.width ; ++xCoord ) {

                let root = instantiate( cellPf ) ;
                root.setParent( UiBagGrid.ins.root ) ;

                let cell = grid[ yCoord ][ xCoord ] ; 

                cell.init( root ) ; 

                // 
                let offset = new Vec3( xCoord * cellSize.width , yCoord * cellSize.height ) ;
                cell.root.position = posStart.clone().add( offset ) ;

                // 
                if( cell.unlocked == false ) {

                    // cell.root.getComponentInChildren( Sprite ).color = Color.BLACK ; 
                    cell.root.active = false ;

                }

            } // end for

        } // end for


    }


}