import { Node, Sprite } from "cc";

// UI
export class PfMgrMoney {

    public static ins = new PfMgrMoney() ; 

    public root : Node ; 

    //  1 
    public money001 : Node ; 
    public meat : Sprite ; 
    public heroSpiece1: Sprite;

    //  2 

    public init( node : Node ) {

        this.root = node ; 

        this.money001 = this.root.getChildByPath( "Money001" ) ;
        this.meat = this.root.getChildByPath( "Meat" ).getComponent(Sprite) ;
        this.heroSpiece1 = this.root.getChildByPath("HeroPiece1").getComponent(Sprite);

    }

    public get_sprite_by_id(id:string) {
        return this.root.getChildByPath( id ).getComponent(Sprite) ;
    }
}