import { instantiate, Label, Node, Prefab, resources, tween, UITransform, Vec3 } from "cc";

export class UiToast {

    public static pf : Prefab ; 

    public static loadPf() {

        if( this.pf != null ) return ; 

        resources.load( "Common/Ui/Toast" , Prefab , ( err , data )=>{
            this.pf = data ;
        } ) ;

    }

    public static create( parent : Node ) : UiToast {

        let root = instantiate( this.pf ) ;
        let ui = new UiToast() ;
        ui.init( root ) ; 
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }

    public static show( parent : Node , str : string , stayDuration = 2 ) : UiToast {

        let ui = this.create( parent ) ;
        ui.txt.string = str ;
        ui.show( stayDuration ) ; 
        return ui ; 

    }


    public root : Node ;

    public txt : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.txt = this.root.getComponentInChildren( Label ) ; 
    }

    public show( stayDuration = 2 ) {

        // 

        this.txt.updateRenderData(true);
        let w = this.txt.getComponent( UITransform ).contentSize.width ; 
        let uitrans = this.root.getComponent( UITransform ) ; 
        let size = uitrans.contentSize.clone() ;
        if( size.width < w ) {
            size.width = w + 60 ; 
            uitrans.setContentSize( size ) ;
        }


        tween( this.root ).by( 0.5 , { position : new Vec3( 0 , 100 , 0 ) } ).delay( stayDuration ).call( ()=>{
            this.root.destroy() ; 
        } ).start();

    }

}