import { Node } from "cc";

export class UiBagPrefab {

    public static ins : UiBagPrefab = null ; 

    public root : Node ; 

    // 1-4
    // public item1 : Node ; 
    // public item2 : Node ; 
    // public item3 : Node ; 
    // public item4 : Node ; 
    public items = new Array< Node >() ;

    public itemLocked : Node ; 

    public itemEmpty : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        for( let i = 1 ; i <=4 ; ++i ) {
            let node = this.root.getChildByPath( "Item" + i ) ;
            this.items.push( node ) ;
        }

        // this.item1 = this.root.getChildByPath( "Item1" ) ;
        // this.item2 = this.root.getChildByPath( "Item2" ) ;
        // this.item3 = this.root.getChildByPath( "Item3" ) ;
        // this.item4 = this.root.getChildByPath( "Item4" ) ;
        this.itemLocked = this.root.getChildByPath( "ItemLocked" ) ;

        this.itemEmpty = this.root.getChildByPath( "ItemEmpty" ) ;

    }



}