import { Input, input, instantiate, Node, Prefab, Sprite, resources, RichText, Size, UITransform, _decorator,Component } from "cc";
const { ccclass, property } = _decorator;


@ccclass('UiBubbleWithIcon')
export class UiBubbleWithIcon  extends Component {

    public static pf : Prefab ; 

    public static loadAndCreate( cb : ( ui : UiBubbleWithIcon )=>void ) {

        resources.load( "Common/Ui/BubbleWithIcon" , Prefab , ( err , data )=>{

            let ui = new UiBubbleWithIcon() ; 
            ui.init( instantiate( data ) ) ;
           
            // ui.root.setParent( parent ) ;
            // ui.txt.string = str ; 
            cb?.( ui ) ;

        } ) ;

    }


    public root : Node ;

    public bg : Node ; 

    @property(Sprite)
    public sp: Sprite

    @property(RichText)
    public txt : RichText ; 

    public init( node : Node )  {
        this.root = node ; 

    }

    public set_text(template: string, values: any[]|undefined = undefined) {
        let tp = '<outline color=#000000 width=5><color=#00ff00>{}</color></outline>'
        
        let outer = '<b><outline color=#000000 width=5><color=#ffffff>{}</color></outline></b>'

        let result = template
        if (values) {
            result = values.reduce((acc, value) => {
                let p = tp.replace('{}', value)
                return acc.replace('{}', p)
                
            }, template);
        }
        
        result = outer.replace('{}', result)
        this.txt.string = result;
    }



}