import { Node } from "cc";
import { UiTabHeaderBottom } from "../../../SL/Cocos/Ui/UiTabHeader";
import { UiBag } from "../Bag/UiBag";
import { UiBattle } from "./Page/Battle/UiBattle";
import { UiShop } from "./Page/Shop/UiShop";
import { Test1 } from "./Page/Test1/Test1";
import { Test2 } from "./Page/Test2/Test2";

// Tab 
export class UiBottomTabPage {

    public static ins : UiBottomTabPage = null ; 

    public root : Node ; 

    public thb = new UiTabHeaderBottom() ; 

    // 
    public page : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.initPage() ; 

        this.initTabHeader() ; 

        // 
        this.thb.show( "Battle" ) ; 
        UiBattle.ins.root.active = true ; 

    }

    private initPage() {

        this.page = this.root.getChildByPath( "Page" ) ;

        UiBag.create() ;
        UiBag.ins.root.setParent( this.page , false ) ; 
        UiBag.ins.root.active = false ; 

        UiBattle.ins.init( this.root.getChildByPath( "Page/Battle" ) ) ;
        UiBattle.ins.root.active = false ; 
        // console.log( "UiBattle" ) ;

        UiShop.ins.init( this.root.getChildByPath( "Page/Shop" ) ) ; 
        UiShop.ins.root.active = false ; 
        // console.log( "UiShop" ) ;

        Test1.ins.init( this.root.getChildByPath( "Page/Test001" ) ) ; 
        Test1.ins.root.active = false ; 
        // console.log( "Test1" ) ;

        Test2.ins.init( this.root.getChildByPath( "Page/Test002" ) ) ; 
        Test2.ins.root.active = false ; 
        // console.log( "Test2" ) ;

    }

    private initTabHeader() {

        let thb = this.thb = new UiTabHeaderBottom() ; 
        thb.init( this.root.getChildByPath( "BottomTabHeader" ) ) ; 
        thb.onSelectChange = ( item )=>{

            let name = item.root.name ;
            switch( name ) {

                case "Battle" : {
                    // hideAll() ; 
                    UiBattle.ins.root.active = true ; 
                    UiBattle.ins.root.setSiblingIndex( 10 ) ; 
                }
                break ; 

                case "Bag" : {
                    // hideAll() ; 
                    UiBag.ins.root.active = true ; 
                    UiBag.ins.root.setSiblingIndex( 10 ) ; 
                    // UiBag.ins.show();
                }   
                break ;

                case "Shop" : {
                    // hideAll() ; 
                    UiShop.ins.root.active = true ; 
                    UiShop.ins.root.setSiblingIndex( 10 ) ; 
                }
                break ; 

                case "Test001" : {
                    this.hideAllPages() ; 
                    Test1.ins.root.active = true ; 
                }
                break ; 

                case "Test002" : {
                    this.hideAllPages() ; 
                    Test2.ins.root.active = true ; 
                }
                break ; 
            }

        } ;

    }

    private hideAllPages() {

        UiBattle.ins.root.active = false ; 
        UiBag.ins.root.active = false ; 
        UiShop.ins.root.active = false ; 
        Test1.ins.root.active = false ; 
        Test2.ins.root.active = false ; 

    }

}