import { EffectFactory } from "../../../../Bag/Merge/EffectFactory";
import { PlayerGradeOp } from "../../../2.Td/Player/PlayerGradeOp";
import { BulletMgr } from "../../Bullet/BulletMgr";
import { WorldNode } from "../../Support/Misc/WorldNode";
import { Role } from "../Base/Role";
import { RoleMgr } from "../Base/RoleMgr";


export class RoleDie {

    public static exe( role : Role ) {

        // 
        let effect = EffectFactory.create( "RoleDie" ) ; 
        effect.setParent( WorldNode.ins.layer2 , false ) ; 
        effect.worldPosition = role.view.root.worldPosition ; 

        RoleMgr.ins.remove( role ) ; 

        role.view.root.destroy() ; 

        PlayerGradeOp.addExp( role.exp.val ) ;

        this.bullet( role ) ; 


    }

    // 
    public static bullet( role : Role ) {

        for( let i = BulletMgr.ins.data.length - 1 ; i >= 0 ; --i ) {

            let bullet = BulletMgr.ins.data[ i ] ;
            if( bullet.target.targetRole == role ) {

                bullet.target.targetRole = null ; 
                // BulletMgr.ins.remove( bullet ) ;
                // bullet.view.root.destroy() ; 

            }

        } // end for

    }

}