import { Size, Tween, tween, UITransform } from "cc";


export class TweenHelper {

    // 
    public static contentSize( uitrans : UITransform , size : Size , duration : number ) {

        let sizeOrig = uitrans.contentSize ;

        let t = { } ;
        t[ "size" ] = sizeOrig ;
        let tw = tween( t ).to( duration , { size : size } , {
            onUpdate : (target ?: object, ratio ?: number): void => {
                let size = target[ "size" ] ;
                // console.log( target[ "size" ] ) ;
                uitrans.setContentSize( size ) ;
            }
        } ).start() ;

        // tw.call( ()=>{
        //     console.log( "" ) ; 
        // } ) ;

        // return tw ;

    }

}