    // 
    export class MoveRole
    {
        /*
            

            
            

        */
        public static exe( role : Role , v : Vec3 ) : void
        {
            var move = role.move;

            this.preCalc(role, v);

            // if ( this.MoveShove.exe(role) == false) return;

            this.translate( role , move.offset , move.speed );


        }

        // 
        public static preCalc( role : Role , v : Vec3 ) : void
        {
            // 
            var vNormal = v.clone().normalize() ;
            role.move.direction = vNormal;

            // / =  * /
            let step = vNormal.multiplyScalar( role.move.speed ) ;
            role.move.offset = step ;
            //role.presentation.root.localPosition += v;

            // 
            role.move.positionTest = role.view.root.position.clone().add( step ) ;

        }

        // 
        public static translate( role : Role , step : Vec3 , stepLen : number ) : boolean
        {
            // var ret = MoveCalc.exe(role, step, stepLen);
            // if (ret.ok == false) return false;

            //role.presentation.root.GetComponentInChildren<Rigidbody>().AddForce(role.move.step );

            // 
            // role.transform.position = ret.pos;
            // 
            // RectSetter.setCenter(ref role.boundingShape.rect, ret.pos);

            // 
            //CtrlMovePresentation.moveTo( role.presentation.node.root.transform , ret.pos , role.presentation.moveTweeners );
            role.view.root.position = role.move.positionTest ;

            return true;

        }



    }