import { instantiate, Label, Node, Size, Sprite, Tween, tween, UITransform, Vec3 } from "cc";
import { PfMgrBagArticle } from "../../../Common/PfMgr/PfMgr";
import { NodeSizeFit } from "../../../SL/Cocos/Ui/NodeSizeFit";
import { ArticleString } from "../../../Common/String/ArticleString";
import { CfgArticle, CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { Account } from "../../../Common/Account/Account";
import { UiBagPrefab } from "./UiBagPrefab";
import { BagArticleGradeHelper } from "../../../Game/Bag/Article/BagArticleGradeHelper";

export class UiBagItem {

    public articleId : string ; 

    public root : Node ; 

    // 
    public name : Label ; 

    // 
    public icon : Sprite ; 

    // 
    public grade : Label ; 

    // 
    public shape : Node ; 

    public updateFragment : UiBagItemUpgradeFragment ;

    public init( node : Node ) {

        this.root = node ; 

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;

        this.icon = this.root.getChildByPath( "Icon" ).getComponent( Sprite ) ; 

        this.grade = this.root.getChildByPath( "Grade/val" ).getComponent( Label ) ; 

        this.shape = this.root.getChildByPath( "Shape" ) ; 

        let node2 = this.root.getChildByPath( "Fragment" ) ; 
        if( node2 != null ) {
            this.updateFragment = new UiBagItemUpgradeFragment() ; 
            this.updateFragment.init( node2 );
        }


    }

    public refresh() {

        // 
        this.name.string = ArticleString.getName( this.articleId ) ;  

        // 
        let pf = PfMgrBagArticle.getPf( this.articleId ) ;
        this.icon.spriteFrame = pf.getChildByPath( "Main" ).getComponent( Sprite ).spriteFrame ; 
        NodeSizeFit.set( this.icon.node , this.icon.spriteFrame.originalSize ) ;
        
        // 
        let cfg = CfgArticleMgr.data.get( this.articleId ) ; 
        let n = 1 ;
        if( Account.ins.bag.grade.has( cfg.gradeLineId ) == true ) {
            n = Account.ins.bag.grade.get( cfg.gradeLineId ) ; 
        }
        this.grade.string = "" + n.toString() ; 

        // 
        let pfShape = UiBagPrefab.ins.root.getChildByPath( "Shape/" + cfg.bagShapeId ) ; 
        if( pfShape != null )  {

            let node = instantiate( pfShape ) ; 
            node.name = "Shape" ; 
            node.setParent( this.shape , false ) ;
            node.position = Vec3.ZERO ; 

        }

        if( this.updateFragment != null ) {
            this.updateFragment.articleId = this.articleId ; 
            this.updateFragment.refresh() ; 
        }

    }

}

// 
export class UiBagItemUpgradeFragment {

    public articleId : string ; 

    public root : Node ; 

    // 
    public bar : Sprite ; 

    // 
    public count : Label ; 

    public icon : Node ; 

    // 
    public upgradeable : Node ; 

    public init( node : Node ) {

        this.root = node ; 
        this.bar = this.root.getChildByPath( "bar" ).getComponent( Sprite ) ; 
        this.bar.fillRange = 0 ;

        this.count = this.root.getChildByPath( "val" ).getComponent( Label ) ;
        this.count.string = "0/0" ; 

        this.upgradeable = this.root.getChildByPath( "Upgradeable" ) ; 
        this.upgradeable.active = false ; 

        this.icon = this.root.getChildByPath( "Icon" ) ; 
        

    }

    public refresh() {
 
        let cfgGradeItem = BagArticleGradeHelper.getCfgCurrent( this.articleId ) ;

        // 
        let n = Account.ins.items.get( cfgGradeItem.upgrade.itemId ) ; 
        if( n == null ) { n = 0 ; }
        this.count.string = n + "/" + cfgGradeItem.upgrade.itemCount ; 
        
        // 
        this.bar.fillRange = n / cfgGradeItem.upgrade.itemCount ; 

        if( n >= cfgGradeItem.upgrade.itemCount ) {
            // 
            this.upgradeable.active = true ; 
            this.playAnimUpgradeable() ; 
        }
        else {
            this.upgradeable.active = false ; 
            // this.icon.active = true ; 
        }

    }

    // 
    public playAnimUpgradeable() {

        let node = this.upgradeable ; 

        if( node[ "tween" ] == true ) { return ; }
        node[ "tween" ] = true ; 

        Tween.stopAllByTarget( node ) ; 

        let pos1 = node.position.clone().add3f( 0 , 10 , 0 ) ; 
        let pos2 = node.position.clone().add3f( 0 , -10 , 0 ) ; 
        tween( node ).sequence(
            tween( node ).to( 0.5 , { position : pos1 } ) , 
            tween( node ).to( 0.5 , { position : pos2 } ) , 
        ).repeatForever().start() ;         

    }

}