import { randomRangeInt } from "cc";
import { WeightProbability } from "../../SL/Common/Misc/WeightProbability";
import { CfgItemDraw } from "../Cfg/CfgItemDraw";
import { ItemData } from "./ItemData";
import { CfgItem, CfgItemMgr } from "../Cfg/CfgItem";
import { ItemType } from "./ItemType";



// 
export class ItemDraw {

    public static exe( id : string ) : Array< ItemData > {

        let ret = new Array< ItemData >() ; 

        let cfgItem = CfgItemDraw.data.get( id ) ;
        if( cfgItem == null ) {
            return ret ; 
        }

        let w = new WeightProbability() ; 

        for( let i = 0 ; i < cfgItem.items.length ; ++i ) {
            let cfgItemItem = cfgItem.items[ i ] ; 
            w.add( cfgItemItem.id , cfgItemItem.w ) ;
        } // end for

        let n = randomRangeInt( cfgItem.min , cfgItem.max + 1 ) ; 
        for( let i = 0 ; i < n ; ++i ) {
            let id = w.get( ) ; 

            let item = ret.find( ( e )=>{ return e.id == id ; } ) ; 
            if( item == null ) {
                item = new ItemData() ; 
                item.id = id ;
                item.cfg = CfgItemMgr.data.get( id ) ; 
                ret.push( item ) ; 
            }
            item.count++ ;

        } // end for
        
        this.sort( ret ) ;

        return ret ; 

    }

    //  
    public static sort( items : Array< ItemData > ) {

        let mapping = new Map< string , number >() ; 
        mapping.set( ItemType.Money001 , 1 ) ;
        mapping.set( ItemType.Money002 , 2 ) ;
        // mapping.set( ItemType.Fragment , 4 ) ;

        items.sort( ( a , b )=>{

            // 
            let p1 = mapping.has( a.id ) ? mapping.get( a.id ) : 10 ; 
            let p2 = mapping.has( b.id ) ? mapping.get( b.id ) : 10 ; 
            if( p1 < p2 ) {
                return -1 ;
            }
            else if( p1 > p2 ) {
                return 1 ;
            }

            // 

            if( a.cfg.quality < b.cfg.quality ) {
                return 1 ;
            }
            else if( a.cfg.quality > b.cfg.quality ) {
                return -1 ;
            }

            return 0 ; 

        } ) ;

    }

}

