import { Account } from "db://assets/Script/Common/Account/Account";
import { ItemData } from "db://assets/Script/Common/Item/ItemData";
import { ItemType } from "db://assets/Script/Common/Item/ItemType";
import { UiToast } from "db://assets/Script/Common/Ui/UiToast";
import { UiMain } from "../../Home/Ui/Main/UiMain";
import { AccountPersist } from "db://assets/Script/Common/Account/AccountPersist";
import { isValid } from "cc";
import { UiMoney001 } from "../../Home/Ui/Main/Margin/UiMoney001";
import { UiMoney002 } from "../../Home/Ui/Main/Margin/UiMoney002";
import { UiStamina } from "../../Home/Ui/Main/Margin/UiStamina";
import { Lang } from "../Misc/Lang";

// 
export class ItemSub {

    public static exe( data : Array< ItemData > ) {

        let toastStayDuration = 1.5 ; 

        let a = Account.ins ; 

        for( let i = 0 ; i < data.length ; ++i ) {

            let item = data[ i ] ;

            switch( item.id ) {

                case ItemType.Money001 : {

                    if( a.money.money001 < item.count ) {
                        UiToast.show( UiMain.ins.root , Lang.get( "Home_Money001NotEnough" ) , toastStayDuration ) ; 
                        return false ; 
                    }

                }
                break ; 

                case ItemType.Money002 : {

                    if( a.money.money002 < item.count ) {
                        UiToast.show( UiMain.ins.root , Lang.get( "Home_Money002NotEnough" ) , toastStayDuration ) ; 
                        return false ; 
                    }

                }
                break ; 

                case ItemType.Stamina : {

                    if( a.stamina.current < item.count ) {
                        UiToast.show( UiMain.ins.root , Lang.get( "Home_SiaminaNotEnough" ) , toastStayDuration ) ; 
                        return false ; 
                    }

                }
                break ; 

                default : {

                    // 
                    if( item.id.startsWith( "Fragment" ) ) {

                        let itemCount = a.items.get( item.id ) ; 
                        if( itemCount == null ) { itemCount = 0 ; } ; 
                        if( itemCount < item.count ) {
    
                            // 
                            UiToast.show( UiMain.ins.root , Lang.get( "Home_FragmentNotEnough" ) , toastStayDuration ) ; 
    
                            return false ; 

                        }

                    }


                }
                break ; 

            }


        } // end for


        // 

        for( let i = 0 ; i < data.length ; ++i ) {

            let item = data[ i ] ;

            switch( item.id ) {

                case ItemType.Money001 : {
                    a.money.money001 -= item.count ; 

                    if( isValid( UiMoney001.ins.root ) ) {
                        UiMoney001.ins.refresh() ; 
                    }
                }
                break ; 

                case ItemType.Money002 : {
                    a.money.money002 -= item.count ; 

                    if( isValid( UiMoney002.ins.root ) ) {
                        UiMoney002.ins.refresh() ; 
                    }
                }
                break ; 

                case ItemType.Stamina : {
                    a.stamina.current -= item.count ; 

                    if( isValid( UiStamina.ins.root ) ) {
                        UiStamina.ins.refresh() ; 
                    }
                }
                break ; 

                default : {

                    // 
                    if( item.id.startsWith( "Fragment" ) ) {

                        let itemCount = a.items.get( item.id ) ; 
                        itemCount -= item.count ; 
                        a.items.set( item.id , itemCount ) ; 

                    }


                }
                break ; 

            }


        } // end for

        AccountPersist.save() ;


        return true ; 

    }
    
}