


export class CfgMushroom {

    public static data = new Map<number, CfgMushroomItem >() ; 

}

export class CfgMushroomItem {

    public id : Number ; 
    public name: string;
}


export class CfgMushroomParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 

            let name = row["Name"];
            if (name == null || name == "") continue;
            
            let cfgItem = new CfgMushroomItem() ;
            cfgItem.id = id;
            cfgItem.name = name;
            CfgMushroom.data.set(id, cfgItem ) ; 

            cfgItem.id = id ; 


        } // endf


    }

}