

import { Lang } from '../Misc/Lang';

export class ArticleString {

    // 
    public static getName( articleId : string ) : string {

        // 1ID
        let strs = articleId.split( "_" ) ;
        if( strs.length == 2 ){
            articleId = strs[ 0 ] + "_1" ; 
        }

        let key = "ArticleName_" + articleId ; 
        let str = Lang.get( key ) ;
        return str ; 

    }

    //  
    public static getDescription( articleId : string ) {

        let strs = articleId.split( "_" ) ;
        if( strs.length == 2 ){
            articleId = strs[ 0 ] + "_1" ; 
        }

        let key = "ArticleDescr_" + articleId ; 
        let str = Lang.get( key ) ;

        return str ;
    }

}