import { Role } from "../Role/Base/Role";
import { RoleMgr } from "../Role/Base/RoleMgr";
import { RoleMove } from "../Role/Misc/RoleMove";

export class Ai001 {

    public static update( role : Role ) {

        // 

        role.ai.target = RoleMgr.ins.local ; 
        this.chase( role ) ; 

    }

    public static chase( role : Role ) {

        let tar = role.ai.target ; 
        let v = tar.view.root.position.clone().subtract( role.view.root.position ) ;
        RoleMove.exe( role , v ) ;

    }

}