import { instantiate, Prefab, resources } from "cc";
import { UiBag } from "../../Game/Bag/Main/UiBag";
import { UiHpBar } from "../../Game/Ui/Bar/UiHpBar";
import { UiFeatureSel } from "../../Game/Ui/Popup/FeatureSel/UiFeatureSel";
import { BulletPfMgr } from "../../Game/World/1.Base/Bullet/BulletPfMgr";
import { PfMgrRole } from "../../Game/World/1.Base/Role/Misc/PfMgrRole";
import { EffectPfMgr } from "../../Game/World/1.Base/Support/Misc/EffectPfMgr";
import { UiBagArticleDetail } from "../../Home/Ui/Bag/ArticleDetail/UiBagArticleDetail";
import { UiBag as HomeUiBag } from "../../Home/Ui/Bag/UiBag";
import { UiDialog } from "../Ui/UiDialog";
import { UiLoadingScene } from "../Ui/UiLoadingScene";
import { PfMgrBagArticle } from "./PfMgr";
import { PfMgrIcon } from "./PfMgrIcon";
import { PfMgrIconBuff } from "./PfMgrIconBuff";
import { PfMgrMoney } from "./PfMgrMoney";
import { PfMgrNumber } from "./PfMgrNumber";
import { UiBubble } from "../Ui/UiBubble";
import { UiSettings } from "../Ui/UiSettings";
import { PfLevelThumbnail } from "../../Home/Ui/Main/Page/Battle/PfLevelThumbnail";
import { UiStaminaBuy } from "../../Home/Ui/Popup/UiStaminaBuy";
import { UiGet } from "../../Home/Ui/Popup/UiGet";
import { UiDailyCheckin } from "../../Home/Ui/Popup/DailyCheckin/UiDailyCheckin";
import { UiHybridGuide } from "../../Home/Ui/Popup/UiHybridGuide";


// 
export class PfLoad {

    public static onProgress: (val: number) => void;

    public static exe(onComplete: () => void) {

        // 
        if (this.flag == true) {
            onComplete();
            return;
        }
        this.flag = false;

        let list = [ 

            // 
            "Common/Ui/Money",
            "Common/Ui/LoadingScene",
            "Common/Ui/Dialog",
            "Common/Ui/Icon", 
            "Common/Ui/Bubble",
            "Common/Ui/Settings" , 

            // 
            "Home/Ui/ArticleDetail",
            "Home/Ui/Bag",
            "Home/Ui/LevelThumbnail",
            "Home/Ui/StaminaBuy" , 
            "Home/Ui/Get" , 
            "Home/Ui/DailyCheckin" , 
            "Home/Ui/HybridGuide" , 

            // 
            "Bag/Bag",
            "Bag/Article",

            // 
            "Game/Role/Role",
            "Game/Ui/Number",
            "Game/Ui/Bar",
            "Game/Bullet/Bullet",
            "Game/Ui/FeatureSel",
            "Game/Ui/Icon",
            "Game/Misc/Effect",

        ];
        resources.load(list, Prefab, this._onProgress.bind(this) , (err, data) => {

            for( let i = 0 ; i < data.length ; ++i ) {

                let path = list[ i ] ;
                let pf = data[ i ] ;
                this.handlePf( path , pf ) ; 

            } // end for

            onComplete?.() ;

        } ) ;

    }

    // 
    private static handlePf( path : string , pf : Prefab ) {

        switch( path ) {

            // 

            case "Common/Ui/Money" : {
                PfMgrMoney.ins.init(instantiate( pf ) ) ;
            }
            break ; 

            case "Common/Ui/LoadingScene" : {
                UiLoadingScene.ins.init(instantiate( pf ));
            }
            break ; 

            case "Common/Ui/Dialog" : {
                UiDialog.pf = pf ;
            }
            break ; 

            case "Common/Ui/Icon" : {
                PfMgrIcon.ins.init(instantiate( pf ));
            }
            break ; 

            case "Common/Ui/Bubble" : {
                UiBubble.pf = pf ; 
            }
            break ; 

            case "Common/Ui/Settings" : {
                UiSettings.pf = pf ; 
            }
            break ; 
            
            // 
            
            case "Home/Ui/ArticleDetail" : {
                UiBagArticleDetail.pf = pf ;
            }
            break ; 

            case "Home/Ui/Bag" : {
                HomeUiBag.pf = pf ;
            }
            break ; 

            case "Home/Ui/LevelThumbnail" : {
                PfLevelThumbnail.ins.init( instantiate( pf ) ) ;
            }
            break ;

            case "Home/Ui/StaminaBuy" : {
                UiStaminaBuy.pf = pf ; 
            }
            break ; 

            case "Home/Ui/Get" : {
                UiGet.pf = pf ; 
            }
            break ;

            case "Home/Ui/DailyCheckin" : {
                UiDailyCheckin.pf = pf ; 
            }
            break ; 

            case "Home/Ui/HybridGuide" : {
                UiHybridGuide.pf = pf ; 
            }
            break ; 

            // 

            case "Bag/Bag" : {
                UiBag.pf = pf ;
            }
            break ;

            case "Bag/Article" : {
                PfMgrBagArticle.bagArticle = instantiate( pf ) ;
            }
            break ; 

            // 

            case "Game/Role/Role" : {
                PfMgrRole.pf = instantiate( pf );
            }
            break ; 

            case "Game/Ui/Number" : {
                PfMgrNumber.ins.init( instantiate( pf ) );
            }
            break ; 

            case "Game/Ui/Bar" : {
                UiHpBar.pf = instantiate( pf );
            }
            break ; 

            case "Game/Bullet/Bullet" : {
                BulletPfMgr.ins.root = instantiate( pf );
            }
            break ; 

            case "Game/Ui/FeatureSel" : {
                UiFeatureSel.pf = pf ;
            }
            break ; 

            case "Game/Ui/Icon" : {
                PfMgrIconBuff.ins.root = instantiate( pf );
            }
            break ; 

            case "Game/Misc/Effect" : {
                EffectPfMgr.ins.root = instantiate( pf );
            }
            break ; 


            case "" : {
                
            }
            break ; 

        }

    }

    private static _onProgress(finished: number, total: number, item: RequestInit) {

        // https://forum.cocos.org/t/topic/147629

        let val = finished / total;
        if (val < this.val) return;

        this.val = val;
        this.onProgress?.(this.val);

    }

    // 
    private static val = 0;

    // 
    public static flag = false;

}