import { Account } from "../../../Common/Account/Account";
import { CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { CfgArticleGrade, CfgArticleGradeItem } from "../../../Common/Cfg/CfgArticleGrade";

// 
export class BagArticleGradeHelper {

    // ID
    public static getGradeByLineId( gradeLineId : string ) : number {

        let currentGrade = Account.ins.bag.grade.get( gradeLineId ) ; 
        if( currentGrade == null ) {
            currentGrade = 1 ; 
        }

        return currentGrade ; 

    }

    // 
    public static getCfgCurrent( idArticle : string ) : CfgArticleGradeItem {

        // 
        let cfgArticle = CfgArticleMgr.data.get( idArticle ) ;
        if( cfgArticle == null ) {
            return null ;
        }

        let grade = CfgArticleGrade.data.get( cfgArticle.gradeLineId ) ; 
        if( grade == null ) {
            return null ; 
        }

        let currentGrade = Account.ins.bag.grade.get( cfgArticle.gradeLineId ) ; 
        if( currentGrade == null ) {
            currentGrade = 1 ; 
        }
        currentGrade-- ; 

        return grade[ currentGrade ] ; 

    }

    // 
    public static getCfgNext( idArticle : string ) : CfgArticleGradeItem {

        // 
        let cfgArticle = CfgArticleMgr.data.get( idArticle ) ;
        if( cfgArticle == null ) {
            return null ;
        }

        let grade = CfgArticleGrade.data.get( cfgArticle.gradeLineId ) ; 
        if( grade == null ) {
            return null ; 
        }

        let currentGrade = Account.ins.bag.grade.get( cfgArticle.gradeLineId ) ; 
        if( currentGrade == null ) {
            currentGrade = 1 ; 
        }
        
        if( currentGrade >= grade.length ) return null ; 

        return grade[ currentGrade ] ; 


    }

    // 0
    public static getCfgCurrentItems( idArticle : string ) : Array< CfgArticleGradeItem > {

        // 
        let cfgArticle = CfgArticleMgr.data.get( idArticle ) ;
        if( cfgArticle == null ) {
            return null ;
        }

        let grade = CfgArticleGrade.data.get( cfgArticle.gradeLineId ) ; 
        if( grade == null ) {
            return null ; 
        }

        let currentGrade = Account.ins.bag.grade.get( cfgArticle.gradeLineId ) ; 
        if( currentGrade == null ) {
            currentGrade = 1 ; 
        }
        currentGrade-- ; 


        let ret = new Array< CfgArticleGradeItem >() ;
        for( let i = 0 ; i <= currentGrade ; ++i ) {
            let item = grade[ i ] ;
            ret.push( item ) ;
        }

        return ret ; 

    }

}