
// 
export class CfgUpgrade {

    public static data = new Array< number >( ) ;   

}

export class CfgUpgradeParser {

    public static parseJson( json : any ) {
        
        // 1
        CfgUpgrade.data.push( 0 ) ;

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 

            CfgUpgrade.data.push( Number( row[ "Exp" ] ) ) ;

        } // end for

    }

}