


import { Music } from "../Audio/Music";
import { AdBase } from "./AdBase";
import { AdRewardVideoFailHandler } from "./Misc/AdRewardVideoFailHandler";
import { UITransform, director, find, tween } from "cc";


//  
export class AdWx extends AdBase {

    public override init() {

        // ID
        let adIdBundle = AdIdBundleMgr.myself() ; 

        // ID
        // let adIdBundle = AdIdBundleMgr.lht() ;  

        //  Banner banner UI
        let size = find( "Canvas" ).getComponent( UITransform ).contentSize ;
        let f = size.width / size.height ;
        let b = f >= 720 / ( 1280 + 100 ) ;
        if( !b ){

            this.banner = new AdBanner() ;
            this.banner.init( adIdBundle.bannerId ) ;

        }

        this.interstitial = new AdInterstitial() ;
        this.interstitial.init( adIdBundle.interstitialId ) ;

        this.rewardedVideo = new AdRewardedVideo() ;
        this.rewardedVideo.init( adIdBundle.rewardedVideoId ) ; 

        this.custom = new AdCustom() ;
        this.custom.init( adIdBundle.customId ) ; 

    }

    public override bannerShow() {
        
        this.banner?.show() ; 

    }

    public override bannerHide() {
        
        this.banner?.hide() ; 

    }

    public override interstitialShow() {

        this.interstitial?.show() ;
    }

    // onComplete  ok 
    public override rewardShow( onSuccess : ()=>void , 
        onCancel : ()=>void = null ,
        onError : ( err : string )=>void = null
         ) {

        let duration = 0.5 ; 

        // 
        this.rewardedVideo.onSuccess = ()=>{

            this.resumeBgm() ; 

            tween( this ).delay( duration ).call( ()=>{ 

                onSuccess() ; 

            } ).start() ;

        } ;
        
        // 
        this.rewardedVideo.onError = ( err : string )=>{ 

            this.resumeBgm() ; 
            
            if( onError != null ) {
                onError( err ) ;
            }
            else {
                AdRewardVideoFailHandler.exe( err ) ; 
            }

        } ;

        // 
        this.rewardedVideo.onCancel = ()=>{

            this.resumeBgm() ; 

            onCancel?.() ; 
        }

        this.rewardedVideo.show() ; 

    }

    // 
    private resumeBgm() {

        let name = director.getScene().name ; 
        // if( name == "Game" ) {
        //     Music.ins.bg1() ; 
        // }
        // else if( name == "Home" ) {
        //     Music.ins.bg1() ; 
        // }
        Music.ins.bg1() ; 

    }

    public override customShow() {

        this.custom?.show() ; 

    }

    public custom : AdCustom ;
    public banner : AdBanner ; 
    public interstitial : AdInterstitial ;
    public rewardedVideo : AdRewardedVideo ; 

}

// ID
