// 
class AdCustom {

    public init( id : string ) {

        // @ts-ignore
        var phone = wx.getSystemInfoSync();
        console.log(phone);
        var w = phone.screenWidth / 2;
        var h = phone.screenHeight;

        //   
        // @ts-ignore
        let CustomAd = wx.createCustomAd({
            adUnitId: id ,
            style: {
                left: w - 290 / 2 , // 
                // left: ( w - 350 ) / 2 , // 
                top: 0,
                width: 350 ,
            }
        })

        this.CustomAd = CustomAd ; 
        
        //   
        CustomAd.onError(err => {
            console.error(err.errMsg)
        });

        CustomAd.onLoad(() => {
            console.log('')
        })


    }

    public show() {

        this.CustomAd.show()

    }

    private CustomAd : any = null ;

}

// Banner 
class AdBanner {

    public init( id : string ) {
        
        // @ts-ignore
        var phone = wx.getSystemInfoSync();
        console.log(phone);
        var w = phone.screenWidth / 2;
        var h = phone.screenHeight;

        // @ts-ignore
        let bannerAd = wx.createBannerAd({
            adUnitId: id ,
            adIntervals: 60 , 
            style: {
                left: 0,
                top: 0,
                width: 400 , 
                height : 100 , 
            }

        })
        this.bannerAd = bannerAd ;

        bannerAd.onError(err => {
            console.log( ":" + err)
        })
        
        bannerAd.onLoad(() => {
            console.log('banner ')
        })

        bannerAd.onResize( ()=> {
            bannerAd.style.left = w - bannerAd.style.realWidth/2+0.1;
            bannerAd.style.top = h - bannerAd.style.realHeight+0.1 ;
            console.log( bannerAd.style.top );
        })

    }

    public show() {
        
        this.bannerAd?.show() ; 

    }

    public hide() {

        this.bannerAd?.hide() ; 

    }

    public bannerAd : any = null ; 

}

// 
class AdInterstitial {

    public init( id : string ) {

        // @ts-ignore
        if (!wx.createInterstitialAd){
            return ;
        }

        // 
        // @ts-ignore
        let interstitialAd = wx.createInterstitialAd({
            adUnitId: id 
        })
        this.interstitialAd = interstitialAd ; 

        interstitialAd.onLoad(() => {
            console.log(' ')
        })

        interstitialAd.onError(err => {
            console.log(err)
        })


    }

    public show() {

        let interstitialAd = this.interstitialAd ;
        if (interstitialAd) {
            interstitialAd.show().catch((err) => {
                console.error(err)
            })
        }

    }

    public interstitialAd : any ;
    
}


// 
class AdRewardedVideo {

    // 
    public onSuccess : ()=>void = null ; 

    // err == null 
    public onError : ( err : string )=>void = null ; 

    // 
    public onCancel : ()=>void = null ; 

    public init( id : string ) {

        // 
        // @ts-ignore
        let videoAd = wx.createRewardedVideoAd({
            adUnitId: id
        }) ;
        this.videoAd = videoAd ; 

        videoAd.onLoad(() => {
            console.log(' ')
        })

        videoAd.onError(err => {
            console.log(err) ;
            this.onError?.( err.toString() ) ;
        })

        videoAd.onClose(res => {
            // 
            //  2.1.0 res  undefined
            if (res && res.isEnded || res === undefined) {
                // 
                this.onSuccess?.( ) ; 
            }
            else {
                // 
                this.onCancel?.( )
            }
        })

    }

    public show() {

        let videoAd = this.videoAd  ; 
        
        if( !videoAd ) return ; 

        // 
        videoAd.show().catch(() => {
            // 
            videoAd.load()
            .then(() => videoAd.show())
            .catch(err => {

                console.log(' ')

                videoAd.load()
                .then(() => videoAd.show())

            })
        })

    }

    public videoAd : any ; 

}