
import { Node } from "cc";
import { Input } from "cc";
import { Label } from "cc";
import { Ad } from "../../../Common/Ad/Ad";
import { UiDialog } from "../../../Common/Ui/UiDialog";
import { UiMain } from "../Main/UiMain";
import { UiStamina } from "../Main/Margin/UiStamina";
import { Account } from "../../../Common/Account/Account";
import { UiToast } from "../../../Common/Ui/UiToast";
import { UiMoney002 } from "../Main/Margin/UiMoney002";
import { Lang } from "../../../Common/Misc/Lang";
import { UiPopupBase } from "../../../Common/Ui/UiPopupBase";
import { DailyLimit } from "../../../Common/Misc/DailyLimit";
import { instantiate } from "cc";
import { UiGet } from "./UiGet";
import { ItemData } from "../../../Common/Item/ItemData";
import { input } from "cc";
import { ItemType } from "../../../Common/Item/ItemType";

// 
export class UiStaminaBuy extends UiPopupBase {

    public static override create( parent : Node ) : UiPopupBase {

        let root = instantiate( this.pf ) ;
        let ui = new UiStaminaBuy() ; 
        ui.init( root ) ;
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }

    public itemAd = new UiStaminaBuyItem() ; 
    public itemMoney002 = new UiStaminaBuyItem() ; 

    public override init( node : Node ) {

        super.init( node ) ; 

        this.itemAd.dailyLimit = new DailyLimit( "DailyLimit_StaminaBuy_Ad" , 5 ) ; 
        this.itemAd.init( this.root.getChildByPath( "List/Ad" ) ) ; 
        this.itemAd.root.on( Input.EventType.TOUCH_END , this.onClick_Ad , this ) ;

        this.itemMoney002.dailyLimit = new DailyLimit( "DailyLimit_Money002_Ad" , 5 ) ;  
        this.itemMoney002.init( this.root.getChildByPath( "List/Money002" ) ) ;
        this.itemMoney002.root.on( Input.EventType.TOUCH_END , this.onClick_Money002 , this ) ;


    }

    private onClick_Ad() {
        
        // 
        if( this.itemAd.dailyLimit.current <= 0 ) {
            UiToast.show( UiMain.ins.root , Lang.get( "Home_NotEnoughForToday" ) , 1.5 ) ;
            return ; 
        }

        // 
        let onSuccess = ()=>{

            // 
            Account.ins.stamina.current += 10 ; 
            UiStamina.ins.refresh() ; 

            //  UI
            let uiGet = UiGet.create( UiMain.ins.root ) ;
            uiGet.show() ; 
            uiGet.itemList.refresh( [ new ItemData( ItemType.Stamina , 10 ) ] ) ;
            uiGet.root.on( Input.EventType.TOUCH_END , ()=>{
                // 
                let str = Lang.get( "Home_SiaminaAdd" ).replace( "{1}" , "10" ) ; 
                UiToast.show( UiMain.ins.root , str , 1.5 ) ;
            } ) ;

            // 
            this.itemAd.dailyLimit.decrement() ; 
            this.itemAd.dailyLimit.save() ; 
            this.itemAd.refresh( ) ;

        } ;
        let onCancel = ()=>{
            UiDialog.create( UiMain.ins.root , "~" ).show() ;
        } ; 
        let onError = ( err )=>{
            UiDialog.create( UiMain.ins.root , "" + err ).show() ;
        } ; 
        Ad.ins.rewardShow( onSuccess , onCancel , onError ) ; 

    }

    private onClick_Money002() {

        if( this.itemMoney002.dailyLimit.current <= 0 ) {
            UiToast.show( UiMain.ins.root , Lang.get( "Home_NotEnoughForToday" ) , 1.5 ) ;
            return ; 
        }

        let m002 = Account.ins.money.money002 ;
        m002 -= 100 ;
        if( m002 < 0 ) {
            // 

            UiToast.show( UiMain.ins.root , Lang.get( "Home_Money002NotEnough" ) , 1.5 ) ;

            return ;
        }
        Account.ins.money.money002 = m002 ; 
        UiMoney002.ins.refresh() ; 

        //  UI
        let uiGet = UiGet.create( UiMain.ins.root ) ;
        uiGet.show() ; 
        uiGet.itemList.refresh( [ new ItemData( ItemType.Stamina , 10 ) ] ) ;
        uiGet.root.on( Input.EventType.TOUCH_END , ()=>{
            // 
            let str = Lang.get( "Home_SiaminaAdd" ).replace( "{1}" , "10" ) ; 
            UiToast.show( UiMain.ins.root , str , 1.5 ) ;
        } ) ;

        // 
        Account.ins.stamina.current += 10 ; 
        UiStamina.ins.refresh() ; 

        // 
        this.itemMoney002.dailyLimit.decrement() ; 
        this.itemMoney002.dailyLimit.save() ; 
        this.itemMoney002.refresh( ) ;

    }
    
    
}


export class UiStaminaBuyItem {

    public root : Node ; 

    public left : Label ; 

    // 
    public dailyLimit : DailyLimit = null ; 

    public init( node : Node ) {

        this.root = node ; 

        this.left = this.root.getChildByPath( "Left" ).getComponent( Label ) ; 

        this.dailyLimit.load() ; 
        this.refresh() ; 

    }

    public refresh() {

        this.refreshLeft( this.dailyLimit.current ) ;

    }

    public refreshLeft( n : number ) {

        let str = Lang.get( "Home_LeftToday" ).replace( "{1}" , n.toString() ) ; 
        // let str = "{1}".replace( "{1}" , n.toString() ) ; 
        this.left.string = str ; 

    }

}