export class UiShopDaily {

    public data = new ShopDailyData() ; 

    public root : Node ; 

    // 
    public countdown : Label ; 

    public items = new Array< UiShopItem >() ; 

    public btnRefresh : UiShopDailyBtnRefresh = null ; 

    public init( node : Node ) {

        this.root = node ; 

        let countdown = new UiShopDailyCountdown() ; 
        countdown.init( this.root.getChildByPath( "Refresh/Countdown" ) ) ; 

        this.btnRefresh = new UiShopDailyBtnRefresh() ; 
        this.btnRefresh.init( this.root.getChildByPath( "Refresh/btnRefresh" ) ) ; 
        this.btnRefresh.shopDaily = this ; 

        // 
        // SoundBtnClickSetter.remove( this.root ) ; 
        // this.root.getChildByPath( "List/Item" ).off( Input.EventType.TOUCH_END ) ; 

        //  8 
        let list = this.root.getChildByPath( "List" ) ; 
        let gen = new UiListItemGenMgr() ; 
        gen.init( list ) ; 
        gen.setItemCount( 8 ) ;
        for( let i = 0 ; i < CfgShop.daliyFixed.length ; ++i ) {

            let uiItem = new UiShopItem() ; 
            this.items.push( uiItem ) ; 
            uiItem.init( gen.list[ i ] ) ; 

        } // end for

        for( let i = CfgShop.daliyFixed.length ; i < 8 ; ++i ) {

            let uiItem = new UiShopDailyItem() ; 
            this.items.push( uiItem ) ; 
            uiItem.init( gen.list[ i ] ) ; 
            uiItem.shopDaily = this ; 

        } // end for

        this.data.load() ; 

        this.refreshFixed() ; 
        this.refreshRnd() ; 

        this.startCheckReset() ; 

    }

    // 
    private startCheckReset() {

        tween( this.root ).sequence(
            tween( this.root ).delay( 2 ) , 
            tween( this.root ).call( ()=>{
                if( this.data.checkGen() == true ) {

                    for( let uiItem of this.items ) {
                        uiItem.adDl?.load() ;
                    }
                    this.refreshFixed() ; 
                    this.refreshRnd() ; 
                    
                    this.btnRefresh.dl.load() ; 
                    this.btnRefresh.refreshLeft() ; 

                }
            } ) , 
        ).repeatForever().start() ;

    }

    // 
    public refreshFixed() {

        let list = CfgShop.daliyFixed ; 
        for( let i = 0 ; i < list.length ; ++i ) {
            let uiItem = this.items[ i ] ;
            uiItem.cfg = list[ i ] ; 
            uiItem.refresh() ; 
        } // end for

    }

    // 
    public refreshRnd() {

        let list = this.data.data ;
        for( let i = 0 ; i < list.length ; ++i ) {

            let dataItem = list[ i ] ; 
            let id = dataItem.id ; 
            let cfg = CfgShop.data.get( id ) ; 

            let idx = CfgShop.daliyFixed.length + i ; 
            if( idx >= this.items.length ) break ; 

            let uiItem = this.items[ idx ] ;
            uiItem.cfg = cfg ; 
            uiItem.refresh() ; 

        } // end for

    }

}


class UiShopDailyItem extends UiShopItem {

    public shopDaily : UiShopDaily = null ; 

    public override init( node : Node ) {

        super.init( node ) ; 

        // 
        /*
             off  UiMain 
        */
        this.root.off( Input.EventType.TOUCH_END ) ; 
        this.root.on( Input.EventType.TOUCH_END , ()=>{ this.onClick( ) ; } ) ; 

        this.onGetItems = ()=>{ this.onGetItems_( ) ; } ;

    }

    public override refresh() {

        super.refresh() ; 

        this.soldOut.active = false ; 

        let dataItem = this.shopDaily.data.data.find( ( e )=>{ return e.id == this.cfg.id ; } ) ;
        if( dataItem.left <= 0 ) {
            // 
            this.setSoldOut( ) ;
        }
        else {
            

        }

    }

    public override onClick( ) {

        let dataItem = this.shopDaily.data.data.find( ( e )=>{ return e.id == this.cfg.id ; } ) ;
        if( dataItem.left <= 0 ) {
            UiToast.show( UiMain.ins.root , Lang.get( "Shop_SoldOut" ) , 1 ) ; 
            return ; 
        }

        super.onClick() ; 

    }

    // 
    public setSoldOut( ) {

        this.cost.root.active = false ; 
        this.costAd.active = false ; 
        this.soldOut.active = true ; 

    }

    // 
    public onGetItems_( ) {

        let dataItem = this.shopDaily.data.data.find( ( e )=>{ return e.id == this.cfg.id ; } ) ;
        dataItem.left-- ;
        if( dataItem.left <= 0 ) {
            this.setSoldOut( ) ; 
        }
        this.shopDaily.data.save() ; 

    }

}


// 
class ShopDailyData {

    // ID
    public data = new Array< ShopDailyDataItem >() ; 

    // 
    public dt : Date = null ; 

    // 
    public checkGen() : boolean {

        let dtNow = DateTimeMgr.ins.getNowDateTime() ; 
        if( DateTimeUtil.dateEquals( this.dt , dtNow ) == false ) {
            // 
            // 
            this.gen() ; 
            return true ; 
        }

        return false ; 

    }

    public load() {

        let str = sys.localStorage.getItem( "ShopDaliy" ) ;
        if( str == null || str == "" ) {
            this.gen() ; 
            return ; 
        }

        let j = JSON.parse( str ) ; 
        this.dt = new Date( j[ "Date" ] ) ; 
        let dtNow = DateTimeMgr.ins.getNowDateTime() ; 

        if( DateTimeUtil.dateEquals( this.dt , dtNow ) == false ) {
            // 
            // 
            this.gen() ; 
            return ; 
        }

        // 
        let rnd = j[ "Rnd" ] ; 
        for( let i = 0 ; i < rnd.length ; ++i ) {
            let j3 = rnd[ i ] ;

            let item = new ShopDailyDataItem() ; 
            let id = j3[ "Id" ] ;
            // 
            if( CfgShop.data.get( id ) == null ) {
                // 
                this.gen() ; 
                return ; 
            }

            this.data.push( item ) ;
            item.id = id ;
            item.left = Number( j3[ "Left" ] ) ; 
        }


    }

    // 
    public gen() {

        this.data.splice( 0 , this.data.length ) ; 

        let list = ShopDailyExtract.exe( 8 - CfgShop.daliyFixed.length ) ; 
        for( let i = 0 ; i < list.length ; ++i ) {
            let cfg = list[ i ] ;
            let dataItem = new ShopDailyDataItem() ;
            dataItem.id = cfg.id ; 
            dataItem.left = 1 ; 
            this.data.push( dataItem ) ; 
        }

        this.save() ; 

    }

    public save() {

        let j = {} ; 

        this.dt = DateTimeMgr.ins.getNowDateTime() ; 
        j[ "Date" ] = DateTimeUtil.toDateString( this.dt ) ; 
        let rnd = [] ;
        j[ "Rnd" ] = rnd ; 
        
        for( let i = 0 ; i < this.data.length ; ++i ) {
            let dataItem = this.data[ i ] ;

            let j3 = {} ;
            rnd.push( j3 ) ;
            j3[ "Id" ] = dataItem.id ; 
            j3[ "Left" ] = dataItem.left ; 

        } // end for

        let str = JSON.stringify( j ) ; 
        sys.localStorage.setItem( "ShopDaliy" , str ) ;

    }
   

}

class ShopDailyDataItem {

    // ID
    public id : string ; 

    // 
    public left : number ; 

}


// 
class ShopDailyExtract {

    public static exe( n : number ) : Array< CfgShopItem > {

        let data = CfgShop.daliy.slice( ) ; 
        Shuffle.shuffleArray( data ) ; 

        return data.slice( 0 , n ) ; 

    }



}



// 
export class UiShopDailyCountdown {

    public root : Node ; 

    public val : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.val = this.root.getChildByPath( "val" ).getComponent( Label ) ; 
        this.refresh() ; 
        this.startAutoRefresh() ; 

    }

    public refresh() {

        let dt = DateTimeMgr.ins.getNowDateTime() ;
        // 
        let s = dt.getMinutes() * 60 + dt.getHours() * 60 * 60 + dt.getSeconds() ; 

        // 
        let s2 = 24 * 60 * 60 - s ;
        this.val.string = this.secondToString( s2 ) ; 

    }

    //  hh:mm:ss 
    public secondToString( s : number ) : string {

        let h = Math.floor( s / ( 60 * 60 ) ) ; 
        let left = s % ( 60 * 60 ) ;
        let m = Math.floor( left / 60 ) ;
        left = left % 60 ;

        let str = StringUtil.fillZero( h , 2 ) + ":" + 
            StringUtil.fillZero( m , 2 ) + ":" + 
            StringUtil.fillZero( left , 2 ) ; 
        return str ; 

    }

    public startAutoRefresh() {

        tween( this.root ).sequence(

            tween( this.root ).delay( 1 ) , 
            tween( this.root ).call( ()=>{
                this.refresh() ; 
            } )

        ).repeatForever().start() ;

    }

}

// 
export class UiShopDailyBtnRefresh {

    public shopDaily : UiShopDaily ; 

    public root : Node ; 

    // 
    public left : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ;

        this.left = this.root.getChildByPath( "Left" ).getComponent( Label ) ; 

        this.dl.load() ; 

        this.refreshLeft() ; 


    }

    // 
    public refreshLeft() {

        let str = Lang.get( "Home_LeftToday" ).replace( "{1}" , this.dl.current.toString() ) ;
        this.left.string = str ; 

    }

    private onClick() {

        if( this.dl.current <= 0 ) {
            // 
            let str = Lang.get( "Home_NotEnoughForToday" ) ; 
            UiToast.show( UiMain.ins.root , str ) ; 
            return ;
        }

        let onSuccess = ()=>{

            this.dl.decrement() ; 
            this.dl.save() ; 
    
            this.refreshLeft() ; 
    
            this.shopDaily.data.gen() ; 
            this.shopDaily.refreshRnd() ; 

        } ;
        let onCancel = ()=>{

        } ;
        let onError = ()=>{

        } ; 
        Ad.ins.rewardShow( onSuccess , onCancel , onError ) ;

    }

    public dl = new DailyLimit( "DailyLimit_ShopDailyBtnRefresh" , 20 ) ;

}