import { Input, Label, Node } from "cc";
import { Account } from "../../../../Common/Account/Account";
import { AccountPersist } from "../../../../Common/Account/AccountPersist";
import { CfgArticleMgr } from "../../../../Common/Cfg/CfgArticle";
import { CfgArticleGrade, CfgArticleGradeItem } from "../../../../Common/Cfg/CfgArticleGrade";
import { ReplaceModeEnter } from "../ReplaceModeEnter";
import { UiBag } from "../UiBag";
import { UiBagArticleDetail } from "./UiBagArticleDetail";
import { BagArticleGradeHelper } from "../../../../Game/Bag/Article/BagArticleGradeHelper";
import { UiToast } from "../../../../Common/Ui/UiToast";
import { UiMain } from "../../Main/UiMain";
import { UiMoney001 } from "../../Main/Margin/UiMoney001";


// 
export class UiBagArticleDetailBtnList {

    public detail : UiBagArticleDetail = null ; 


    public root : Node ; 

    public btnUpgrade : UiBagArticleDetailUpgrade ; 

    public btnRemove : Node ; 

    public btnDeploy : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.btnUpgrade = new UiBagArticleDetailUpgrade() ; 
        this.btnUpgrade.detail = this.detail ; 
        this.btnUpgrade.init( this.root.getChildByPath( "btnUpgrade" ) ) ;

        this.btnRemove = this.root.getChildByPath( "btnRemove" ) ;
        this.btnRemove.on( Input.EventType.TOUCH_END , this.onClick_btnRemove , this ) ; 

        this.btnDeploy = this.root.getChildByPath( "btnDeploy" ) ;
        this.btnDeploy.on( Input.EventType.TOUCH_END , this.onClick_btnDeploy , this ) ; 

    }

    public setShowModeAsDeploy( ) {

        this.btnRemove.active = true ;
        this.btnDeploy.active = false ; 
    }

    public setShowModeAsUnlocked( ) {
        this.btnRemove.active = false ;
        this.btnDeploy.active = true ; 
    }

    private onClick_btnRemove() {

        // this.root.destroy() ; 

        let id = this.detail.articleId ;

        //   
        let idx = Account.ins.bag.deploy.indexOf( id ) ; 
        Account.ins.bag.deploy.splice( idx , 1 ) ; 
        AccountPersist.save() ; 

        // 
        UiBag.ins.deploy.refresh() ; 
        UiBag.ins.unlocked.refresh() ; 

        // 
        this.detail.root.destroy() ; 

    }

    private onClick_btnDeploy() {

        // this.root.destroy() ; 

        let hasEmpty = Account.ins.bag.deploy.length < Account.ins.bag.deployCellUnlocked.size ; 

        if( hasEmpty == true ) {
            // 

            let id = this.detail.articleId ;

            Account.ins.bag.deploy.push( id ) ;
            AccountPersist.save() ; 
    
            // 
            UiBag.ins.deploy.refresh() ; 
            UiBag.ins.unlocked.refresh() ; 

            // 
            this.detail.root.destroy() ; 

            return ; 

        }

        // 

        // 
        this.detail.root.destroy() ; 

        ReplaceModeEnter.enter( this.detail.articleId ) ;

    }


}


export class UiBagArticleDetailUpgrade {

    public detail : UiBagArticleDetail = null ; 

    public root : Node ; 

    public upgradeCost : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ; 

        this.upgradeCost = this.root.getChildByPath( "Cost/val" ).getComponent( Label ) ; 

    }

    public refresh() {
 
        let cfg = BagArticleGradeHelper.getCfgCurrent( this.detail.articleId ) ;
        this.upgradeCost.string = cfg.upgrade.money1.toString() ; 

    }

    private onClick() {

        let id = this.detail.articleId ;

        if( this.check( ) == false ) return ;  

        // ID
        let cfgArctileItem = CfgArticleMgr.data.get( id ) ; 
        let glId = cfgArctileItem.gradeLineId ; 

        let grade = Account.ins.bag.grade.get( glId ) ; 
        if( grade == null ) {
            grade = 1 ; 
        }

        grade++ ;

        Account.ins.bag.grade.set( glId , grade ) ; 

        AccountPersist.save() ; 


        // 

        UiBag.ins.deploy.refresh() ; 
        UiBag.ins.unlocked.refresh() ; 
        this.detail.unlocked.refresh() ; 
        this.detail.refresh() ; 
        this.detail.item.refresh() ; 
        UiMoney001.ins.refresh() ; 

    }

    // 
    private check( ) : boolean {

        let a = Account.ins ; 

        let cfgGrade = BagArticleGradeHelper.getCfgCurrent( this.detail.articleId ) ;
        if( a.money.money001 < cfgGrade.upgrade.money1 ) 
        {
            // 
            UiToast.show( UiMain.ins.root , "" ) ; 

            return false ;
        }

        // 
        let itemCount = a.items.get( cfgGrade.upgrade.itemId ) ; 
        if( itemCount == null ) { itemCount = 0 ; } ; 
        if( itemCount < cfgGrade.upgrade.itemCount ) {

            // 
            UiToast.show( UiMain.ins.root , "" , 1.5 ) ; 

            return false ; 
        }

        // 
        a.money.money001 -= cfgGrade.upgrade.money1 ; 
        itemCount -= cfgGrade.upgrade.itemCount ; 
        a.items.set( cfgGrade.upgrade.itemId , itemCount ) ; 
        AccountPersist.save() ;


        return true ; 

    }

}