import { Node } from "cc";
import { UiListItemGenMgr } from "./UiListItemGenMgr";
import { CfgShop } from "./CfgShop";
import { UiShopItem } from "./UiShopItem";
import { instantiate } from "cc";
import { random } from "cc";
import { Vec3 } from "cc";
import { randomRangeInt } from "cc";

export class UiShopMoney001 {

    public root : Node ; 


    public init( node : Node ) {

        this.root = node ; 

        let cfg = CfgShop.money001 ; 

        let list = new UiListItemGenMgr() ; 
        list.init( this.root.getChildByPath( "List" ) ) ; 
        list.setItemCount( cfg.length ) ;

        // 
        let t = [ 3 , 10 , 45 ] ; 
        for( let i = 0 ; i < cfg.length ; ++i ) {

            let uiItem = new UiShopItem() ; 
            uiItem.init( list.list[ i ] ) ; 
            
            let cfgItem = cfg[ i ] ; 
            uiItem.cfg = cfgItem ; 
            uiItem.refresh() ; 

            // 
            this.rndIcon( uiItem.icon.root.children[ 0 ] , t[ i ] ) ; 

        } // end for



    }

    private rndIcon( node : Node , n : number ) {

        for( let i = 0 ; i < n ; ++i ) {

            let node2 = instantiate( node ) ;
            node2.setParent( node.parent , false ) ;
            node2.position = new Vec3( randomRangeInt( -80 , 80 ) , randomRangeInt( -80 , 80 ) ) ;

        }

    }

}