import { sys } from "cc";
import { Account } from "./Account";
import { ItemData } from "../Item/ItemData";


export class AccountPersist {

    public static load() {

        let a = Account.ins ; 

        let str = sys.localStorage.getItem( "Account" ) ; 
        if( str == null || str == "" ) return ; 

        let json = JSON.parse( str ) ; 

        this.loadBag( json[ "Bag" ] ) ; 
        this.loadMainLevel( json[ "MainLevel" ] ) ;
        this.loadMoney( json[ "Money" ] ) ;
        this.loadItem( json[ "Item" ] ) ; 
        this.loadMainLevelWaveReward( json[ "MainLevelWaveReward" ] ) ;
        
    }

    public static loadItem( j : any ) {

        if( j == null ) return ; 

        let a = Account.ins ; 

        for( let i = 0 ; i < j.length ; ++i ) {
            let t = j[ i ] ; 
            let item = new ItemData() ; 
            let id = t[ "ItemId" ] ; 
            let count = Number( t[ "ItemCount" ] ) ; 
            a.items.set( id , count ) ; 

        } // end for

    }

    public static loadMoney( j : any ) {

        if( j == null ) return ; 

        let a = Account.ins ; 

        a.money.money001 = Number( j[ "Money1" ] ) ; 
        a.money.money002 = Number( j[ "Money2" ] ) ; 

    }

    public static loadBag( jBag : any ) {

        if( jBag == null ) return ; 

        let a = Account.ins ; 

        // 
        let jGrade = jBag[ "Grade" ] ;
        // 
        for( let k in jGrade ) {
            a.bag.grade.set( k , jGrade[ k ] ) ; 
        }

        // 
        a.bag.deploy = jBag[ "Ready" ] ;

        // 
        a.bag.unlocked = jBag[ "Unlocked" ] ;

    }

    public static loadMainLevel( j : any ) {

        if( j == null ) return ; 

        let a = Account.ins ; 

        a.mainLevel.levelIdx = j[ "LevelIdx" ] ;
        a.mainLevel.waveIdx = j[ "WaveIdx" ] ;


    }

    public static loadMainLevelWaveReward( j : any ) {

        if( j == null ) return ;

        let reward = Account.ins.waveReward ; 

        for( let i = 0 ; i < j.length ; ++i ) {
            let jItem = j[ i ] ;

            let levelIdx = Number( jItem[ "LevelIdx" ] ) ; 
            let waves = jItem[ "Waves" ] ; 

            let set = new Set< number >() ; 
            for( let n of waves ){ set.add( n ) ; } 

            reward.set( levelIdx , set ) ; 

        }

    }


    public static save() {

        let json = {} ; 

        // 
        let jBag = this.makeBag() ; 
        json[ "Bag" ] = jBag ; 
        json[ "MainLevel" ] = this.makeMainLevel() ; 
        json[ "Money" ] = this.makeMoney() ; 
        json[ "Item" ] = this.makeItem() ; 
        json[ "MainLevelWaveReward" ] = this.makeWaveReward() ; 

        let str = JSON.stringify( json ) ;

        sys.localStorage.setItem( "Account" , str ) ; 


    }

    public static makeWaveReward() : any {

        let a = Account.ins ; 
        let reward = a.waveReward ; 

        let j = [] ; 
        for( let kv of reward ) {
            let levelIdx = kv[ 0 ] ;
            let waves = kv[ 1 ] ; 

            let wavesList = [] ;
            for( let e of waves ){ wavesList.push( e ) ; }

            let item = {} ;
            item[ "LevelIdx" ] = levelIdx ; 
            item[ "Waves" ] = wavesList ; 
            j.push( item ) ;
        }
        
        return j ; 

    }

    public static makeItem() : any {

        let a = Account.ins ; 

        let j = [] ; 

        for( let kv of a.items ) {
            let item = {} ; 
            item[ "ItemId" ] = kv[ 0 ] ; 
            item[ "ItemCount" ] = kv[ 1 ] ; 
            j.push( item ) ;
        }

        return j ; 

    }

    public static makeMoney() : any {

        let a = Account.ins ; 

        let j = {} ;
        j[ "Money1" ] = a.money.money001 ; 
        j[ "Money2" ] = a.money.money002 ; 

        return j ; 

    }

    //  Bag  
    public static makeBag( ) : {} {

        let a = Account.ins ; 

        let jBag = {} ;

        // 
        let jGrade = {} ;
        jBag[ "Grade" ] = jGrade ; 
        for( let kv of a.bag.grade ) {
            jGrade[ kv[0] ] = kv[1] ;
        }

        // 
        jBag[ "Ready" ] = a.bag.deploy ; 

        // 
        jBag[ "Unlocked" ] = a.bag.unlocked ; 

        return jBag ; 

    }

    public static makeMainLevel( ) : {} {

        let a = Account.ins ; 

        let j = {} ;

        // 
        j[ "LevelIdx" ] = a.mainLevel.levelIdx ; 

        // 
        j[ "WaveIdx" ] = a.mainLevel.waveIdx ; 

        return j ; 

    }

}