import { find, Node } from "cc";

// 
export class WorldNode {

    public static ins = new WorldNode() ; 

    public root : Node ; 

    // 
    public layer0 : Node ; 
    // 
    public layer1 : Node ; 
    // 
    public layer2 : Node ; 

    // 
    public layer0_Road : Node ; 

    // 
    public layer0_Spawn : Node ; 

}

export class WorldNodeInit {

    public static exe() {

        let root = WorldNode.ins.root = find( "Canvas/World" ) ;

        let ins = WorldNode.ins ;
        ins.layer0 = find( "Layer0" , root ) ;
        ins.layer1 = find( "Layer1" , root ) ;
        ins.layer2 = find( "Layer2" , root ) ;

        ins.layer0_Road = ins.layer0.getChildByPath( "Road" ) ;
        ins.layer0_Spawn = ins.layer0.getChildByPath( "Spawn" ) ;

    }

}