import { Role } from "../Role/Base/Role";
import { RoleMgr } from "../Role/Base/RoleMgr";
import { Ai001 } from "./Ai001";
import { Ai002 } from "./Ai002";


export class AiSys {

    public static update() {

        var all = RoleMgr.ins.allEnemies;

        for ( var i = 0; i < all.length; ++i )
        {
            var role = all[i];
            if ( role.ai == null ) continue;
            this.updateRole( role );

        } // end for

    }

    public static updateRole( role : Role ) {

        Ai002.update( role ) ;

    }

}