import { Account } from "../../Common/Account/Account";
import { AccountPersist } from "../../Common/Account/AccountPersist";
import { CfgArticleUnlock, CfgArticleUnlockItem } from "../../Common/Cfg/CfgArticleUnlock";
import { UiArticleUnlock } from "../Ui/Popup/UiArticleUnlock";
import { UiMain } from "../Ui/Main/UiMain";
import { CfgGeneral } from "../../Common/Cfg/CfgGeneral";

// 
export class ArticleUnlockLevel {

    // ID
    public static exe() : Array< string > {

        let ret = new Array< string >() ;

        for( let kv of CfgArticleUnlock.data ) {

            let cfgItem = kv[1] ;

            if( this.canUnlock( cfgItem ) == false ) continue ; 

            let id = cfgItem.id ; 
            ret.push( id ) ; 
            Account.ins.bag.unlocked.push( id ) ; 

        } // end for
        
        if( ret.length > 0 ) {
            AccountPersist.save() ; 

            this.ui( ret ) ;

        }

        this.unlockDeployCell() ; 

        return ret ; 

    }

    // 
    private static unlockDeployCell() {

        let levelIdx = Account.ins.mainLevel.levelIdx ; 

        for( let kv of CfgGeneral.ins.bagDeployCellUnlocked ) {
            let cellIdx = kv[ 0 ] ; 
            let lvIdx = kv[ 1 ] ; 

            if( levelIdx >= lvIdx ) {
                Account.ins.bag.deployCellUnlocked.add( cellIdx ) ; 
            }

        } // end for


    }

    // 
    private static canUnlock( cfgItem : CfgArticleUnlockItem ) {

        if( cfgItem.levelIdx <= -1 ) return false ; 

        let id = cfgItem.id ; 

        let levelIdx = Account.ins.mainLevel.levelIdx ; 

        // 
        if( cfgItem.levelIdx > levelIdx ) return false ; 

        // 
        if( Account.ins.bag.unlocked.findIndex( ( e )=>{ return e == id ; } ) != -1 ) return false ; 

        return true ; 

    }

    // UI
    private static ui( ids : Array< string > ) {

        UiArticleUnlock.loadAndCreate( ( ui )=>{

            ui.root.setParent( UiMain.ins.root , false ) ; 
            ui.refresh( ids ) ; 
            ui.show() ; 

        } ) ;

    }

}