import { UITransform, Vec3 } from "cc";
import { UiHpBar } from "../../../Ui/Bar/UiHpBar";
import { Life } from "../../1.Base/Role/Base/Life";
import { Role, RoleView } from "../../1.Base/Role/Base/Role";
import { RoleCfgSet, RoleFactory } from "../../1.Base/Role/Base/RoleFactory";
import { WorldNode } from "../../1.Base/Support/Misc/WorldNode";
import { RoleMgr } from "../../1.Base/Role/Base/RoleMgr";

// 
export class PlayerRole {

    public static ins = new PlayerRole() ; 

    public main : Role ; 

}

//  
export class PlayerRoleInit {

    public static init() {

        // 
        let node = WorldNode.ins.root.getChildByPath( "Cfg/PlayerRole" ) ; 

        let role = RoleFactory.create( "G1#Player001" ) ; 
        PlayerRole.ins.main = role ; 
        role.view.root.setParent( WorldNode.ins.layer1 , false ) ;
        role.view.root.worldPosition = node.worldPosition ; 

        //  
        // role.life.hp.current = 20 ;

        let rect = role.view.main.getComponent( UITransform ).getBoundingBox() ; 

        // 
        let hpBar = UiHpBar.create( "HpBarPlayerRole" ) ;
        role.view.hpBar = hpBar ; 
        hpBar.role = role ;
        hpBar.root.setParent( role.view.root , false );
        hpBar.root.position = new Vec3( 0 , rect.yMax + 20 ) ;
        hpBar.refresh() ; 

    }

}