
import { CfgLoad } from "../../Common/Cfg/CfgLoad";
import { PfLoad } from "../../Common/PfMgr/PfLoad";

//  
export class ResLoad {

    public static onProgress: (val: number) => void;

    public static exe(onComplete: () => void) {

        let cfgLoader = new CfgLoad() ; 
        cfgLoader.exe(() => {
            PfLoad.onProgress = this.onProgress;
            PfLoad.exe(() => {
                onComplete();
            }); 
        });

    }

}