import { Node } from "cc";
import { UiPopupBase } from "../../../Common/Ui/UiPopupBase";
import { UiItemList } from "../../../Common/Item/UiItemList";
import { instantiate } from "cc";
import { Input } from "cc";
import { Sound } from "../../../Common/Audio/Sound";

// 
export class UiGet extends UiPopupBase {

    public static create( parent : Node ) : UiGet {

        let root = instantiate( this.pf ) ;
        let ui = new UiGet() ;
        ui.init( root ) ; 
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }

    public itemList = new UiItemList() ; 

    public override init( node : Node ) {

        super.init( node ) ;

        this.itemList.init( this.root.getChildByPath( "List" ) ) ; 

        this.root.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;

        // 
        Sound.ins.getReward() ; 

    }

}