import { tween, UITransform, Vec2 } from "cc";
import { UiBagGrid } from "../Grid/UiBagGrid";
import { BagArticle } from "./BagArticle";
import { UiBagCell } from "../Grid/UiBagCell";

// 
export class BagArticleGridPlace {

    public static exe( article : BagArticle , cells : Array< UiBagCell > , animatedMovement = true ) {

        this.unbind( article ) ; 
        this.bind( article , cells ) ; 

        // 
        let cell = this.getOrigCellInBoundingRect( cells ) ; 
        this.view( article , cell , animatedMovement ) ; 

    }

    // CellCell Cells 
    private static getOrigCellInBoundingRect( cells : Array< UiBagCell > ) : UiBagCell {

        let coord = new Vec2( Number.MAX_VALUE , Number.MAX_VALUE ) ; 
        for( let cell of cells ) {
            if( cell.coord.x < coord.x ) {
                coord.x = cell.coord.x ; 
            }

            if( cell.coord.y < coord.y ) {
                coord.y = cell.coord.y ; 
            }
        }

        let cell = UiBagGrid.ins.getCell( coord ) ; 
        return cell ; 

    }

    // 
    public static view( article : BagArticle , cellOrig : UiBagCell , animatedMovement = true ) {

        // 
        let s = article.view.root.scale.clone() ;

        article.view.root.setParent( UiBagGrid.ins.root , true ) ;

        article.view.root.scale = s ;

        let uitrans = article.view.root.getComponent( UITransform ) ;
        
        // 
        let posShapeCoordOrig = uitrans.convertToWorldSpaceAR( article.view.posShapeCoordOrig ) ;
        posShapeCoordOrig.subtract( uitrans.node.worldPosition ) ;

        let pos = cellOrig.root.worldPosition.clone().subtract( posShapeCoordOrig ) ;

        // pos = article.root.parent.getComponent( UITransform ).convertToNodeSpaceAR( pos ) ;

        if( animatedMovement ) {
            // 
            tween( article.view.root ).to( 0.2 , { worldPosition : pos } ).start() ; 
        }
        else {
            // 
            article.view.root.worldPosition = pos ; 
        }





    }

    // 
    public static bind( article : BagArticle , cells : Array< UiBagCell > ) {

        for( let cell of cells ) {
            cell.article = article ; 
        }

        article.cells.splice( 0 , article.cells.length ) ;
        for( let cell of cells ) {
            article.cells.push( cell ) ;
        }

    }

    // 
    public static unbind( article : BagArticle ) {

        for( let cell of article.cells ) {
            cell.article = null ; 
        }

        article.cells.splice( 0 , article.cells.length ) ;

    }

}