import { _decorator, Component, Node } from 'cc';

export class CfgInviteConfig{

    public Id:string;
    //
    public InviteCount:number;
    //ID
    public GiftID:string;

}


export class CfgInviteConfigMgr{
    public static data:Map<string , CfgInviteConfig> = new Map<string , CfgInviteConfig>() ;

}

export class CfgInviteConfigParser{
    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) return ; 
            
            let cfgItem = new CfgInviteConfig() ;

            CfgInviteConfigMgr.data.set( id , cfgItem ) ;

            cfgItem.Id = id ;

            let str : string = null ; 
            cfgItem.InviteCount=Number(row[ "InviteCount" ]);

            cfgItem.GiftID=row[ "GiftID" ];
   
        } 

    }
}

