import { math, Vec3 } from "cc";


// 2D  
export class Vec2dHelper {

    // 
    public static rotate( v : Vec3 , degree : number ) {

        let rad = math.toRadian( degree ) ; 

        let cosVal = Math.cos( rad ) ; 
        let sinVal = Math.sin( rad ) ; 

        // [x*cosA-y*sinA  x*sinA+y*cosA]
        v.x = v.x * cosVal - v.y * sinVal ; 
        v.y = v.x * sinVal + v.y * cosVal ; 

    }

}
