import { CfgArticleMgr } from "../../../../../Common/Cfg/CfgArticle";
import { CfgBullet } from "../../../../../Common/Cfg/CfgBullet";
import { CfgRole } from "../../../../../Common/Cfg/CfgRole";
import { TimeScale } from "../../../../../Common/Misc/TimeScale";
import { Player } from "../../../2.Td/Player/Player";
import { BulletMgr } from "../../Bullet/BulletMgr";
import { RoleMgr } from "../../Role/Base/RoleMgr";
import { RoleAttributeUpdate } from "../../Role/Misc/RoleAttributeUpdate";
import { TowerAllUpdate } from "../../Tower/Misc/TowerAllUpdate";




// 
export class PlaySpeedCtrl {

    /*
        
    */
    public static exe() {

        let speed = Player.ins.playSpeed ; 

        SpeedChangeData.make() ; 

        this.role( speed ) ; 
        this.bullet( speed ) ; 
        this.tower( speed ) ; 

        TimeScale.set( speed ) ; 

    }


    // 
    private static role( speed : number ) {

        let sc = SpeedChangeData.ins ; 

        for( let kv of CfgRole.data ) {
            let id = kv[ 0 ] ;
            let cfgItem = kv[1] ;

            let dataItem = sc.role.get( id ) ;

            cfgItem.moveSpeed = dataItem.moveSpeed[ speed - 1 ] ; 
            cfgItem.attackCd = dataItem.attackCd[ speed - 1 ] ; 

        } // end for

        for( let role of RoleMgr.ins.allEnemies ) {

            RoleAttributeUpdate.exe( role ) ; 

        } // end for

    }

    // 
    private static bullet( speed : number ) {

        let c = SpeedChangeData.ins.bullet ; 

        for( let kv of CfgBullet.data ) {
            let id = kv[0] ; 
            let cfgItem = kv[ 1 ] ;

            let dataItem = c.get( id ) ;

            cfgItem.moveSpeed = dataItem.moveSpeed[ speed - 1 ] ; 

        } // end for

        for( let bullet of BulletMgr.ins.data ) {

            let dataItem = c.get( bullet.id ) ;

            bullet.move.speed = dataItem.moveSpeed[ speed - 1 ] ; 

        } // end for

    }

    private static tower( speed : number ) {

        let c = SpeedChangeData.ins.tower ; 

        for( let kv of CfgArticleMgr.data ) {

            let id = kv[0] ; 
            let cfgItem = kv[ 1 ] ;

            let dataItem = c.get( id ) ;

            cfgItem.tower.atkCd = dataItem.attackCd[ speed - 1 ] ; 

        } // end for

        TowerAllUpdate.updateAll() ; 

    }

}

// 
class SpeedChangeData {

    public static ins : SpeedChangeData = null ; 

    public static make() {

        if( this.ins != null ) return ; 

        this.ins = new SpeedChangeData() ; 

        // 
        for( let kv of CfgRole.data ) {
            let id = kv[0] ; 
            let cfgItem = kv[1] ;

            let item = new SpeedData() ; 
            this.ins.role.set( id , item ) ; 
            for( let i = 1 ; i <= 3 ; ++i ) {

                // 
                item.moveSpeed.push( cfgItem.moveSpeed * i ) ; 

                // 
                let val = Math.round( cfgItem.attackCd / ( 1 + i - 1 ) ) ; 
                item.attackCd.push( val ) ; 

            } // end for
            

        } // end for

        // 
        for( let kv of CfgBullet.data ) {
            let id = kv[0] ; 
            let cfgItem = kv[ 1 ] ;

            let item = new SpeedData() ; 
            this.ins.bullet.set( id , item ) ; 
            for( let i = 1 ; i <= 3 ; ++i ) {

                // 
                item.moveSpeed.push( cfgItem.moveSpeed * i ) ; 

            } // end for

        } // end for

        // 
        for( let kv of CfgArticleMgr.data ) {

            let id = kv[0] ; 
            let cfgItem = kv[ 1 ] ;

            cfgItem.tower.atkCd ;

            let item = new SpeedData() ; 
            this.ins.tower.set( id , item ) ; 
            for( let i = 1 ; i <= 3 ; ++i ) {

                // 
                let val = Math.round( cfgItem.tower.atkCd / ( 1 + i - 1 ) ) ; 
                item.attackCd.push( val ) ; 

            } // end for

        } // end for

    }

    public role = new Map< string , SpeedData >() ; 

    public bullet = new Map< string ,  SpeedData >() ; 

    public tower = new Map< string , SpeedData >() ; 

}

class SpeedData {

    public moveSpeed = new Array< number >() ; 

    public attackCd = new Array< number >() ; 

}