import { UITransform, Vec3 } from "cc";
import { UiHpBar } from "../../../Ui/Bar/UiHpBar";
import { RoleFactory } from "../../1.Base/Role/Base/RoleFactory";
import { RoleMgr } from "../../1.Base/Role/Base/RoleMgr";
import { WorldNode } from "../../1.Base/Support/Misc/WorldNode";
import { EnemyMovePath } from "../Flow/EnemyMovePath";
import { AiCom } from "../../1.Base/Ai/AiCom";
import { PlayerBuffAffect } from "../Player/PlayerBuffAffect";
import { RoleAttributeUpdate } from "../../1.Base/Role/Misc/RoleAttributeUpdate";
import { Role } from "../../1.Base/Role/Base/Role";
import { PathMove } from "../../1.Base/Ai/PathMove";

export class EnemyGen {

    // 
    public static genOne( id : string ) : Role {

        let role = RoleFactory.create( id ) ;

        RoleMgr.ins.add( role ) ; 

        // 

        let hpBarId = role.cfg.hpBarId ;
        let hpBar = UiHpBar.create( hpBarId ) ;
        role.view.hpBar = hpBar ; 
        hpBar.role = role ;
        hpBar.root.setParent( role.view.root , true );
        let rect = role.view.main.getComponent( UITransform ).getBoundingBox() ; 
        hpBar.root.position = new Vec3( 0 , rect.yMax + 10 ) ;
        hpBar.refresh() ; 
 
        role.view.root.setParent( WorldNode.ins.layer1 , false ) ;
        role.view.root.position = EnemyMovePath.ins.data[ 0 ] ;

        role.ai = new AiCom() ; 
        role.ai.pathMoved = new PathMove() ; 
        role.ai.pathMoved.idx = 1 ; 
        role.ai.pathMoved.path = EnemyMovePath.ins.data ; 

        // PlayerBuffAffect.affectRole( role ) ; 



        RoleAttributeUpdate.exe( role ) ; 

        return role ; 

    }

}