import { PlayerRole } from "../../2.Td/Player/PlayerRole";
import { Role } from "../Role/Base/Role";
import { RoleAnimSpine } from "../Role/Misc/RoleAnimSpine";
import { RoleAttack } from "../Role/Misc/RoleAttack";
import { RoleMove } from "../Role/Misc/RoleMove";


export class Ai002 {

    public static update( role : Role ) {

        switch( role.ai.state ) {

            case 0 : {
                this.state_pathMove( role ) ; 
            }
            break ; 

            case 1 : {
                this.state_chase( role ) ;
            }
            break ; 

        }

    }

    private static state_pathMove( role : Role ) {

        let pathMove = role.ai.pathMoved ; 
        let path = pathMove.path ; 
        let idx = pathMove.idx ; 
        let targetPos = path[ idx ] ; 
        let v = targetPos.clone().subtract( role.view.root.position ) ;
        if( v.lengthSqr() <= 20 * 20 ) {

            // 
            pathMove.idx++ ; 

            if( pathMove.idx >= path.length ) {
                // 

                role.ai.target = PlayerRole.ins.main ; 
                role.ai.state = 1 ; 

                return ; 

            }

            // 
            targetPos = path[ pathMove.idx ] ; 
            v = targetPos.clone().subtract( role.view.root.position ) ;

        }
        
        // 
        let bMove = RoleMove.exe( role , v ) ;
        if( bMove ) {
            // 
            role.ai.pathMoved.lenMoved += role.move.offset.lengthSqr() ;
        }

    }

    private static state_chase( role : Role ) {

        let target = role.ai.target ; 

        if( target.life.isDead == true ) {
            //  

            RoleAnimSpine.playIdle( role.view.skeleton ) ; 

            return ; 
        }

        let v = target.view.root.position.clone().subtract( role.view.root.position ) ; 
        if( v.lengthSqr() > 100 * 100 ) {
            // 

            RoleMove.exe( role , v ) ;

            return ; 
        }

        // 
        RoleAttack.exe( role , v ) ;

    }

}