import { ItemDraw } from "../../../../Common/Item/ItemDraw";
import { ItemGet } from "../../../../Common/Item/ItemGet";
import { GameArg } from "../../../Init/GameArg";
import { UiMain } from "../../../Ui/Main/UiMain";
import { UiLose } from "../../../Ui/Popup/Settle/UiLose";
import { GamePause } from "./GamePause";

export class GameLose {

    public static exe() {
        console.log( "" ) ;

        // 
        GamePause.pause() ; 

        let uiLose = UiLose.create( UiMain.ins.root );
        uiLose.show() ; 
        let id = "Level_" + GameArg.ins.levelId + "_Lose" ; 
        let items = ItemDraw.exe( id ) ; 
        uiLose.itemList.refresh( items ) ; 
        uiLose.itemList.showAnim() ; 
        ItemGet.addList( items ) ; 
        
    }

}