import { Label, Node } from "cc";
import { Account } from "../../../../Common/Account/Account";
import { Input } from "cc";
import { UiBottomTabPage } from "../UiBottomTab";
import { UiShop } from "../Page/Shop/UiShop";
import { ScrollView } from "cc";

// 1
export class UiMoney001 {

    public static ins : UiMoney001 = null ; 

    public root : Node ; 

    public val : Label ;

    public init( node : Node ) {

        this.root = node ; 

        this.val = this.root.getChildByPath( "val" ).getComponent( Label ) ;
        this.val.string = "0" ; 

        this.refresh() ; 
        
        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ; 
    }

    public refresh() {

        let a = Account.ins ; 
        this.val.string = a.money.money001.toString() ; 

    }

    private onClick() {

        let headerItem = UiBottomTabPage.ins.thb.items.find( ( e )=>{ return e.root.name == "Shop" ; } ) ;
        headerItem.root.emit( Input.EventType.TOUCH_END ) ; 

        let sv = UiShop.ins.root.getComponentInChildren( ScrollView ) ;
        sv.scrollToBottom() ; 

    }

}