
import { GameLose } from "../../../2.Td/Flow/GameLose";
import { PlayerRole } from "../../../2.Td/Player/PlayerRole";
import { AffixType } from "../../Buff/Affix";
import { Buff } from "../../Buff/Buff";
import { BuffOp } from "../../Buff/BuffOp";
import { SkillType } from "../../Skill/SkillBase";
import { SkillCloseDamage, SkillCloseDamageOp } from "../../Skill/SkillCloseDamage/SkillCloseDamage";
import { Stats } from "../../Support/Misc/Stats";
import { Role } from "../Base/Role";
import { RoleMgr } from "../Base/RoleMgr";
import { RoleDie } from "./RoleDie";
import { RoleGetDamage } from "./RoleGetDamage";

export class RoleSys {

    public static update() {

        let all = RoleMgr.ins.allEnemies ; 
        for( let i = all.length - 1 ; i >= 0 ; --i ) {

            let role = all[ i ] ;

            if( role.life.hp.current <= 0 ) {
                RoleDie.exe( role ) ;
                continue ; 
            }

            let atkCd = role.attack.cd ;
            if( atkCd.current > 0 ) { atkCd.current-- ; } 

            this.skill( role ) ;

            RoleBuffSys.update( role ) ; 

        } // end for


        // 
        if( PlayerRole.ins.main.life.hp.current <= 0 ) {
            GameLose.exe() ; 
        }

    }

    public static skill( role : Role ) {

        let skillCom = role.skill ;
        if( skillCom == null ) return ; 

        for( let skill of skillCom.list ) {

            if( skill.cd.current > 0 ) {
                skill.cd.current-- ; 
            }
            if( skill.cd.current != 0 ) continue ;

            switch( skill.type ) {
                case SkillType.CloseDamage : {

                    let closeDamage = skill as SkillCloseDamage ;

                    // 
                    SkillCloseDamageOp.exe( closeDamage ) ;


                }
                break ;

            }

        }

    }

}

export class RoleBuffSys {

    public static update( role : Role ) {

        let list = role.buff.list ; 
        for( let i = list.length - 1 ; i >= 0 ; --i ) {

            let buff = list[ i ] ;

            buff.duration.current-- ; 
            if( buff.duration.current < 0 ) {
                // 
                BuffOp.roleRemove( role , i ) ; 
                continue ; 
            }

            if( buff.interval.max >= 0 ) {
                buff.interval.current-- ; 
                if( buff.interval.current == -1 ) {

                    buff.interval.setCurrentAsMax() ; 

                    // 
                    this.trigger( role , buff ) ; 

                }
            }

        } // end for

    }

    public static trigger( role : Role , buff : Buff ) {

        switch( buff.affix.type ) {

            case AffixType.Burn : {
                RoleGetDamage.exe( role , buff.affix.argN ) ;
                Stats.ins.damage( buff.sender.id , buff.affix.argN ) ; 
            }
            break ; 

            case AffixType.Poisoning :
            case AffixType.PoisoningPercent :
            {
                RoleGetDamage.exe( role , buff.affix.argN ) ;
                Stats.ins.damage( buff.sender.id , buff.affix.argN ) ; 
            }
            break ; 

        }

    }





}