import { find, Input, instantiate, Label, Node, Prefab, resources, tween } from "cc";

export class AdBase {

    public init() {
        console.log( "AdBase.init" ) ;
    }

    public bannerShow() {
        console.log( "AdBase.showBanner" ) ;
    }

    public bannerHide() {
        console.log( "AdBase.hideBanner" ) ;
    }

    public interstitialShow() {
        console.log( "AdBase.showInterstitial" ) ;
    }

    public customShow() {
        console.log( "AdBase.showCustom" ) ;
    }

    public rewardShow( 
        onSuccess : ()=>void = null , 
        onCancel : ()=>void = null ,
        onError : ( err : string )=>void = null
         ) {

        console.log( "AdBase.showRewardedVideo" ) ;

        UiAdSimReward.create( ( ui )=>{

            let parent = find( "Canvas" ) ; 
            ui.root.setParent( parent , false ) ; 

            ui.btnError.on( Input.EventType.TOUCH_END , ()=>{
                ui.root.destroy() ; 
                onError?.( "" ) ;
            } );

            ui.btnComplete.on( Input.EventType.TOUCH_END , ()=>{
                ui.root.destroy() ; 
                onSuccess?.( ) ;
            } );

            ui.btnCancel.on( Input.EventType.TOUCH_END , ()=>{
                ui.root.destroy() ; 
                onCancel?.( ) ;
            } );

        } ) ; 
        
    }

}


export class UiAdSimReward {

    public static create( cb : ( ui : UiAdSimReward )=>void ) {

        resources.load( "Common/Ui/AdSimReward" , Prefab , ( err , data )=>{

            let root = instantiate( data ) ;

            let ui = new UiAdSimReward() ; 
            ui.init( root ) ; 
            cb( ui ) ;

        } ) ;

    }

    public root : Node ; 

    public btnError : Node ; 
    public btnCancel : Node ; 
    public btnComplete : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.btnError = this.root.getChildByPath( "BtnList/btnError" ) ;
        this.btnCancel = this.root.getChildByPath( "BtnList/btnCancel" ) ;
        this.btnComplete = this.root.getChildByPath( "BtnList/btnComplete" ) ;

        // 
        let countdown = this.root.getChildByPath( "Countdown" ).getComponent( Label ) ; 
        let count = 60 * 3 ;
        tween( countdown ).sequence(
            tween( countdown ).call( ()=>{
                countdown.string = count.toString() ;
                count-- ; 
            } ) , 
            tween( countdown ).delay( 1 ) , 

        ).repeatForever().start() ;


    }

}