import { Node, Sprite, SpriteFrame } from "cc";

// Buff 
export class PfMgrIconBuff {

    public static ins = new PfMgrIconBuff() ; 

    public root : Node ; 

    public getIconById( id : string ) : Sprite {

        let strs = id.split( "-" ) ; 
        if( strs.length >= 2 ) {

            let id2 = strs[ 0 ] + "/" + id ; 
            let pfIcon = this.root.getChildByPath( id2 ) ; 
            let spr = pfIcon?.getComponent( Sprite ) ;
            if( spr != null ) { 
                return spr ; 
            }

        }

        


        let spr = this.root.getChildByPath( id )?.getComponent( Sprite ) ; 
        if( spr != null ) {
            return spr ; 
        }

        spr = this.root.getChildByPath( "Default" ).getComponent( Sprite ) ;
        return spr ; 

        return spr ; 

    }

}