import { BagArticle } from "../../../Bag/Article/BagArticle";
import { GridTraverse } from "../../../Bag/Grid/GridTraverse";
import { UiBagCell } from "../../../Bag/Grid/UiBagCell";
import { TowerFactory } from "../../1.Base/Tower/Entity/TowerFactory";
import { TowerMgr } from "../../1.Base/Tower/Entity/TowerMgr";

// 
export class BagArticleReloaded {

    public static exe() {

        // 
        TowerMgr.ins.clear() ; 

        GridTraverse.exe( ( cell : UiBagCell )=>{
            if( cell.article == null ) return ; 
            TowerMgr.ins.add( cell.article.tower ) ;
        } ) ;
               
    }


}