import { Tween, tween, Vec3 } from "cc";
import { GridTraverse } from "../Grid/GridTraverse";
import { MergeDefinition } from "./MergeDefinition";
import { BagArticle } from "../Article/BagArticle";
import { ArticleHelper } from "../Grid/BagArticleHelper";
import { UiTray } from "../Main/UiTray";

// 
export class MergeHint {

    // 
    public static mergeable( a : BagArticle ) {

        let all = ArticleHelper.getAllAriclesInGrid() ;

        // 
        all.push( ... UiTray.ins.articles ) ; 

        let id = a.cfg.id ; 

        for( let a2 of all ) {

            if( a == a2 ) continue ; 

            let id2 = MergeDefinition.ins.getMergedId( id , a2.cfg.id ) ; 
            if( id2 == null ) continue ; 

            // 
            this.anim( a2 ) ;

        }

    }

    public static anim( a : BagArticle ) {

        let node = a.view.root ;
        let t = tween( node ).sequence(
            tween( node ).to( 0.5 , { scale : new Vec3( 0.9 , 0.9 , 0.9 ) } ) , 
            tween( node ).to( 0.5 , { scale : new Vec3( 1.0 , 1.0 , 1.0 ) } ) , 

        ).repeatForever().start() ;
        a.view.tweenMergeHint = t ; 

    }

    // 
    public static restore() {

        Tween.stopAllByTag( 10001 ) ; 

        // 
        let all = ArticleHelper.getAllAriclesInGrid() ;

        all.push( ... UiTray.ins.articles ) ; 

        for( let a of all ) {

            if( a.view.tweenMergeHint == null ) continue ;

            a.view.tweenMergeHint.stop() ; 
            a.view.tweenMergeHint = null ; 

            // 
            // a.view.root.scale = Vec3.ONE ; 
            tween( a.view.root ).to( 0.1 , { scale : Vec3.ONE } ).start() ; 



        } // end for

    }

}