import { Input, Node } from "cc";
import { UiMoney001 as UiMoney001 } from "./UiMoney001";
import { UiPause } from "../Popup/UiPause";
import { UiMain } from "./UiMain";
import { GamePause } from "../../World/2.Td/Flow/GamePause";
import { UiWaveInfo } from "./UiWaveInfo";
import { UiStateBtn } from "../Popup/UiStats";
import { UiHybridGuide } from "../../../Home/Ui/Popup/UiHybridGuide";
import { UiPlaySpeed } from "./UiPlaySpeed";

export class UiTop {

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        UiMoney001.ins = new UiMoney001() ; 
        UiMoney001.ins.init( this.root.getChildByPath( "Money001" ) );

        UiWaveInfo.ins = new UiWaveInfo() ; 
        UiWaveInfo.ins.init( this.root.getChildByPath( "WaveInfo" ) ) ; 
        UiWaveInfo.ins.refresh() ; 

        let btnPause = this.root.getChildByPath( "btnPause" ) ;
        btnPause.on( Input.EventType.TOUCH_END , ()=>{

            GamePause.pause() ; 
            UiPause.loadAndCreate( UiMain.ins.root , ( uiPause : UiPause )=>{
                uiPause.refresh() ; 
            } ) ;
            

        } ) ;

        new UiStateBtn().init( this.root.getChildByPath( "btnStats" ) ) ;

        let btn = this.root.getChildByPath( "btnHybridGuide" ) ;
        btn.on( Input.EventType.TOUCH_END , ()=>{
            UiHybridGuide.create( UiMain.ins.root ).show() ;
        } ) ; 

        UiPlaySpeed.ins = new UiPlaySpeed() ; 
        UiPlaySpeed.ins.init( this.root.getChildByPath( "PlaySpeed" ) ) ; 
        
    }

}

