import { JsonAsset } from "cc";
import { assetManager } from "cc";
import { Asset } from "cc";
import { LoaderBase } from "./LoaderBase";

// 
export class LoaderRemote extends LoaderBase {

    // 
    public onComplete : ()=>void = null ;

    // 
    public onProgress: (val: number) => void = null ;

    public list = new Array< any >() ;

    public add( path : string , handler : ( data : Asset )=>void ) {

        let item = {} ;
        item[ "path" ] = path ;
        item[ "handler" ] = handler ;
        this.list.push( item ) ;

    }

    public exe() {

        let count = this.list.length ; 
        let counter = 0 ; 

        for( let i = 0 ; i < this.list.length ; ++i ) {

            let item = this.list[ i ] ;
            let path = item[ "path" ] ; 
            let handler = item[ "handler" ] ;

            let url = path ;

            // assetManager  hfs 
            assetManager.loadRemote( url , ( err , data : JsonAsset )=>{

                if( err != null ) {
                    console.error( "" + url ) ;
                    return ; 
                }

                handler( data ) ;

                ++counter ;
                if( counter == count ) {
                    // 
                    this.onComplete() ; 
                    
                }

            } ) ;

        } // end for

    }

}