import { Label, Node, tween } from "cc";
import { CfgArticle } from "../../../Common/Cfg/CfgArticle";
import { CfgArticleGrade } from "../../../Common/Cfg/CfgArticleGrade";
import { CfgBuff, CfgBuffItem } from "../../../Common/Cfg/CfgBuff";
import { AffixType } from "../../World/1.Base/Buff/Affix";
import { WorldNode } from "../../World/1.Base/Support/Misc/WorldNode";
import { TowerAuraScopeExpand } from "../../World/1.Base/Tower/Misc/TowerAuraScopeExpand";
import { BagArticle } from "../Article/BagArticle";
import { UiBagCell } from "../Grid/UiBagCell";
import { EffectFactory } from "../Merge/EffectFactory";


// 
export class TowerAuraHint {

    // 
    public exe( article : BagArticle , cells : Array< UiBagCell > ) {

        if( article == null || cells == null ) {
            this.clear() ; 
            return ; 
        }

        // 
        this.set2.clear() ; 
        this.set.clear() ; 
        for( let kv of this.map ) {
            let ui = kv[1] ;
            ui.txt.string = "" ;
            ui.flagAttackDamage = false ; 
            ui.flagAttackSpeed = false ; 
            ui.flagAttackCritProbability = false ; 

        } // end for


        let auras = this.getBuffs( article.cfg ) ; 

        // 
        for( let aura of auras ) {

            switch( aura.affixType ) {
 
                // 
                case AffixType.AuraAttackDamageIncreasePercent : 
                {
                    let ret = TowerAuraScopeExpand.exe( aura.auraScopeType , cells ) ; 
                    for( let a of ret.article ) {

                        // 
                        if( a == article ) continue ; 
                        if( this.canAttack( a ) == false ) continue ; 

                        this.set.add( a ) ;

                        let uiItem = this.genOrGetEffect( a ) ;
                        if( uiItem.flagAttackDamage != true ) {
                            uiItem.flagAttackDamage = true ;

                            uiItem.txt.string += "\n" ; 

                        }

                    } // end for
                }
                break ;

                // 
                case AffixType.AuraAttackSpeedIncreasePercent : {
                    let ret = TowerAuraScopeExpand.exe( aura.auraScopeType , cells ) ; 
                    for( let a of ret.article ) {
                        
                        // 
                        if( a == article ) continue ; 
                        if( this.canAttack( a ) == false ) continue ; 

                        this.set.add( a ) ;

                        let uiItem = this.genOrGetEffect( a ) ;
                        if( uiItem.flagAttackSpeed != true ) {
                            uiItem.flagAttackSpeed = true ;

                            uiItem.txt.string += "\n" ; 

                        }

                    } // end for
                }
                break ;

                // 
                case AffixType.AuraAttackCritProbabilityIncrease : {
                    let ret = TowerAuraScopeExpand.exe( aura.auraScopeType , cells ) ; 
                    for( let a of ret.article ) {

                        // 
                        if( a == article ) continue ; 
                        if( this.canAttack( a ) == false ) continue ; 
                        
                        this.set.add( a ) ;

                        let uiItem = this.genOrGetEffect( a ) ;
                        if( uiItem.flagAttackCritProbability != true ) {
                            uiItem.flagAttackCritProbability = true ;

                            uiItem.txt.string += "\n" ; 

                        }

                    } // end for
                }
                break ;


            }
            
        } // end for

        // 
        for( let kv of this.map ) {
            let k = kv[0] ;
            if( this.set.has( k ) == false ) {
                this.set2.add( k ) ;
            }
        } // end for

        for( let k of this.set2 ) {
            let ui = this.map.get( k ) ;
            this.map.delete( k ) ;
            ui.root.destroy() ; 
        }


    }

    // 
    public place() {

        for( let kv of this.map ) {
            let a = kv[ 0 ] ; 

            if( a.cells == null || a.cells.length == 0 ) continue ; 

            let effect = EffectFactory.create( "AuraHint2" ) ; 
            effect.setParent( WorldNode.ins.layer2 , false );
            effect.worldPosition = a.view.root.worldPosition ; 

        } // end for

        this.clear() ; 

    }

    // 
    public clear() {

        for( let kv of this.map ) {
            kv[1].root.destroy() ; 
        }
        this.map.clear() ; 

        this.set2.clear() ; 
        this.set.clear() ; 

    }

    private canAttack( a : BagArticle ) : boolean {

        return a.cfg.tower.atkDamage == 0 ? false : true ;

    }

    //  or  UI
    private genOrGetEffect( a : BagArticle ) : UiTowerAuraHint {

        let uiItem = this.map.get( a ) ;
        if( uiItem == null ) {

            uiItem = UiTowerAuraHint.create() ; 
            this.map.set( a , uiItem ) ;

            uiItem.root.setParent( WorldNode.ins.layer1 , false );
            uiItem.root.worldPosition = a.view.root.worldPosition ; 

        }

        return uiItem ;

    }

    private set = new Set< BagArticle >() ; 
    private set2 = new Set< BagArticle >() ; 

    private map = new Map< BagArticle , UiTowerAuraHint >() ;

    //  Buff
    public getBuffs( cfg : CfgArticle ) : Array< CfgBuffItem > {

        let data = this.auraPool.get( cfg.id ) ;
        if( data != null ) {
            return data ; 
        }

        let buffs = new Array< CfgBuffItem >() ; 

        //  aura
        for( let id of cfg.tower.buffs ) {
            let cfgBuffItem = CfgBuff.data.get( id ) ;
            if( cfgBuffItem.isAura == false ) continue ; 
            buffs.push( cfgBuffItem ) ; 
        }

        //  aura
        let items = CfgArticleGrade.data.get( cfg.gradeLineId ) ;
        if( items != null ) {

            for( let item of items ) {

                // 
                for( let id of item.buffIds ) {
                    let cfgBuffItem = CfgBuff.data.get( id ) ;
                    if( cfgBuffItem.isAura == false ) continue ; 
                    buffs.push( cfgBuffItem ) ; 
                }

                // 
                for( let id of item.unlocked ) {
                    let cfgBuffItem = CfgBuff.data.get( id ) ;
                    if( cfgBuffItem.isAura == false ) continue ; 
                    buffs.push( cfgBuffItem ) ; 
                }
    
            } // end for            

        }

        this.auraPool.set( cfg.id , buffs ) ;

        return buffs ; 

    }

    // 
    public auraPool = new Map< string , Array< CfgBuffItem > >() ;

    public towerDamage = new Array< BagArticle >() ;

}


class UiTowerAuraHint {

    public static create() : UiTowerAuraHint {

        let ui = new UiTowerAuraHint() ; 
        let root = EffectFactory.create( "AuraHint" , false ) ; 
        ui.init( root ) ;
        return ui ; 

    }

    public flagAttackDamage = false ; 
    public flagAttackSpeed = false ; 
    public flagAttackCritProbability = false ; 

    public root : Node ; 

    public txt : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.txt = this.root.getComponentInChildren( Label ) ; 
        this.txt.string = "" ;

        // 
        let pos1 = this.txt.node.position.clone() ; 
        let pos2 = this.txt.node.position.clone().add3f( 0 , -10 , 0 ) ;
        tween( this.txt.node ).sequence(
            tween( this.txt.node ).to( 0.5 , { position : pos2 } ) , 
            tween( this.txt.node ).to( 0.5 , { position : pos1 } )

        ).repeatForever().start() ;

    }


}