import { Input, input, instantiate, Node, Prefab, Rect, resources, RichText, Size, UITransform } from "cc";


export class UiBubble {

    public static pf : Prefab ; 

    public static loadAndCreate( cb : ( ui : UiBubble )=>void ) {

        resources.load( "Common/Ui/Bubble" , Prefab , ( err , data )=>{

            let ui = new UiBubble() ; 
            ui.init( instantiate( data ) ) ;
            // ui.root.setParent( parent ) ;
            // ui.txt.string = str ; 
            cb?.( ui ) ;

        } ) ;

    }

    public static create() : UiBubble {

        let ui = new UiBubble() ; 
        ui.init( instantiate( this.pf ) ) ;
        return ui ; 

    }

    public root : Node ;

    public bg : Node ; 

    public txt : RichText ; 

    public init( node : Node )  {

        this.root = node ; 

        this.bg = this.root.getChildByPath( "bg" ) ;
        this.bgTrans = this.bg.getComponent( UITransform ) ; 

        this.txt = this.root.getComponentInChildren( RichText ) ; 
        this.txtTrans = this.txt.getComponent( UITransform ) ; 

        this.hDiff = this.bgTrans.contentSize.height - this.txtTrans.contentSize.height ; 

    }

    // 
    public clickToDestroy() {

        this.root.getComponent( UITransform ).setContentSize( new Size( 5000 , 5000 ) ) ;
        this.root.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;

    }

    public setString( str : string ) {

        this.txt.string = str ; 
        this.refreshSize() ; 

    }

    // 
    public refreshSize() {

        let h = this.txtTrans.contentSize.height + this.hDiff ;
        let size = this.bgTrans.contentSize.clone() ;
        size.height = h ; 
        this.bgTrans.contentSize = size ; 

    }

    private bgTrans : UITransform ; 
    private txtTrans : UITransform ; 

    private hDiff : number ; 

}