import { Input, Node } from "cc";
import { UiFeatureSel } from "./UiFeatureSel";
import { GamePause } from "../../../World/2.Td/Flow/GamePause";
import { FeatureAcquire } from "./FeatureAcquire";
import { FeatureExtract } from "./FeatureExtract";
import { Ad } from "../../../../Common/Ad/Ad";
import { Vec3 } from "cc";

// 
export class UiFeatureSelBtnList {

    public featureSel : UiFeatureSel = null ;


    // 
    public root : Node ; 

    public btnRefresh : Node ; 

    public btnGetAll : Node ; 

    public init( node : Node ) {

        this.root = node ;

        this.btnRefresh = this.root.getChildByPath( "btnRefresh" ) ;
        this.btnRefresh.on( Input.EventType.TOUCH_END , this.onClick_Refresh , this ) ; 

        this.btnGetAll = this.root.getChildByPath( "btnGetAll" ) ;
        this.btnGetAll.on( Input.EventType.TOUCH_END , this.onClick_GetAll , this ) ; 

    }

    // 
    private onClick_Refresh() {

        let onSuccess = ()=>{
            this.featureSel.refresh() ; 
        };

        Ad.ins.rewardShow( onSuccess ) ; 

    }

    private onClick_GetAll() {

        let onSuccess = ()=>{

            // this.FeatureSel.root.destroy() ; 
            this.featureSel.root.active = false ; 

            let ids = new Array< string >() ;
            for( let uiItem of this.featureSel.itemList.items ) {
                let id = uiItem.cfgItem.id ; 
                ids.push( id ) ; 
                FeatureExtract.countLimit( id ) ; 
            } // end for
    
            FeatureAcquire.exe( ids ) ; 
    
            // 
            GamePause.resume() ; 
        };

        Ad.ins.rewardShow( onSuccess ) ; 

    }

}
