import { BulletMoveCalc } from "../../Bullet/BulletMoveCalc";
import { TowerBulletEmit as TowerBulletEmit } from "./TowerBulletEmit";
import { FrameCall, FrameCallDataItem } from "../../Support/Misc/FrameCall";
import { FrameSecConvert } from "../../../../../Common/Misc/FrameSecConvert";

// 
export class TowerAttack {

    public static exe( tower : Tower ) {

        this.attackAnim( tower ) ;

        tower.attack.count++ ; 

        // 
        TowerBulletEmit.exe( tower ) ; 


    }

    public static canAttack( tower : Tower ) {

        let allEnmeies = RoleMgr.ins.allEnemies ; 
        if( allEnmeies.length == 0 ) {
            // 
            return false ;
        }

        let atk = tower.attack ; 
        if( atk == null ) return false ; 

        if( atk.bulletId == null ) return false ; 

        return true ; 

    }

    // 
    public static attackAnim( tower : Tower ) {

        // 
        let root = tower.article.view.root ; 

        tween( root ).sequence(

            tween( root ).to( 0.2 , {scale : new Vec3( 1.2 , 1.2 , 1.2 ) } ) , 
            tween( root ).to( 0.2 , {scale : new Vec3( 1.0 , 1.0 , 1.0 ) } ) , 

        ).start() ;

    }



}


