import { Vec3 } from "cc";
import { Role } from "../Base/Role";
import { BulletFactory } from "../../Bullet/BulletFactory";
import { WorldNode } from "../../Support/Misc/WorldNode";
import { RoleAnimSpine } from "./RoleAnimSpine";
import { RoleGetDamage } from "./RoleGetDamage";

export class RoleAttack {

    // 
    public static exe( role : Role , dir : Vec3 ) {

        if( this.canAttack( role ) == false ) return ;

        // dir.normalize() ; 

        // let pos = this.getBulletEmitPos( role , dir ) ; 

        // let bullet = BulletFactory.create( "004_1" ) ;
        // bullet.view.root.setParent( WorldNode.ins.layer2 , false ) ;
        // bullet.view.root.position = pos ;

        role.attack.cd.setCurrentAsMax() ; 

        RoleAnimSpine.playAttack( role.view.skeleton ) ;

        //  
        this.targetGetDamage( role ) ;
        

    }

    public static canAttack( role : Role ) : boolean {

        let atk = role.attack ; 

        if( atk.lockCount > 0 ) return false ; 

        // 
        if( role.attack.cd.current > 0 ) return false ; 

        return true ; 

    }

    // dir 
    public static getBulletEmitPos( role : Role , dir : Vec3 ) : Vec3 {

        // 
        let len = 100 ; 
        let offset = dir.clone().multiply3f( len , len , 1 ) ; 
        // 
        offset.y += 100 ;

        offset.add( role.view.root.position ) ; 

        return offset ;

    }

     //  
    public static targetGetDamage( role : Role ) {

        let target = role.ai.target ; 
        if( target == null ) return ; 

        RoleGetDamage.exe( target , role.attack.damage );

        // if( target )

    }


}