import { UITransform } from "cc";
import { Size } from "cc";
import { Sprite } from "cc";
import { Node } from "cc";
import { PfMgrBagArticle } from "../PfMgr/PfMgr";
import { NodeSizeFit } from "../../SL/Cocos/Ui/NodeSizeFit";
import { PfMgrIcon } from "../PfMgr/PfMgrIcon";
import { ItemType } from "./ItemType";

// 
/*
    

    root 
    |- Icon
    |- FragmentFlag
    
*/
export class UiItemIcon {

    public root : Node ; 

    // 
    public icon : Sprite ;

    // 
    public flagmentFlag : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon = this.root.getChildByPath( "Icon" )?.getComponent( Sprite ) ; 
        if( this.icon != null ) {
            this.iconSize = this.icon.node.getComponent( UITransform ).contentSize.clone() ;
        }

        this.flagmentFlag = this.root.getChildByPath( "FragmentFlag" ) ;
        if( this.flagmentFlag != null ) {
            this.flagmentFlag.active = false ; 
        }

    }

    public refresh( id : string ) {

        if( this.icon == null ) return ; 

        // 
        switch( id ) {

            case ItemType.Money001 : {

                this.icon.spriteFrame = PfMgrIcon.ins.money001.spriteFrame ;
                NodeSizeFit.trimmed( this.icon , this.iconSize ) ;

            }
            break ; 

            case ItemType.Money002 : {

                this.icon.spriteFrame = PfMgrIcon.ins.money002.spriteFrame ;
                NodeSizeFit.trimmed( this.icon , this.iconSize ) ;

            }
            break ; 

            case ItemType.Stamina : {

                this.icon.spriteFrame = PfMgrIcon.ins.stamina.spriteFrame ;
                NodeSizeFit.trimmed( this.icon , this.iconSize ) ;

            }
            break ; 

            default : {

                if( id.startsWith( "Fragment" ) ) {
                    // 

                    this.flagmentFlag.active = true ; 

                    // 

                    // 1
                    let id2 = id.replace( "Fragment" , "" ) + "_1" ; 
        
                    let pf = PfMgrBagArticle.getPf( id2 ) ; 
        
                    let main = pf.getChildByPath( "Main" ) ; 
                    let spr = main.getComponent( Sprite ) ; 
                    if( spr != null ) {
                        this.icon.node.active = true ; 
                        this.icon.spriteFrame = spr.spriteFrame ; 
                        NodeSizeFit.trimmed( this.icon , this.iconSize ) ;
                    }

                }

            }
            break ;

            // case ItemType.Fragment : 
            // {
            //     this.flagmentFlag.active = true ; 

            //     // 1
            //     let id2 = cfg.type2 + "_1" ; 
    
            //     let pf = PfMgrBagArticle.getPf( id2 ) ; 
    
            //     let main = pf.getChildByPath( "Main" ) ; 
            //     let spr = main.getComponent( Sprite ) ; 
            //     if( spr != null ) {
            //         this.icon.node.active = true ; 
            //         this.icon.spriteFrame = spr.spriteFrame ; 
            //         NodeSizeFit.trimmed( this.icon , this.iconSize ) ;
            //     }

            // }
            // break ; 

        }

        

    }

   
    private iconSize : Size ; 
    
}