import { instantiate, Label, Node, Prefab, Sprite } from "cc";
import { UiBar } from "./UiBar";
import { Role } from "../../World/1.Base/Role/Base/Role";
import { UiBar2 } from "./UiBar2";

// 
export class UiHpBar {

    public static pf : Node ; 

    public static create( id : string = null ) : UiHpBar {

        if( id == null ) id = "HpBar" ;

        let ui = new UiHpBar() ; 

        let root = instantiate( this.pf.getChildByPath( id ) );
        ui.init( root ) ;

        return ui ; 

    }

    // 
    public role : Role ; 

    public root : Node ; 

    // 
    public label : Label ; 

    //   1 
    public bar = new UiBar2() ; 

    //   2 
    public bar2 : UiBar2 = null ; 

    public init( node : Node ) {

        this.root = node ; 

        this.bar.init( this.root.getChildByPath( "bar" ) );

        let rootBar2 = this.root.getChildByPath( "bar2" ) ;
        if( rootBar2 ) {

            this.bar2 = new UiBar2() ; 
            this.bar2.init( rootBar2 ) ;
        }
        
        this.label = this.root.getComponentInChildren( Label ) ; 

    }

    // 
    public refresh() {
        
        let hp = this.role.life.hp ; 
        let val = hp.current / hp.max ; 

        let diff = this.bar.bar.fillRange - val ;
        this.bar.bar.fillRange = val ; 

        // 
        if( this.bar2 != null ) {

            if( diff > 0.02 ) {
                this.bar2.anim( val , 0.8 ) ; 
            }
            else {
                // 
                this.bar2.bar.fillRange = val ; 
            }

        }
        
        // 
        if( this.label != null ) {
            this.label.string = hp.current + " / " + hp.max ; 
        }

    }

}

