import { Label, Node } from "cc";
import { Player } from "../../World/2.Td/Player/Player";

// UI
export class UiMoney001 {

    public static ins : UiMoney001 = null ; 

    public root : Node ; 

    public icon : Node ;

    public val : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.val = this.root.getChildByPath( "val" ).getComponent( Label ) ; 
        this.val.string = "" ; 

        this.icon = this.root.getChildByPath( "Icon" ) ; 

        this.refresh() ; 

    }

    public refresh() {

        this.val.string = Player.ins.money001.toString() ; 

    }

}