import { Node, Tween, Vec2, Vec3, tween } from "cc";

// 
export class Shake
{
    public static exePreset( node : Node , id : string , scalePos : number = 1 ){

        let item = this.genData(id) ;

        // 
        for( let t of item ) {
            let pos = t[ 0 ] ;
            pos.multiply3f( scalePos , scalePos , scalePos ) ;
            pos.add( node.position ) ;
        }

        this.exe( node , item ) ;

    }

    public static exe( node : Node , data : Array< [ Vec3 , number ] > )
    {
        let seq = tween( node ) ;
        for( let i = 0 ; i < data.length ; ++i ){
            let item = data[ i ]
            let pos = item[ 0 ] ;
            let duration = item[ 1 ] ;
            let t = tween( node ).to( duration , { position : pos} ) ;
            seq.then( t ) ;
        }
        seq.start() ;

    }

    // 
    public static genData( id : string ) : Array< [ Vec3 , number ] > {

        let list : Array< [ Vec3 , number ] > = null ;

        if( id == "1" ) {
            list = new Array< [ Vec3 , number ] >() ;
            list.push( [ new Vec3( 10,-10 ) , 0.1 ] ) ;
            list.push( [ new Vec3( 0 , 0  ) , 0.05 ] ) ;
        }
        else if( id == "2" ) {
            list = new Array< [ Vec3 , number ] >() ;
            list.push( [ new Vec3( 6,10 ) , 0.1 ] ) ;
            list.push( [ new Vec3( 10,-7 ) , 0.1 ] ) ;
            list.push( [ new Vec3( -5,-4 ) , 0.05 ] ) ;
            list.push( [ new Vec3( 5,5 ) , 0.05 ] ) ;
            list.push( [ new Vec3( 0 , 0 ) , 0.05 ] ) ;
        }
        else if( id == "3" ) {
            list = new Array< [ Vec3 , number ] >() ;
            list.push( [ new Vec3( 10,1 ) , 0.05 ] ) ;
            list.push( [ new Vec3( -9,-7 ) , 0.1 ] ) ;
            list.push( [ new Vec3( 13,5 ) , 0.05 ] ) ;
            list.push( [ new Vec3( 2,-7 ) , 0.05 ] ) ;
            list.push( [ new Vec3( 9,-1 ) , 0.05 ] ) ;
            list.push( [ new Vec3( 5,1) , 0.05 ] ) ;
            list.push( [ new Vec3( 0 , 0 ) , 0.05 ] ) ;
        }

        return list ;

    }

}