import { math, Node, UITransform, Vec3 } from "cc";
import { WorldNode } from "../../World/1.Base/Support/Misc/WorldNode";
import { BagArticle } from "../Article/BagArticle";
import { GridTraverse } from "../Grid/GridTraverse";
import { MergeDefinition } from "./MergeDefinition";
import { EffectFactory } from "./EffectFactory";



// 
export class MergeHybridHintMgr {

    public static ins : MergeHybridHintMgr = null ; 

    // 
    public record = new Set< string >() ;

    public effect = new Array< Node >() ; 

    public exe() {

        let all = new Array< BagArticle >() ; 
        // 
        GridTraverse.exe( ( cell )=>{

            let a = cell.article ;
            if( a == null ) return ; 

            if( all.indexOf( a ) == -1 ) {
                all.push( a ) ; 
            }

        } ) ; 

        for( let a of all ) {
            this.hideShape( a ) ; 
        }

        for( let i = 0 ; i < all.length ; ++i ) {
            let a = all[ i ] ;

            for( let k = i + 1 ; k < all.length ; ++k ) {

                let b = all[ k ] ;
                let id = MergeDefinition.ins.getMergedIdhybrid( a.cfg.id , b.cfg.id ) ;
                if( id == null ) continue ;

                this.showShape( a ) ;
                this.showShape( b ) ;

                if( this.record.has( id ) == true ) continue ; 

                this.record.add( id ) ;

                let effect = MergeHybridHint.show( a , b ) ; 
                this.effect.push( effect ) ;

            } // end for
        } // end for

    }

    public showShape( a : BagArticle ) {

        if( a.view.effectMergeHintShape == null ) {
            
            let id = "MergeHintShape_" + a.cfg.bagShapeId ; 
            let effect = EffectFactory.create( id , false ) ;
            if( effect == null ) return ;
    
            effect.setParent( a.view.root , false ) ; 
            effect.position = Vec3.ZERO ; 
            effect.setSiblingIndex( 0 ) ;
            a.view.effectMergeHintShape = effect ; 

        }

        a.view.effectMergeHintShape.active = true ; 

    }

    public hideShape( a : BagArticle ) {

        let effect = a.view.effectMergeHintShape ; 
        if( effect == null ) return ; 
        
        effect.active = false ; 
        // a.view.effectMergeHintShape = null ; 


    }

    // 
    public clearAllLines() {

        for( let node of this.effect ) {
            node.destroy() ; 
        }
        this.effect.splice( 0 , this.effect.length ) ; 

    }

}

// 
export class MergeHybridHint {

    public static show( article1 : BagArticle , article2 : BagArticle ) : Node {

        // 
        let root = new Node( "MergeHint1" ) ; 
        root.addComponent( UITransform );
        root.setParent( WorldNode.ins.layer2 , false ) ;

        let pos = article1.view.root.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ; 
        WorldNode.ins.layer2.getComponent( UITransform ).convertToNodeSpaceAR( pos , pos ) ; 
        root.position = pos ; 

        let effect = EffectFactory.create( "MergeHint1" , false ) ; 
        effect.setParent( root , false ) ;
        effect.position = Vec3.ZERO ; 
        // effect.worldPosition = article1.view.root.worldPosition ; 


        // 
        // 
        let len = 320 ;
        let v = article2.view.root.position.clone().subtract( article1.view.root.position ) ;
        let lenTarget = v.length() ; 
        let f = lenTarget / len ; 
        effect.scale = new Vec3( f , 1 , 1 ) ; 

        // 
        let y = math.toDegree( Math.atan2( v.y , v.x ) ) ;
        effect.eulerAngles = new Vec3( 0 , 0 , y ) ;

        // 
        let v2 = v.multiplyScalar( 0.5 ) ;
        // let pos2 = pos.clone().add( v2 ) ;
        let effect2 = EffectFactory.create( "MergeHint2" , false ) ; 
        effect2.setParent( root , false ) ;
        effect2.position = v2 ; 

        return root ; 
        
    }

}
