import { Vec3 } from "cc";

/*
    

    
        https://www.cnblogs.com/dixiaodiaohome/p/14157362.html
        https://zhuanlan.zhihu.com/p/180264303

*/
export class BezierCalc
{
    // 2
    public static bezier2Path( startPos : Vec3 , endPos : Vec3 , controlPos : Vec3 , count : number ) : Array<Vec3>
    {
        let path = new Array< Vec3 >();
        for ( let i = 1 ; i <= count ; i++ )
        {
            let t = i / count;
            let val = this.bezier2( startPos , endPos , controlPos , t );
            path.push( val ) ; 
        }
        return path;
    }

    // 2 
    /// <param name="t">0101</param>
    /// <param name="start"></param>
    /// <param name="center"></param>
    /// <param name="end"></param>
    private static bezier2( start : Vec3 , end : Vec3 , center : Vec3 , t : number ) : Vec3
    {
        let v = start.clone() ;
        v.multiplyScalar( ( 1 - t ) * ( 1 - t ) ) ; 

        let temp = center.clone().multiplyScalar( 2 * t * ( 1 - t ) ) ;
        v.add( temp ) ; 

        temp = end.clone().multiplyScalar( t * t ) ;
        v.add( temp ) ; 

        return v ; 

        // return ( 1 - t ) * ( 1 - t ) * start + 2 * t * ( 1 - t ) * center + t * t * end;
    }

    // 3
    public static bezier3Path( startPos : Vec3 , endPos : Vec3 , controlPos1 : Vec3 , controlPos2 : Vec3 , count : number ) : Array<Vec3>
    {
        let path = new Array<Vec3>() ;
        for ( let i = 1 ; i <= count ; i++ )
        {
            let t = i / count;
            path[ i - 1 ] = this.bezier3( startPos , endPos , controlPos1 , controlPos2 , t );
        }
        return path;

    }

    // 3 
    private static bezier3( startPos : Vec3 , endPos : Vec3 , controlPos1 : Vec3 , controlPos2 : Vec3 , t : number ) : Vec3
    {
        let t2 = 1 - t;

        let v = startPos.clone().multiplyScalar( t2 * t2 * t2 ) ; 

        let temp = controlPos1.clone().multiplyScalar( 3 * t * t2 * t2 ) ; 
        v.add( temp ) ; 

        temp = controlPos2.clone().multiplyScalar( 3 * t * t * t2 ) ; 
        v.add( temp ) ; 

        temp = endPos.clone().multiplyScalar( t * t * t ) ; 
        v.add( temp ) ; 

        return v ; 

        // return t2 * t2 * t2 * startPos
        //     + 3 * t * t2 * t2 * controlPos1
        //     + 3 * t * t * t2 * controlPos2
        //     + t * t * t * endPos;
    }

}