import { Label } from "cc";
import { Node } from "cc";
import { Input } from "cc";
import { Lang } from "../../../Common/Misc/Lang";
import { Player } from "../../World/2.Td/Player/Player";
import { PlaySpeedCtrl } from "../../World/1.Base/Support/Misc/PlaySpeedCtrl";


//   
export class UiPlaySpeed {

    public static ins : UiPlaySpeed = null ; 

    public root : Node ; 

    // 
    public multiple : Label = null ; 

    public init( node : Node ) {

        this.root = node ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ;

        this.multiple = this.root.getChildByPath( "Label" ).getComponent( Label ) ;
        
        this.refresh() ; 

    }

    public refresh() {

        let speed = Player.ins.playSpeed ; 

        this.multiple.string = Lang.get( "Game_PlaySpeed_Text" ).replace( "{1}" , speed.toString() ) ; 



    }

    public onClick() {

        Player.ins.playSpeed++ ; 
        if( Player.ins.playSpeed > 3 ) {
            Player.ins.playSpeed = 1 ; 
        }

        this.refresh() ; 

        PlaySpeedCtrl.exe() ; 

    }


}



