import { sys } from "cc";
import { DateTimeUtil } from "../../SL/Common/Misc/DateTimeUtil";

// 
export class DailyLimit {

    // ID
    public id : string = "DailyLimit_XXX" ;

    // 
    public max = 0 ; 

    // 
    public current = 0 ; 

    public constructor( id : string , max : number = 10 ) {

        this.id = id ; 
        this.max = max ; 

    }

    //  1 
    public decrement() {

        if( this.current <= 0 ) return ; 
        this.current-- ; 

        this.save() ; 

    }

    public load() {

        let str = sys.localStorage.getItem( this.id );
        if( str == null || str == "" ) {

            this.current = this.max ; 

            return ; 
        }

        let data = JSON.parse( str ) ; 
        this.current = Number( data[ "Current" ] ) ; 
        let dt = new Date( data[ "Date" ] ) ;
        let dtNow = new Date( Date.now() ) ; 
        if( DateTimeUtil.dateEquals( dt , dtNow ) == false ) {
            this.current = this.max ; 
        }

    }

    public save() {

        let data = {} ; 
        data[ "Date" ] = DateTimeUtil.toDateString( new Date( Date.now() ) )  ;
        data[ "Current" ] = this.current ;

        let str = JSON.stringify( data ) ; 
        sys.localStorage.setItem( this.id , str ) ; 

    }

}