import { sp } from "cc";
import { Role } from "../Base/Role";
import { FrameSecConvert } from "../../../../../Common/Misc/FrameSecConvert";

// spine
export class RoleAnimSpine {

    // 
    public static playIdle( sk : sp.Skeleton ) {
        this.play( sk , "idle" ) ; 
    }

    // 
    public static playMove( sk : sp.Skeleton ) {
        this.play( sk , "move" ) ; 
    }

    public static playAttack( sk : sp.Skeleton ) {

        if( sk == null ) return ; 

        let name = "attack" ; 
        if( sk.getCurrent( 0 ).animation.name == name ) return ; 
        let te = sk.setAnimation( 0 , name , false ) ;
        // te.timeScale = 0.5 ;
        sk.addAnimation( 0 , "idle" , true ) ; 

        // sk.skeletonData.

    }

    public static play( sk : sp.Skeleton , name : string ) {

        if( sk == null ) return ; 

        let te = sk.getCurrent( 0 ) ; 
        if( te == null ) {
            // 
            sk.setAnimation( 0 , name , true ) ;    
            return ; 
        }

        if( te.animation.name == name ) return ; 
        sk.setAnimation( 0 , name , true ) ;

    }

}

