import { Label, Node, RichText, Sprite } from "cc";
import { FrameSecConvert } from "../../../Common/Misc/FrameSecConvert";
import { Lang } from "../../../Common/Misc/Lang";
import { CfgArticle } from "../../../Common/Cfg/CfgArticle";
import { BagArticle } from "../Article/BagArticle";
import { Affix, AffixType } from "../../World/1.Base/Buff/Affix";
import { Buff } from "../../World/1.Base/Buff/Buff";

// 
export class UiArticleDetailAttribute {

    // public cfgArtile : CfgArticle = null ; 

    public article : BagArticle = null ; 

    public root : Node ; 

    public cd = new UiArticleDetailAttributeItem() ; 
    public target = new UiArticleDetailAttributeItem() ; 
    public attackDamage = new UiArticleDetailAttributeItem() ; 
    public hpMaxIncrease =  new UiArticleDetailAttributeItem() ; 
    public HpRegen =  new UiArticleDetailAttributeItem() ; 
    public Money001 =  new UiArticleDetailAttributeItem() ; 

    public AuraAttackDamage = new UiArticleDetailAttributeItem() ; 
    public AuraAttackSpeed = new UiArticleDetailAttributeItem() ; 
    public AuraAttackCritProbability = new UiArticleDetailAttributeItem() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.target.init( this.root.getChildByPath( "Target" ) ); 

        this.cd.init( this.root.getChildByPath( "Cd" ) ) ;

        this.attackDamage.init( this.root.getChildByPath( "Attack" ) ) ;

        this.hpMaxIncrease.init( this.root.getChildByPath( "HpMaxIncrease" ) ) ;

        this.HpRegen.init( this.root.getChildByPath( "HpRegen" ) ) ;

        this.Money001.init( this.root.getChildByPath( "Money001" ) ) ;

        this.AuraAttackDamage.init( this.root.getChildByPath( "AuraAttackDamage" ) ) ;

        this.AuraAttackSpeed.init( this.root.getChildByPath( "AuraAttackSpeed" ) ) ;

        this.AuraAttackCritProbability.init( this.root.getChildByPath( "AuraAttackCritProbability" ) ) ;

        this.hideAll() ; 

    }

    public refresh() {

        this.hideAll() ; 

        // 
        this.refreshAttack() ; 

        this.refreshMisc() ; 

    }

    public hideAll() {

        this.target.root.active = false ; 
        this.cd.root.active = false ; 
        this.attackDamage.root.active = false ; 
        this.hpMaxIncrease.root.active = false ; 
        this.HpRegen.root.active = false ; 
        this.Money001.root.active = false ; 

        this.AuraAttackDamage.root.active = false ; 
        this.AuraAttackSpeed.root.active = false ; 
        this.AuraAttackCritProbability.root.active = false ; 

    }
    
    public refreshAttack() {

        if( this.article.tower.attack.damage <= 0 ) return ; 

        // 
        this.attackDamage.root.active = true ; 
        this.attackDamage.val.string = "<b>" + this.article.tower.attack.damage.toString() + "</b>" ; 

        // 
        this.target.root.active = true ; 
        this.target.val.string = "<b>" + Lang.get( "AttackTargeSelType_" + this.article.cfg.tower.atkTargeSelType ) + "</b>" ; 

        //  CD 
        this.cd.root.active = true ; 
        let val = FrameSecConvert.toSecond( this.article.tower.attack.cd.max ) ; 
        val = Math.round( val * 100 ) / 100 ; // 2
        this.cd.val.string = "<b>" + val.toString() + "</b>" ; 

    }

    public refreshMisc() {

        let str1 = "<b>{1}</b>" ; 
        let str2 = "<b>{1}%</b>" ; 

        // Buff
        for( let buff of this.article.tower.buff.list ) {

            switch( buff.affix.type ) {
                
                case AffixType.HpMaxIncrease : {
                    this.hpMaxIncrease.root.active = true ; 
                    this.hpMaxIncrease.val.string = str1.replace( "{1}" , buff.affix.argN.toString() ) ; 
                }
                break ;

                case AffixType.HpRegenIncrease : {
                    this.HpRegen.root.active = true ; 
                    this.HpRegen.val.string = str1.replace( "{1}" , buff.affix.argN.toString() ) ; 
                }
                break ;

                case AffixType.HpRegenIncreasePercent : {
                    this.HpRegen.root.active = true ; 
                    let n = buff.affix.argN * 100 ;
                    this.HpRegen.val.string = str2.replace( "{1}" , n.toString() ) ; 
                }
                break ;

                case AffixType.GetMomeyPerWave : {
                    this.Money001.root.active = true ; 
                    this.Money001.val.string = str1.replace( "{1}" , buff.affix.argN.toString() ) ; 
                }
                break ;

            }

        } // end for

        // 
        for( let buff of this.article.tower.aura.list ) {

            switch( buff.affix.type ) {
                
                // 
                case AffixType.AuraAttackDamageIncreasePercent : {
                    this.AuraAttackDamage.root.active = true ; 
                    let n = buff.affix.argN * 100 ;
                    this.AuraAttackDamage.val.string = str2.replace( "{1}" , "+" + n.toString() ) ; 
                }
                break ; 

                // 
                case AffixType.AuraAttackSpeedIncreasePercent : {
                    let item = this.AuraAttackSpeed ; 
                    item.root.active = true ; 
                    let n = buff.affix.argN * 100 ;
                    item.val.string = str2.replace( "{1}" , "+" + n.toString() ) ; 
                }
                break ; 

                // 
                case AffixType.AuraAttackCritProbabilityIncrease : {
                    let item = this.AuraAttackCritProbability ; 
                    item.root.active = true ; 
                    let n = buff.affix.argN * 100 ;
                    item.val.string = str2.replace( "{1}" , "+" + n.toString() ) ; 
                }
                break ; 

            }

        }

    }

}

// 
class UiArticleDetailAttributeItem {

    public root : Node ; 

    public name : Label ; 

    public icon : Sprite ; 

    public val : RichText ; 

    public init( node : Node ) {

        this.root = node ; 

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;

        this.val = this.root.getChildByPath( "Val" ).getComponent( RichText ) ;

    }

}