import { Bullet } from "./Bullet";

export class BulletMgr {

    public static ins : BulletMgr = null ;

    public data = new Array< Bullet >() ; 

    public add( bullet : Bullet ) {

        let idx = this.data.indexOf( bullet ) ;
        if( idx != -1 ) return ; 
        this.data.push( bullet ) ;

    }

    public remove( bullet : Bullet ) {
        let idx = this.data.indexOf( bullet ) ;
        if( idx == -1 ) return ; 
        this.data.splice( idx , 1 ) ;
    }

}

