import { CfgUpgrade } from "../../../../Common/Cfg/CfgUpgrade";
import { UiMain } from "../../../Ui/Main/UiMain";
import { UiPlayerGrade } from "../../../Ui/Main/UiPlayerGrade";
import { UiFeatureSel } from "../../../Ui/Popup/FeatureSel/UiFeatureSel";
import { GamePause } from "../Flow/GamePause";
import { Player } from "./Player";

// 
export class PlayerGradeOp {

    // 
    public static expFactor = 1.0 ; 

    // 
    public static addExp( val : number ) {

        let g = Player.ins.grade ;
        g.exp += val * this.expFactor ; 

        // 
        this.upgrade() ; 

        // UI
        UiPlayerGrade.ins.refresh() ; 

    }

    // 
    public static upgrade() {

        let g = Player.ins.grade ;

        if( g.grade > CfgUpgrade.data.length ) {
            // 
            return ;
        }

        let expNeeded = CfgUpgrade.data[ g.grade ] ; 
        if( g.exp < expNeeded ) {
            // 
            return ;

        }

        
        g.grade++ ; 
        g.exp = g.exp - expNeeded ; 

        let uiFeatureSel = UiFeatureSel.ins ;
        uiFeatureSel.refresh() ; 
        uiFeatureSel.show() ; 



    }


}


