import { Account } from "../../../../Common/Account/Account";
import { CfgArticleMgr } from "../../../../Common/Cfg/CfgArticle";
import { CfgBuff, CfgBuffItem } from "../../../../Common/Cfg/CfgBuff";
import { CfgFeature, CfgFeatureItem } from "../../../../Common/Cfg/CfgFeature";
import { Shuffle } from "../../../../SL/Common/Misc/Shuffle";
import { BagArticleGradeHelper } from "../../../Bag/Article/BagArticleGradeHelper";
import { Buff } from "../../../World/1.Base/Buff/Buff";

// 
export class FeatureExtractPool {

    public static ins : FeatureExtractPool = null ;

    // 
    public count = new Map< string , number >() ;

    public data = new Array< CfgBuffItem >() ; 

    public init() {

        let ids1 = this.getCommon() ; 
        let ids2 = this.getDeploy() ; 
        ids2.push( ... ids1 ) ;

        // 
        // ids2 = [ "General-RndArticleMergeGradeUpgrade" ] ;

        for( let i = 0 ; i < ids2.length ; ++i ) {
            let cfgItem = CfgBuff.data.get( ids2[ i ] ) ;
            this.data.push( cfgItem ) ;

            if( cfgItem.unlockCount > 0 ) {
                this.count.set( cfgItem.id , cfgItem.unlockCount ) ; 
            }

        } // end for

    }

    // 
    public getDeploy() : Array< string > {

        let ret = new Array< string >() ;

        let deploy = Account.ins.bag.deploy ;
        for( let i = 0 ; i < deploy.length ; ++i ) {
            let id = deploy[ i ] ;

            let items = BagArticleGradeHelper.getCfgCurrentItems( id ) ; 
            for( let item of items ) {
                ret.push( ... item.unlocked ) ; 
            }

        } // end for

        return ret ;

    }

    // 
    public getCommon() : Array< string > {

        let ret = new Array< string >() ;

        for( let cfgItem of CfgFeature.data ) {
            ret.push( cfgItem.id ) ; 
        }

        return ret ; 

    }

}

// 
export class FeatureExtract {

    public static exe() : Array< CfgBuffItem > {

        let ret = new Array< CfgBuffItem >() ; 
        
        // 
        this.rnd( ret ) ; 

        return ret ; 

    }
   
    // 
    public static rnd( ret : Array< CfgBuffItem > ) {

        let poolData = FeatureExtractPool.ins.data ; 
        let list = new Array< CfgBuffItem >() ; 
        list.push( ... poolData ) ;
        Shuffle.shuffleArray( list ) ;

        let count = FeatureExtractPool.ins.count ; 
        for( let i = 0 ; i < 3 && i < list.length ; ++i ) {
            let item = list[ i ] ; 
            ret.push( item ) ; 
            
        } // end for


    }

    // 
    public static countLimit( id : string ) {

        let poolData = FeatureExtractPool.ins.data ; 
        let count = FeatureExtractPool.ins.count ; 

        let n = count.get( id ) ; 
        if( n == null ) {
            // 
            return ;
        }
        
        n-- ; 
        if( n > 0 ) {
            // 
            count.set( id , n ) ; 
            return ;
        }   

        let idx = poolData.findIndex( ( e )=>{ return e.id == id ; } ) ;
        if( idx == -1 ) return ; 
        poolData.splice( idx , 1 ) ; 

        count.delete( id ) ; 

    }

}