import { assetManager } from "cc";
import { Asset } from "cc";
import { resources } from "cc";
import { LoaderBase } from "./LoaderBase";


// Resources 
export class LoaderResources extends LoaderBase {

    // 
    public onComplete : ()=>void = null ;

    // 
    public onProgress: (val: number) => void = null ;

    public list = new Array< any >() ;

    public add( path : string , handler : ( data : Asset )=>void ) {

        let item = {} ;
        item[ "path" ] = path ;
        item[ "handler" ] = handler ;
        this.list.push( item ) ;

    }

    public exe( ) {

        // cocos
        let list2 = new Array< string >() ; 
        for( let i = 0 ; i < this.list.length ; ++i ) {
            list2.push( this.list[ i ][ "path" ] ) ;
        } // end for 

        resources.load( list2 , this._onProgress.bind(this) , ( err , data )=>{

            if( err != null ) {
                // 
            }

            for( let i = 0 ; i < data.length ; ++i ) {

                let dataItem = data[ i ] ; 

                let item = this.list[ i ] ;
                let handler = item[ "handler" ] ;
                handler( dataItem ) ;

            } // end for

            this.onComplete?.() ;

        } ) ;

    }

    private _onProgress(finished: number, total: number, item: RequestInit) {

        // https://forum.cocos.org/t/topic/147629

        let val = finished / total;
        if (val < this.val) return;

        this.val = val;
        this.onProgress?.(this.val);

    }

    // 
    private val = 0;



}