import { instantiate } from "cc";
import { UiPopupBase } from "../../../Common/Ui/UiPopupBase";
import { Node } from "cc";
import { UiArticleIcon, UiArticleIconBg } from "../../../Common/Ui/UiArticleIcon";
import { CfgArticle, CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { MergeDefinition } from "../../../Game/Bag/Merge/MergeDefinition";
import { SoundBtnClickSetter } from "../../../Common/Audio/SoundBtnClickSetter";
import { sp } from "cc";
import { tween } from "cc";
import { Label } from "cc";
import { ArticleString } from "../../../Common/String/ArticleString";

export class UiHybridGuide extends UiPopupBase {

    // new
    public static create( parent : Node ) : UiHybridGuide {

        let root = instantiate( this.pf ) ;
        let ui = new UiHybridGuide() ; 
        ui.init( root ) ;
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }

    public override init( node : Node ) {
        
        super.init( node ) ;

        this.pfItem = this.root.getChildByPath( "sv/view/content/Item" ) ; 
        this.pfItem.active = false ; 
        
        for( let i = 0 ; i < MergeDefinition.ins.orig.length ; ++i ) {
            let cfgItem = MergeDefinition.ins.orig[ i ] ;
            if( cfgItem.isHybrid == false ) continue ; 

            let uiItem = new UiHybridGuideItem() ; 
            let node = instantiate( this.pfItem ) ; 
            node.active = true ; 
            node.setParent( this.pfItem.parent , false ) ; 
            uiItem.init( node ) ; 
            uiItem.item1.refresh( cfgItem.id1 ) ; 
            uiItem.item2.refresh( cfgItem.id2 ) ; 
            uiItem.item3.refresh( cfgItem.id3 ) ; 
            let sks = uiItem.root.getComponentsInChildren( sp.Skeleton ) ;
            // Spine
            tween( uiItem.root ).delay( 0.01 ).call( ()=>{
                for( let sk of sks ) {
                    // let animations = sk.skeletonData.getRuntimeData().animations;
                    sk.addAnimation( 0 , "donghua" , true ) ; 
                }
            } ).start() ; 


        } // end for


    }

    private pfItem : Node ; 

}

// 
export class UiHybridGuideItem {

    public root : Node = null ; 

    public item1 = new UiItem() ; 
    public item2 = new UiItem() ; 
    public item3 = new UiItem() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.item1.init( this.root.getChildByPath( "Item1" ) ) ;
        this.item2.init( this.root.getChildByPath( "Item2" ) ) ;
        this.item3.init( this.root.getChildByPath( "Item3" ) ) ;

    } 


}

// 
export class UiItem {

    public root : Node ; 

    public icon = new UiArticleIcon() ; 

    public bg = new UiArticleIconBg()  ;

    public name : Label = null ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon.init( this.root.getChildByPath( "Icon" ) ) ; 

        this.bg.init( this.root.getChildByPath( "bg" ) ) ;

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;
        this.name.string = "" ; 

    }

    public refresh( id : string ) {

        this.icon.refresh( id ) ;
        this.bg.refresh( id ) ;

        this.name.string = ArticleString.getName( id ) ; 

    }

}