import { CfgRole } from "../../../../../Common/Cfg/CfgRole";
import { Player } from "../../../2.Td/Player/Player";
import { AffixType } from "../../Buff/Affix";
import { Buff } from "../../Buff/Buff";
import { BuffIncreaseCalc, DataIncrease } from "../../Buff/BuffIncreaseCalc";
import { Role } from "../Base/Role";

// 
export class RoleAttributeUpdate {

    public static dataIncr = new DataIncrease() ; 

    public static exe( role : Role ) {

        this.buff( role ) ;
        
    }

    public static buff( role : Role ) {

        /*
            Player buff 2
                Player Buff  Role  Role 
                Player Buff 

             Role Bug Role  Buff 
            Player Buff  Player 
        
        */

        this.setRoleAsOrig( role ) ; 

        this.dataIncr.reset() ; 

        //  Buff
        BuffIncreaseCalc.exeBuffs( Player.ins.buff.list , this.dataIncr ) ;

        //  role Buff
        BuffIncreaseCalc.exeBuffs( role.buff.list , this.dataIncr ) ;

        this.handleIncr( role , this.dataIncr ) ; 

        // //  Buff
        // this.buffList( role , Player.ins.buff.list ) ; 

        // //  role Buff
        // this.buffList( role , role.buff.list ) ; 

    }

    // 
    public static setRoleAsOrig( role : Role ) {

        let cfg = CfgRole.data.get( role.id ) ;

        // 
        role.move.speed = cfg.moveSpeed ; 

        // 
        role.attack.cd.max = cfg.attackCd ; 

    }

    // 
    public static handleIncr( role : Role , dataIncr : DataIncrease ) {

        if( dataIncr.moveSpeedIncreasePercent != 0 ) {
            role.move.speed += role.move.speed * dataIncr.moveSpeedIncreasePercent ; 
        }

        let val = dataIncr.attackSpeedIncreasePercent ; 
        if( val != 0 ) {
            let atk = role.attack ;
            let max = atk.cd.max ; 
            if( val > 0 ) {
                // 
                atk.cd.max = Math.round( max / ( 1 + val ) ) ; 
            }
            else {
                // 
                atk.cd.max += Math.round( max * Math.abs( val ) ) ; 
            }

        }
        

    }

    public static buffList( role : Role , list : Array< Buff > ) {
        
        let cfg = CfgRole.data.get( role.id ) ;

        let moveSpeedOrig = cfg.moveSpeed ; 

        for( let i = 0 ; i < list.length ; ++i ) {

            let buff = list[ i ] ; 

            switch( buff.affix.type ) {
                case AffixType.MoveSpeedDecreasePercent : 
                {
                    role.move.speed += moveSpeedOrig * buff.affix.argN ; 
                }
                break ; 

            }
            

        } // end for

    }

}

