import { Role } from "./Role";

// 
export class RoleMgr
{
    public static ins : RoleMgr = null ;

    public local : Role = null;

    // 
    public allEnemies = new Array<Role>();

    public add( role : Role ) : void 
    {
        if ( this.allEnemies.indexOf(role) != -1 ) return;
        this.allEnemies.push(role);

    }

    public remove( role : Role ) {

        let idx = this.allEnemies.indexOf(role) ;
        if( idx == -1 ) return ; 

        this.allEnemies.splice( idx , 1 ) ; 

    }

}