import { isValid, Vec3 } from "cc";
import { Role } from "../Base/Role";
import { FrameSecConvert } from "../../../../../Common/Misc/FrameSecConvert";

/*
    

*/ 
export class RoleMoveSys {

    public static ins = new RoleMoveSys() ; 

    public add( role : Role , second : number , len : number , dir : Vec3 , onComplete : ()=>void = null ) {

        let item = new RoleMoveSysItem() ; 
        this.items.push( item ) ; 

        item.role = role ; 
        item.frame = FrameSecConvert.toFrame( second ) ; 
        item.offset = dir.clone().multiplyScalar( len / item.frame ) ;  
        item.onComplete = onComplete ; 

    }

    public clear() {
        this.items.splice( 0 , this.items.length ) ; 
    }

    public update() {

        for( let i = this.items.length - 1 ; i >= 0 ; --i ) {

            let item = this.items[ i ] ; 

            item.frame-- ; 
            if( item.frame < 0 || isValid( item.role.view.root ) == false ){
                item.onComplete?.() ; 
                this.items.splice( i , 1 ) ; 
                continue ; 
            }

            // 
            let pos = item.role.view.root.position.clone() ;
            pos.add( item.offset ) ;
            item.role.view.root.position = pos ; 

        } // end for

    }

    public items = new Array< RoleMoveSysItem >() ; 

}

export class RoleMoveSysItem {

    public role : Role ; 

    // 
    public frame = 0 ; 

    // 
    public offset : Vec3 ; 

    public onComplete : ()=>void = null ; 

}