import { instantiate, Label, Node, Size, Sprite, UITransform, Widget } from "cc";
import { ItemData } from "./ItemData";
import { SeqScaleShow } from "../Misc/SeqScaleShow";
import { UiItem } from "./UiItem";

// 
export class UiItemList {

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.pfItem = this.root.getChildByPath( "Item" ) ; 
        this.pfItem.active = false ; 
        // 
        /*
            3.8.3  widget.enabled = false , destroy 
             enabled = false  Widget 
        */ 
        let ws = this.pfItem.getComponentsInChildren( Widget ) ; 
        for( let w of ws ) {
            w.enabled = false ; 
            w.destroy() ; 
        }

        // 
        for( let i = this.root.children.length - 1 ; i > 0 ; --i ) {
            this.root.children[ i ].destroy()  ; 
        } // end for

    }


    public refresh( items : Array< ItemData > ) {

        for( let i = 0 ; i < items.length ; ++i ) {

            let root = instantiate( this.pfItem ) ;
            root.setParent( this.pfItem.parent , false ) ; 
            root.active = true ; 

            let uiItem = new UiItem() ; 
            uiItem.init( root ) ;
            uiItem.refreshByItemData( items[ i ] ) ; 

        } // end for

    }

    // 
    public showAnim( Interval = 0.2 ) {

        let list = this.root.children.slice( 1 ) ;
        if( list.length == 0 ) return ; 
        SeqScaleShow.exe( list , Interval ) ;

    }

    private pfItem : Node ; 

}

