import { Input } from "cc";
import { CfgLevelWaveMgr } from "../../../../../Game/World/2.Td/Level/CfgLevelWaveMgr";
import { UiLevel } from "./UiLevel";
import { Node } from "cc";

// 
export class UiLevelArrow {

    public uiLevel : UiLevel = null ; 

    public root : Node ; 

    // 
    public btnArrowLeft : Node ; 
    public btnArrowRight : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.btnArrowLeft = this.root.getChildByPath( "btnArrowLeft" ) ; 
        this.btnArrowLeft.on( Input.EventType.TOUCH_END , this.onClick_btnArrowLeft , this ) ;

        this.btnArrowRight = this.root.getChildByPath( "btnArrowRight" ) ; 
        this.btnArrowRight.on( Input.EventType.TOUCH_END , this.onClick_btnArrowRight , this ) ;

    }

    public refresh() {

        let main = CfgLevelWaveMgr.main ; 
        this.btnArrowLeft.active = this.uiLevel.selLevelIdx > 1 ; 
        this.btnArrowRight.active = this.uiLevel.selLevelIdx < main.length - 1 ; 

    }

    private onClick_btnArrowLeft() {
        if( this.uiLevel.thumbnail.isMoveCompleted == false ) return ; 
        this.uiLevel.selLevelIdx-- ; 
        this.uiLevel.refresh() ; 
    }

    private onClick_btnArrowRight() {
        if( this.uiLevel.thumbnail.isMoveCompleted == false ) return ; 
        this.uiLevel.selLevelIdx++ ; 
        this.uiLevel.refresh() ; 
        
    }

}