import { ListHelper } from "../../../../../SL/Common/Misc/ListHelper";
import { Tower } from "./Tower";

export class TowerMgr {

    public static ins = new TowerMgr() ; 

    public data = new Array< Tower >() ;

    public add( tower : Tower ) {
        ListHelper.addUniquely( this.data , tower ) ; 
    }

    public remove( tower : Tower ) {
        ListHelper.remove( this.data , tower  ) ; 
    }

    public clear() {
        ListHelper.clear( this.data ) ; 
    }

}