import { color, Color, instantiate, Label, Node, randomRangeInt, Tween, tween, Vec3 } from "cc";
import { Role } from "../Base/Role";
import { PfMgrNumber } from "../../../../../Common/PfMgr/PfMgrNumber";
import { WorldNode } from "../../Support/Misc/WorldNode";
import { PlayerRole } from "../../../2.Td/Player/PlayerRole";
import { Buff_HpRegenIncreasePercentBelowPercentPerWave } from "../../Buff/Specific/Buff_HpRegenIncreasePercentBelowPercentPerWave";
import { Sender } from "../../Bullet/Bullet";
import { Stats } from "../../Support/Misc/Stats";

// 
export class RoleGetDamage {

    public static exe( role : Role , damage : number , showDamageValue = true , sender : Sender = null ) {

        // 
        role.life.hp.current -= damage ; 

        role.view.hpBar?.refresh() ;

        if( showDamageValue ) {
            DamageValueShow.normal( role.view.root.worldPosition.clone().add3f( 0 , 50 , 0 ) , damage ) ;
        }

        this.view( role ) ;

        RoleGetDamageLocal.exe( role ) ; 

        // 
        if( sender != null ) {
            Stats.ins.damage( sender.id , damage ) ; 
        }

    }

    // 
    public static view( role : Role ) {

        if( role.view.skeleton == null ) return ;

        Tween.stopAllByTarget( role.view.skeleton ) ; 
        role.view.skeleton.color = Color.RED ; 
        tween(role.view.skeleton).delay( 0.1 ).call( ()=>{ 
            role.view.skeleton.color = Color.WHITE ; 
        } ).start() ;

    }

}

export class RoleGetDamageLocal {

    public static exe( role : Role ) {

        if( role != PlayerRole.ins.main ) return ; 

        Buff_HpRegenIncreasePercentBelowPercentPerWave.tryExe( role ) ; 

    }

}


// 
export class DamageValueShow {

    public static crit( pos : Vec3 , n : number ) {

        //
        this.show( PfMgrNumber.ins.damageCrit , pos , n ) ;

    }

    // 
    public static normal( pos : Vec3 , n : number ) {

        this.show( PfMgrNumber.ins.damage , pos , n ) ;

    }

    // 
    private static rndPos( pos : Vec3 ) {

        pos.x += randomRangeInt( -50 , 50 ) ; 
        pos.y += randomRangeInt( -20 , 20 ) ; 

    }

    public static show( pf : Node , pos : Vec3 , n : number ) {

        let node = instantiate( pf );

        let txt = node.getComponent( Label );
        if( txt == null ) {
            txt = node.getComponentInChildren( Label );
        }
        txt.string = "-" + n.toString() ;

        var parent = WorldNode.ins.layer2 ; 
        node.setParent(parent , false ) ;
        node.worldPosition = pos ; 

        // 
        let pos2 = new Vec3( 0 , 200 ) ; 
        this.rndPos( pos2 ) ;

        node.scale = new Vec3( 0.5 , 0.5 , 0.5 ) ; 
        // 
        tween( node ).sequence(

            tween( node ).parallel(
                tween( node ).by( 0.3 , { position : pos2 } ) , 
                tween( node ).to( 0.3 , { scale : new Vec3( 1 , 1 , 1 ) } )
            ) , 
            tween( node ).delay( 0.2 ) , 
            tween( node ).parallel(
                tween( node ).by( 0.3 , { position : new Vec3( 0 , 50 ) } ) , 
                tween( node ).to( 0.3 , { scale : new Vec3( 0.5 , 0.5 , 0.5 ) } )
            ) , 
            tween( node ).call( ()=>{
                node.destroy() ; 
            } )

        ).start() ;         

    }

}