import { AffixType } from "../../1.Base/Buff/Affix";
import { Role } from "../../1.Base/Role/Base/Role";
import { RoleMgr } from "../../1.Base/Role/Base/RoleMgr";
import { RoleAttributeUpdate } from "../../1.Base/Role/Misc/RoleAttributeUpdate";
import { Player } from "./Player";

//  Buff 
export class PlayerBuffAffect {

    // 
    public static affectAllRoles() {

        for( let role of RoleMgr.ins.allEnemies ) {
            // this.affectRole( role ) ;
            RoleAttributeUpdate.exe( role ) ; 
        }

    }

    //  
    public static affectRole( role : Role ) {

        let buffList = Player.ins.buff.list ; 

        for( let buff of buffList ) {

            switch( buff.affix.type ) {
                case AffixType.MoveSpeedDecreasePercent : 
                {
                    role.buff.add( buff ) ;
                }
                break ; 
            }

        } // end for

        RoleAttributeUpdate.exe( role ) ;

    }



}