import { GridTraverse } from "./GridTraverse";
import { UiBagCell } from "./UiBagCell";


export class GridHint {

    // 
    public static hintPlaceableShow( cells : Array< UiBagCell > ) {

        for( let cell of cells ) {
            cell.hintPlaceable.active = true ; 
            cell.hintExpandable.active = false ; 
        }

    }

    // 
    public static hintPlaceableClear() {

        GridTraverse.exe( ( cell )=>{
            
            cell.hintPlaceable.active = false ; 

            cell.hintExpandable.active = !cell.unlocked ;
            

        } ) ;

    }

}