import { BagArticle } from "../Article/BagArticle";
import { GridTraverse } from "./GridTraverse";

export class ArticleHelper {

    private static list = new Array< BagArticle >() ; 

    public static getAllAriclesInGrid() : Array< BagArticle > {

        this.list.splice( 0 , this.list.length ) ;
    
        GridTraverse.exe( ( cell )=>{
    
            let a = cell.article ; 
            if( a == null ) return ; 
    
            if( this.list.indexOf( a ) != -1 ) return ; 
            this.list.push( a ) ;
            
        } ) ;
    
        return this.list ; 
    
    }

}

