import { instantiate } from "cc";
import { Prefab } from "cc";
import { UiEffectPopup } from "../../SL/Cocos/Ui/UiEffect";
import { Node } from "cc";
import { Input } from "cc";
import { Sprite } from "cc";
import { SpriteFrame } from "cc";
import { UITransform } from "cc";
import { SoundBtnClickSetter } from "../Audio/SoundBtnClickSetter";
import { BlockInputEvents } from "cc";

// UI
export class UiPopupBase {

    public static pf : Prefab ; 

    // new
    public static create( parent : Node ) : UiPopupBase {

        let root = instantiate( this.pf ) ;
        let ui = new UiPopupBase() ; 
        ui.init( root ) ;
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }

    public root : Node ; 

    public btnClose : Node ; 

    public cover : Sprite ; 

    public init( node : Node ) {

        this.root = node ; 

        SoundBtnClickSetter.exe( this.root ) ; 
        
        // 
        this.initBtnClose() ; 

        // 
        this.initCover() ; 

    }

    private initBtnClose() {
        this.btnClose = this.root.getChildByPath( "btnClose" ) ;
        this.btnClose?.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;
    }

    // 
    private initCover() {

        let coverNode = this.root.getChildByPath( "Cover" ) ;
        if( coverNode == null ) {
            // this.cover = new Node() ; 
            // let spr = this.cover.addComponent( Sprite ) ; 
            // spr.spriteFrame = SpriteFrame.createWithImage()
            return ; 
        }

        // 
        if( coverNode.getComponent( BlockInputEvents ) == null ) {
            coverNode.addComponent( BlockInputEvents ) ; 
        }

        // 
        coverNode.getComponent( UITransform ).setContentSize( 9999 , 9999 ) ; 

        // 
        let spr = coverNode.getComponent( Sprite ) ; 
        let cr = spr.color.clone() ;
        cr.a = 150 ; 
        spr.color = cr ; 

        this.cover = spr ; 

    }

    public show() {

        UiEffectPopup.show( this.root ) ; 

    }

}