import { Vec3 } from "cc";
import { BagArticle } from "../Article/BagArticle";
import { BagArticleFactory } from "../Article/BagArticleFactory";
import { BagArticleGridPlace } from "../Article/BagArticleGridPlace";
import { BagArticleGridPlaceByPos } from "../Article/BagArticleGridPlaceByPos";
import { UiBagCell } from "../Grid/UiBagCell";
import { UiTray } from "../Main/UiTray";
import { MergeDefinition } from "./MergeDefinition";
import { CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { MergeEffect } from "./MergeEffect";

// 
export class MergeGridHybrid {

    public static tryMerge( article : BagArticle , cells : Array< UiBagCell > ) : boolean {

        let ret = this.canMerge( article , cells ) ; 
        if( ret == null ) return false ; 

        this.merge( article , ret[ 1 ] , ret[ 0 ] ) ; 

        return true ; 

    }

    public static canMerge( article : BagArticle , cells : Array< UiBagCell > ) : [ string , BagArticle ] {

        for( let i = 0 ; i < cells.length ; ++i ) {

            let article2 = cells[ i ].article ;
            if( article2 == null ) continue ; 
            if( article == article2 ) continue ; 

            // 
            let id = MergeDefinition.ins.getMergedIdhybrid( article.cfg.id , article2.cfg.id ) ; 
            if( id != null ) {

                return [ id , article2 ] ; 
            }

        } // end for

        return null ; 

    }

    public static merge( article : BagArticle , article2 : BagArticle , id : string ) {

        let parent = article2.view.root.parent ; 

        // 
        let shapeId = CfgArticleMgr.data.get( id ).bagShapeId ;
        let pos = shapeId == article.cfg.bagShapeId ? article.view.root.worldPosition : article2.view.root.worldPosition ; 

        // 
        BagArticleGridPlace.unbind( article ) ; 
        UiTray.ins.removeAndArrange( article ) ;
        article.view.root.destroy() ; 

        // 
        BagArticleGridPlace.unbind( article2 ) ; 
        article2.view.root.destroy() ; 

        // 
        let articleNew = BagArticleFactory.create( id ) ;
        articleNew.view.root.setParent( parent , false ) ;
        articleNew.view.root.worldPosition = pos ; 
        BagArticleGridPlaceByPos.exe( articleNew ) ; 
        
        // 
        MergeEffect.merge( articleNew ) ; 

    }
    
}