import { tween } from "cc";
import { UiBubble } from "../../../Common/Ui/UiBubble";
import { BagArticle } from "./BagArticle";
import { UiMain } from "../../Ui/Main/UiMain";
import { Lang } from "../../../Common/Misc/Lang";
import { World } from "../../../Home/World/World";
import { WorldNode } from "../../World/1.Base/Support/Misc/WorldNode";


// 
export class BagArticleSpeak {

    public static memory = new Set< string >() ; 

    public static clearMemory() {
        this.memory.clear() ; 
    }

    // 
    public static place( article : BagArticle ) {

        let id = article.cfg.id ; 
        if( this.memory.has( id ) == true ) return ; 
        this.memory.add( id ) ; 

        let str = Lang.get( "AriclePlaceSpeak#" + id ) ; 
        if( str == null || str == "" ) return ; 

        let ui = UiBubble.create() ; 
        ui.root.setParent( WorldNode.ins.layer2 , true ) ; 
        ui.root.active = false ; 

        tween( ui.root ).sequence(

            tween( ui.root ).delay( 0.3 ) , 

            tween( ui.root ).call( ()=>{

                // 
                if( article.cells.length == 0 ) {
                    this.memory.delete( id ) ; 
                    return ; 
                }
                
                ui.root.active = true ; 
                ui.setString( str ) ;
                let pos = article.view.root.worldPosition.clone().add3f( 0 , 0 , 0 ) ; 

                let cell = article.cells[ 0 ] ;
                for( let cell2 of article.cells ) {
                    if( cell2.coord.y > cell.coord.y ){ cell = cell2 ; }
                }

                pos.y = cell.root.worldPosition.y + 150 ; 

                ui.root.worldPosition = pos ; 

            } ) , 

            tween( ui.root ).delay( 1 ) , 

            tween( ui.root ).call( ()=>{
                ui.root.destroy() ; 
            } ) ,

        ).start() ;

    }

}