import { Asset, instantiate, Node, resources } from "cc";
import { UiBag } from "../../Game/Bag/Main/UiBag";
import { Prefab } from "cc";
import { PfMgrRole } from "../../Game/World/1.Base/Role/Misc/PfMgrRole";

//  
export class PfMgrBagArticle {

    public static bagArticle : Node ; 

    public static getPf( id : string ) : Node {

        let strs = id.split( '_' ) ;
        if( strs.length >= 2 ) {

            let id2 = strs[ 0 ] + "/" + id ;
            let pf = PfMgrBagArticle.bagArticle.getChildByPath( id2 ) ;
            return pf ; 

        }

        // 
        let pf = PfMgrBagArticle.bagArticle.getChildByPath( id ) ; 
        return pf ; 

    }

}

