import { find, Node, Size, size, Tween, tween, UITransform, Vec2, Vec3 } from "cc";
import { BagGridDraw as BagGridDraw } from "./BagGridDraw";
import { UiBagGridBg } from "./UiBagGridBg";
import { Player } from "../../World/2.Td/Player/Player";
import { GridTraverse } from "./GridTraverse";
import { UiBagCell } from "./UiBagCell";
import { UiEffectFade } from "../../../SL/Cocos/Ui/UiEffect";
import { UiBagCfg } from "../Main/UiBagCfg";
import { BagArticle } from "../Article/BagArticle";


// 
export class UiBagGrid {

    public static ins = new UiBagGrid() ; 

    public root : Node ; 

    // 
    public gridSize = new Size( 5 , 7 ) ;

    // 
    public grid = new Array< Array< UiBagCell > >() ;

    // 
    public cellPf : Node ; 

    // 
    public posOrig : Vec3 ; 

    public init( node : Node ) {

        this.root = node ; 

        this.posOrig = this.root.position.clone() ; 

        this.cellPf = find( "Cell" , node ) ; 

        UiBagCfg.ins = new UiBagCfg() ; 
        UiBagCfg.ins.cellSize = this.cellPf.getComponent( UITransform ).contentSize ; 

        this.initGrid() ; 

        BagGridDraw.exe() ; 

        UiBagGridBg.ins.init( this.root.getChildByPath( "bg" ) ) ; 

    }

    // 
    public initGrid() {

        // 
        let size = this.gridSize ; 
        let grid = this.grid ;
        for( let yCoord = 0 ; yCoord < size.height ; ++yCoord ) {

            let row = new Array< UiBagCell >() ;
            grid.push( row ) ;

            for( let xCoord = 0 ; xCoord < size.width ; ++xCoord ) {

                let cell = new UiBagCell() ; 
                row.push( cell ) ;
                cell.unlocked = false ; 
                cell.coord = new Vec2( xCoord , yCoord ) ; 

            } // end for

        } // end for


        // 
        for( let coord of Player.ins.bag.cells ) {
            let cell = grid[ coord.y ][ coord.x ] ;
            cell.unlocked = true ; 
        }


    }

    public getCell( coord : Vec2 ) : UiBagCell {

        // 
        if( coord.x < 0 || coord.y < 0 || 
            coord.x >= this.gridSize.width || coord.y >= this.gridSize.height ) { 
            return null ; 
        }

        return this.grid[ coord.y ][ coord.x ] ; 

    }

    // 
    public showExpandable() {

        // if( this.showMode == 2 ) return ; 
        // this.showMode = 2 ;

        // 
        UiBagGridBg.ins.entiretyAnim( 0.2 ) ; 

        tween( this.root ).delay( 0.2 ).call( ()=>{

            GridTraverse.exe( ( cell : UiBagCell )=>{ 

                if( cell.unlocked == false ) {
                    cell.root.active = true ; 
                    cell.bg.active = false ; 
                    cell.hintExpandable.active = true ; 
                    UiEffectFade.fade( cell.hintExpandable , 0.2 , 0 , 255 ) ;
                }
                // cell.hintPlaceable.active = true ; 

            } ) ; 

        } ).start() ;

        // 
        GridTraverse.exe( ( cell : UiBagCell )=>{
            let a = cell.article ; 
            if( a == null ) return ; 
            UiEffectFade.fade( a.view.root , 0.4 , 255 , 255 * 0.5 ) ; 

        } ) ;

    }

    // 
    public showNormal() {

        // if( this.showMode == 1 ) return ; 
        // this.showMode = 1 ;

        // 
        Tween.stopAllByTarget( this.root ) ;

        GridTraverse.exe( ( cell : UiBagCell )=>{ 

            if( cell.unlocked == false ) {
                // 
                cell.root.active = false ; 
            }
            else {
                // 
                cell.root.active = true ; 
                cell.bg.active = true ; 
                cell.hintExpandable.active = false ; 
                cell.hintPlaceable.active = false ; 
            }
            // cell.hintPlaceable.active = true ; 

            // 
            let a = cell.article ;
            if( a != null ) {
                UiEffectFade.fade( a.view.root , 0.4 , 255 * 0.5 , 255 ) ;
            }

        } ) ; 

        UiBagGridBg.ins.unlockedAnim( 0.2 ) ; 

    }

    // 
    public alignBgCenter( duration = 0.2 ) {

        let pos1 = UiBagGridBg.ins.rect.center ; 
        let offset = new Vec3( pos1.x , pos1.y ) ; 
        let pos = this.posOrig.clone().subtract( offset ) ;

        if( duration == 0 ) {
            this.root.position = pos ; 
        }
        else {
            tween( this.root ).to( duration , { position : pos } ).start() ;
        }
        

    }

    // 
    private showMode = 1 ; 
}









