
import { BagArticleReloaded } from "../Bag/BagArticleReloaded";
import { LevelCtrl } from "../Level/LevelCtrl";
import { TowerAllUpdate } from "../../1.Base/Tower/Misc/TowerAllUpdate";
import { TowerMgr } from "../../1.Base/Tower/Entity/TowerMgr";
import { Buff_HpRegenIncreasePercentBelowPercentPerWave } from "../../1.Base/Buff/Specific/Buff_HpRegenIncreasePercentBelowPercentPerWave";
import { PlayerRole } from "../Player/PlayerRole";
import { WorldNode } from "../../1.Base/Support/Misc/WorldNode";
import { Vec3 } from "cc";
import { EffectFactory } from "../../../Bag/Merge/EffectFactory";
import { GamePause } from "./GamePause";

// 
export class GameStart {

    // 
    public static start() {

        GamePause.resume() ; 

        // 
        let effect = EffectFactory.create( "Battle" ) ;
        effect.setParent( WorldNode.ins.layer2 , true ) ;
        effect.position = Vec3.ZERO ; 

        // 
        BagArticleReloaded.exe() ; 

        // 
        TowerAllUpdate.updateAll() ; 

        Buff_HpRegenIncreasePercentBelowPercentPerWave.resetAllTowers() ; 
        Buff_HpRegenIncreasePercentBelowPercentPerWave.tryExe( PlayerRole.ins.main ) ;

        LevelCtrl.ins.start() ; 
        // WaveCtrl.ins.start() ; 

    }

}


