import { Input, instantiate, Node, Prefab, resources, RichText, Sprite } from "cc";
import { UiArticleIcon } from "../../../Common/Ui/UiArticleIcon";
import { ArticleString } from "../../../Common/String/ArticleString";
import { Lang } from "../../../Common/Misc/Lang";
import { UiEffectPopup } from "../../../SL/Cocos/Ui/UiEffect";

// UI  
export class UiArticleUnlock {

    public static loadAndCreate( cb : ( ui : UiArticleUnlock )=>void ) {

        resources.load( "Home/Ui/ArticleUnlock" , Prefab , ( err , data )=>{

            let root = instantiate( data ) ;
            let ui = new UiArticleUnlock() ;
            ui.init( root ) ; 
            cb( ui ) ;

        } ) ; 

    }

    public root : Node ; 

    public mask : Node ; 

    public icon = new UiArticleIcon() ; 

    public description : RichText ; 

    public init( node : Node ) {

        this.root = node ; 

        this.mask = this.root.getChildByPath( "mask" ) ; 
        this.mask.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;

        this.icon.init( this.root.getChildByPath( "Icon" ) ) ;

        this.description = this.root.getChildByPath( "Description" ).getComponent( RichText ) ;

    }

    public refresh( ids : Array< string > ) {

        // 
        this.icon.refresh( ids[ 0 ] ) ;

        // 
        let id = ids[ 0 ] ;
        let name = ArticleString.getName( id ) ;
        let str = Lang.get( "Home_AricleUnlock_Description" ) ;
        str = str.replace( "{name}" , name ) ;
        this.description.string = str ; 

    }

    public show() {

        UiEffectPopup.show( this.root ) ;

    }

}