import { Node, tween, UIOpacity, Vec3 } from "cc";
import { WorldNode } from "../../1.Base/Support/Misc/WorldNode";
import { UiBagBottom } from "../../../Bag/Main/UiBagBottom";
import { UiTray } from "../../../Bag/Main/UiTray";
import { BagTrayAricleGen } from "./BagTrayAricleGen";
import { UiBagTouch } from "../../../Bag/Touch/UiBagTouch";
import { UiFeature } from "../../../Ui/Main/Feature/UiFeature";
import { PlayerRole } from "../Player/PlayerRole";
import { UiPlaySpeed } from "../../../Ui/Main/UiPlaySpeed";

// 
export class BagView {

    // 
    public static enterDirect() {

        let val = 1.6 ; 
        WorldNode.ins.root.scale = new Vec3( val , val , val ) ;
        WorldNode.ins.root.position = new Vec3( val , 100 , val ) ; 

        // 
        UiBagBottom.ins.root.active = true ; 

        UiTray.ins.root.active = true ; 
        BagTrayAricleGen.ins.genFirst() ; 

        // 
        UiBagTouch.ins.root.active = true ; 

        // UI
        UiTray.ins.root.scale = new Vec3( val , val , val ) ;

        // 
        UiFeature.ins.root.parent.active = false ; 

        WorldNode.ins.layer0_Road.active = false ; 
        WorldNode.ins.layer0_Spawn.active = false ; 
        PlayerRole.ins.main.view.root.active = false ; 
        UiPlaySpeed.ins.root.active = false ; 
        
    }

    //  
    public static enter() {

        let val = 1.6 ; 
        let rootWorld = WorldNode.ins.root ; 

        // 
        tween( rootWorld ).to( 0.5 , { position : new Vec3( 0 , 100 , 0 ) } ).start() ;

        tween( rootWorld ).sequence(

            tween( rootWorld ).to( 0.5 , { scale : new Vec3( val , val , val ) } ) , 
            tween( rootWorld ).call( ()=>{

                // 
                UiBagBottom.ins.root.active = true ; 

                UiTray.ins.root.active = true ; 
                BagTrayAricleGen.ins.gen() ; 

                // 
                UiBagTouch.ins.root.active = true ; 

            } )

        ).start() ; 

        // UI
        this.fadeOut( UiFeature.ins.root.parent ) ; 

        this.fadeOut( PlayerRole.ins.main.view.root ) ;
        this.fadeOut( WorldNode.ins.layer0_Road ) ;
        this.fadeOut( WorldNode.ins.layer0_Spawn ) ;

        UiPlaySpeed.ins.root.active = false ; 

    }

    //  
    public static leave() {

        // 
        tween( WorldNode.ins.root ).to( 0.5 , { position : new Vec3( 0 , 0 , 0 ) } ).start() ;

        // 
        // tween( WorldNode.ins.root ).to( 0.5 , { scal } )
        tween( WorldNode.ins.root ).to( 0.5 , { scale : new Vec3( 1.0 , 1.0 , 1.0 ) } ).start()  ; 

        // 
        UiBagBottom.ins.root.active = false ; 

        UiTray.ins.root.active = false ; 
        UiTray.ins.clear() ; 

        // 
        UiBagTouch.ins.root.active = false ; 


        this.fadeIn( UiFeature.ins.root.parent ) ;

        this.fadeIn( PlayerRole.ins.main.view.root ) ;
        this.fadeIn( WorldNode.ins.layer0_Road ) ;
        this.fadeIn( WorldNode.ins.layer0_Spawn ) ;

        this.fadeIn( UiPlaySpeed.ins.root ) ; 

    }



    public static fadeOut( node : Node ) {

        let opa = node.getComponent( UIOpacity ) ; 
        if( opa == null ) {
            opa = node.addComponent( UIOpacity ) ; 
        }

        tween( opa ).to( 0.3 , { opacity : 0 } ).call( ()=>{
            node.active = false ; 
        } ).start() ; 

    }

    public static fadeIn( node : Node ) {

        let opa = node.getComponent( UIOpacity ) ; 
        if( opa == null ) {
            opa = node.addComponent( UIOpacity ) ; 
        }

        node.active = true ; 
        opa.opacity = 0 ; 

        tween( opa ).to( 0.3 , { opacity : 255 } ).call( ()=>{

        } ).start() ; 

    }

}