import { Node, Sprite } from "cc";

export class PfMgrHeroUp {

    public static ins = new PfMgrHeroUp() ; 

    public root : Node ; 

    public init( node : Node ) {
        this.root = node ; 
    }

    public getSprite(number){
        return this.root.getChildByPath(number.toString()).getComponent( Sprite ) ; 
    }

}