import { Input, Node } from "cc";

/*
    
     root  isOn 
*/ 
export class UiSwitch {

    public root : Node ; 

    public on : Node ;
    public off : Node ;

    public init( node : Node ) {

        this.root = node ; 
        this.on = this.root.getChildByPath( "On" ) ;
        this.off = this.root.getChildByPath( "Off" ) ;

        this.isOn = true ; 

        this.root.on( Input.EventType.TOUCH_END , ()=>{
            this.isOn = !this.isOn ; 
        } , this ) ;

    }

    public set isOn( val : boolean ) {
        this.on.active = val ;
        this.off.active = !val ;
    }

    public get isOn(){
        return this.on.active ;
    }

}