import { sys } from "cc";

import { DateTimeUtil } from "../../../../SL/Common/Misc/DateTimeUtil";
import { DateTimeMgr } from "../../../../SL/Common/Misc/DateTimeMgr";


// 
export class DailyCheckinData {

    public static ins : DailyCheckinData = new DailyCheckinData() ; 

    // 
    public lastDate = new Date( 0 ) ;

    // 
    public count = 0 ;

    // 
    public checkin() {
        this.lastDate = DateTimeMgr.ins.getNowDateTime() ; 
        this.count++ ;
    }

    // 
    public isTodayCheckin() : boolean {

        let dtNow = DateTimeMgr.ins.getNowDateTime() ;
        let b = DateTimeUtil.dateEquals( this.lastDate , dtNow ) ; 
        return b ; 

    } 

}

export class DailyCheckinDataPersist {

    public static load() {

        let str = sys.localStorage.getItem( "DailyCheckin" ) as string ; 
        if( str == null || str == "" ) return ; 

        let json = null ; 
        try {
            json = JSON.parse( str ) ; 
        }
        catch {
            return ; 
        }

        if( json[ "Date" ] == null ) return ; 

        let dtLast = new Date( Date.parse( json[ "Date" ] ) ) ; 
        DailyCheckinData.ins.lastDate = dtLast ; 
        DailyCheckinData.ins.count = Number( json[ "Count" ] ) ; 

    }

    public static save() {

        /*
            
            
        */ 

        let obj = {} ;
        obj[ "Date" ] = DateTimeUtil.toDateString( DailyCheckinData.ins.lastDate ) ;
        obj[ "Count" ] =  DailyCheckinData.ins.count ; 

        let str = JSON.stringify( obj ) ;
        sys.localStorage.setItem( "DailyCheckin" , str ) ; 

    }

}