import { Vec2 } from "cc";

/*
     
    
*/ 
export class GestureClick {

    public rest() {
        this.val = true ; 
    }

    public start( pos : Vec2 ) {

        this.posOrig = pos ; 

    }

    public move( pos : Vec2 ) {

        if( this.val == false ) return ;

        let v = this.posOrig.clone().subtract( pos ) ;
        if( v.lengthSqr() > 20 * 20 ) {
            this.val = false ; 
        } ; 

    }

    // 
    public getVal() : boolean {
        return this.val ;
    }

    //   
    private posOrig : Vec2 = null ; 

    private val = true ; 

}