import { Input, instantiate, Label, Node, Prefab, resources, RichText, tween, Vec3 } from "cc";
import { CfgArticle, CfgArticleMgr } from "../../../../Common/Cfg/CfgArticle";
import { CfgArticleGrade } from "../../../../Common/Cfg/CfgArticleGrade";
import { Account } from "../../../../Common/Account/Account";
import { ArticleString } from "../../../../Common/String/ArticleString";
import { UiBagArticleDetailBaseAttribute } from "./UiBagArticleDetailBaseAttribute";
import { UiBagArticleDetailUnlocked } from "./UiBagArticleDetailUnlocked";
import { UiBagArticleDetailBtnList } from "./UiBagArticleDetailBtnList";
import { UiBagItem } from "../UiBagItem";
import { UiEffectPopup } from "../../../../SL/Cocos/Ui/UiEffect";
import { SoundBtnClickSetter } from "../../../../Common/Audio/SoundBtnClickSetter";

export class UiBagArticleDetail {

    public static pf : Prefab ; 

    public static create( parent : Node ) : UiBagArticleDetail {

        let root = instantiate( this.pf ) ; 
        let ui = new UiBagArticleDetail() ; 
        ui.init( root ) ;
        ui.root.setParent( parent , false ) ;

        return ui ; 

    }

    public articleId : string = null ; 

    public root : Node ; 

    public name : Label ; 

    public intro : Label ; 

    public btnList = new UiBagArticleDetailBtnList() ; 

    public attribute = new UiBagArticleDetailBaseAttribute() ; 

    public unlocked = new UiBagArticleDetailUnlocked() ; 

    public item : UiBagItem ; 

    public unlockedInfo : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        SoundBtnClickSetter.exe( this.root ) ;

        let btnClose = this.root.getChildByPath( "btnClose" ) ;
        btnClose.on( Input.EventType.TOUCH_END , ()=>{
            this.root.destroy() ; 
        } ) ;

        this.btnList.detail = this ; 
        this.btnList.init( this.root.getChildByPath( "btnList" ) ) ;
        this.btnList.setShowModeAsDeploy() ;

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;
        
        this.intro = this.root.getChildByPath( "Intro/val" ).getComponent( Label ) ;

        this.attribute.init( this.root.getChildByPath( "Attribute" ) ) ;

        this.unlocked.detail = this ; 
        this.unlocked.init( this.root.getChildByPath( "Unlocked" ) ) ; 

        this.unlockedInfo = this.root.getChildByPath( "UnlockedInfo" ).getComponent( Label ) ; 
        this.unlockedInfo.node.active = false ; 
        
    }

    public show() {
        this.root.active = true ; 
        UiEffectPopup.show( this.root ) ; 
        
    }

    public refresh() {
        
        let cfgArctileItem = CfgArticleMgr.data.get( this.articleId ) ; 
        let glId = cfgArctileItem.gradeLineId ; 
        let cfgGrade = CfgArticleGrade.data.get( glId ) ; 
        // 
        let grade = Account.ins.bag.grade.get( glId ) ; 
        if( grade == null ) {
            grade = 1 ; 
        }
        
        if( grade > cfgGrade.length ) {
            // 
            this.btnList.btnUpgrade.root.active = false ; 
        }
        else {
            this.btnList.btnUpgrade.refresh() ; 
        }

        this.attribute.cfgArctile = cfgArctileItem ; 
        this.attribute.refresh() ; 
        
        // 
        this.intro.string = ArticleString.getDescription( this.articleId ) ;  

        this.unlocked.cfgArctile = cfgArctileItem ; 
        this.unlocked.refresh() ; 

    }

}







