export class UiEffectScale {

    public static show( node : Node ) : void {

        node.active = true ; 
        node.scale = new Vec3( 0.6 , 0.6 ) ; 
        tween( node )
        .to( 0.2 , { scale : new Vec3( 1.1 , 1.1 , 1.1 ) } )
        .to( 0.1 , { scale : new Vec3( 1 , 1 , 1 ) } )
        .start()  ; 

    }

}

// 
export class UiEffectPopup
{
    // 
    public static show( node : Node ) : void 
    {
        node.active = true ; 
        node.scale = new Vec3( 0.6 , 0.6 ) ; 
        tween( node )
        .to( 0.2 , { scale : new Vec3( 1.1 , 1.1 , 1.1 ) } )
        .to( 0.1 , { scale : new Vec3( 1 , 1 , 1 ) } )
        .start()  ; 

        var opa = node.getComponent( UIOpacity ) ;
        if( opa == null )
        {
            opa = node.addComponent( UIOpacity ) ;
        }
        opa.opacity = 0 ; 
        tween( opa ).to( 0.3 , { opacity : 255 } ).start() ; 

    }

    // 
    public static hide( node : Node ) : void 
    {
        tween( node )
        .to( 0.2 , { scale : new Vec3( 1.1 , 1.1 , 1.1 ) } )
        .to( 0.1 , { scale : new Vec3( 0.6 , 0.6 , 0.6 ) } )
        .start()  ; 

        var opa = node.getComponent( UIOpacity ) ;
        if( opa == null )
        {
            opa = node.addComponent( UIOpacity ) ;
        }
        tween( opa ).to( 0.3 , { opacity : 0 } ).call( ()=>{ opa.opacity = 255 ; node.active = false ; } ).start() ; 

    }

}