import { AffixType } from "../../Game/World/1.Base/Buff/Affix";
import { FrameSecConvert } from "../Misc/FrameSecConvert";


export class CfgFeature {

    public static data = new Array< CfgFeatureItem >() ; 

}

export class CfgFeatureItem {

    public id : string ; 

}


export class CfgFeatureParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgFeatureItem() ;
            CfgFeature.data.push( cfgItem ) ; 

            cfgItem.id = id ; 


        } // endf

    }

}