import { math, Vec3 } from "cc";
import { Bullet, BulletMoveType, BulletRotationType } from "./Bullet";
import { MoveCom } from "../Role/Move/MoveCom";
import { BulletBounceRect } from "./BulletBounceRect";
import { Role } from "../Role/Base/Role";
import { BulletMoveCalc } from "./BulletMoveCalc";
import { BulletViewHelper } from "./BulletViewHelper";
import { Player } from "../../2.Td/Player/Player";

// 
export class BulletMove {

    public static update( bullet : Bullet ) {

        if( bullet.pathMove != null ) {
            this.pathMove( bullet ) ; 
            return ; 
        }

        this.move( bullet ) ; 
        
        this.rectBounce( bullet ) ; 

        this.view( bullet ) ;

    }

    public static pathMove( bullet : Bullet ) {

        let pathMove = bullet.pathMove ; 
        let path = pathMove.path ; 
        let idx = pathMove.idx ; 
        let targetPos = path[ idx ] ; 
        let v = targetPos.clone().subtract( bullet.view.root.position ) ;
        if( v.lengthSqr() <= 50 * 50 ) {

            // 
            pathMove.idx++ ; 

            if( pathMove.idx >= path.length ) {
                // 

                bullet.life.hp.current = 0 ; 

                return ; 

            }

            // 
            targetPos = path[ pathMove.idx ] ; 
            v = targetPos.clone().subtract( bullet.view.root.position ) ;
            v.normalize() ;
            v.multiplyScalar( bullet.move.speed ) ; 
            bullet.move.offset = v ; 
        }
        
        // 
        let pos = bullet.view.root.position.clone() ;
        pos.add( bullet.move.offset ) ; 
        bullet.view.root.position = pos ; 

        // 
        // pathMove.lenMoved += role.move.offset.lengthSqr() ;

    }

    // 
    public static view( bullet : Bullet ) {

        switch( bullet.view.rotationType ) {
            case BulletRotationType.Direction : {
                BulletViewHelper.rotateDir( bullet ) ; 
            }
            break ; 
        }

    }

    // 
    public static move( bullet : Bullet ) {

        if( bullet.target.targetRole != null && bullet.target.moveType == BulletMoveType.Chase ) {
            this.chaseTarget( bullet ) ; 
            return ; 
        }

        //   
        let pos = bullet.view.root.position.clone() ;
        pos.add( bullet.move.offset ) ; 
        bullet.view.root.position = pos ; 

    }

    // 
    public static chaseTarget( bullet : Bullet ) {

        BulletMoveCalc.target( bullet ) ;

        let pos = bullet.view.root.position.clone() ;
        pos.add( bullet.move.offset ) ; 
        bullet.view.root.position = pos ; 

    }

    // 
    public static rectBounce( bullet : Bullet ) {

        // if( bullet.bounce.bounceRect <= 0 && bullet.bounce.bounceRectEnemy <= 0 ) return ; 

        let rect = BulletBounceRect.ins.rect ; 

        let pos = bullet.view.root.position ; 
        if( pos.x < rect.xMin || pos.x > rect.xMax ) {
            
            if( bullet.bounce.bounceRect > 0 ) {
                bullet.bounce.bounceRect-- ; 
            }
            else if( bullet.bounce.bounceRectEnemy > 0 ) {
                bullet.bounce.bounceRectEnemy-- ; 
            }
            else {
                return ;
            }

            bullet.move.offset.x *= -1 ; 
            bullet.move.direction.x *= -1 ; 
            
        }
        else if( pos.y < rect.yMin || pos.y > rect.yMax ){

            if( bullet.bounce.bounceRect > 0 ) {
                bullet.bounce.bounceRect-- ; 
            }
            else if( bullet.bounce.bounceRectEnemy > 0 ) {
                bullet.bounce.bounceRectEnemy-- ; 
            }
            else {
                return ;
            }

            bullet.move.offset.y *= -1 ; 
            bullet.move.direction.y *= -1 ; 


        }


    }


}

