import { Buff } from "./Buff";

export class BuffCom {

    // Buff 
    public list = new Array< Buff >() ;

    //  Buff
    public add( buff : Buff ) {

        let idx = this.list.indexOf( buff ) ; 
        if( idx != -1 ) return ; 

        this.list.push( buff ) ; 

    }

    public remove( buff : Buff ) {

        let idx = this.list.indexOf( buff ) ; 
        if( idx == -1 ) return ; 

        this.list.splice( idx , 1 ) ; 

    }

}