import { director } from "cc";


// cocos 
export class TimeScale {

    // 
    private static tickOrig = director.tick ; 

    // cocos 
    public static set( speed : number ) {

        if( this.tickOrig == null ) {
            this.tickOrig = director.tick;
        } 
        
        if( speed == 1 ) {
            director.tick = this.tickOrig ; 
            return ; 
        }

        // 
        director.tick = (dt: number) => {
            this.tickOrig.call(director, dt * speed ) ; 
        }

    }

}