import { Label, Node, Size, Sprite, UITransform } from "cc";
import { ItemData } from "./ItemData";
import { CfgItem, CfgItemMgr } from "../Cfg/CfgItem";
import { UiItemIcon } from "./UiItemIcon";

// UI 

/*
    

    root 
    |- bg
    |- Count
    |- Icon
    |- FragmentFlag
    
*/
export class UiItem {

    public root : Node ; 

    public bg : Node ; 

    // 
    public icon : UiItemIcon ;

    // 
    public count : Label ; 


    public init( node : Node ) {

        this.root = node ; 
        
        this.count = this.root.getChildByPath( "Count" ).getComponent( Label ) ;

        this.icon = new UiItemIcon() ; 
        this.icon.init( this.root ) ;

        // // 
        // let w = this.icon.node.getComponent( Widget ) ; 
        // w.enabled = false ; w.destroy() ; 

        this.bg = this.root.getChildByPath( "bg" ) ; 
        this.refreshBg( 1 ) ; 

    }

    //  UI 
    public refreshByItemData( item : ItemData ) {

        this.count.string = "x" + item.count ; 

        let cfg = item.cfg ; 
        if( cfg == null ) {
            cfg = CfgItemMgr.data.get( item.id ) ; 
        }

        this.icon.refresh( item.id ) ; 

        this.refreshBg( cfg.quality ) ;

    }

    public refreshBg( n : number ) {

        if( this.bg == null ) return ; 

        for( let node of this.bg.children ) {
            node.active = false ; 
        } // end for

        let idx = n - 1 ; 
        if( idx < 0 || idx >= this.bg.children.length ) return ; 

        this.bg.children[ idx ].active = true ; 

    }

}