import { sys } from "cc";
import { Ad } from "../Ad";

// 
export class AdStartup {

    //  X 
    public static times = 2 ; 

    public static exe() {

        AdStartupData.ins.load() ; 
        let count = AdStartupData.ins.count ; 
        AdStartupData.ins.count++ ; 
        AdStartupData.ins.save() ; 

        if( count >= this.times ) {
            // Ad.ins.showInterstitial() ; 
        }

    }

}

class AdStartupData {

    public static ins = new AdStartupData() ;

    // 
    public count = 0 ; 

    public load() {

        let str = sys.localStorage.getItem( "AdStartupCount" ) as string ; 
        if( str == null || str == "" ) return ; 

        this.count = Number( str ) ; 

    }

    public save() {
        sys.localStorage.setItem( "AdStartupCount" , this.count ) ; 
    }

}
