import { math, Vec3 } from "cc";
import { Bullet } from "./Bullet";



export class BulletViewHelper {

    // 
    public static rotateDir( bullet : Bullet ) {

        // 
        let v = bullet.move.offset ; 
        let y = math.toDegree( Math.atan2( v.y , v.x ) ) ;
        bullet.view.root.eulerAngles = new Vec3( 0 , 0 , y ) ;

        // 
        let s = bullet.view.root.scale.clone() ; 
        if( v.x < 0 ) {
            if( s.y > 0 ) {
                s.y *= -1 ; 
                bullet.view.root.scale = s ;
            }
        }
        else {
            if( s.y < 0 ) {
                s.y = Math.abs( s.y ) ; 
                bullet.view.root.scale = s ;
            }
        }

       

    }

}