import { instantiate, Layout, Node, Size, UITransform, Vec2, Vec3 } from "cc";
import { CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { BagArticle, BagArticleView } from "./BagArticle";
import { PfMgrBagArticle } from "../../../Common/PfMgr/PfMgr";
import { UiBagCfg } from "../Main/UiBagCfg";
import { CfgShape } from "../../../Common/Cfg/CfgShape";
import { TowerFactory } from "../../World/1.Base/Tower/Entity/TowerFactory";


export class BagArticleFactory {

    public static create( id : string ) : BagArticle {

        let article = new BagArticle() ; 

        BagArticleCfgSet.set( article , id ) ; 

        BagArticleViewSet.set( article ) ;

        //  Tower 
        TowerFactory.create( article ) ;

        return article ; 

    }


}

// 
class BagArticleCfgSet {

    public static set( article : BagArticle , id : string ) {

        let cfg = CfgArticleMgr.data.get( id ) ; 
        article.cfg = cfg
        article.gradeMerge = cfg.mergeGrade ; 

        article.cfgShape = CfgShape.data.get( cfg.bagShapeId ) ;

        // 
        if( id.startsWith( "BagExpand" ) == true ) {
            article.isBagExpand = true ; 
        }

    }

}


// 
class BagArticleViewSet {

    public static set( article : BagArticle ) {

        let view = article.view = new BagArticleView() ; 

        let root = this.createView( article.cfg.id );

        view.root = root ; 
        view.main = root.getChildByPath( "Main" );

        // 
        view.posShapeCoordOrig = this.calcPos( article.cfgShape.coords ) ; 

        // 
        root.getComponent( Layout )?.destroy() ; 


        this.mergeGrade( article ) ;

        // 
        view.size = root.getComponent( UITransform ).getBoundingBoxToWorld().size ; 

    }


    public static createView( id : string ) : Node {

        // 
        let pf = PfMgrBagArticle.getPf( id ) ;
        let root = instantiate( pf ) ;
        return root ; 

    }

    // 
    private static calcPos( shape : Array< Vec2 > ) : Vec3 {

        let tileSize = UiBagCfg.ins.cellSize ;

        // 
        let size = new Vec2( -1 , -1 ) ;
        for( var coord of shape ) {
            if( coord.x > size.x ) {
                size.x = coord.x ; 
            }

            if( coord.y > size.y ) {
                size.y = coord.y ; 
            }

        }
        size.x += 1 ; 
        size.y += 1 ; 

        // 
        let pos = new Vec3() ; 
        pos.x = size.x * tileSize.width / -2 + tileSize.width / 2 ;
        pos.y = size.y * tileSize.height / -2 + tileSize.height / 2

        // // 
        // pos.x += coordOrig.x * tileSize.width ; 
        // pos.y += coordOrig.y * tileSize.height ; 

        return pos ;

    }

    // 
    private static mergeGrade( article : BagArticle ) {

        let key = "MergeGrade/Grade" + article.gradeMerge ; 
        let pf = PfMgrBagArticle.bagArticle.getChildByPath( key ) ; 
        if( pf == null ) return ; 

        let node = instantiate( pf ) ;
        node.setParent( article.view.root , false ) ;
        node.position = Vec3.ZERO ; 

        switch( article.cfgShape.id ) {
            case "Dot" : {
                node.position = new Vec3( 25 , -20 ).multiplyScalar( 1.5 ) ; 
            }
            break ; 

            case "H2" : {
                node.position = new Vec3( 60 , -20 ).multiplyScalar( 1.5 ) ; 
                
            }
            break ; 

            case "H3" : {
                // node.position = new Vec3( 25 , -20 ).add3f( 60 * 2 , 0 , 0 ) ; 
            }
            break ; 

            case "V2" : {
                node.position = new Vec3( 25 , -60 ).multiplyScalar( 1.5 ) ; 
            }
            break ; 

            case "V3" : {
                node.position = new Vec3( 25 , -100 ).multiplyScalar( 1.5 ) ; 
            }
            break ; 

            case "R2x2_3_2" : {
                node.position = new Vec3( -10 , -60 ).multiplyScalar( 1.5 ) ; 
            }
            break ; 

            case "R2x2_3_1" : 
            case "R2x2_3_3" : 
            case "R2x2_3_4" : 
            case "R2x2" : 
            {
                node.position = new Vec3( 60 , -60 ).multiplyScalar( 1.5 ) ; 
            }
            break ;

            case "T" : {
                node.position = new Vec3( 25 , -60 ).multiplyScalar( 1.5 ) ; 
            }   
            break ; 


        }

    }

}