import { Input } from "cc";
import { Button } from "cc";
import { Node } from "cc";
import { Sound } from "./Sound";

//  
export class SoundBtnClickSetter {

    public static exe( root : Node ) {

        let btns = root.getComponentsInChildren( Button ) ;
        // let t = root.getComponent( Button ) ; 
        // if( t != null ) { btns.push( t ) ; }
        for( let btn of btns ) {

            // 
            btn.node.off( Input.EventType.TOUCH_END , this.play , this ) ; 
            btn.node.on( Input.EventType.TOUCH_END , this.play , this ) ; 

            // btn.node.off( Input.EventType.TOUCH_END , Sound.ins.btnClick , Sound.ins ) ; 
            // btn.node.on( Input.EventType.TOUCH_END , Sound.ins.btnClick , Sound.ins ) ; 

        } // end for

        // this.remove( root ) ; 

    }

    public static remove( root : Node ) {

        let btns = root.getComponentsInChildren( Button ) ;
        // let t = root.getComponent( Button ) ; 
        // if( t != null ) { btns.push( t ) ; }
        for( let btn of btns ) {
            btn.node.off( Input.EventType.TOUCH_END , this.play , this ) ; 

            // btn.node.off( Input.EventType.TOUCH_END , Sound.ins.btnClick , Sound.ins ) ; 

        } // end for

    }

    public static play() {

        Sound.ins.btnClick() ; 
        
    }

}