import { _decorator, Component, Node } from 'cc';

const { ccclass, property } = _decorator;

export class CfgGiftGroup{

    //ItemNumID Num
    public Id:string;

    public ItemsId:Array<number>=new Array<number>();

    public ItemsNum:Array<number>=new Array<number>();
}

export class CfgGiftGroupMgr{
    public static data:Map< string , CfgGiftGroup > = new Map< string , CfgGiftGroup >() ;

}

export class CfgGiftGroupParser{
    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) return ; 
            
            let cfgItem = new CfgGiftGroup() ;

            CfgGiftGroupMgr.data.set( id , cfgItem ) ;

            cfgItem.Id = id ;

            // let str : string = null ; 

            let str= row["ItemsId"];
            if(str!=null&&typeof str==="number"){
                cfgItem.ItemsId.push(Number(str));
            }
            if (str !=""&&typeof str==="string") {
                let strs = str.split( /[|\n]/ ) ;
                for(let s in strs){
                    let num = Number(s);
                    cfgItem.ItemsId.push(num);
                }     
            }


            str = row["ItemsNum"];
            if(str!=null&&typeof str==="number"){
                cfgItem.ItemsNum.push(Number(str));
            }
            if (str !="") {
                let strs = str.split( /[|\n]/ ) ;
                cfgItem.ItemsNum.push(...strs.map(str => Number(str)));
            }
        } 

    }
}


