import { director, Input, Node } from "cc";
import { UiLevel } from "./UiLevel";
import { UiLeft } from "./UiLeft";
import { UiRight } from "./UiRight";

// 
export class UiBattle {

    public static ins = new UiBattle() ; 

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        UiLevel.ins = new UiLevel() ; 
        UiLevel.ins.init( this.root.getChildByPath( "Level" ) ) ;

        UiLeft.ins.init(this.root.getChildByPath( "Left" ));

        UiRight.ins.init(this.root.getChildByPath("Right"));

    }

}