import { Vec3 } from "cc";
import { BagArticle } from "../Article/BagArticle";
import { UiTray } from "../Main/UiTray";
import { BagArticleGridPlace } from "../Article/BagArticleGridPlace";
import { MergeDefinition } from "./MergeDefinition";
import { BagArticleFactory } from "../Article/BagArticleFactory";
import { MergeEffect } from "./MergeEffect";


export class MergeTray {

    // 
    public static tryMerge( article : BagArticle ) : boolean {

        let article2 = this.getTarget( article ) ; 
        if( article2 == null ) return false ; 

        let id = MergeDefinition.ins.getMergedId( article.cfg.id , article2.cfg.id ) ;
        if( id == null ) {
            id = MergeDefinition.ins.getMergedIdhybrid( article.cfg.id , article2.cfg.id ) ;
            if( id == null ) {
                return false ; 
            }
            
        }
        let articleNew = BagArticleFactory.create( id ) ; 
        articleNew.view.root.setParent( article2.view.root , false );
        articleNew.view.root.worldPosition = article2.view.root.worldPosition ; 

        // 
        BagArticleGridPlace.unbind( article2 ) ; 
        UiTray.ins.remove( article2 ) ;
        article2.view.root.destroy() ; 

        // 
        BagArticleGridPlace.unbind( article ) ; 
        UiTray.ins.remove( article ) ;
        article.view.root.destroy() ; 

        UiTray.ins.insertArticle( articleNew ) ; 

        MergeEffect.merge( articleNew ) ;
        
        // 

        return true ; 

    }

    // 
    public static getTarget( article : BagArticle ) : BagArticle {

        for( let article2 of UiTray.ins.articles ) {

            if( article == article2 ) continue ; 

            let v = article2.view.root.worldPosition.clone().subtract( article.view.root.worldPosition ) ;
            let len = v.lengthSqr() ; 
            if( len <= 150 * 150 ) {

                return article2 ;
            }
        }

        return null ; 

    }    


}