//  
export class GamePause {

    // 
    public static pause() {

        // 
        find( "Entry" ).active = false ; 

        // 
        TimeScale.set( 1 ) ; 

    }

    // 
    public static resume() {

        find( "Entry" ).active = true ; 

        TimeScale.set( Player.ins.playSpeed ) ; 

    }

}