import { CircleCollider2D, Collider2D, Contact2DType, director, instantiate, IPhysics2DContact, isValid, randomRangeInt, tween, UITransform, Vec3 } from "cc";
import { Bullet, Sender } from "../Bullet/Bullet";
import { Role } from "../Role/Base/Role";
import { BulletMgr } from "../Bullet/BulletMgr";
import { DamageValueShow, RoleGetDamage } from "../Role/Misc/RoleGetDamage";
import { RoleMgr } from "../Role/Base/RoleMgr";
import { BulletMoveCalc } from "../Bullet/BulletMoveCalc";
import { BuffOp } from "../Buff/BuffOp";
import { FrameSecConvert } from "../../../../Common/Misc/FrameSecConvert";
import { RoleMoveSys } from "../Role/Misc/RoleMoveSys";
import { PlayerRole } from "../../2.Td/Player/PlayerRole";
import { AffixType } from "../Buff/Affix";
import { CfgBuff, CfgBuffItem } from "../../../../Common/Cfg/CfgBuff";
import { BulletFactory } from "../Bullet/BulletFactory";
import { WorldNode } from "../Support/Misc/WorldNode";
import { EffectFactory } from "../../../Bag/Merge/EffectFactory";
import { FrameCall, FrameCallDataItem } from "../Support/Misc/FrameCall";
import { BulletPfMgr } from "../Bullet/BulletPfMgr";
import { RoleAttributeUpdate } from "../Role/Misc/RoleAttributeUpdate";
import { BuffIncreaseCalc, DataIncrease } from "../Buff/BuffIncreaseCalc";




//   
export class CollisionBullet {

    public static onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {

        let bullet = selfCollider[ "Bullet" ] ; 

        let role = otherCollider[ "Role" ] ; 
        if( role != null ) {
            this.bulletAndRole( bullet , role ) ;
        }

    }

    public static bulletAndRole( bullet : Bullet , role : Role ) {

        console.log( "" );

        // 
        if( bullet.target.targetRole == role ) {
            bullet.target.targetRole = null ; 
        }

        // buff
        this.buff( bullet , role ) ; 

        // 
        this.damage( bullet , role ) ; 

        // 
        this.bounce( bullet , role ) ; 

        // 
        this.pass( bullet ) ; 

    }

    // 
    public static pass( bullet : Bullet ) {

        bullet.passCount-- ; 
        if( bullet.passCount < 0 ) {
            bullet.life.hp.current = 0 ; 
        }

    }


    // 
    private static damage( bullet : Bullet , role : Role ) {

        // 
        let d = bullet.damge ; 

        // 
        let d2 = this.crit( bullet ) ;
        let hasCrit = d2 != d ;
        d = d2 ; 

        // 
        RoleGetDamage.exe( role , d , !hasCrit , bullet.sender ) ; 

        if( hasCrit ) {
            // 
            DamageValueShow.crit( role.view.root.worldPosition.clone().add3f( 0 , 50 , 0 ) , d ) ;
        }

        // 
        this.lifeStealing( bullet , d ) ;

    }

    // 
    private static crit( bullet : Bullet ) : number {

        let d = bullet.damge ; 

        let crit = bullet.crit ;
        if( crit == null ){
            // 
            return d ; 
        }

        let prob = bullet.crit.probability ; 
        if( prob <= 0 ) {
            // 
            return d ; 
        }
            
        // 
        prob = prob * 100 ;
        let n = randomRangeInt( 0 , 100 ) ; 
        if( n < prob ) {
            // 
            d = d * bullet.crit.multiple ; 
        }

        return d ;

    }

    // 
    private static lifeStealing( bullet : Bullet , damage : number ) {

        let ls = bullet.lifeStealing ;
        if( ls == null || ls.percent == 0 ) return ; 

        let n = damage * ls.percent ;

        PlayerRole.ins.main.life.add( n ) ;
        PlayerRole.ins.main.view.hpBar.refresh() ; 

    }

    // 
    private static bounce( bullet : Bullet , role : Role ) {

         // 
        if( bullet.bounce.bounceBetweenEnemies.count > 0 ) {

            bullet.bounce.bounceBetweenEnemies.count-- ; 

            bullet.bounce.bounceBetweenEnemies.record.add( role ) ;
    
            // 
            bullet.target.targetRole = this.findNearest( bullet.view.root.position , bullet.bounce.bounceBetweenEnemies.record ) ; 
            BulletMoveCalc.target( bullet ) ;

            // 
            bullet.passCount++ ; 

            return ; 

        } 

        if( bullet.bounce.bounceEnemy > 0 ) {
            bullet.bounce.bounceEnemy-- ;
        }
        else if( bullet.bounce.bounceRectEnemy > 0 ) {
            bullet.bounce.bounceRectEnemy-- ;
        }
        else {
            return ; 
        }

        // 
        let rect = role.view.main.getComponent( UITransform ).getBoundingBox() ; 

        let pos = bullet.view.root.getComponent( UITransform ).convertToWorldSpaceAR( Vec3.ZERO ) ; 
        role.view.main.parent.getComponent( UITransform ).convertToNodeSpaceAR( pos , pos ) ; 

        //  Rect  
        let len1 = Math.abs( pos.y - rect.yMax ) ; 
        let len2 = Math.abs( pos.y - rect.yMin ); 
        let len3 = Math.abs( pos.x - rect.xMin ); 
        let len4 = Math.abs( pos.x - rect.xMax ); 

        if( ( len3 < len1 && len3 < len2 && len3 < len4 ) || ( len4 < len1 && len4 < len2 && len4 < len3 ) ) {
            bullet.move.offset.x *= -1 ; 
            bullet.move.direction.x *= -1 ; 

            bullet.bounce.bounceRect-- ; 

            // 
            bullet.passCount++ ; 

        }
        if( ( len1 < len2 && len1 < len3 && len1 < len4 ) || ( len2 < len1 && len2 < len3 && len2 < len4 ) ) {
            bullet.move.offset.y *= -1 ; 
            bullet.move.direction.y *= -1 ; 

            bullet.bounce.bounceRect-- ; 

            // 
            bullet.passCount++ ; 

        }

    }

    private static dataIncr = new DataIncrease() ; 

    // BuffRoleBuff 
    public static buff( bullet : Bullet , role : Role ) {

        this.dataIncr.reset() ; 

        BuffIncreaseCalc.exeBuffs( bullet.buffs , this.dataIncr );

        this.AttackBulletProbabilityAddBuff( bullet , role , this.dataIncr ) ; 
        this.AttackDamageIncreasePercentFullHp( bullet , role , this.dataIncr ) ; 


        for( let i = 0 ; i < bullet.buffIds.length ; ++i ) {

            let buffId = bullet.buffIds[ i ] ;
            let cfgBuffItem = CfgBuff.data.get( buffId ) ;
            if( cfgBuffItem == null ) continue ; 

            switch( cfgBuffItem.affixType ) {

                case AffixType.Knockback : {
                    // 
                    let dir = bullet.move.direction.clone() ; 
                    RoleMoveSys.ins.add( role , 0.2 , 80 , dir ) ;
                }
                break ;

                case AffixType.PoisoningPercent : {
                    this.PoisoningPercent( bullet , role , buffId ) ; 
                }
                break ;

                case AffixType.Explode : {
                    this.Explode( bullet , role , cfgBuffItem ) ; 
                }
                break ; 

                case AffixType.AttackBulletAddBuffRadius : {
                    this.AttackBulletAddBuffRadius( bullet , role , cfgBuffItem ) ; 
                }
                break ; 

                default : {

                    let ret = BuffOp.roleAdd( role , buffId ) ;
                    ret[1].sender = bullet.sender ; 

                }
                break ; 

            }

        } // end for

    }

    // Buff
    private static AttackBulletProbabilityAddBuff( bullet : Bullet , role : Role , dataIncr : DataIncrease ) {

        // Buff
        for( let kv of this.dataIncr.AttackBulletProbabilityAddBuff ) {
            let prob = kv[ 1 ] ; 
            let id = kv[ 0 ] ; 

            let n = randomRangeInt( 0 , 100 ) ;
            if( n < prob * 100 ) {
                let ret = BuffOp.roleAdd( role , id ) ;
                ret[1].sender = bullet.sender ; 
            }

        } // end for

    }

    // 
    private static AttackDamageIncreasePercentFullHp( bullet : Bullet , role : Role , dataIncr : DataIncrease ) {

        if( this.dataIncr.AttackDamageIncreasePercentFullHp > 0 && role.life.hp.current == role.life.hp.max ) {
            let d = bullet.damge ; 
            d *= 1 + this.dataIncr.AttackDamageIncreasePercentFullHp ; 
            bullet.damge = d ; 

        }

    }

    //  
    private static PoisoningPercent( bullet : Bullet , role : Role , buffId : string ) {

        // 
        let ret = BuffOp.roleAdd( role , buffId ) ;
        let isNew = ret[ 0 ] ;
        let buff = ret[ 1 ] ; 
        if( isNew == false ) return ; 
            
        buff.sender = bullet.sender ; 

        let d = bullet.damge * buff.affix.argN ; 

        if( this.dataIncr.AttackBulletPoisoningDamageIncreasePercent > 0 ) {
            // 
            d *= 1 + this.dataIncr.AttackBulletPoisoningDamageIncreasePercent ; 
        }

        if( this.dataIncr.AttackBulletPoisoningDurationIncreasePercent > 0 ) {
            // 
            let max = buff.duration.max ; 
            max *= 1 + this.dataIncr.AttackBulletPoisoningDurationIncreasePercent ; 
            buff.duration.setCurrentAndMax( max ) ; 
        }

        d = Math.round( d ) ; 
        buff.affix.argN = d ; 

    }

    // 
    public static Explode( bullet : Bullet , role : Role , cfgBuffItem : CfgBuffItem ) {

        let pos = bullet.view.root.worldPosition ;

        let fn = ()=>{

            let explode = BulletFactory.create( "Explode" ) ; 
            explode.view.root.setParent( WorldNode.ins.layer2 , false ) ;
            explode.view.root.worldPosition = pos ; 
            explode.Lifecycle = FrameSecConvert.toFrame( 0.1 ) ; 
            explode.damge = bullet.damge * cfgBuffItem.affixArgN ; 
            explode.sender = bullet.sender ;
            BulletMgr.ins.add( explode ) ; 

            let collider = explode.view.root.getComponentInChildren( CircleCollider2D ) ; 

            // 
            if( cfgBuffItem.affixArgNList != null ) {
                let r = cfgBuffItem.affixArgNList[ 1 ] ;
                collider.radius = r ;
            }

            // 
            if( this.dataIncr.AttackBulletExplodeRadiusIncreasePercent > 0 ) {
                collider.radius *= 1 + this.dataIncr.AttackBulletExplodeRadiusIncreasePercent ; 
            }

            // 
            if( this.dataIncr.AttackBulletExplodeDamageIncreasePercent > 0 ) {
                explode.damge *= 1 + this.dataIncr.AttackBulletExplodeDamageIncreasePercent ; 
            }

            // 
            let id = cfgBuffItem.affixArgStr ; 
            if( id == null ) {
                id = "Explore" ; 
            }
            let effect = EffectFactory.create( id ) ; 
            effect.setParent( WorldNode.ins.layer2 , false ) ;
            effect.worldPosition = pos ; 
            effect.scale = explode.view.root.scale ; 

        } ;
        FrameCall.ins.once( fn ) ;

    }

    //  Buff
    public static AttackBulletAddBuffRadius( bullet : Bullet , role : Role , cfgBuffItem : CfgBuffItem ) {

        let parent = bullet.view.root.parent ;
        let pos = bullet.view.root.position.clone() ; 

        let fn = ()=>{

            let r = cfgBuffItem.affixArgN ; 
            let buffId = cfgBuffItem.affixArgStr ; 

            let pf = BulletPfMgr.ins.root.getChildByPath( "Collider/Circle" ) ; 
            let node = instantiate( pf ) ; 
            node.name = "AttackBulletAddBuffRadius" ; 
            node.setParent( parent , false ) ; 
            node.position = pos ; 

            let onbeginContact = ( selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null )=>{

                let role = otherCollider[ "Role" ] ; 
                if( role == null ) return ; 

                let ret = BuffOp.roleAdd( role , buffId ) ;
                if( ret[ 0 ] == false ) return ;

                let buff = ret[ 1 ] ;
                buff.affix.argN += this.dataIncr.AttackBulletAddBuffRadiusBuffIncreasePercent ;

                buff.sender = bullet.sender ; 

                switch( buff.affix.type ) {
                    case AffixType.PoisoningPercent : {
                        let d = bullet.damge * buff.affix.argN ; 
                        d = Math.round( d ) ; 
                        buff.affix.argN = d ; 
                    }
                    break ;
                }

                RoleAttributeUpdate.exe( role ) ;

            } ;

            // 
            if( this.dataIncr.AttackBulletAddBuffRadiusIncreasePercent > 0 ) {
                r *= 1 + this.dataIncr.AttackBulletAddBuffRadiusIncreasePercent ; 
            }
            let collider = node.getComponent( CircleCollider2D ) ;
            collider.radius = r ; 
            collider.on(Contact2DType.BEGIN_CONTACT, onbeginContact );

            FrameCall.ins.once( ()=>{

                node.destroy() ; 

            } , 5 ) ; 

        } ;
        FrameCall.ins.once( fn ) ; 

    }

    // 
    public static findNearest( pos : Vec3 , ignore : Set< Role > = null ) {

        let len = Number.MAX_VALUE ; 
        let role : Role = null ; 
        for( let role2 of RoleMgr.ins.allEnemies ) {

            if( ignore != null ) {
                if( ignore.has( role2 ) ) {
                    continue ; 
                }
            }

            let v = role2.view.root.position.clone().subtract( pos ) ;
            let len2 = v.lengthSqr() ;
            if( len > len2 ) {
                len = len2 ; 
                role = role2 ; 
            }

        } // end for

        return role ; 

    }
    

}

