import { Input, Label, Node, RichText } from "cc";
import { ArticleString } from "../../../Common/String/ArticleString";
import { UiEffectScale } from "../../../SL/Cocos/Ui/UiEffect";
import { BagArticle } from "../Article/BagArticle";
import { UiArticleDetailAttribute } from "./UiArticleDetailAttribute";
import { UiArticleDetailIcon } from "./UiArticleDetailIcon";

// 
export class UiArticleDetail {

    public static ins = new UiArticleDetail() ; 

    public root : Node

    public name : Label ; 

    public icon : UiArticleDetailIcon ; 


    public gradeMerge : Label ; 

    public intro : RichText ; 

    public attribute : UiArticleDetailAttribute ; 


    public init( node : Node ) {

        this.root = node ; 
        this.root.active = false ; 

        let btnClose = this.root.getChildByPath( "btnClose" ) ;
        btnClose.on( Input.EventType.TOUCH_END , ()=>{
            this.root.active = false ; 
        } ) ; 

        this.name = this.root.getChildByPath( "Name/val" ).getComponent( Label ) ;

        this.gradeMerge = this.root.getChildByPath( "MergeGrade" ).getComponent( Label ) ;

        this.icon = new UiArticleDetailIcon() ; 
        this.icon.init( this.root.getChildByPath( "Icon" ) ) ;

        this.intro = this.root.getChildByPath( "Intro" ).getComponent( RichText ) ;

        this.attribute = new UiArticleDetailAttribute() ; 
        this.attribute.init( this.root.getChildByPath( "Info/Attribute" ) ) ;
        
    }

    public show() {

        this.root.active = true ; 

        UiEffectScale.show( this.root ) ;

    }

    public refresh( article : BagArticle ) {

        let cfg = article.cfg ; 
        let id = cfg.id ; 

        // 
        this.name.string = ArticleString.getName( id ) ;  

        // 
        this.icon.refresh( article ) ; 

        // 
        this.gradeMerge.string = " lv." + cfg.mergeGrade ;

        // 
        this.intro.string = "<br>" + ArticleString.getDescription( id ) + "</br>" ;  

        // this.attribute.cfgArtile = cfg ; 
        this.attribute.article = article ;
        this.attribute.refresh() ; 

    }

}


