import { CfgBuff } from "../../../../Common/Cfg/CfgBuff";
import { Lang } from "../../../../Common/Misc/Lang";
import { ArticleString } from "../../../../Common/String/ArticleString";


export class BuffString {

    public static getDescription( id : string ) : string {

        console.log( "BuffString.getDescription " + id ) ; 

        let str = this.get1( id ) ;
        if( str != null ) return str ; 

        str = this.get2( id ) ;
        if( str != null ) return str ; 

        return null ;

    }

    // 1+ID 
    private static get1( id : string ) {

        let key = "Buff@" + id ; 
        let str = Lang.get( key ) ;
        if( str == null || str == "" ) return null ;

        str = this.replace( id , str ) ; 

        return str ; 

    }

    // 2
    private static get2( id : string ) {

        let cfg = CfgBuff.data.get( id ) ;

        let name : string = null ; 

        let key = "Buff@" ; 
        if( cfg.specified != null ) {
            key += "Specified-" ;
        }
        else {
            key += "General-" ;
        }

        key += cfg.affixType.toString()  ;

        let str = Lang.get( key ) ;
        if( str == null ) { 
            // console.log( "Buff" ) ;
            return str ; 
        }

        str = this.replace( id , str ) ; 

        return str ; 

    }

    private static replace( buffId : string , str : string ) : string {

        let cfg = CfgBuff.data.get( buffId ) ;

        if( cfg.specified != null ) {

            let id2 : string = null ; 
            for( let v of cfg.specified ) {
                id2 = v ;
                break ; 
            }

            // let id2 = [...cfg.specified][0] ;
            let name = ArticleString.getName( id2 ) ; 
            // 
            str = str.replace( "{name}" , name ) ;

        }


        let val = cfg.affixArgN ;
        // 
        val = Math.abs( val ) ; 
        // 
        if( buffId.indexOf( "Percent" ) != -1 || 
            buffId.indexOf( "Probability" ) != -1 ) {
            val *= 100 ;
        }
        str = str.replace( "{1}" , val.toString() ) ;

        return str ; 

    }

}