import { tween, Vec2 } from "cc";
import { GridTraverse } from "../Grid/GridTraverse";
import { UiBagCell } from "../Grid/UiBagCell";
import { BagArticle } from "./BagArticle";
import { UiBagGrid } from "../Grid/UiBagGrid";
import { MergeGrid } from "../Merge/MergeGrid";
import { UiTray } from "../Main/UiTray";
import { GridHint } from "../Grid/GridHint";
import { BagArticleGridPlace } from "./BagArticleGridPlace";
import { GridModeTransition } from "../Grid/GridModeTransition";
import { UiBagTouch } from "../Touch/UiBagTouch";
import { MergeTray } from "../Merge/MergeTray";
import { UiBubble } from "../../../Common/Ui/UiBubble";
import { UiMain } from "../../Ui/Main/UiMain";
import { BagArticleSpeak } from "./BagArticleSpeak";

// 
export class BagArticleGridPlaceByPos {

    public static exe( article : BagArticle ) {

        if( article.isBagExpand ) {

            this.placeBagExpand( article ) ;

            // return ;
        }

        let cells = this.getCellsPlace( article ) ;
        if( cells == null ) {
            // 

            // 
            if( MergeTray.tryMerge( article ) == true ) {
                return ; 
            }

            UiTray.ins.insertArticle( article ) ; 

            // 
            if( article.isBagExpand ) {
                GridModeTransition.toNormal() ; 
                UiBagTouch.ins.limit.clear() ; 
            }

            return ; 
        }

        // 

        GridHint.hintPlaceableClear() ; 

        // 
        UiTray.ins.removeAndArrange( article ) ; 

        // 
        if( MergeGrid.tryMerge( article , cells ) == true ) {
            return ; 
        }

        
        this.goToTray( cells , article ) ;
        BagArticleGridPlace.exe( article , cells ) ; 

        // 
        BagArticleSpeak.place( article ) ; 

       

    }

    // 
    private static placeBagExpand( article : BagArticle ) {

        // let cells = this.getCellsPlace( article ) ;

        // UiBagGrid.ins.showNormal() ; 

        // // 
        // UiTray.ins.insertArticle( article ) ; 

    }

    // 
    private static goToTray( cells : Array< UiBagCell > , articleIgnore : BagArticle ) {

        let list = new Set< BagArticle >() ; 
        for( let cell of cells ) {
            if( cell.article == null ) continue ; 
            list.add( cell.article ) ;

        } // end for

        for( let a of list ) {
            if( a == articleIgnore ) continue ; 
            UiTray.ins.insertArticle( a ) ; 
        }

    }

    // 
    public static getCellsPlace( article : BagArticle ) : Array< UiBagCell > {

        // 
        let cell = this.matchCell( article ) ;
        if( cell == null ) return null ; 
        
        // 
        let cells = this.getShapeCells( cell.coord , article.cfgShape.coords ) ;
        if( cells == null ) return ; 
            
        // 
        for( let cell of cells ) {
            if( cell.unlocked == article.isBagExpand ) {
                return null ; 
            }
        }

        return cells ; 

    }

    //   
    private static matchCell( article : BagArticle ) : UiBagCell {

        let pos = article.view.root.worldPosition.clone().add( article.view.posShapeCoordOrig ) ; 

        let lenMin = Number.MAX_VALUE ; 
        let cellRet : UiBagCell = null ; 
        GridTraverse.exe( ( cell )=>{

            let v = cell.root.worldPosition.clone().subtract( pos ) ;
            let len = v.lengthSqr() ; 
            if( len < 200 * 200 && len < lenMin ) {
                lenMin = len ;
                cellRet = cell ; 
            }

        } ) ;

        return cellRet ; 

    }

    // 
    private static getShapeCells( coordStart : Vec2 , shape : Array< Vec2 > ) : Array< UiBagCell > {

        let ret = new Array< UiBagCell >() ;

        for( let coord of shape ) {

            let coord2 = coordStart.clone().add( coord ) ; 
            let cell2 = UiBagGrid.ins.getCell( coord2 ) ;
            if( cell2 == null ) {
                return null ; 
            }

            ret.push( cell2 ) ; 

        }

        return ret ; 

    }

}