import { Rect, UITransform } from "cc";
import { WorldNode } from "../Support/Misc/WorldNode";

export class BulletBounceRect {

    public static ins = new BulletBounceRect() ; 

    public rect : Rect ; 

    public init() {

        let node = WorldNode.ins.root.getChildByPath( "Cfg/BulletBounceRect" ) ; 
        this.rect = node.getComponent( UITransform ).getBoundingBox() ; 

        node.destroy() ; 

    }

}