import { randomRangeInt } from "cc";
import { Bullet } from "../../Bullet/Bullet";
import { BulletMoveCalc } from "../../Bullet/BulletMoveCalc";
import { Role } from "../../Role/Base/Role";
import { RoleMgr } from "../../Role/Base/RoleMgr";
import { AttackTargetSelectionType } from "../Entity/AttackCom";
import { Tower } from "../Entity/Tower";



// 
export class TowerAttackTargetSelect {

    public static exe( tower : Tower , bullet : Bullet ) {

        let atk = tower.attack ;

        switch( atk.targeSelType ) {
            case AttackTargetSelectionType.First : {
                bullet.target.targetRole = this.first() ; 
            }
            break ; 

            case AttackTargetSelectionType.Rnd : {
                bullet.target.targetRole = this.rnd() ;
            }
            break ;

        }

        // 
        BulletMoveCalc.target( bullet ) ;

    }

    public static first() : Role {

        let role : Role = null ; 
        let len = -Number.MIN_VALUE ; 
        for( let role2 of RoleMgr.ins.allEnemies ) {
            let len2 = role2.ai.pathMoved.lenMoved ; 
            if( len2 > len ) {
                len = len2 ; 
                role = role2 ; 
            }

        } // end for

        return role ; 

    }

    public static rnd() : Role {

        let list = RoleMgr.ins.allEnemies ; 
        let idx = randomRangeInt( 0 , list.length ) ; 
        return list[ idx ] ;

    }

}