import { director, Input, instantiate, Node, Prefab, resources } from "cc";
import { GamePause } from "../../World/2.Td/Flow/GamePause";
import { SceneTransition } from "../../../Common/Ui/UiLoadingScene";
import { SoundBtnClickSetter } from "../../../Common/Audio/SoundBtnClickSetter";
import { UiSettings } from "../../../Common/Ui/UiSettings";
import { UiMain } from "../Main/UiMain";
import { UiFeature } from "../Main/Feature/UiFeature";


export class UiPause {

    public static pf : Prefab ;

    public static loadAndCreate( parent : Node , cb : ( ui : UiPause )=>void ) {

        resources.load( "Game/Ui/Pause" , Prefab , ( err , data )=>{

            this.pf = data ;

            let root = instantiate( this.pf ) ;
            let ui = new UiPause() ; 
            ui.init( root ) ;
            ui.root.setParent( parent , false ) ;
            cb( ui ) ;

        } ) ;

    }


    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        let btnHome = this.root.getChildByPath( "BtnList/btnHome" ) ;
        btnHome.on( Input.EventType.TOUCH_END , ()=>{
            // director.loadScene( "Home" ) ; 
            SceneTransition.exe( "Home" ) ; 
        } ) ; 

        let btnContinue = this.root.getChildByPath( "BtnList/btnContinue" ) ;
        btnContinue.on( Input.EventType.TOUCH_END , ()=>{
            GamePause.resume() ; 
            this.root.destroy() ; 
        } ) ; 

        let btnSettings = this.root.getChildByPath( "BtnList/btnSettings" ) ;
        btnSettings.on( Input.EventType.TOUCH_END , ()=>{
            UiSettings.create( UiMain.ins.root ).show() ; 
        } ) ; 


        SoundBtnClickSetter.exe( this.root ) ; 

    }

    public refresh() {

        let content = this.root.getChildByPath( "Feature/sv/view/content" ) ;

        // 
        for( let item of UiFeature.ins.items ) {
            let node = instantiate( item.root ) ; 
            node.setParent( content ) ; 
        }

    }

}