import { Node, Size, sp, Sprite, UITransform, Vec3 } from "cc";
import { NodeSizeFit } from "../../../SL/Cocos/Ui/NodeSizeFit";
import { PfMgrBagArticle } from "../../../Common/PfMgr/PfMgr";
import { BagArticle } from "../Article/BagArticle";

// 
export class UiArticleDetailIcon {

    public root : Node ;
    public icon : Sprite ; 
    public iconSk : sp.Skeleton ; 

    public init( node : Node ) {

        this.root = node ; 

        this.icon = this.root.getChildByPath( "Icon" ).getComponent( Sprite ) ;
        this.iconSize = this.icon.getComponent( UITransform ).contentSize.clone() ; 
        this.iconSk = this.root.getChildByPath( "IconSk" ).getComponent( sp.Skeleton ) ;

    }

    public refresh( article : BagArticle ) {

        this.icon.node.active = false ; 
        this.iconSk.node.active = false ; 

        let id = article.cfg.id ; 

        // 
        let pf = PfMgrBagArticle.getPf( id ) ;
        let main = pf.getChildByPath( "Main" ) ; 
        let spr = main.getComponent( Sprite ) ; 
        if( spr != null ) {
            this.icon.node.active = true ; 
            this.icon.spriteFrame = spr.spriteFrame ; 
            NodeSizeFit.set2( this.icon.node , this.iconSize , this.icon.spriteFrame.originalSize ) ;
            return ; 
        }

        this.refreshSkeleton( main ) ; 

    }

    public refreshSkeleton( main : Node ) {

        let sk = main.getComponent( sp.Skeleton ) ; 
        if( sk == null ) return ; 

        this.iconSk.node.active = true ; 

        // 
        this.iconSk.skeletonData = sk.skeletonData ; 

        // 
        let name = this.iconSk.skeletonData.getRuntimeData().animations[ 0 ].name ; 
        this.iconSk.setAnimation( 0 , name , true );

        // 
        let node = this.iconSk.node ; 
        // let node = instantiate( main ) ;
        // node.scale = Vec3.ONE ; 
        let rect = node.getComponent( UITransform ).getBoundingBox() ; 
        node.setParent( this.icon.node.parent ) ; 
        NodeSizeFit.set2( node , this.iconSize , this.icon.spriteFrame.originalSize ) ; 

        // 
        node.position = new Vec3( 0 , -rect.height / 2 , 0 ) ; 

    }

    private iconSize : Size ; 

}
