// 
export class UiLevelBtnList {

    public uiLevel : UiLevel ; 

    public root : Node ; 

    public btnStart = new UiBtnStart() ; 

    public lock : Node ; 
    public lockTxt : Label ; 

    public btnGrinding = new UiBtnGrinding() ; 

    public init( node : Node ) {

        this.root = node ;

        this.btnStart.init( this.root.getChildByPath( "btnStart" ) ) ;
        this.btnStart.refresh() ; 

        this.lock = this.root.getChildByPath( "Lock" ) ; 
        this.lockTxt = this.lock.getComponentInChildren( Label ) ;

        this.btnGrinding.init( this.root.getChildByPath( "btnGrinding" ) ) ; 

    }

    public refresh( currentLevelIdx : number ) {

        if( currentLevelIdx < Account.ins.mainLevel.levelIdx ) {
            // 
            this.btnStart.root.active = true ; 

            this.lock.active = false ; 

            this.btnGrinding.root.active = true ; 
            this.btnGrinding.refresh() ; 

        }
        else if( currentLevelIdx == Account.ins.mainLevel.levelIdx ) {
            // 
            this.btnStart.root.active = true ; 

            this.lock.active = false ; 

            this.btnGrinding.root.active = false ; 
        }
        else {
            // 
            this.btnStart.root.active = false ; 

            this.lock.active = true ; 
            let str = "{1}" ;
            str = str.replace( "{1}" , currentLevelIdx.toString() ) ;
            this.lockTxt.string = str  ;

            this.btnGrinding.root.active = false ; 

        }
        
    }


}


//  
export class UiBtnStart {

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 
        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ;

        this.refresh() ; 

    }

    public refresh() {

        this.animScale() ; 

    }

    // 
    private animScale() {

        Tween.stopAllByTarget( this.root ) ; 
        
        let d = 0.5 ; 

        tween( this.root ).sequence(
            tween( this.root ).to( d , { scale : new Vec3( 1.1 , 1.1 , 1.1 ) } ) , 
            tween( this.root ).to( d , { scale : Vec3.ONE } ) ,
            tween( this.root ).delay( 0.5 ) , 
        ).repeatForever().start() ;  

    }

    public onClick() {

        // 
        let hasEmpty = Account.ins.bag.deploy.length < Account.ins.bag.deployCellUnlocked.size ; 
        if( hasEmpty ) {
            UiToast.show( UiMain.ins.root , Lang.get( "Home_Bag_Deploy_empty" ) ) ;
            return ;
        }


        // 
        let s = Account.ins.stamina ;
        let t = s.current - 5 ;
        if( t < 0 ) {
            // 

            UiStaminaBuy.create( UiMain.ins.root ).show() ; 

            UiToast.show( UiMain.ins.root , Lang.get( "Home_SiaminaNotEnough" ) ) ;

            return ;
        }
        s.current = t ;
        // 
        UiStamina.ins.refresh() ; 

        let uiLevel = UiLevel.ins ; 

        let cfgLevel = CfgLevelWaveMgr.main[ uiLevel.selLevelIdx ] ; 

        GameArg.ins.levelId = cfgLevel.id ; 
        GameArg.ins.mainIdx = uiLevel.selLevelIdx ; 

        SceneTransition.exe( "Game" ) ;

    }

}

// 
export class UiBtnGrinding {

    public root : Node ; 

    // 
    public leftToday : Label ;

    // 
    public adFlag : Node ; 

    public dailyLimit = new DailyLimit( "DailyLimit_Grinding" , 5 ) ; 

    public init( node : Node ) {

        this.dailyLimit.load() ; 

        this.root = node ; 

        this.leftToday = this.root.getChildByPath( "LeftToday" ).getComponent( Label ) ;

        this.adFlag = this.root.getChildByPath( "AdFlag" ) ; 

        this.refresh() ; 
        
        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ; 

    }


    public refresh() {

        this.leftToday.node.active = false ; 
        this.adFlag.active = false ; 

        if( this.dailyLimit.current <= 0 ) {

            this.adFlag.active = true ; 

            return ; 
        }

        this.leftToday.node.active = true ; 
        let str = Lang.get( "Home_LeftToday" ).replace( "{1}" , this.dailyLimit.current.toString() ) ;
        this.leftToday.string = str ; 

    }

    private onClick() {

        if( this.dailyLimit.current > 0 ) {
            this.dailyLimit.decrement() ; 
            this.dailyLimit.save() ; 

            this.refresh() ; 

            // 
            this.rewardGet() ; 

            return ; 

        }

        // 

        let onSuccess = ()=>{
            this.rewardGet() ; 
        } ;
        let onCancel = ()=>{

        } ;
        let onError = ()=>{

        } ; 
        Ad.ins.rewardShow( onSuccess , onCancel , onError ) ;

    }

    // 
    private rewardGet() {

        let levelId = UiLevel.ins.selLevelIdx.toString() ;

        let uiWin = UiWin.create( UiMain.ins.root ) ; 
        uiWin.show() ; 
        let id = "Level_" + levelId + "_Win" ; 
        let items = ItemDraw.exe( id ) ; 
        uiWin.itemList.refresh( items ) ; 
        uiWin.itemList.showAnim( 0.1 ) ; 
        // uiWin.itemList.showAnim() ; 
        ItemGet.addList( items ) ; 

        //  
        uiWin.btnOK.off( Input.EventType.TOUCH_END ) ; 
        uiWin.btnOK.on( Input.EventType.TOUCH_END , ()=>{
            Sound.ins.btnClick() ; 
            uiWin.root.destroy() ; 
        } ) ; 

        //  
        uiWin.btnShare.off( Input.EventType.TOUCH_END ) ; 

        // 
        UiMoney001.ins.refresh() ; 
        UiMoney002.ins.refresh() ; 

    }

}