import { Size, Sprite, UITransform } from "cc";

//  Sprite 
export class TextureOriginalRatioSprite {

    public static exe( spr : Sprite , sizeSpr : Size = null ) {

        let trans = spr.getComponent( UITransform ) ;

        if( sizeSpr == null ) {
            sizeSpr = trans.contentSize.clone() ;
        }
        else{
            sizeSpr = sizeSpr.clone() ;
        }

        let sizeOrig = spr.spriteFrame.originalSize ;

        let f = sizeOrig.width / sizeOrig.height ; 
        if( sizeOrig.width > sizeOrig.height ) {
            // 
            sizeSpr.height = sizeSpr.width / f ;
        }
        else if( sizeOrig.height > sizeOrig.width ) {
            sizeSpr.width = sizeSpr.height * f ;
        }
        trans.setContentSize( sizeSpr ) ;

    }

}