import { director, game, sys, tween } from "cc";


// 
export class DateTimeSync {

    // 
    public static count = 20 ;

    public static exe( cb : ( ok : boolean )=>void ) {

        DateTimeRequest.getFromSuNing( ( ok : boolean , date : Date )=>{

            if( ok == false ) {

                console.log( ",1" ) ;

                this.count-- ; 
                if( this.count <= 0 ) {
                    cb( false ) ; 
                    return ; 
                }

                tween( this ).delay( 1 ).call( ()=>{
                    this.exe( cb ) ; 
                } ).start() ; 

                return ; 
            }

            DateTimeMgr.ins = new DateTimeMgr() ; 
            DateTimeMgr.ins.dtStart = date ; 

            cb( true ) ; 

        } ) ;

    }

}

// 
export class DateTimeRequest {

    // API
    public static getFromSuNing( cb : ( ok : boolean , date : Date )=>void ) {

        if( sys.platform == sys.Platform.WECHAT_GAME ) {
            // http
            this.wxRequest( cb ) ;
            return ; 

        }
        else if( sys.platform == sys.Platform.BYTEDANCE_MINI_GAME )
        {
            // http
            this.ttRequest( cb ) ;
        }
        else {
            this.fetch( cb ) ; 
        }
        
        

    }

    // fetch 
    public static fetch( cb : ( ok : boolean , date : Date )=>void ) {

        /*
            http 
            https://docs.cocos.com/creator/manual/zh/advanced-topics/http.html?h=http

            fetch 
            https://segmentfault.com/a/1190000008484070?utm_source=tag-newest
            
        */ 
        let url = "https://quan.suning.com/getSysTime.do" ; 
        fetch( url ).then((response: Response) => {
            // return response.text() ; 
            return response.json()
        }).then((value) => {
            // console.log(value);

            let dt = new Date( value[ "sysTime2" ] ) ;
            console.log( "" + dt.toString() ) ; 
            
            cb( true , dt ) ; 

        }).catch( ( e )=>{

            console.log( "" ) ; 

            cb( false , null ) ; 

        } ) ;

    }

    // 
    public static wxRequest( cb : ( ok : boolean , date : Date )=>void ) {

        /*
            https://developers.weixin.qq.com/miniprogram/dev/api/network/request/wx.request.html
        
        */

        let url = "https://quan.suning.com/getSysTime.do" ; 

        // @ts-ignore
        wx.request({
            url: url , 
            success (res) {
                console.log(res.data)

                let dt = new Date( res.data[ "sysTime2" ] ) ;
                cb( true , dt ) ; 
                
            } , 

            fail() {

                cb( false , null ) ; 

            }

        }) ; 

    }

    // 
    public static ttRequest( cb : ( ok : boolean , date : Date )=>void ) {

        /*
            https://developers.weixin.qq.com/miniprogram/dev/api/network/request/wx.request.html
        
        */

        let url = "https://quan.suning.com/getSysTime.do" ; 

        // @ts-ignore
        tt.request({
            url: url , 
            success (res) {
                console.log(res.data)

                let dt = new Date( res.data[ "sysTime2" ] ) ;
                cb( true , dt ) ; 
                
            } , 

            fail() {

                cb( false , null ) ; 

            }

        }) ; 

    }

}

