export class DateTimeMgr {

    public static ins : DateTimeMgr = null ; 

    // 
    public dtStart : Date = null ; 

    // 
    public getNowDateTime() : Date {

        let n = Math.floor( game.totalTime / 1000 ) ; 
        let dt = new Date( this.dtStart ) ; 
        dt.setSeconds( this.dtStart.getSeconds() + n ) ; 
        return dt ; 

        // // 
        // let dt = new Date( this.dtStart ) ;
        // dt.setSeconds( this.dtStart.getSeconds() + 100 ) ; 

        // 
        // let n = ( dt.valueOf() - this.dtStart.valueOf() ) / 1000 ; 

    }


}