import { UiBagCell } from "./UiBagCell";
import { UiBagGrid } from "./UiBagGrid";

// 
export class GridTraverse {

    public static exe( cb : ( cell : UiBagCell )=>void ) {

        let grid = UiBagGrid.ins.grid ; 
        for( let y = 0 ; y < grid.length ; ++y ) {
            let row = grid[ y ] ;
            for( let x = 0 ; x < row.length ; ++x ) {
                let cell = row[ x ] ;

                cb( cell ) ;

            }
        }

    }

    public static exe2( cb : ( cell : UiBagCell )=>boolean ) {

        let grid = UiBagGrid.ins.grid ; 
        for( let y = 0 ; y < grid.length ; ++y ) {
            let row = grid[ y ] ;
            for( let x = 0 ; x < row.length ; ++x ) {
                let cell = row[ x ] ;

                if( cb( cell ) == false ) return ;

            }
        }

    }

}