import { director, Game, game, Node, Prefab, resources, Tween, tween, UIOpacity } from "cc";
import { GameArg } from "../../Game/Init/GameArg";
import { StringUtil } from "../../SL/Common/StringUtil";
import { LevelLoad } from "../../Game/Init/LevelLoad/LevelLoad";
import { CfgLevelGeneralMgr } from "../../Game/World/2.Td/Level/CfgLevelGeneralMgr";

export class UiLoadingScene {

    public static ins = new UiLoadingScene() ; 

    public root : Node ; 

    public opa : UIOpacity ; 

    // 
    public loading : Node ; 

    public init( node : Node ) {

        this.root = node ; 
        this.root.active = false ; 

        this.loading = this.root.getChildByPath( "Loading" ) ; 

        this.opa = this.root.getComponent( UIOpacity ) ; 
        this.opa.opacity = 0 ; 

        director.addPersistRootNode( this.root ) ; 

    }

    public fadeIn( onComplete : ()=>void = null ) {

        this.loading.active = false ; 

        this.root.active = true ; 
        this.opa.opacity = 0 ; 
        tween( this.opa ).to( 0.2 , { opacity : 255 } ).call( ()=>{
            onComplete?.() ; 

            // 
            tween( this.loading ).delay( 0.3 ).call( ()=>{
                this.loading.active = true ; 
            } ).start() ;

        } ).start() ; 

    }

    public fadeOut() {

        tween( this.opa ).to( 0.2 , { opacity : 0 } ).call( ()=>{
            this.root.active = false ; 

            Tween.stopAllByTarget( this.loading ) ; 
            this.loading.active = false ; 

        } ).start() ; 

    }

}

// 
export class SceneTransition {

    public static sceneName : string = null ; 

    public static exe( name : string ) {

        this.sceneName = name ; 

        this.preloadComplete = false ; 

        UiLoadingScene.ins.fadeIn( ()=>{
            this.startCheck() ; 
        } ) ; 
        
        // 
        director.preloadScene( name , ( err )=>{

            if( name == "Game" ) {
                this.loadGameLevel() ;
                return ; 
            }

            this.preloadComplete = true ; 

        } ) ;

    }

    // 
    private static startCheck() {

        // 
        let t = tween( UiLoadingScene.ins.root ).sequence(

            tween( UiLoadingScene.ins.root ).delay( 0.01 ) , 
            tween( UiLoadingScene.ins.root ).call( ()=>{

                if( this.preloadComplete == true ) {
                    t.stop() ; 
                    director.loadScene( this.sceneName , ()=>{
                        UiLoadingScene.ins.fadeOut() ; 
                    } );
                    
                }

            } )

        ).repeatForever().start() ; 

    }

    // 
    private static loadGameLevel() {

        let id = GameArg.ins.mainIdx.toString() ;
        let cfg = CfgLevelGeneralMgr.data.get( id ) ;
        let path = "Game/Level/" + cfg.mapId ;
        resources.load( path , Prefab , ( err , data )=>{

            if( err != null ) {

                console.error( "" ) ;

                // path = "Game/Level/0001_Layer0" ;
                // resources.load( path , Prefab , ( err , data )=>{
                //     LevelLoad.pfLayer0 = data ; 
                //     this.preloadComplete = true ; 
                // } ) ;

                return ;
            }

            LevelLoad.pfMap = data ; 
            this.preloadComplete = true ; 

        } ) ; 

    }

    public static preloadComplete = false ; 

}