import { BagArticleGradeHelper } from "../../../../Bag/Article/BagArticleGradeHelper";
import { AffixType } from "../../Buff/Affix";
import { Tower } from "../Entity/Tower";
import { BuffIncreaseCalc, DataIncrease } from "../../Buff/BuffIncreaseCalc";

// 
export class TowerAttributeUpdate {

    // 
    public static dataIncrease = new DataIncrease() ; 

    public static exe( tower : Tower ) {

        this.setAsOrig( tower ) ; 

        this.grade( tower ) ;

        this.buff( tower ) ;

        this.buff2( tower ) ; 

    }

    // 
    public static setAsOrig( tower : Tower ) {

        let cfgTower = tower.article.cfg.tower ; 

        // 
        let atk = tower.attack ; 
        atk.damage = cfgTower.atkDamage ; 
        atk.cd.max = cfgTower.atkCd ; 
        atk.crit.probability = cfgTower.atkCritProbability ; 
        atk.crit.multiple = cfgTower.atkCritMultiple ; 
        atk.lifeStealing.percent = 0 ; 

    }

    //  + 
    public static grade( tower : Tower ) {

        let cfgAricle = tower.article.cfg ; 
        let cfgGrade = BagArticleGradeHelper.getCfgCurrent( tower.article.cfg.id ) ;
        if( cfgGrade == null ) return ;

        //  
        tower.attack.damage = cfgGrade.attackDamage * cfgAricle.mergeGrade ; 

        //  

        //  


    }

    //  Buff 
    public static buff( tower : Tower ) {


        this.dataIncrease.reset() ; 
        BuffIncreaseCalc.exeBuffs( tower.buff.list , this.dataIncrease ) ; 
        let dataIncr = this.dataIncrease ; 
        
        // 
        if( dataIncr.attackDamageIncreasePercent != 0 ) {

            tower.attack.damage += Math.round( tower.attack.damage * dataIncr.attackDamageIncreasePercent ) ;
        }
        
        // 
        if( dataIncr.attackSpeedIncreasePercent > 0 ) {

            let max = tower.attack.cd.max ; 
            tower.attack.cd.max = Math.round( max / ( 1 + dataIncr.attackSpeedIncreasePercent ) ) ; 
            
        }

        // 
        tower.attack.crit.probability += dataIncr.attackCritProbabilityIncreasePercent ; 

        // 
        tower.attack.crit.multiple += dataIncr.attackCritMultipleIncreasePercent ; 

        //  
        tower.attack.lifeStealing.percent += dataIncr.attackLifeStealingPercent ; 

        // 
        if( dataIncr.hpRegenIncreasePercent > 0 ) {

            let buff = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.HpRegenIncreasePercent && e.isNative == true ; } ) ;
            buff.affix.argN += buff.affix.argN * dataIncr.hpRegenIncreasePercent ; 

        }

        // 
        if( dataIncr.hpRegenSpeedIncreasePercent > 0 ) {

            let buff = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.HpRegenIncreasePercent && e.isNative == true ; } ) ;
            let max = buff.interval.max ; 
            buff.interval.max = Math.round( max / ( 1 + dataIncr.hpRegenSpeedIncreasePercent ) ) ; 

            // CD
            buff.interval.current = buff.interval.max ; 

        }

    }

    //  Buff
    public static buff2( tower : Tower ) {

        let buffs = tower.buff.list ; 
        for( let i = 0 ; i < buffs.length ; ++i ) {
            
            let buff = buffs[ i ] ;
            switch( buff.affix.type ) {
                case AffixType.AttackCritPerAttackNTimesIncrease : {
                    let buff2 = buffs.find( ( e )=>{ return e.affix.type == AffixType.AttackCritPerAttackNTimes ; } ) ;
                    if( buff2 != null ) {
                        buff2.affix.argN += buff.affix.argN ; 
                        if( buff2.affix.argN < 1 ) {
                            buff2.affix.argN = 1 ; 
                        }
                    }
                }
                break ; 
            }
            
        } // end for

    }

}