import { Node } from "cc";
import { UiMoney001 } from "./UiMoney001";
import { UiStamina } from "./UiStamina";
import { UiMoney002 } from "./UiMoney002";

export class UiTop {

    public static ins : UiTop = null ; 

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        // 
        UiMoney001.ins = new UiMoney001() ; 
        UiMoney001.ins.init( this.root.getChildByPath( "Money001" ) ) ;

        UiMoney002.ins = new UiMoney002() ; 
        UiMoney002.ins.init( this.root.getChildByPath( "Money002" ) ) ;

        UiStamina.ins = new UiStamina() ; 
        UiStamina.ins.init( this.root.getChildByPath( "Stamina" ) ) ; 

    }

}