import { AudioClip, AudioSource, Node, resources } from "cc";


/*
    
     resources 
*/ 
export class Music {

    public set enabled(val: boolean) {

        this.m_enabled = val;

        if (val == false) {
            this.stop();
        }
    }

    public get enabled() : boolean {
        return this.m_enabled;
    }

    private m_enabled = true;

    public play(key: string, isLoop: boolean = true): void {

        if( this.enabled == false ) return ;

        /*
            3.8.1 bug  AudioSource stopplay
             stop 
        */ 
        this.stop() ; 

        if( this.audioSrc == null ) {

            var node = new Node() ; 
            this.audioSrc = node.getComponent( AudioSource ) ;
            if( this.audioSrc == null ){
                this.audioSrc = node.addComponent( AudioSource ) ;
            }

        }

        resources.load( key , AudioClip , (err, audio) =>
        { 
            this.audioSrc.clip = audio ; 
            this.audioSrc.loop = isLoop ; 
            this.audioSrc.play( ) ; 

        } );

    }

    public stop(): void {

        if( this.audioSrc == null ) return ;
        this.audioSrc.stop() ; 

    }

    private audioSrc : AudioSource = null ; 



}