import { Vec2 } from "cc";
import { BagArticle } from "../../../../Bag/Article/BagArticle";
import { UiBagCell } from "../../../../Bag/Grid/UiBagCell";
import { UiBagGrid } from "../../../../Bag/Grid/UiBagGrid";
import { Tower } from "../Entity/Tower";
import { TowerMgr } from "../Entity/TowerMgr";
import { CellExpand } from "../../../../Bag/Grid/CellExpand";
import { AuraScopeType } from "../../Buff/AuraScopeType";
import { GridTraverse } from "../../../../Bag/Grid/GridTraverse";


export class TowerAuraScopeExpandRet {

    //  cell
    public cells = new Set< UiBagCell >() ;

    //  
    public article = new Set< BagArticle >() ;

    //  
    public towers = new Array< Tower >() ;

}

// 
export class TowerAuraScopeExpand {

    public static exe( type : AuraScopeType ,  cells : Array< UiBagCell > ) : TowerAuraScopeExpandRet {

        switch( type ) {
            case AuraScopeType.Dir8 : {
                return this.dir8( cells ) ;
            }
            break ; 

            case AuraScopeType.HV : {
                return this.hv( cells ) ; 
            }
            break ; 

            case AuraScopeType.All : {
                return this.all() ; 
            }
            break ; 
        }

    }

    public static all() : TowerAuraScopeExpandRet {

        let ret = new TowerAuraScopeExpandRet() ; 
        ret.towers.push( ... TowerMgr.ins.data ) ;

        GridTraverse.exe( ( cell )=>{

            if( cell.unlocked == false ) return ; 

            ret.cells.add( cell ) ;

            if( cell.article != null ) {
                ret.article.add( cell.article ) ;
            }
            

        } ) ;

        return ret ; 

    }

    // 8 
    public static dir8( cells : Array< UiBagCell > ) : TowerAuraScopeExpandRet {

        let ret = new TowerAuraScopeExpandRet() ; 

        for( let i = 0 ; i < cells.length ; ++i ) {
            let cell = cells[ i ] ; 
            let cells2 = CellExpand.exe8( cell.coord ) ; 

            for( let cell2 of cells2 ) {

                ret.cells.add( cell2 ) ;

                let article = cell2.article ; 
                if( article != null && ret.article.has( article ) == false ) {
                    ret.article.add( article ) ; 

                    //  Tower
                    let tower = TowerMgr.ins.data.find( ( e )=>{ return e.article == article ; } ) ; 
                    if( tower == null ) {
                        // 
                        continue ; 
                    }

                    ret.towers.push( tower ) ; 

                }

            }

        }
        
        
        return ret ; 

    }

    // 
    public static hv( cells : Array< UiBagCell > ) : TowerAuraScopeExpandRet {

        let ret = new TowerAuraScopeExpandRet() ; 

        let size = UiBagGrid.ins.gridSize ;

        let getTower = ( coord : Vec2 )=>{

            let cell = UiBagGrid.ins.getCell( coord ) ;
            if( cell == null ) return ; 

            ret.cells.add( cell ) ;

            let article = cell.article ; 
            if( article == null || ret.article.has( article ) == true ) return ; 

            ret.article.add( article ) ; 
            
            let tower = TowerMgr.ins.data.find( ( e )=>{ return e.article == article ; } ) ; 
            ret.towers.push( tower ) ;

        } ;

        let coordTemp = new Vec2() ; 
        for( let i = 0 ; i < cells.length ; ++i ) {

            let cell = cells[ i ] ;
            let coord = cell.coord ; 

            // 
            coordTemp.x = coord.x ; 
            coordTemp.y = coord.y ; 
            for( coordTemp.x = 0 ; coordTemp.x < size.width ; ++coordTemp.x ) {
                getTower( coordTemp ) ; 
            } // end for
            
            // 
            coordTemp.x = coord.x ; 
            coordTemp.y = coord.y ; 
            for( coordTemp.y = 0 ; coordTemp.y < size.height ; ++coordTemp.y ) {
                getTower( coordTemp ) ; 
            } // end for


        } // end for

        return ret ; 

    }



}