
import { CfgBuff, CfgBuffItem } from "../../../../../Common/Cfg/CfgBuff";
import { CfgTowerItem } from "../../../../../Common/Cfg/CfgTower";
import { FrameSecConvert } from "../../../../../Common/Misc/FrameSecConvert";
import { BagArticle } from "../../../../Bag/Article/BagArticle";
import { BagArticleGradeHelper } from "../../../../Bag/Article/BagArticleGradeHelper";
import { Affix, AffixType } from "../../Buff/Affix";
import { AuraCom } from "../../Buff/AuraCom";
import { Buff } from "../../Buff/Buff";
import { BuffCom } from "../../Buff/BuffCom";
import { BuffFactory } from "../../Buff/BuffFactory";
import { BuffResetMergeGrade } from "../../Buff/BuffResetMergeGrade";
import { CurrentMax } from "../../Support/SimpleStruct/CurrentMax";
import { TowerAttributeUpdate } from "../Misc/TowerAttributeUpdate";
import { AttackCom, AttackTargetSelectionType } from "./AttackCom";
import { Tower } from "./Tower";


export class TowerFactory {

    public static create( article : BagArticle ) : Tower {

        let tower = new Tower() ; 
        tower.article = article ; 
        article.tower = tower ; 

        TowerCfgSet.exe( tower ) ;

        return tower ; 

    }

}


export class TowerCfgSet {

    public static exe( tower : Tower ) {

        let cfg = tower.article.cfg.tower ;

        this.attack( tower , cfg ) ;

        this.buff( tower , cfg ) ; 

        this.buffGrade( tower ) ; 

        TowerAttributeUpdate.grade( tower ) ; 

    }

    public static attack( tower : Tower , cfg : CfgTowerItem ) {

        let atk = tower.attack ; 
        if( atk == null ) {
            atk = tower.attack = new AttackCom() ; 
        }

        atk.bulletId = cfg.atkBulletId ; 

        // CD
        atk.cd.current = 0 ; 
        atk.cd.max = cfg.atkCd ; 

        // 
        atk.damage = cfg.atkDamage ; 

        // 
        atk.targeSelType = cfg.atkTargeSelType ; 

        // 
        atk.crit.probability = cfg.atkCritProbability ; 
        atk.crit.multiple = cfg.atkCritMultiple ; 

        // 
        atk.scatterCount = cfg.atkBulletScatterCount ; 
        

    }

    //  Buff
    public static buff( tower : Tower , cfg : CfgTowerItem ) {

        if( tower.buff == null ) {
            tower.buff = new BuffCom() ; 
        }
        
        if( tower.aura == null ) {
            tower.aura = new AuraCom() ; 
        }
        
        for( let id of cfg.buffs ) {
            this.handeBuff( tower , id ) ;
        } // end for

    }


    //  Buff 
    public static buffGrade( tower : Tower ) {

        let cfgItems = BagArticleGradeHelper.getCfgCurrentItems( tower.article.cfg.id ) ; 
        if( cfgItems == null ) return ; 

        for( let k = 0 ; k < cfgItems.length ; ++k ) {
            let cfg = cfgItems[ k ] ;
            let buffIds = cfg.buffIds ; 
            // Buff
            for( let i = 0 ; i < buffIds.length ; ++i ) {
                let id = buffIds[ i ] ;
                this.handeBuff( tower , id ) ;
            } // end for

        } // end for



    }

    //  Buff
    private static handeBuff( tower : Tower , id : string ) {

        let cfgBuff = CfgBuff.data.get( id ) ;

        if( cfgBuff.isAura == false ) {

            // buff
            let buff = tower.buff.list.find( ( e )=>{ return e.cfg.id == id ; } ) ;
            if( buff == null ) {
                buff = BuffFactory.create( id ) ;
                tower.buff.list.push( buff ) ; 
                buff.isNative = true ; 
            }

            // 
            BuffResetMergeGrade.exe( buff , tower.article.cfg.mergeGrade ) ;

            return ;
        }
        else {
            // aura

            let buff = tower.aura.list.find( ( e )=>{ return e.cfg.id == id ; } ) ;
            if( buff == null ) {
                buff = BuffFactory.create( id ) ;
                tower.aura.list.push( buff ) ; 
                buff.isNative = true ; 
            }

            // 
            BuffResetMergeGrade.exe( buff , tower.article.cfg.mergeGrade ) ;

        }

    }

    public static grade2( tower : Tower ) {


        let cfg = BagArticleGradeHelper.getCfgCurrent( tower.article.cfg.id ) ; 
        if( cfg == null ) return ; 

        let multiple = 1 ;
        switch( tower.article.cfg.mergeGrade ) {
            case 1 : { multiple = 1 ; } break ; 
            case 2 : { multiple = 2 ; } break ; 
            case 3 : { multiple = 4 ; } break ; 
            case 4 : { multiple = 8 ; } break ; 
        }

        if( cfg.hpMaxIncrease > 0 ) {
            // 

            let buff = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.HpMaxIncrease && e.isNative == true ; } ) ;
            if( buff == null ) {
                buff = new Buff() ;
                tower.buff.list.push( buff ) ; 
                buff.isNative = true ; 
                buff.affix = new Affix() ;
                buff.affix.type = AffixType.HpMaxIncrease ;
            }
            
            buff.affix.argN = cfg.hpMaxIncrease * multiple ; 

        }

        // if( cfg.getMomeyPerWave > 0 ) {
        //     // 

        //     let buff = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.GetMomeyPerWave && e.isNative == true ; } ) ;
        //     if( buff == null ) {
        //         buff = new Buff() ;
        //         tower.buff.list.push( buff ) ; 
        //         buff.isNative = true ; 
        //         buff.affix = new Affix() ;
        //         buff.affix.type = AffixType.GetMomeyPerWave ;
        //     }

        //     buff.affix.arg1 = cfg.getMomeyPerWave * multiple ; 

        // }

        if( cfg.hpRegenIncreasePercent > 0 ) {

            //  

            let buff = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.HpRegenIncreasePercent && e.isNative == true ; } ) ;
            if( buff == null ) {
                buff = new Buff() ;
                tower.buff.list.push( buff ) ; 
                buff.interval = new CurrentMax() ; 
                buff.isNative = true ; 
                buff.affix = new Affix() ;
                buff.affix.type = AffixType.HpRegenIncreasePercent ;
            }
            
            buff.interval.setCurrentAndMax( FrameSecConvert.toFrame( 3 ) );
            buff.affix.argN = cfg.hpRegenIncreasePercent * tower.article.cfg.mergeGrade ; 
            
        }

        if( cfg.hpRegenIncrease > 0 ) {
            //  

            let buff = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.HpRegenIncrease && e.isNative == true ; } ) ;
            if( buff == null ) {
                buff = new Buff() ;
                tower.buff.list.push( buff ) ; 
                buff.interval = new CurrentMax() ; 
                buff.isNative = true ; 
                buff.affix = new Affix() ;
                buff.affix.type = AffixType.HpRegenIncrease ;
            }

            buff.interval.setCurrentAndMax( FrameSecConvert.toFrame( 3 ) );
            buff.affix.argN = cfg.hpRegenIncrease * multiple ; 
            

        }

    }


}

