import { Input, instantiate, Label, Node, Prefab, RichText, Sprite, tween, Vec3 } from "cc";
import { GamePause } from "../../../World/2.Td/Flow/GamePause";
import { UiFeatureSelItemList } from "./UiFeatureSelItemList";
import { UiFeatureSelBtnList } from "./UiFeatureSelBtnList";
import { FrameCall } from "../../../World/1.Base/Support/Misc/FrameCall";
import { SoundBtnClickSetter } from "../../../../Common/Audio/SoundBtnClickSetter";
import { SeqScaleShow } from "../../../../Common/Misc/SeqScaleShow";

// 
export class UiFeatureSel {

    public static pf : Prefab ; 

    public static ins : UiFeatureSel = null ; 

    public static create( parent : Node ) : UiFeatureSel {

        let ui = new UiFeatureSel() ; 
        ui.init( instantiate( this.pf ) ) ;

        ui.root.setParent( parent , false ) ;

        return ui ; 

    }

    public root : Node ; 

    public title : Node ; 

    // 
    public list : Node ; 

    // 
    public itemList = new UiFeatureSelItemList() ; 

    // 
    public btnList = new UiFeatureSelBtnList() ; 
    
    public prompt : Node ; 

    public init( node : Node ) {

        this.root = node ; 

        this.title = this.root.getChildByPath( "Title" ) ;

        this.prompt = this.root.getChildByPath( "Prompt" ) ;

        this.btnList.featureSel = this ;
        this.btnList.init( this.root.getChildByPath( "BtnList" ) ) ;

        this.itemList.featureSel = this ; 
        this.itemList.init( this.root.getChildByPath( "List" ) ) ;

        

    }

    public show() {

        this.root.active = true ;

        // 
        GamePause.pause() ; 

        FrameCall.ins.once( ()=>{
            this.showAnim() ; 
        } , 2 ) ;
        

    }

    // 
    public showAnim() {

        let nodes = new Array< Node >() ; 
        for( let item of this.itemList.items ) {
            nodes.push( item.root ) ; 
        }
        nodes.push( this.btnList.root ) ; 
        nodes.push( this.prompt ) ; 
        SeqScaleShow.exe( nodes ) ; 
        
    }

    public refresh() {
        
        this.itemList.refresh() ; 

        this.showAnim() ; 

    }
    

}





