import { Vec2 } from "cc";
import { TileShapeDataParser } from "./TileShapeDataParser";

export class CfgShape {

    public static data = new Map< string , CfgShapeItem >() ; 

}

export class CfgShapeItem {

    public id : string ; 

    // 
    public str : string ; 

    // 
    public coords : Array< Vec2 > ; 

}

// 
export class CfgShapeParser {

    public static parseJson( json : any ) {

        for( let i = 0 ; i < json.length ; ++i ) {
            let row = json[ i ] ;

            let id = row[ "Id" ] ;
            if( id == null || id == "" ) continue ; 
            
            let cfgItem = new CfgShapeItem() ;
            CfgShape.data.set( id , cfgItem ) ;
            cfgItem.id = id ;

            cfgItem.str = row[ "Shape" ] ; 

            cfgItem.coords = TileShapeDataParser.exe( cfgItem.str ) ; 


        } // endf

    }

}
