
import { Role } from "../../Role/Base/Role";
import { RoleGetDamage } from "../../Role/Misc/RoleGetDamage";
import { SkillBase, SkillType } from "../SkillBase";

// 
export class SkillCloseDamage extends SkillBase {

    public constructor() {
        
        super();

        this.type =  SkillType.CloseDamage;
        this.cd.setCurrentAndMax( 60 ) ;

    }

    // 
    public damageVal = 1 ;

    // 
    public targets = new Array< Role >() ; 

}

// 
export class SkillCloseDamageOp {

    // 
    public static tryAdd( role1 : Role , role2 : Role ) {

        let skillbase = role1.skill?.list.find( ( e )=>{ return e.type == SkillType.CloseDamage } ) ;
        if( skillbase == null ) return ;
        let skill = skillbase as SkillCloseDamage ; 

        if( skill.targets.indexOf( role2 ) != -1 ) return ;

        skill.targets.push( role2 ) ;

    }

    // 
    public static tryRemove( role1 : Role , role2 : Role ) {

        let skillbase = role1.skill?.list.find( ( e )=>{ return e.type == SkillType.CloseDamage } ) ;
        if( skillbase == null ) return ;
        let skill = skillbase as SkillCloseDamage ; 

        let idx = skill.targets.indexOf( role2 ) ;
        if( idx == -1 ) return ; 
        skill.targets.splice( idx , 1 ) ;

    }

    // 
    public static exe( skill : SkillCloseDamage ) {

        if( skill.targets.length == 0 ) return ; 

        for( let roleTarget of skill.targets ) {

            console.log( "" ) ;
            RoleGetDamage.exe( roleTarget , skill.damageVal ) ;

        }

        // CD
        skill.cd.setCurrentAsMax() ; 

    }

}