import { Input, instantiate, Node, Sprite, Vec3 } from "cc";
import { UiBagItem } from "./UiBagItem";
import { Account } from "../../../Common/Account/Account";
import { UiBagPrefab } from "./UiBagPrefab";
import { UiBagArticleDetail } from "./ArticleDetail/UiBagArticleDetail";
import { UiMain } from "../Main/UiMain";
import { CfgGeneral } from "../../../Common/Cfg/CfgGeneral";
import { UiBagItemLocked } from "./UiBagItemLocked";
import { SoundBtnClickSetter } from "../../../Common/Audio/SoundBtnClickSetter";

//  
export class UiBagLocked {

    public root : Node ; 

    public items = new Array< UiBagItem >() ;

    public init( node : Node ) {

        this.root = node ; 

        // this.list = this.root.getChildByPath( "List" ) ; 
        

        this.refresh() ; 

    }

    public refresh() {

        this.items.splice( 0 , this.items.length ) ; 
        this.root.removeAllChildren() ; 
        
        let all = CfgGeneral.ins.BagAllArticleIds.slice() ; 

        //  
        let unlocked = Account.ins.bag.unlocked ; 
        let list = all.filter( item => !unlocked.includes(item) ) ;
        //  
        let deploy = Account.ins.bag.deploy ; 
        list = list.filter( item => !deploy.includes(item) )

        for( let i = 0 ; i < list.length ; ++i ) {

            let uiItem = new UiBagItemLocked() ; 
            this.items.push( uiItem ) ;
            uiItem.init( instantiate( UiBagPrefab.ins.itemLocked ) ) ;
            uiItem.root.active = true ; 
            uiItem.root.setParent( this.root , false ) ;
            uiItem.articleId = list[ i ] ;
            uiItem.refresh() ; 

            uiItem.root.on( Input.EventType.TOUCH_END , ()=>{
                this.onClick_Item( uiItem ) ;
            } ) ;

        } // end for

        SoundBtnClickSetter.exe( this.root ) ;
        
    }

    private onClick_Item( uiItem : UiBagItemLocked ) {

        let uiDetail = UiBagArticleDetail.create( UiMain.ins.root ) ;
        uiDetail.articleId = uiItem.articleId ; 
        uiDetail.name.string = uiItem.name.string ; 
        uiDetail.refresh() ; 
        uiDetail.show() ; 
        uiDetail.btnList.setShowModeAsUnlocked() ; 

        let item = uiDetail.root.getChildByPath( "Item" ) ; 
        let itemRoot = instantiate( uiItem.root ) ;
        itemRoot.scale = new Vec3( 1.2 , 1.2 , 1.2 ) ; 
        itemRoot.setParent( item.parent , false ) ;
        itemRoot.position = item.position ; 
        item.destroy() ; 
        let uiItem2 = new UiBagItem() ; 
        uiDetail.item = uiItem2 ; 
        uiItem2.init( itemRoot ) ; 
        uiItem2.name.node.active = false ; 
        uiItem2.articleId = uiItem.articleId ; 
        uiItem2.refresh() ; 

        // 
        uiDetail.btnList.root.active = false ; 
        uiDetail.unlockedInfo.node.active = true ; 
        uiDetail.unlockedInfo.string = uiItem.unlockInfo.string ;

    }

}