import { Input, Label, Node, randomRangeInt } from "cc";
import { UiTray } from "./UiTray";
import { BagTrayAricleGen } from "../../World/2.Td/Bag/BagTrayAricleGen";
import { Ad } from "../../../Common/Ad/Ad";
import { Player } from "../../World/2.Td/Player/Player";

// 
export class UiBagBottom {

    public static ins : UiBagBottom = null ;

    public root : Node ; 

    // 
    public btnStart : Node ; 

    // 
    public btnRefresh : Node ; 

    // 2
    public btnRefreshGrade2 : Node ; 

    // 
    public btnGetGridExpand : UiBtnGetGridExpand ; 

    public init( node : Node ) {

        this.root = node ; 

        this.btnStart = this.root.getChildByPath( "BtnList/btnStart" ) ;
        this.btnStart.on( Input.EventType.TOUCH_END , this.onClick_Start , this ) ; 

        this.btnRefresh = this.root.getChildByPath( "BtnList/btnRefresh" ) ;
        // this.btnRefresh.on( Input.EventType.TOUCH_END , this.onClick_Refresh , this ) ; 

        this.btnRefreshGrade2 = this.root.getChildByPath( "BtnList/btnRefreshGrade2" ) ;
        this.btnRefreshGrade2.on( Input.EventType.TOUCH_END , this.onClick_btnRefreshGrade2 , this ) ; 

        this.btnGetGridExpand = new UiBtnGetGridExpand() ; 
        this.btnGetGridExpand.init( this.root.getChildByPath( "btnGetGridExpand" ) ) ;

    }



    public show() {
        this.root.active = true ; 
        this.showAllBtns( true ) ; 
    }

    public hide() {
        this.root.active = false ; 
    }


    // 
    public showAllBtns( val : boolean = true ) {

        this.btnStart.active = val ; 
        this.btnRefresh.active = val ; 
        this.btnRefreshGrade2.active = val ; 
        this.btnGetGridExpand.root.active = val ; 

    }

    private onClick_Start() {

    }

    private onClick_Refresh() {


    }


    private onClick_btnRefreshGrade2() {

        Ad.ins.rewardShow( ()=>{

            BagTrayAricleGen.ins.genGrade2() ; 
            this.refreshGrade2Count++ ;
            if( this.refreshGrade2Count >= 2 ) {
                this.btnRefreshGrade2.active = false ; 
            }

        } ) ;

    }

    private refreshGrade2Count = 0 ; 

}

//  
export class UiBtnGetGridExpand {

    public root : Node ; 

    public count : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick_btnGetGridExpand , this ) ; 

        this.count = this.root.getChildByPath( "Count" ).getComponent( Label ) ;
        this.count.string = "x3" ;

        this.refresh() ; 

    }

    private refresh() {

        let count = Player.ins.getGridExpandCount ; 
        if( count <= 0 ) {
            this.root.destroy() ; 
            return ; 
        }
        this.count.string = "x" + count ;

    }

    private onClick_btnGetGridExpand() {

        Ad.ins.rewardShow( ()=> {

            // 
            let list = [ "BagExpand_H2" , "BagExpand_V2" ] ; 
            let id = list[ randomRangeInt( 0 , list.length ) ] ;

            UiTray.ins.addArticleById( id , 2 ) ;

            Player.ins.getGridExpandCount-- ; 
            this.refresh() ; 

        } ) ; 

    }

}