import { instantiate } from "cc";
import { UiPopupBase } from "../../../../Common/Ui/UiPopupBase";
import { Node } from "cc";
import { Label } from "cc";
import { Lang } from "../../../../Common/Misc/Lang";
import { UiItemList } from "../../../../Common/Item/UiItemList";
import { ItemData } from "../../../../Common/Item/ItemData";
import { Sprite } from "cc";
import { Input } from "cc";
import { DailyCheckinData, DailyCheckinDataPersist } from "./DailyCheckinData";
import { UiGet } from "../UiGet";
import { CfgDailyCheckin } from "./CfgDailyCheckin";
import { UiMain } from "../../Main/UiMain";
import { ItemGet } from "../../../../Common/Item/ItemGet";
import { UiStamina } from "../../Main/Margin/UiStamina";
import { UiMoney001 } from "../../Main/Margin/UiMoney001";
import { UiMoney002 } from "../../Main/Margin/UiMoney002";
import { Ad } from "db://assets/Script/Common/Ad/Ad";

export class UiDailyCheckin extends UiPopupBase {

    public static create( parent : Node ) : UiDailyCheckin {

        let root = instantiate( this.pf ) ;
        let ui = new UiDailyCheckin() ;
        ui.init( root ) ; 
        ui.root.setParent( parent , false ) ; 
        return ui ; 

    }

    public items = new Array< UiDailyCheckinItem >() ; 

    public btnGet : Node ; 
    public btnGetAd : Node ;
    public claimedToday : Node ; 

    public override init( node : Node ) {

        super.init( node ) ;

        //  

        this.btnGet = this.root.getChildByPath( "BtnList/btnGet" ) ;
        this.btnGet.on( Input.EventType.TOUCH_END , this.onClick_Get , this ) ;

        this.btnGetAd = this.root.getChildByPath( "BtnList/btnGetAd" ) ;
        this.btnGetAd.active = false ; 
        this.btnGetAd.on( Input.EventType.TOUCH_END , this.onClick_GetAd , this ) ;

        this.claimedToday = this.root.getChildByPath( "BtnList/ClaimedToday" ) ;
        this.claimedToday.active = false ; 


        let list = this.root.getChildByPath( "List" ) ;
        for( let i = 0 ; i < list.children.length ; ++i ) {

            let uiItem = new UiDailyCheckinItem() ; 
            uiItem.init( list.children[ i ] ) ;
            this.items.push( uiItem ) ; 

            // 
            uiItem.title.string = Lang.get( "DailyCheckin_DayX" ).replace( "{1}" , ( i + 1 ).toString() ) ;

            // 
            let rewardData = CfgDailyCheckin.data[ i ] ; 
            uiItem.refreshReward( rewardData ) ; 

        } // end for

        this.refresh() ; 

    }

    public refresh() {

        this.refreshBtn() ; 

        this.refreshListClaimState() ; 

    }

    // 
    public refreshBtn() {

        this.btnGet.active = false ; 
        this.btnGetAd.active = false ; 
        this.claimedToday.active = false ; 

        let isTodayCheckin = DailyCheckinData.ins.isTodayCheckin() ; 
        if( isTodayCheckin ) {
            this.claimedToday.active = true ; 
            return ; 
        }

        this.btnGet.active = true ; 
        this.btnGetAd.active = true ; 

    }

    // 
    public refreshListClaimState() {

        let isTodayCheckin = DailyCheckinData.ins.isTodayCheckin() ; 
        let n1 = 0 , n2 = 0 ; 
        if( isTodayCheckin ) {
            // 
            n1 = DailyCheckinData.ins.count % 7 ;
            if( n1 == 0 ) {
                n1 = 7 ;
            }
            n2 = n1 ; 

        }
        else {
            // 
            n1 = DailyCheckinData.ins.count % 7 ; 
            n2 = n1 + 1 ; 

            // 
            this.items[ n1 ].setClaimable() ; 

        }

        // 
        for( let i = 0 ; i < n1 ; ++i ) {
            this.items[ i ].setClaimed() ; 
        } // end for

        // 
        for( let i = n2 ; i < this.items.length ; ++i ) {
            this.items[ i ].setNotClaimable() ; 
        }

    }

    // 
    private onClick_Get() {
        this.get( 1 ) ; 
    }

    private onClick_GetAd() {

        let onSuccess = ()=>{
            this.get( 2 ) ; 
        } ;
        Ad.ins.rewardShow( onSuccess ) ;

    }

    // 
    private get( multiple : number = 1 ) {

        let item = this.items.find( ( e )=>{ return e.claimType == ClaimType.Claimable ; } ) ; 
        if( item == null ) return ; 
        let rewardData = item.rewardData ; 
        if( multiple > 1 ) {
            // 2
            rewardData = rewardData.slice() ;
            for( let i = 0 ; i < rewardData.length ; ++i ) {
                rewardData[ i ].count *= 2 ; 
            } // end for

        }

        // 
        let uiGet = UiGet.create( UiMain.ins.root ) ; 
        uiGet.show() ; 
        uiGet.itemList.refresh( rewardData ) ; 
        // 
        ItemGet.addList( rewardData ) ; 
        
        DailyCheckinData.ins.checkin() ; 
        DailyCheckinDataPersist.save() ; 

        // 
        this.refresh() ; 

    }


}


export class UiDailyCheckinItem {

    // 
    public rewardData : Array< ItemData > = null ; 

    public claimType = ClaimType.None ; 

    public root : Node ; 

    public title : Label ; 

    public claimableFlag : Node ; 

    public claimedFlag : Node ; 

    public tick : Node ; 

    public list = new UiItemList() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.title = this.root.getChildByPath( "Title" ).getComponent( Label ) ; 

        this.claimableFlag = this.root.getChildByPath( "ClaimableFlag" ) ; 

        this.claimedFlag = this.root.getChildByPath( "ClaimedFlag" ) ; 

        this.tick = this.root.getChildByPath( "Tick" ) ; 
        this.tick.active = false ; 

        this.list.init( this.root.getChildByPath( "Reward" ) ) ;


    }

    // 
    public refreshReward( data : Array< ItemData > ) {

        this.rewardData = data ; 
        this.list.refresh( data ) ;

    }

    // 
    public setClaimed() {

        this.claimType = ClaimType.Claimed ; 

        this.tick.active = true ; 
        this.claimedFlag.active = true ; 
        this.claimableFlag.active = false ; 


    }

    // 
    public setClaimable() {

        this.claimType = ClaimType.Claimable ; 

        this.tick.active = false ; 
        this.claimedFlag.active = false ; 
        this.claimableFlag.active = true ; 


    }

    // 
    public setNotClaimable() {

        this.claimType = ClaimType.NotClaimable ; 

        this.tick.active = false ; 
        this.claimedFlag.active = false ; 
        this.claimableFlag.active = false ; 


    }

}

enum ClaimType {

    None , 

    // 
    Claimed , 

    // 
    Claimable , 

    // 
    NotClaimable

}