
import { EnemyGen } from "./EnemyGen";
import { LevelData, WaveData } from "./CfgLevelWaveMgr";
import { EffectFactory } from "../../../Bag/Merge/EffectFactory";
import { UiMain } from "../../../Ui/Main/UiMain";
import { Vec3 } from "cc";


// 
export class WaveCtrl {

    public static ins = new WaveCtrl() ; 

    public onComplete : ()=>void = null ; 

    // 
    public data : WaveData = null ; 

    // 
    public idx : number = null ; 

    // 
    private frames : number = 0 ; 

    // 
    private enable = false ; 

    public reset() {
        this.idx = 0 ; 
        this.frames = 0 ; 
        this.enable = false ; 
    }

    public start() {
        this.reset() ;

        this.enable = true ; 

    }

    public update() {

        if( this.enable == false ) { return ; }

        let items = this.data.items ;
        if( this.idx >= items.length ) {
            return ; 
        }

        let item = items[ this.idx ] ; 
        if( this.frames >= item.triggerTime ) {
            this.idx++ ;

            let role = EnemyGen.genOne( item.genRoleId ) ;
            role.life.hp.setCurrentAndMax( role.life.hp.max * item.hpMaxFactor ) ; 
            role.attack.damage = role.attack.damage * item.attackFactor ; 

            if( this.idx >= items.length ) {
                // 
                this.onComplete?.() ; 
            }

            if( role.cfg.hpBarId == "HpBarBoss" ) {
                let effect = EffectFactory.create( "BossIncoming" ) ; 
                effect.setParent( UiMain.ins.root , false ) ; 
                effect.position = Vec3.ZERO ; 
            }

        }

        this.frames++ ;

    }



}

