import { RoleAnimSpine } from "../Misc/RoleAnimSpine";


export class RoleFactory {

    public static create( id : string ) : Role {

        let role = new Role() ;

        role.id = id ;

        RoleCfgSet.exe( role ) ;

        RoleViewSet.exe( role ) ;

        return role ; 

    }


}


// 
export class RoleCfgSet {

    public static exe( role : Role ) {

        let cfg = CfgRole.data.get( role.id ) ; 

        role.cfg = cfg ; 

        this.attack( role , cfg ) ; 
        this.life( role , cfg ) ; 
        this.move( role , cfg ) ;
        this.skill( role , cfg ) ;
        this.exp( role , cfg ) ;

        role.buff = new BuffCom() ; 

    }

    private static attack( role : Role , cfg : CfgRoleItem ) {

        role.attack = new AttackCom() ;

        role.attack.damage = cfg.attackDamage ; 

        role.attack.cd.current = 0 ; 
        role.attack.cd.max = cfg.attackCd ;

    }

    private static exp( role : Role , cfg : CfgRoleItem ) {

        role.exp = new Exp() ; 
        role.exp.val = cfg.exp ;

    }

    private static life( role : Role , cfg : CfgRoleItem ) {

        role.life = new Life() ; 
        role.life.hp.setCurrentAndMax( cfg.lifeHpMax ) ; 

    }

    private static move( role : Role , cfg : CfgRoleItem ) {

        role.move = new MoveCom() ; 
        role.move.speed = cfg.moveSpeed ; 

    }

    private static skill( role : Role , cfg : CfgRoleItem ) {

        role.skill = new SkillCom() ; 

    }
    
}


// 
export class RoleViewSet {

    public static exe( role : Role ) {


        let root = this.createView( role.cfg ) ; 
        role.view = new RoleView() ; 
        role.view.root = root ; 
        role.view.main = root.getChildByPath( "Main" );

        // 
        role.view.skeleton = role.view.main.getComponent( sp.Skeleton ) ; 

        this.collsion( role ) ;  
       
    }

    // 
    public static createView( cfg : CfgRoleItem ) : Node {

        let path : string = null ;         

        if( cfg.viewId == null ) {
            
            let id = cfg.id ;
            let strs = id.split( "#" ) ; 
            path = strs[ 0 ] + "/" + strs[ 1 ] ;

        }
        else {
            path = cfg.viewId ; 
        }

        let pf = PfMgrRole.pf.getChildByPath( path ) ;

        let root = instantiate( pf ) ;
        return root ; 


    }

    // 
    public static collsion( role : Role ) {

        let root = role.view.root ; 

        let collider = root.getComponentInChildren(Collider2D);
        if( collider == null ) return ;

        role.view.collider = collider ; 
        collider[ "Role" ] = role ; 
        // collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this)
        // CollisionHandler

    }

    static onBeginContact(BEGIN_CONTACT: string, onBeginContact: any, arg2: typeof RoleViewSet) {
        console.log( "" ) ;
    }



}