import { Mask } from "cc";
import { Node } from "cc";
import { UiShopDaily } from "./UiShopDaily";
import { instantiate } from "cc";
import { UiShopMoney001 } from "./UiShopMoney001";

export class UiShop {

    public static ins : UiShop = new UiShop() ; 

    public root : Node ; 

    public sv 

    public init( node : Node ) {

        this.root = node ; 

        let sv = this.root.getChildByPath( "sv" ) ;
        let svView = sv.getChildByPath( "view" );
        svView.getComponent( Mask ).enabled = true ; 
        let content = svView.getChildByPath( "content" ) ;

        let shopDaily = new UiShopDaily() ; 
        shopDaily.init( content.getChildByPath( "Daily" ) ) ; 

        let shopMoney001 = new UiShopMoney001() ; 
        shopMoney001.init( content.getChildByPath( "Money001" ) ) ; 

    }

}

