import { Tower } from "../Entity/Tower";
import { TowerMgr } from "../Entity/TowerMgr";
import { TowerAttack } from "../Attack/TowerAttack";
import { Buff } from "../../Buff/Buff";
import { AffixType } from "../../Buff/Affix";
import { PlayerRole } from "../../../2.Td/Player/PlayerRole";
import { TowerSysBuff } from "./TowerSysBuff";
import { FrameCall, FrameCallDataItem } from "../../Support/Misc/FrameCall";
import { FrameSecConvert } from "../../../../../Common/Misc/FrameSecConvert";


export class TowerSys {

    public static update() {

        for( let t of TowerMgr.ins.data ) {
            this.updateTower( t ) ;
        }

    }

    public static updateTower( tower : Tower ) {

        // 
        this.attack( tower ) ;

        // buff
        TowerSysBuff.update( tower ) ;

    }

    public static attack( tower : Tower ) {

        // CD
        let atk = tower.attack ; 
        if( atk.cd.current > 0 ) {
            atk.cd.current-- ;
            if( atk.cd.current > 0 ) return ;
        }

        if( TowerAttack.canAttack( tower ) == false ) return ;

        // 
        TowerAttack.exe( tower ) ; 

        // CD
        atk.cd.setCurrentAsMax() ;

        let buffAttackContinuous = tower.buff.list.find( ( e )=>{ return e.affix.type == AffixType.AttackContinuous ; } ) ; 
        if( buffAttackContinuous != null ) {

            let item = new FrameCallDataItem() ; 
            item.interval.setCurrentAndMax( FrameSecConvert.toFrame( 0.3 ) ) ; 
            item.times = buffAttackContinuous.affix.argN ;
            item.fn = ()=>{
                if( TowerAttack.canAttack( tower ) == false ) return ;
                TowerAttack.exe( tower ) ;
            } ;
            FrameCall.ins.items.push( item ) ; 

        }


    }


}


