import { Buff } from "./Buff";


/*
    Buff 
    buff 
*/ 
export class BuffResetMergeGrade {

    // 
    public static exe( buff : Buff , mergeGrade : number ) {

        let cfg = buff.cfg ; 

        // 
        let f = this.getMergeGradeFactor( cfg.mergeGradeFactor , mergeGrade ) ; 
        buff.affix.argN = cfg.affixArgN * f ; 

    }

    // 
    public static getMergeGradeFactor ( mergeGradeFactor : Array< number > , mergeGrade : number ) : number {

        if( mergeGradeFactor == null ) {
            return 1 ;
        }

        let f = mergeGradeFactor[ mergeGrade - 1 ] ;
        return f ;

    } ;

}