import { tween, Vec3 } from "cc";
import { BagArticle } from "../Article/BagArticle";
import { EffectFactory } from "./EffectFactory";


export class MergeEffect {

    public static merge( article : BagArticle ) {

        // 
        let effect = EffectFactory.create( "Merge" ) ;
        effect.setParent( article.view.root , false ) ;
        effect.position = Vec3.ZERO ; 

        // 
        let root = article.view.root ; 
        tween( root )
            .to( 0.2 , { scale : new Vec3( 1.3 , 1.3 , 1.3 ) } )
            .to( 0.2 , { scale : new Vec3( 1.0 , 1.0 , 1.0 ) } )
            .start() ; 

    }

}