
// 
export class Stats {

    public static ins : Stats = null ; 

    // key ID
    public data = new Map< string , StatsItem >() ;

    // 
    public damage( id : string , d : number ) {

        // 1
        let strs = id.split( "_" ) ; 
        if( strs.length >= 2 ) {
            id = strs[ 0 ] + "_1" ; 
        }

        let item = this.data.get( id ) ; 
        if( item == null ) {
            item = new StatsItem() ; 
            item.id = id ; 
            Stats.ins.data.set( id , item ) ; 
        }
        item.damage += d ; 

    }

    // 
    public sort() : Array< StatsItem > {

        this.list.splice( 0 , this.list.length ) ; 

        for( let kv of this.data ) {
            let item = kv[1] ;
            this.list.push( item ) ; 
        } // end for

        this.list.sort( ( a , b )=>{
            if( a.damage < b.damage ) return 1 ;
            else if( a.damage > b.damage ) return -1 ; 
            return 0 ; 
        } ) ;

        return this.list ;

    }

    private list = new Array< StatsItem >() ;

}

export class StatsItem {

    public id : string ; 

    public damage = 0 ; 

}