// 
export class MergeGrid {

    //  
    public static tryMerge( article : BagArticle , cells : Array< UiBagCell > ) : boolean {

        if( MergeGridHybrid.tryMerge( article , cells ) == true ) {
            return true ;
        }

        if( this.canMerge( article , cells ) == false ) return false ;
            
        this.merge( article , cells[ 0 ].article ) ;

        return true ; 
  
    }

    /*
        

        article 
    */ 
    public static canMerge( article : BagArticle , cells : Array< UiBagCell > ) {

        let article2 = cells[ 0 ].article ;
        if( article2 == null ) return false ;

        if( article == article2 ) return false ; 

        // 
        if( article2.cells[ 0 ] != cells[ 0 ] ) return false ;

        console.log( " " ) ;

        // 

        let id = MergeDefinition.ins.getMergedId( article.cfg.id , article2.cfg.id ) ;
        return id != null ? true : false ; 


    }
    

    /*
        article 
        article2 
    */ 
    public static merge( article : BagArticle , article2 : BagArticle ) {

        // 
        BagArticleGridPlace.unbind( article ) ; 
        UiTray.ins.removeAndArrange( article ) ;
        article.view.root.destroy() ; 

        // 
        let cells = article2.cells.slice( 0 , article2.cells.length ) ; 
        BagArticleGridPlace.unbind( article2 ) ; 
        article2.view.root.destroy() ; 

        // 
        let id = MergeDefinition.ins.getMergedId( article.cfg.id , article2.cfg.id ) ;
        let articleNew = BagArticleFactory.create( id ) ;
        BagArticleGridPlace.exe( articleNew , cells , false ) ; 
        
        MergeEffect.merge( articleNew );


        // let pf = EffectPfMgr.ins.root.getChildByPath( "Merge" ) ;
        // let node = instantiate( pf ) ;
        // node.setParent( WorldNode.ins.layer2 , false ) ;
        // node.worldPosition = articleNew.view.root.worldPosition ; 
        // let sk = node.getComponent( sp.Skeleton ) ;
        // sk.setCompleteListener( ()=>{
        //     node.destroy() ; 
        // } ) ;

    }

}




