import { Node } from "cc";
import { Label } from "cc";
import { Account } from "../../../../Common/Account/Account";
import { StaminaRecovery } from "../../../../Common/Misc/StaminaRecovery";
import { StringUtil } from "../../../../SL/Common/StringUtil";
import { tween } from "cc";
import { Input } from "cc";
import { UiStaminaBuy } from "../../Popup/UiStaminaBuy";
import { UiMain } from "../UiMain";

// 
export class UiStamina {

    public static ins : UiStamina = null ; 

    public root : Node ; 

    public val : Label ;

    public countdown : Label ; 

    public init( node : Node ) {

        this.root = node ; 

        this.val = this.root.getChildByPath( "val" ).getComponent( Label ) ;
        this.val.string = "0" ; 

        this.countdown = this.root.getChildByPath( "Countdown" ).getComponent( Label ) ;
        this.countdown.string = "0" ; 

        this.refresh() ; 
        
        this.autoRefreshStart() ; 

        this.root.on( Input.EventType.TOUCH_END , this.onClick , this ) ;

    }

    private onClick() {
        
        UiStaminaBuy.create( UiMain.ins.root ).show() ; 

    }

    public autoRefreshStart() {

        tween( this.root ).sequence(

            tween( this.root ).call( ()=>{
                this.refresh() ; 
            } ) , 

            tween( this.root ).delay( 1 ) , 

        ).repeatForever().start() ; 

    }

    public refresh() {

        let a = Account.ins ; 
        let str = a.stamina.current + "/" + a.stamina.max ; 
        this.val.string = str ; 

        // 
        this.refreshCountdown() ; 

    }

    // 
    public refreshCountdown() {

        // 
        let stamina = Account.ins.stamina ; 
        if( stamina.current >= stamina.max ) {
            this.countdown.node.active = false ; 
            return ;
        }

        StaminaRecovery.ins.exe() ; 
        this.countdown.node.active = true ; 

        let countdown = StaminaRecovery.ins.countdown ; 
        let m = Math.floor( countdown / 60 ) ;
        let s = countdown % 60 ;
        let str = StringUtil.fillZero( m , 2 ) + ":" + StringUtil.fillZero( s , 2 ) ; 
        this.countdown.string = str ; 

    }


    
}