

export class DateTimeUtil {

    // 
    public static dateEquals( dt1 : Date , dt2 : Date ) : boolean {

        let y1 = dt1.getFullYear() ;
        let y2 = dt2.getFullYear() ; 
        let m1 = dt1.getMonth()  ;
        let m2 = dt2.getMonth() ; 
        let d1 = dt1.getDate() ; 
        let d2 = dt2.getDate()

        let b = y1 == y2 &&
            m1 == m2 && 
            d1 == d2 ; 

        return b ; 

    }


    public static toString( dt : Date ) {

        /*
            toLocaleString 
             cocos  parse 

            iOS  /  -  Invalid Date 
            https://blog.csdn.net/qq_38974163/article/details/129039155
        */

        let str = dt.getFullYear() + "/" + 
            ( dt.getMonth() + 1 ) + "/" + 
            dt.getDate() + " " + 
            dt.getHours() + ":" + 
            dt.getMinutes() + ":" + 
            dt.getSeconds() ;

        return str ; 

    }
    
    public static toDateString( dt : Date ) : string {

        // "2023-12-18 14:51:28" 
        let str = dt.getFullYear() + "/" + 
            ( dt.getMonth() + 1 ) + "/" + 
            dt.getDate() ;
            // dt.getUTCHours() + ":" + 
            // dt.getUTCMinutes() + ":" + 
            // dt.getUTCSeconds() ;

        return str ; 

    } 

}