export class UiKeyboard {

    public static ins : UiKeyboard = null ; 

    public init() {

        // let root = new Node( "Keyboard" ) ; 
        // root.setParent( UiMain.ins.root ) ;

        // 
        input.on( Input.EventType.KEY_DOWN, this.onKeyDown, this ) ; 
        input.on( Input.EventType.KEY_UP, this.onKeyUp, this ) ; 

    }
    
    private onKeyDown (event: EventKeyboard) {

        switch(event.keyCode) {

            case KeyCode.KEY_A: {

            }
            break;

            case KeyCode.KEY_D : {

                if( UiDebug.ins == null ) {
                    UiDebug.loadAndCreateIns() ; 
                }
                else {
                    UiDebug.ins.root.destroy() ;
                    UiDebug.ins = null ; 

                }
                
            }
            break ;

            case KeyCode.KEY_W : {

            }
            break ; 

            case KeyCode.KEY_S : {

            }
            break ; 
            
        }



    }

    private onKeyUp (event: EventKeyboard) {

        switch(event.keyCode) {

            case KeyCode.KEY_Q : {
                this.onClick_Q() ; 
            }
            break ;

            case KeyCode.KEY_A: {
                this.onClick_A() ; 
            }
            break;
            
        }

    }

    private onClick_Q() {

        PlayerGradeOp.addExp( 150 ) ;

    }

    private onClick_A() {

        BagView.enter() ; 

    }


}