import { EventTouch, Input, instantiate, Node, UIOpacity, UITransform, Vec2, Vec3 } from "cc";
import { UiTray } from "../Main/UiTray";
import { BagArticle } from "../Article/BagArticle";
import { HitTest } from "./HitTest";
import { GridHint } from "../Grid/GridHint";
import { GestureClick } from "./GestureArticleInfo";
import { GridModeTransition } from "../Grid/GridModeTransition";
import { UiMain } from "../../Ui/Main/UiMain";
import { BagArticleGridPlaceByPos } from "../Article/BagArticleGridPlaceByPos";
import { MergeHybridHint, MergeHybridHintMgr } from "../Merge/MergeHybridHint";
import { MergeHint } from "../Merge/MergeHint";
import { TowerAuraHint } from "./AuraHint";
import { WorldNode } from "../../World/1.Base/Support/Misc/WorldNode";
import { UiDialog } from "../../../Common/Ui/UiDialog";
import { Ad } from "../../../Common/Ad/Ad";
import { UiArticleDetail } from "../ArticleDetail/UiArticleDetail";

// 
export class UiBagTouch {

    public static ins : UiBagTouch = null ; 

    //  false 
    public onBeforeArticleSelect : ( article : BagArticle )=>boolean = null ; 

    //  false 
    public onBeforeArticlePlace : ( article : BagArticle )=>boolean = null ; 

    // 
    public onCompleteArticlePlace : ( article : BagArticle )=>void = null ; 



    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 
        this.root.on( Input.EventType.TOUCH_START , this.onTouchStart , this ) ;
        this.root.on( Input.EventType.TOUCH_MOVE , this.onTouchMove , this ) ;
        this.root.on( Input.EventType.TOUCH_END , this.onTouchEnd , this ) ;
        this.root.on( Input.EventType.TOUCH_CANCEL , this.onTouchEnd , this ) ;

        MergeHybridHintMgr.ins = new MergeHybridHintMgr() ; 

    }

    private onTouchStart( event : EventTouch ) {

        let pos = event.getUILocation() ;

        let article = HitTest.hit( pos ) ;
        if( article == null ) {
            return ;
        }

        if( this.onBeforeArticleSelect != null ) {
            if( this.onBeforeArticleSelect( article ) == false ) return ; 
        }

        // 
        if( this.limit.article != null ) {
            if( this.limit.article != article ) return ; 
        }

        if( article.isBagExpand == true ) {
            GridModeTransition.toExpandable() ; 
            this.limit.limit( article ) ;
        }

        this.gestureClick.rest() ;
        this.gestureClick.start( pos ) ; 

        this.articleSel = article ; 

        // 
        if( article.view.root.parent == UiTray.ins.root ) {
            // 
            article.view.root.setSiblingIndex( 999 ) ;
        }
        else {
            // 
            article.view.root.setParent( WorldNode.ins.layer2 , true ) ; 
        }

        MergeHybridHintMgr.ins.clearAllLines() ; 

        MergeHybridHintMgr.ins.hideShape( article ) ; 

        MergeHint.mergeable( article ) ;

    }

    private onTouchMove( event : EventTouch ) {

        if( this.articleSel == null ) return ; 

        // 
        let pos = event.getUILocation() ; 
        let pos2 = new Vec3( pos.x , pos.y ) ;
        this.articleSel.view.root.worldPosition = pos2 ; 

        // 
        GridHint.hintPlaceableClear() ; 
        // 
        let cells = BagArticleGridPlaceByPos.getCellsPlace( this.articleSel ) ;
        if( cells != null ) {
            // 
            GridHint.hintPlaceableShow( cells ) ;

        }

        // 
        this.towerAuraHint.exe( this.articleSel , cells ) ; 

        this.gestureClick.move( pos ) ;

    }

    private onTouchEnd( event : EventTouch ) {

        if( this.articleSel == null ) return ; 

        // 
        let articleSel = this.articleSel ; 
        this.articleSel = null ;

        if( articleSel.view.adTag != null ) {
            // 
            this.adGet( articleSel ) ;
            return ; 
        }

        if( this.onBeforeArticlePlace?.( articleSel ) == false ) {
            return ; 
        } 

        this.articlePlace( articleSel ) ; 

        this.onCompleteArticlePlace?.( articleSel ) ; 

    }

    // 
    private articlePlace( articleSel : BagArticle ) {

        if( this.gestureClick.getVal() ) {
            this.showArticleInfo( articleSel ) ; 
        }

        BagArticleGridPlaceByPos.exe( articleSel ) ; 

        MergeHybridHintMgr.ins.exe() ; 

        this.towerAuraHint.place( ) ; 

        MergeHint.restore( ) ;

    }

    // 
    private articleGotoTray( articleSel : BagArticle ) {

        UiTray.ins.insertArticle( articleSel ) ; 
        
        // 
        GridHint.hintPlaceableClear() ; 

        MergeHybridHintMgr.ins.exe() ; 

        // 
        this.towerAuraHint.clear( ) ; 

        MergeHint.restore( ) ;

    }

    // 
    private adGet( articleSel : BagArticle ) {

        let uiDialog = UiDialog.create( UiMain.ins.root , "" ) ; 
        uiDialog.btnClose.on( Input.EventType.TOUCH_END , ()=>{
            this.articleGotoTray( articleSel ) ; 
        } ) ; 
        uiDialog.btnOK.on( Input.EventType.TOUCH_END , ()=>{

            let cancel = ()=>{
                this.articleGotoTray( articleSel ) ; 
            } ;

            let complete = ()=>{
                articleSel.view.adTag.destroy() ; 
                articleSel.view.adTag = null ; 

                this.articlePlace( articleSel ) ; 

            } ;

            Ad.ins.rewardShow( complete , cancel , cancel ) ;

        } ) ;

    }

    private showArticleInfo( article : BagArticle ) {

        //  
        if( article.isBagExpand ) return ; 

        let uiAricleDetail = UiArticleDetail.ins ;
        uiAricleDetail.root.setParent( UiMain.ins.root );
        uiAricleDetail.show() ; 
        uiAricleDetail.refresh( article ) ;

    }

    public limit = new TouchLimit() ; 

    // 
    private articleSel : BagArticle = null ; 

    private gestureClick = new GestureClick() ; 
    
    public towerAuraHint = new TowerAuraHint() ;

}


export class TouchLimit {

    public limit( a : BagArticle ) {
        this.article = a ;

        // 
        let list = UiTray.ins.articles ; 
        for( let a2 of list ) {
            if( a2 == a ) continue ; 
            let opa = a2.view.root.getComponent( UIOpacity ) ;
            if( opa == null ) {
                opa = a2.view.root.addComponent( UIOpacity ) ;
            }
            opa.opacity = 150 ; 
        }

    }

    // 
    public clear() {

        this.article = null ; 

        let list = UiTray.ins.articles ; 
        for( let a2 of list ) {
            let opa = a2.view.root.getComponent( UIOpacity ) ;
            if( opa == null ) continue ; 
            opa.destroy() ; 
        }

    }

    public article : BagArticle = null ; 

}