import { UITransform, Vec2, Vec3 } from "cc";
import { BagArticle } from "../Article/BagArticle";
import { UiTray } from "../Main/UiTray";
import { GridTraverse } from "../Grid/GridTraverse";

// 
export class HitTest {

    public static hit( posWorld : Vec2 ) {

        let article = HitTest.hitTray( posWorld ) ;
        if( article != null ) return article ;
        
        article = HitTest.hitGrid( posWorld ) ;
        return article ;
        
    }

    // 
    public static hitTray( posWorld : Vec2 ) : BagArticle {

        let pos = new Vec3( posWorld.x , posWorld.y ) ;
        let pos2 = UiTray.ins.root.getComponent( UITransform ).convertToNodeSpaceAR( pos ) ;

        let articles = UiTray.ins.articles ;
        for( let a of articles ) {
            let v = a.view.root.position.clone().subtract( pos2 ) ;
            if( v.lengthSqr() < 100 * 100 ) {
                return a ;
            }
        }

        return null ;

    }

    // 
    public static hitGrid( posWorld : Vec2 ) : BagArticle {

        let lenMin = Number.MAX_VALUE ; ;

        let article : BagArticle = null ; 
        let pos = new Vec3( posWorld.x , posWorld.y ) ; 
        GridTraverse.exe( ( cell )=>{

            // let v = UiTray.ins.root.getComponent( UITransform ).convertToNodeSpaceAR( pos ) ; 
            let v = cell.root.worldPosition.clone().subtract( pos ) ; 
            let len = v.lengthSqr() ; 
            if( Math.abs( v.x ) < 150 && Math.abs( v.y ) < 150 && len < lenMin ) {
                lenMin = len ;
                article = cell.article ; 
            }

        } ) ;

        return article ; 

    }

}